import React from 'react'
import { ReactComponent as IC_REFRESH } from '../../assets/images/common/ic_refresh.svg'

export const Button = ({
    type = 'primary',
    hasLoading = false,
    loading = false,
    full = false,
    disabled = false,
    onClick,
    children,
    style = {},
    transparent = false,
    className = '',
    refs,
    ...props
}) => {
    return (
        <button
            ref={refs}
            className={`alt-btn alt-btn-${type}` + ' ' + className}
            disabled={disabled}
            onClick={onClick}
            style={{
                ...style,
                backgroundColor: transparent ? 'transparent' : `var(--PRIMARY__BG__COLOR)`,
                opacity: disabled ? 0.65 : 1,
                width: full ? '100%' : 'auto',
                color: transparent ? 'transparent' : 'var(--PRIMARY__CONTENT__COLOR)',
            }}
            {...props}>
            {hasLoading ? (
                <span>
                    <IC_REFRESH className={loading ? 'spin-element' : ''} style={{ display: 'block' }} fill={'var(--PRIMARY__CONTENT__COLOR)'}/>
                </span>
            ) : loading && (
                <span>
                    <IC_REFRESH style={{ display: 'block' }} fill={'var(--PRIMARY__CONTENT__COLOR)'}/>
                </span>
            )}
            <span className={hasLoading && children ? 'ml-1' : ''}>{children}</span>
        </button>
    )
}
