import React, { useState, useContext, useRef, useLayoutEffect } from 'react';
import { StoreContext } from '../../store';
import { useTranslation } from 'react-i18next';
import HeaderComponent from '../../components/header';
import { glb_sv, dataCryption, reqFunct, Screens, sendRequest } from '../../utils'
import { dimensions, fontSizes } from '../../styles';
import { useHistory } from "react-router-dom";
import { Button, Row, Form, Input, Checkbox, message } from 'antd';
import { routes } from 'navigation/routes';
import { LeftOutlined, UserOutlined, LockOutlined, EyeTwoTone, EyeInvisibleOutlined } from '@ant-design/icons';
import Text from 'antd/lib/typography/Text';
import {ReactComponent as IconBack} from '../../assets/images/common/ic_back.svg'


const ServiceInfo = {
    CHANGE_PASSWORD_LOGIN: {
        reqFunct: reqFunct.GET_USER_INFO,
        WorkerName: 'ALTxCommon',
        ServiceName: 'ALTxCommon_ChangeInfo',
        Operation: 'U',
        ClientSentTime: '0'
    },
    CHANGE_PASSWORD_ORDER: {
        reqFunct: reqFunct.GET_USER_INFO,
        WorkerName: 'ALTxAccount',
        ServiceName: 'ALTxAccount_0102_1',
        Operation: 'U',
        ClientSentTime: '0'
    }
}

export default function ChangePasswordLogin({ }) {
    let history = useHistory()
    const { type } = history.location.state;
    const { styles } = useContext(StoreContext);
    const { t } = useTranslation();

    const curr_pass_ref = useRef(null)
    const emailRef = useRef(null);
    const phoneRef = useRef(null);
    const fgtPass_SendReqFlag = useRef(false)

    const [curr_pass, setcurr_pass] = useState('');
    const [new_pass, setnew_pass] = useState('');
    const [new_pass_cfm, setnew_pass_cfm] = useState('');

    const [disabled, setDisabled] = useState(true)

    const [showCurPass, setShowCurPass] = useState(false)
    const [showNewPass, setShowNewPass] = useState(false)
    const [showNewAgianPass, setShowNewAgianPass] = useState(false)

    const [spinner, setSpinner] = useState(false)

    const changePassword = () => {
        if (!curr_pass || curr_pass.trim().length < 6 || curr_pass.trim().length > 30) {
            curr_pass_ref.current && curr_pass_ref.current._root.focus()
            message.warning({
                content: t('login_pass_length'),
                className: 'custom-class',
                style: {
                    marginTop: '1vh',
                },
            })
            return
        }
        if (!new_pass || new_pass.trim().length < 6 || new_pass.trim().length > 30) {
            emailRef.current && emailRef.current._root.focus()
            message.warning({
                content: t('chgLoginPass_newCode'),
                className: 'custom-class',
                style: {
                    marginTop: '1vh',
                },
            })
            return
        }
        if (!new_pass_cfm || new_pass_cfm.trim().length < 6 || new_pass_cfm.trim().length > 30) {
            phoneRef.current && phoneRef.current._root.focus()
            message.warning({
                content: t('login_pass_length'),
                className: 'custom-class',
                style: {
                    marginTop: '1vh',
                },
            })
            return
        }
        if (new_pass_cfm !== new_pass) {
            phoneRef.current && phoneRef.current._root.focus()
            message.warning({
                content: t('pass_confirm_not_correct'),
                className: 'custom-class',
                style: {
                    marginTop: '1vh',
                },
            })
            return
        }

        if (fgtPass_SendReqFlag.current) {
            return
        }
        fgtPass_SendReqFlag.current = true
        const userID = glb_sv.objShareGlb.sessionInfo?.userID?.toLowerCase()?.trim();
        if (!userID) return;
        const curpass = dataCryption.encryptString(curr_pass.trim())
        const newpass = dataCryption.encryptString(new_pass.trim());
        const InVal = ['chgpswd', userID, curpass, newpass];
        sendRequest(ServiceInfo.CHANGE_PASSWORD_LOGIN, InVal, changePasswordResultProc, true, changePasswordResultProcTimeout)
        setSpinner(true)
    }

    const changePasswordOrder = () => {
        if (new_pass_cfm !== new_pass) {
            phoneRef.current && phoneRef.current._root.focus()
            message.warning({
                content: t('pass_confirm_not_correct'),
                className: 'custom-class',
                style: {
                    marginTop: '1vh',
                },
            })
            return
        }

        if (fgtPass_SendReqFlag.current) {
            return
        }
        fgtPass_SendReqFlag.current = true
        const userID = glb_sv.objShareGlb['AcntMain']
        if (!userID) return;
        const curpass = dataCryption.encryptString(curr_pass.trim())
        const newpass = dataCryption.encryptString(new_pass.trim());
        const InVal = [userID, curpass, newpass];
        sendRequest(ServiceInfo.CHANGE_PASSWORD_ORDER, InVal, changePasswordResultProc, true, changePasswordResultProcTimeout)
        setSpinner(true)
    }

    const changePasswordResultProcTimeout = () => {
        fgtPass_SendReqFlag.current = false;
        setSpinner(false);
        // navigation.navigate(Screens.ALERT_MODAL, {
        //     title: t('request_hanlde_not_success_try_again'),
        //     content: errmsg,
        //     typeColor: styles.WARN__COLOR
        // })
    }

    const changePasswordResultProc = (reqInfoMap, message) => {
        fgtPass_SendReqFlag.current = false
        setSpinner(false)
        const errmsg = message['Message']
        if (Number(message['Result']) === 0) {
            // navigation.navigate(Screens.ALERT_MODAL, {
            //     title: t('common_notify'),
            //     content: errmsg,
            //     typeColor: styles.WARN__COLOR
            // })
        } else {
            // navigation.navigate(Screens.ALERT_MODAL, {
            //     title: t('common_notify'),
            //     content: errmsg,
            //     typeColor: styles.SUCCESS__COLOR,
            //     linkCallback: () => type === 'change_login' ? RNRestart.Restart() : navigation.pop()
            // })
        }
    }

    useLayoutEffect(() => {
        if (type === 'change_login') {
            if (curr_pass && curr_pass.trim().length > 5 &&
                new_pass && new_pass.trim().length > 5 &&
                new_pass_cfm && new_pass_cfm.trim().length > 5) {
                setDisabled(false)
            } else {
                setDisabled(true)
            }
        } else {
            if (curr_pass && curr_pass.trim().length > 3 && curr_pass.trim().length < 7 &&
                new_pass && new_pass.trim().length > 3 && new_pass.trim().length < 7 &&
                new_pass_cfm && new_pass_cfm.trim().length > 3 && new_pass_cfm.trim().length < 7) {
                setDisabled(false)
            } else {
                setDisabled(true)
            }
        }
    }, [curr_pass, new_pass, new_pass_cfm])

    const onSubmit = (values) => {
        if (type === 'change_login') {
            changePassword()
        } else {
            changePasswordOrder()
        }
    }
    return (
        <div className='menu__layout'>
            <HeaderComponent
                title={type === 'change_login' ? t('common_change_login_pass') : t('common_change_order_pass')}
                iconLeft={[
                    <IconBack  style={{ fontSize: 16 }} onClick={() => history.goBack()} />
                ]}
                iconRight={[]}
            />
            <div style={{ paddingHorizontal: 10 }}>
                <Form
                    name="normal_login"
                    className="login__form"
                    initialValues={{ remember: true }}
                    onFinish={onSubmit}
                >
                    <Form.Item
                        name="curr_pass"
                        rules={[{ required: true, message: 'Please input your Password!' }]}
                        style={{ borderRadius: 5, marginVertical: dimensions.halfVerticalIndent, marginTop: dimensions.verticalIndent }}>

                        <Input.Password
                            autoFocus
                            name='curr_pass'
                            size='large'
                            prefix={<LockOutlined />}
                            style={{ width: '100%' }}
                            placeholder={t('login_pass_curr')}
                            iconRender={visible => (visible ? <EyeTwoTone /> : <EyeInvisibleOutlined />)}
                            onChange={e => setcurr_pass(e.target.value)}

                        />

                        {/* <Icon active name='lock' type='AntDesign' style={{ color: styles.PRIMARY__CONTENT__COLOR }} />
                        <Input
                            style={{
                                ...UIbasic.Text__Medium,
                                color: styles.PRIMARY__CONTENT__COLOR,
                            }}
                            placeholderTextColor={styles.PLACEHODLER__COLOR}
                            textContentType='password'
                            returnKeyType='next'
                            ref={curr_pass_ref}
                            onSubmitEditing={() => emailRef.current._root.focus()}
                            secureTextEntry={!showCurPass}
                            maxLength={type === 'change_login' ? 30 : 6}
                            placeholder={t('login_pass_curr')}
                            value={curr_pass}
                            onChangeText={value => setcurr_pass(value)} /> */}
                    </Form.Item>
                    <Form.Item
                        name="new_pass"
                        rules={[{ required: true, message: 'Please input your Password!' }]}
                        style={{ borderRadius: 5, marginVertical: dimensions.halfVerticalIndent }}>
                        <Input.Password
                            name='new_pass'
                            size='large'
                            prefix={<LockOutlined />}
                            style={{ width: '100%' }}
                            placeholder={t('login_pass_new')}
                            iconRender={visible => (visible ? <EyeTwoTone /> : <EyeInvisibleOutlined />)}
                            onChange={e => setnew_pass(e.target.value)}

                        />
                        {/* <Icon active name='lock' type='AntDesign' style={{ color: styles.PRIMARY__CONTENT__COLOR }} />
                        <Input
                            style={{ ...UIbasic.Text__Medium, color: styles.PRIMARY__CONTENT__COLOR }}
                            placeholderTextColor={styles.PLACEHODLER__COLOR}
                            returnKeyType='next'
                            ref={emailRef}
                            onSubmitEditing={() => new_pass_cfmRef.current._root.focus()}
                            placeholder={t('login_pass_new')}
                            maxLength={type === 'change_login' ? 30 : 6}
                            textContentType='newPassword'
                            secureTextEntry={!showNewPass}
                            value={new_pass}
                            onChangeText={value => setnew_pass(value)} />
                        {showNewPass ?
                            <Icon onPress={() => setShowNewPass(showNewPass => !showNewPass)}
                                type='AntDesign'
                                name='eye' style={{ color: styles.BLUE__COLOR }} color={styles.BLUE__COLOR} /> :
                            <Icon onPress={() => setShowNewPass(showNewPass => !showNewPass)}
                                type='Entypo'
                                name='eye-with-line'
                                style={{ color: styles.PRIMARY__CONTENT__COLOR }} color={styles.PRIMARY__CONTENT__COLOR} />} */}
                    </Form.Item>
                    <Form.Item
                        name="new_pass_cfm"
                        rules={[{ required: true, message: 'Please input your Password!' }]}
                        style={{ borderRadius: 5, marginVertical: dimensions.halfVerticalIndent }}>
                        <Input.Password
                            name='new_pass_cfm'
                            size='large'
                            prefix={<LockOutlined />}
                            style={{ width: '100%' }}
                            placeholder={t('login_pass_new_cfm')}
                            iconRender={visible => (visible ? <EyeTwoTone /> : <EyeInvisibleOutlined />)}
                            onChange={e => setnew_pass_cfm(e.target.value)}

                        />
                        {/* <Icon active name='lock' type='AntDesign' style={{ color: styles.PRIMARY__CONTENT__COLOR }} />
                        <Input
                            style={{ ...UIbasic.Text__Medium, color: styles.PRIMARY__CONTENT__COLOR }}
                            placeholderTextColor={styles.PLACEHODLER__COLOR}
                            ref={phoneRef}
                            placeholder={t('login_pass_new_cfm')}
                            textContentType='newPassword'
                            maxLength={type === 'change_login' ? 30 : 6}
                            secureTextEntry={!showNewAgianPass}
                            value={new_pass_cfm}
                            onChangeText={value => setnew_pass_cfm(value)} />
                        {showNewAgianPass ?
                            <Icon onPress={() => setShowNewAgianPass(showNewAgianPass => !showNewAgianPass)}
                                type='AntDesign'
                                name='eye' style={{ color: styles.BLUE__COLOR }} color={styles.BLUE__COLOR} /> :
                            <Icon onPress={() => setShowNewAgianPass(showNewAgianPass => !showNewAgianPass)}
                                type='Entypo'
                                name='eye-with-line'
                                style={{ color: styles.PRIMARY__CONTENT__COLOR }} color={styles.PRIMARY__CONTENT__COLOR} />} */}
                    </Form.Item>
                    <Form.Item>
                        <Text className='fs__normal' style={{ color: 'var(--PRIMARY)'}}>
                            {type === 'change_login' ? t('password_rule') : t('password_rule_place_order')}
                        </Text>
                    </Form.Item>
                    <Button
                        // disabled={disabled}
                        htmlType='submit'
                        size='large'
                        type={'primary'}
                        block
                        style={{ opacity: disabled ? 0.8 : 1 }}>
                        <Text style={{ fontSize: fontSizes.medium, color: 'white' }}>
                            {t('common_button_change_pass').toLocaleUpperCase()}
                        </Text>
                    </Button>
                </Form>


            </div>
            {/* <Spinner
                visible={spinner}
            /> */}
        </div>
    );
}




















