import { Avatar, Col, Row } from 'antd'
import AvatarImage from 'components/avatar-image'
import BlockFeature from 'components/block-feature'
import { routes } from 'navigation/routes'
import React, { useContext, useEffect, useState } from 'react'
import { useTranslation } from 'react-i18next'
import { useHistory } from 'react-router-dom'
import { StoreContext } from 'store'
import { ReactComponent as IconBack } from '../../assets/images/common/ic_arrow_right.svg'
import { ReactComponent as IconUser } from '../../assets/images/common/ic_user.svg'
import { ReactComponent as Setting } from '../../assets/images/common/setting.svg'
import { StoreTrading } from '../../store-trading'
import { dimensions, fontSizes } from '../../styles'
import { glb_sv, reqFunct, sendRequest } from '../../utils'
import CONFIG from '../exchange/config/config'

const ServiceInfo = {
    GET_LIST_EVENT: {
        reqFunct: reqFunct.GET_LIST_EVENT,
        WorkerName: 'ALTxCommon02',
        ServiceName: 'ALTxCommon02_Event_Mgt',
        Operation: 'Q',
        ClientSentTime: '0',
    },
    AddNewFavList: {
        reqFunct: reqFunct.ADD_NEW_FAV_LIST,
        WorkerName: 'ALTxCommon01',
        ServiceName: 'ALTxCommon01_0820',
        ClientSentTime: '0',
        Operation: 'I',
    },
    AddStockToFavList: {
        reqFunct: reqFunct.ADD_STOCK_TO_FAV_LIST,
        WorkerName: 'ALTxCommon01',
        ServiceName: 'ALTxCommon01_0820',
        ClientSentTime: '0',
        Operation: 'I',
    },
}

export default function SettingsLayout() {
    let history = useHistory()
    const { styles } = useContext(StoreContext)
    const { authFlag } = useContext(StoreTrading)
    const CONFIG_EXCHANGE = CONFIG.getListConfig(routes)
    const { t } = useTranslation()

    const [ListPromotion, setList] = useState([])

    useEffect(() => {
        if (authFlag) {
            getListEvent()
        }
    }, [authFlag])

    const getListEvent = () => {
        const InputParams = ['CHK_EVENT']
        sendRequest(ServiceInfo.GET_LIST_EVENT, InputParams, handleGetListEvent)
    }

    const handleGetListEvent = (reqInfoMap, message) => {
        if (Number(message['Result']) === 0) {
            return
        } else {
            let json = []
            try {
                json = message['Data'] ? JSON.parse(message.Data.replace(/\\n/g, '').replace(/\n/g, '')) : []
            } catch (err) {
                console.log(err)
            }
            if (json.length) {
                setList(json)
            }
        }
    }

    const gotoUserInfo = () => {
        if (authFlag) {
            history.push(routes.USER_INFO.path, { type: 'USER_INFO', state: {} })
        } else {
            history.push(routes.SIGN_IN.path, { type: 'SIGN_IN', state: {} })
        }
    }

    return (
        <div
            className="settings__layout"
            style={{ backgroundColor: 'var(--PRIMARY__BG__COLOR)', height: '100%', overflowY: 'auto' }}
        >
            <Row
                className="settings__userinfo"
                gutter={[16, 8]}
                style={{ width: '100%', backgroundColor: 'var(--PRIMARY__BG__COLOR)' }}
                onClick={gotoUserInfo}
            >
                <Col span={4} className="center__item" style={{ justifyContent: 'flex-start' }}>
                    {authFlag ? (
                        glb_sv.userInfo.avatar ? (
                            <Avatar
                                size={52}
                                style={{ color: 'var(--PRIMARY__BG__COLOR)', backgroundColor: 'var(--PRIMARY)' }}
                            >
                                {authFlag ? glb_sv.objShareGlb['AcntMainNm'].slice(0, 1) : 'L'}
                            </Avatar>
                        ) : (
                                <AvatarImage width={43} style={{ paddingHorizontal: 20 }} />
                            )
                    ) : (
                            <IconUser height={56} />
                        )}
                </Col>
                <Col span={16} style={{ display: 'flex' }}>
                    <span style={{ fontSize: 'var(--fs__xmedium)' }} className="settings__userinfo__username">
                        {authFlag ? glb_sv.objShareGlb['AcntMain'] : t('login')}
                    </span>
                </Col>
                <Col
                    span={4}
                    style={{
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'flex-end',
                    }}
                >
                    <IconBack style={{ color: styles.PRIMARY__CONTENT__COLOR }} />
                </Col>
            </Row>
            {ListPromotion?.length ? (
                <Row
                    wrap={false}
                    gutter={[16, 8]}
                    style={{ width: '100%', backgroundColor: 'var(--PRIMARY__BG__COLOR)', overflow: 'scroll' }}
                >
                    {ListPromotion?.map((item) => (
                        <Col
                            key={item.c1}
                            onClick={() => history.push(routes.NEWS_DETAIL.path, { link: item.c6, title: item.c1 })}
                            className="promotion__list"
                            span={10}
                        >
                            <img
                                src={item.c7}
                                style={{
                                    width: fontSizes.normal + 14,
                                    height: fontSizes.normal + 14,
                                    position: 'absolute',
                                    top: 3,
                                    left: 5,
                                }}
                            />
                            <div style={{ height: 24 }}></div>
                            <span style={{ color: 'white' }}>{item?.c1}</span>
                        </Col>
                    ))}
                </Row>
            ) : null}

            {authFlag && (
                <div>
                    <div style={UI.wrapperBlock}>
                        <Row>
                            <span
                                style={{
                                    fontSize: fontSizes.medium,
                                    fontWeight: 'bold',
                                    color: styles.PRIMARY__CONTENT__COLOR,
                                }}
                            >
                                {t('sb_securities_trading')}
                            </span>
                        </Row>
                        <Row wrap>
                            {CONFIG_EXCHANGE.customer.tabs[0].map((item) => (
                                <Col key={item.title} span={6}>
                                    <BlockFeature item={item} />
                                </Col>
                            ))}
                        </Row>
                    </div>
                    <div style={UI.wrapperBlock}>
                        <Row>
                            <span
                                style={{
                                    fontSize: fontSizes.medium,
                                    fontWeight: 'bold',
                                    color: styles.PRIMARY__CONTENT__COLOR,
                                }}
                            >
                                {t('transaction_cash')}
                            </span>
                        </Row>
                        <Row wrap>
                            {CONFIG_EXCHANGE.customer.tabs[1].map((item) => (
                                <Col key={item.title} span={6}>
                                    <BlockFeature item={item} />
                                </Col>
                            ))}
                        </Row>
                    </div>
                </div>
            )}
            <div style={{ height: 1 }} />
            <Row
                gutter={[16, 8]}
                style={{ width: '100%', backgroundColor: 'var(--PRIMARY__BG__COLOR)' }}
                onClick={() => history.push(routes.MENU.path, { type: 'MENU', state: {} })}
            >
                <Col span={4} style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
                    <Setting fill={styles.ICON__PRIMARY} fillFirst={styles.ICON__PRIMARY} height={56} width={25} />
                </Col>
                <Col span={16}>
                    <div>
                        <span style={{ fontSize: 'var(--fs__medium)' }}>{t('app_settings')}</span>
                    </div>
                    <div>
                        <span style={{ fontSize: 'var(--fs-normal)' }} level={5}>
                            {t('app_setting_notes')}
                        </span>
                    </div>
                </Col>
                <Col
                    span={4}
                    className="center__item"
                    style={{
                        display: 'flex',
                        justifyContent: 'flex-end',
                    }}
                >
                    <IconBack style={{ color: styles.ICON__PRIMARY }} />
                </Col>
            </Row>
        </div>
    )
}

const UI = {
    viewEvent: {
        width: '100%',
        height: dimensions.moderate(100, 0.3),
        marginHorizontal: 8,
        borderRadius: 8,
        padding: 9,
        flexDirection: 'row',
    },
    GroupInput: {
        marginVertical: dimensions.halfVerticalIndent,
        paddingHorizontal: dimensions.halfIndent,
    },
    RowInput: {
        marginVertical: dimensions.vertical(8),
        marginHorizontal: dimensions.moderate(16),
        borderRadius: 12,
    },
    wrapperBlock: {
        background: 'var(--BUTTON__SECONDARY)',
        margin: '0.75rem',
        padding: '0.5rem',
        borderRadius: '1rem',
    },
}
