import React, { useContext, useState, useEffect } from 'react';
import { Col, List } from 'antd'
import { useHistory } from "react-router-dom";
import HeaderComponent from '../../components/header';
import { useTranslation } from 'react-i18next';
import { StoreContext } from '../../store';
import Text from 'antd/lib/typography/Text';
import { glb_sv, reqFunct, sendRequest } from '../../utils';
import IMAGE from '../../utils/constant/image';
import Paragraph from 'antd/lib/typography/Paragraph';

const ServiceInfo = {
    GET_VERSION_SERVER: {
        reqFunct: reqFunct.GET_VERSION_SERVER,
        WorkerName: 'ALTxCommon02',
        ServiceName: 'ALTxCommon02_Token_Mgt',
        Operation: 'Q',
        ClientSentTime: '0'
    }
}

const AboutUsLayout = () => {
    const { t } = useTranslation();
    let history = useHistory()
    const { activeCode, language } = useContext(StoreContext)
    const [data, setData] = useState({});

    useEffect(() => {
        getVersionServer();
    }, [])

    const OpenLink = (url, type) => {
        switch (type) {
            case 'tel':
                window.open('tel:'+url)
                break;
            case 'email':
                window.open('mailto:'+url)
                break;
            default:
                window.open(url, '_blank');
                break;
        }
    };

    const getVersionServer = () => {
        const InputParams = ['CHECK', '']
        sendRequest(ServiceInfo.GET_VERSION_SERVER, InputParams, handleGetVersionServer)
    }

    const handleGetVersionServer = (reqInfoMap, message) => {
        if (Number(message['Result']) === 0) {
            return
        } else {
            try {
                const res = JSON.parse(message.Data.replace(/\\n/g, '').replace(/\n/g, ''))[0]
                setData(res)
            } catch (err) {
                console.log(err)
                return
            }
        }
    }
    return (
        <div className='menu__layout' style={{ backgroundColor: 'var(--PRIMARY__BG__COLOR)', height: '100%' }}>
            <HeaderComponent
                title={t('about') + ' ' + glb_sv.configInfo.shortName}
            />
            <div>
                <div className="about__logo__block center__item">
                    <img src={IMAGE[activeCode].LOGO} style={{ height: '8vh', display: 'block' }} />
                </div>
                <Paragraph className="center__item">{glb_sv.configInfo.shortName} v0.0.12</Paragraph>
                <List>
                    <List.Item onClick={() => OpenLink(data.c10)}>
                        <Col span={6}>
                            <Text>{t('comp_full_name')}</Text>
                        </Col>
                        <Col span={18}>
                            <Text>{data.c6}</Text>
                        </Col>
                    </List.Item>
                    <List.Item onClick={() => OpenLink(data.c7, 'tel')}>
                        <Col span={6}>
                            <Text>{t('hotline')}</Text>
                        </Col>
                        <Col span={18}>
                            <Text>{data.c7}</Text>
                        </Col>
                    </List.Item>
                    <List.Item onClick={() => OpenLink(data.c8, 'email')}>
                        <Col span={6}>
                            <Text>{t('comp_email')}</Text>
                        </Col>
                        <Col span={18}>
                            <Text>{data.c8}</Text>
                        </Col>
                    </List.Item>
                    <List.Item onClick={() => OpenLink('https://www.google.com/maps/search/?api=1&query=' + glb_sv.configInfo.shortName)}>
                        <Col span={6}>
                            <Text>{t('comp_address')}</Text>
                        </Col>
                        <Col span={18}>
                            <Text>{data.c9}</Text>
                        </Col>
                    </List.Item>
                </List>
                <List>
                    <List.Item onClick={() => OpenLink('http://altisss.vn/')}>
                        <Text>{t('privacy_policy')}</Text>
                    </List.Item>
                    <List.Item onClick={() => OpenLink('http://altisss.vn/')}>
                        <Text>{t('term_service')}</Text>
                    </List.Item>
                    <List.Item onClick={() => OpenLink('http://altisss.vn/')}>
                        <Text>{t('data_disclaimer')}</Text>
                    </List.Item>
                    <List.Item onClick={() => OpenLink('https://play.google.com/store/apps/details?id=vn.altisss.mts.android')}>
                        <Text>{t('rate_us')}</Text>
                    </List.Item>
                </List>
                <div className="about__copyright">
                  <Text onClick={() => OpenLink(data.c10)}>
                         {language === 'VI'
                            ? glb_sv.configInfo.copyright
                            : language === 'EN'
                                ? glb_sv.configInfo.copyright_e
                                : glb_sv.configInfo.copyright_cn}
                        <Text style={{
                            color: 'var(--BLUE__COLOR)'
                        }} onClick={() => OpenLink('http://altisss.com/')}> Power by Altisss.vn</Text>
                    </Text>
                </div>
            </div>
        </div>
    )
}

export default AboutUsLayout