import React, { memo, useRef, useEffect } from 'react'
import cloneDeep from 'lodash/cloneDeep'
import isEqual from 'lodash/isEqual'
import F2 from '@antv/f2';
import { FormatNumber } from 'utils';

const ChartBuySell = ({ TP }) => {
  const chart = useRef(null);

  useEffect(() => {
    chart.current = new F2.Chart({
      id: 'chart-buysell',
      pixelRatio: window.devicePixelRatio, // 指定分辨率
      height: 100
    });
    chart.current.scale('price', {
      range: [0, 1],
    });
    chart.current.scale('qty', {
      tickCount: 3
    });
    chart.current.legend(false)
    chart.current.tooltip(false)
    chart.current.source([]);
    chart.current.axis('price', {
      label: function label(text, index, total, arr) {
        return {
          text: formatNumber(text*1),
          fill: '#cacaca'
        };
      },
      grid: function grid(text) {
        return {
          stroke: '#888888',
          lineWidth: 0
        }
      },
      line: {
        stroke: 'transparent',
        lineWidth: 0
      }
    });

    chart.current.axis('qty', {
      position: 'right',
      label: function label(text) {
        return {
          text: FormatNumber(text*1,0,0, 'short'),
          fill: '#cacaca'
        };
      },
      grid: function grid(text) {
        return {
          stroke: '#888888',
          lineWidth: 0
        }
      },
      line: {
        stroke: 'transparent',
        lineWidth: 0
      }
    });
    chart.current.line().position('price*qty')
      .color('type', function (val) {
        if (val === 'buy') {
          return '#0EBE8A';
        } else if (val === 'sell') {
          return '#C11D48';
        }
      }).style({
        lineWidth: 1
      });
    chart.current.area().position('price*qty')
      .color('type', function (val) {
        if (val === 'buy') {
          return '#0EBE8A';
        } else if (val === 'sell') {
          return '#C11D48';
        }
      });

    chart.current.render();
  }, []);

  useEffect(() => {
    if (!TP.length) return;
    const data = cloneDeep(TP).filter(e => e.t132 !== 777777710000 && e.t133 !== 777777710000 && e.t132 !== 777777720000 && e.t133 !== 777777720000)

    const arr = data.map(e => {
      return {
        qty: e.t1321,
        price: e.t132,
        type: 'buy'
      }
    }).concat(data.map(e => {
      return {
        qty: e.t1331,
        price: e.t133,
        type: 'sell'
      }
    }))
    arr.push({
      qty: 0,
      price: data[0] ? (data[0].t132 + data[0].t133) / 2 : 0,
      type: 'buy'
    }, {
      qty: 0,
      price: data[0] ? (data[0].t132 + data[0].t133) / 2 : 0,
      type: 'sell'
    });
    if (chart.current) chart.current.changeData(arr)
  }, [TP])

  return (
    <div style={{ height: 100, backgroundColor: 'var(--PRIMARY__BG__COLOR)', width: '100%' }}>
      <canvas id="chart-buysell"></canvas>
    </div>
  )
}

export default ChartBuySell

function areEqual(prevProps, nextProps) {
  if (isEqual(nextProps.TP, prevProps.TP)) return true
  else return false
}
function formatNumber(n) {
  return String(Math.round(n * 100) / 100).replace(/\B(?=(\d{3})+(?!\d))/g, ',');
}