import { AutoComplete } from 'antd';
import React, { memo, useContext, useEffect, useRef, useState } from 'react';
import { useTranslation } from 'react-i18next';
import styled from 'styled-components';
import { StoreContext } from '../../store';
import { FormatNumber, glb_sv } from '../../utils';
import { isNumber, isEmpty } from 'lodash'

const listKeyOrder = ['02', '03', '04', '06', '07', '08', '15'];
const listNameOrder = ['MP', 'ATO', 'ATC', 'MOK', 'MAK', 'MTL', 'PLO']


const FloatingLabel = styled.label`
    font-size: 0.75rem;
    position: absolute;
    transition: all 0.2s ease-in-out;
    transform-origin: left top;
    left: ${props =>
        props.active ? '12px' : '12px'};
    top: ${props =>
        props.active ? '4px' : '8px'};
    transform: ${props =>
        props.active ? 'translate3d(0, -20%, 0) scale(0.70)' : 'none'};
    color: #bfbfbf;
`;

const InputPrice = ({ stock, sellbuy_tp, id, orderTps, nextFocus, price, priceOrder, onPressEnter, refs }) => {

    const { t } = useTranslation()
    const {
        size,
        fractionPrice
    } = useContext(StoreContext);

    const [focusInput, setFocusInput] = useState(false)

    const [value, setValue] = useState('');
    const [valueText, setValueText] = useState('');
    const [options, setOptions] = useState([]);
    const [OtpSelect, setOtpSelect] = useState({})
    const searchRef = useRef('');

    const [open, setOpen] = useState(false)

    useEffect(() => {
        setValue('');
        setValueText('');
        setOptions([]);
        searchRef.current = ''
    }, [stock, sellbuy_tp])

    useEffect(() => {
        if (priceOrder === '') {
            setValue(fractionPrice ? price / 1000 : price)
            setValueText(fractionPrice ? price / 1000 : price)
        } else if (price === 0 && priceOrder === '') {
            setValue('')
            setValueText('')
        }
    }, [price, priceOrder])



    const onSearch = (text) => {
        if (fractionPrice) {
            let name = text.replace(/[^0-9.]+/g, '');
            if (isNaN(Number(name))) name = name.substring(0, name.length - 1)
            searchRef.current = name;
            setValue(name);
            setValueText(name);
        } else {
            const name = Number(text.replace(/[^0-9]/g, '')) || '';
            searchRef.current = name;
            setValue(name);
            setValueText(name);
        }
    };

    const onSelect = (data, opt) => {
        console.log(data, opt)
        setOtpSelect(opt)
        if (listKeyOrder.includes(data)) {
            setValue(opt.label);
            setValueText(opt.label);
        } else {
            setValue(data);
            setValueText(data);

        }
        setOpen(false);

    };

    const onChange = (data, opt) => {
        console.log("onChange ", data, opt)
        if (isEmpty(opt)) {
            setOtpSelect({})
            setValue(data)
        } else {
            setValue(opt.label);
        }
    };

    const onFocus = () => {
        setFocusInput(true)
        setOpen(true)
        console.log('orderTps', orderTps)
        const list = orderTps.map(e => ({ value: e.key, label: e.name }))
        setOptions(list)
    }

    const onBlur = () => {
        setOpen(false);
        console.log(OtpSelect, value)
        if (!isEmpty(OtpSelect)) {
            nextFocus(OtpSelect);
        } else {
            nextFocus(value)
        }


    }

    console.log(FormatNumber(value))
    return (<>
        <AutoComplete
            ref={refs}
            dropdownClassName="input-price-placeorder"
            className="place_order_layout"
            value={focusInput ? valueText : (isNumber(Number(value)) && value ? (
                fractionPrice ? value : FormatNumber(value)
            ) : (value || '0'))}
            options={options}
            style={{ width: '100%' }}
            onSelect={onSelect}
            onSearch={onSearch}
            onChange={onChange}
            onFocus={onFocus}
            onBlur={onBlur}
            id={id}
            open={open}
            onInputKeyDown={e => {
                if (e.key === 'Enter') {
                    if (!listNameOrder.includes(value)) nextFocus && nextFocus(fractionPrice ? value * 1000 : value);
                    requestAnimationFrame(() => onPressEnter())
                }
            }}
        />
        {/* <FloatingLabel className='cursor-text place_order_layout' active={!!valueText} onClick={() => glb_sv.focusInput(id)}>
            {valueText ? t('price') : ''}
        </FloatingLabel> */}
    </>
    )
}

export default memo(InputPrice)