import React, { useContext, useRef, useCallback, useState, useEffect } from 'react';
import { useHistory } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import { Tabs, DefaultTabBar } from 'rmc-tabs';
import { Spin } from 'antd';
import { StoreTrading } from 'store-trading';
import { StoreContext } from 'store';
import Account from 'components/account';
import NotAuthView from 'components/notAuth-view';
import AssetInfo from './asset-info';
import StockInfo from './stock-info';
import DebtInfo from './debt-info';
import { dimensions, fontSizes } from 'styles';
import { eventList, FormatNumber, glb_sv, reqFunct, sendRequest } from 'utils';
import preventCompanyRender from 'utils/checkCompanyApplyFunct';

const ServiceInfo = {
    GET_ASSETS_TOTAL_INFO: {
        reqFunct: reqFunct.GET_ASSETS_TOTAL_INFO,
        WorkerName: 'FOSqAsset',
        ServiceName: 'FOSqAsset_1801_1',
        Operation: 'Q',
        ClientSentTime: '0',
    },
    GET_ASSETS_STOCK_LIST: {
        reqFunct: reqFunct.GET_ASSETS_STOCK_LIST,
        WorkerName: 'FOSqAsset',
        ServiceName: 'FOSqAsset_1801_1',
        Operation: 'Q',
        ClientSentTime: '0',
    },
}

const Asset = () => {
    const { styles } = useContext(StoreContext);
    const { authFlag, userInfo } = useContext(StoreTrading);

    const { t } = useTranslation();
    const history = useHistory();

    const tabBarProps = useRef(null);
    const tabRef = useRef(0);
    const [currentTabs, setCurrentTabs] = useState(0);

    const [assetInfo, setAsset] = useState({})
    const [isAllSub, setAllSub] = useState(true)
    const isAllSubRef = useRef(false)
    const mounted = useRef(true)

    const [dataStock, setDataStock] = useState([])
    const tempList = useRef([])
    const [hiddenFinanceInfo, setHiddenFinanceInfo] = useState(false)

    const [, updateState] = React.useState();
    const forceUpdate = useCallback(() => updateState({}), []);
    const [profitLostValue, setProfitLostValue] = useState(0);
    const [profitLostRatio, setProfitLostRatio] = useState(0);
    const [isLoadingAsset, setIsLoadingAsset] = useState(false);
    const [isLoadingStock, setIsLoadingStock] = useState(false);

    // Kiểm tra user có đăng kí thông tin tài chính
    useEffect(() => {
        if (preventCompanyRender(['023', '036'])) {
            setHiddenFinanceInfo(false)
        } else {
            setHiddenFinanceInfo(true)
        }
    }, [])

    useEffect(() => {
        mounted.current = true
        if (!isAllSubRef.current) {
            isAllSubRef.current = true
            handleRefreshData()
        } else {
            setAllSub(false)
            isAllSubRef.current = false
            handleRefreshData()
        }

        const commonEvent = glb_sv.commonEvent.subscribe((msg) => {
            if (msg.type === eventList.LOGIN_SUCCESS) {
                handleRefreshData()
            }
        })

        return () => {
            mounted.current = false
            commonEvent.unsubscribe()
        }
    }, [userInfo, isAllSub])

    const getAssetsTotalInfo = () => {
        setIsLoadingAsset(true);
        if (!mounted.current) return
        const { actn_curr, sub_curr } = userInfo
        const InputParams = ['01', actn_curr, isAllSubRef.current && userInfo['sub_list'].length > 1 ? '%' : sub_curr]
        // const InputParams = ['01', actn_curr, sub_curr]
        sendRequest(
            ServiceInfo.GET_ASSETS_TOTAL_INFO,
            InputParams,
            getAssetsTotalInfoResult,
            true,
            getAssetsTotalInfoTimeout
        )
        setAsset({})
    }

    const getAssetsTotalInfoTimeout = () => setIsLoadingAsset(false);

    const getAssetsTotalInfoResult = (reqInfoMap, message) => {
        if (!mounted.current) return
        if (Number(message['Result']) === 0) {
            setIsLoadingAsset(false);
            return
        } else {
            try {
                let jsondata = message['Data'] ? JSON.parse(message['Data'])[0] : {}
                setAsset(jsondata)
                setIsLoadingAsset(false);
            } catch (err) {
                console.log('getAssetsTotalInfoResult', err)
            }
        }
    }

    const getAssetsStockList = () => {
        setIsLoadingStock(true);
        if (!mounted.current) return
        const { actn_curr, sub_curr } = userInfo
        const InputParams = ['02', actn_curr, isAllSubRef.current && userInfo['sub_list'].length > 1 ? '%' : sub_curr]
        // const InputParams = ['02', actn_curr, sub_curr]
        sendRequest(
            ServiceInfo.GET_ASSETS_STOCK_LIST,
            InputParams,
            getAssetsStockListResult,
            true,
            getAssetsStockListTimeout,
            '',
            0,
            'equal_service'
        )
        tempList.current = []
        setDataStock([])
    }

    const getAssetsStockListTimeout = () => setIsLoadingStock(false)

    const getAssetsStockListResult = (reqInfoMap, message) => {
        if (Number(message['Result']) === 0) {
            setIsLoadingStock(false)
            return
        } else {
            let jsondata = []
            try {
                jsondata = message['Data'] ? JSON.parse(message['Data']) : []
                const costPrice = jsondata.reduce((total, item) => total + Number(item.c18), 0);
                const valuePrice = jsondata.reduce((total, item) => total + Number(item.c16), 0);
                const profit_loss_value = valuePrice - costPrice;
                const profit_loss_ratio = profit_loss_value / costPrice * 100;
                setProfitLostValue(profit_loss_value);
                setProfitLostRatio(profit_loss_ratio);
            } catch (err) {
                console.log('getAssetsStockListResult', err)
                return
            }
            tempList.current = tempList.current.concat(jsondata)
            if (Number(message['Packet']) <= 0) {
                setDataStock(tempList.current)
                setIsLoadingStock(false)
            }
        }
    }

    const changeAllSub = (value) => {
        setAllSub(value)
        isAllSubRef.current = !value
    }

    const handleRefreshData = () => {
        getAssetsTotalInfo()
        getAssetsStockList()
    }

    const listTabRender = [
        { title: t('asset_tab'), key: 0 },
        { title: t('debt'), key: 1 },
    ]

    const changeTab = value => {
        if (tabRef.current === value) return;
        setCurrentTabs(value);
        tabRef.current = value;
    }

    const renderContent = tab => {
        const { key } = tab;
        if (key === 0) {
            return (
                <div className='mt-2'>
                    <Spin spinning={isLoadingAsset}>
                    <AssetInfo assetInfo={assetInfo} />
                    </Spin>
                    <div className="ant-divider" role="separator" style={{backgroundColor: 'var(--DIVIDER__COLOR)', margin: '5px 0'}}></div>
                    <Spin spinning={isLoadingStock}>
                    <StockInfo stockInfo={dataStock} onRefresh={handleRefreshData} />
                    </Spin>
                </div>
            )
        }
        if (key === 1) {
            return (
                <div className='mt-2'>
                    <Spin spinning={isLoadingAsset}>
                    <DebtInfo debtInfo={assetInfo} />
                    </Spin>
                </div>
            )
        }
    }

    if (!authFlag) {
        return <NotAuthView />
    }
    return (
        <div className='mt-2 mb-2' style={{ backgroundColor: 'var(--PRIMARY__BG__COLOR)', paddingRight: 16, paddingLeft: 16, overflowX: 'scroll' }}>
            <div style={{ overflow: 'hidden' }}>
                <Account showSubAll={true} isAll={isAllSub} changeAllSub={changeAllSub} />
            </div>
            <div className=''>
                <DefaultTabBar
                    {...tabBarProps.current}
                    activeTab={currentTabs}
                    page={2}
                    tabBarBackgroundColor='var(--PRIMARY__BG__COLOR)'
                    tabBarTextStyle={{
                        fontSize: fontSizes.normal,
                        lineHeight: fontSizes.normal + 'px',
                        paddingTop: dimensions.verticalIndent,
                        paddingBottom: dimensions.verticalIndent,
                        color: 'var(--PRIMARY__CONTENT__COLOR)'
                    }}
                />
                <Tabs tabs={listTabRender}
                    tabBarTextStyle={{
                        padding: 0
                    }}
                    renderTabBar={(props) => {
                        if (!tabBarProps.current) { // diff?
                            tabBarProps.current = props;
                            setTimeout(() => {
                                forceUpdate();
                            });
                        }
                        return null;
                    }}
                    page={currentTabs}
                    onChange={(_tab, index) => changeTab(index)}
                >
                    {renderContent}
                </Tabs>
            </div>
            {/* <div className='profit_loss flex flex-direction-row'
                style={{ margin: '5px 0', position: 'sticky', bottom: 0, background: profitLostValue > 0 ? 'var(--UP__COLOR)' : 'var(--ERROR__COLOR)', borderRadius: 8, padding: '10px' }}>
                <span className='flex-2' style={{color: '#fff'}}>{t('cal_profit_loss')}</span>
                <span className='flex-3 text-right'
                    style={{ color: profitLostValue > 0 ? '#fff' : '#fff' }}>
                    {FormatNumber(profitLostValue)} <span style={{ fontSize: fontSizes.verySmall }}>({FormatNumber(profitLostRatio, 2)}%)</span>
                </span>
            </div> */}
        </div>
    )
}

export default React.memo(Asset);