import React, { useEffect, useState, useContext, useRef } from "react";
import { StoreContext } from "../../store";
import { useTranslation } from "react-i18next";
import { glb_sv, socket_sv, STORE_KEY, eventList } from "../../utils";
import ModalAddStock2FavList from "../modal-add-stock-fav";
import {
    CloseCircleFilled,
    SearchOutlined,
    PlusCircleFilled,
    CheckCircleFilled,
} from "@ant-design/icons";
import { SearchBar } from "antd-mobile";
import { Button } from "antd";
import { useHistory } from "react-router-dom";
import { routes } from "navigation/routes";
import { dimensions, fontSizes } from "styles";

export default function SearchStock() {
    const history = useHistory();
    const { actionType, changeStock, activeList } = history.location.state;

    const { language, recentStkList, setRecentStkList } = useContext(
        StoreContext
    );

    const { t } = useTranslation();
    const [seach, setSearch] = useState("");
    const [listStock, setListStock] = useState([]);
    const [isModalVisible, setModal] = useState(false);
    const timeOutSearch = useRef(null);
    const [selectStock, setSelectStock] = useState("");

    const [recentList, setRecentList] = useState([]);
    const [activeListHIS, setActiveListHIS] = useState({})
    const searchRef = useRef(null);

    useEffect(() => {
        searchRef.current.focus()
        const stateHist = history?.location?.state || {};
        setActiveListHIS(stateHist);
        if (glb_sv.mrk_stklist.length) {
            if (glb_sv.mrk_stklist_lang !== language) loadStkList();
            else getListRencent();
        } else {
            loadStkList();
        }
        const commonEvent = glb_sv.commonEvent.subscribe((msg) => {
            if (msg.type === eventList.CHANGE_LIST_STOCK) {
                getListRencent();

                if (!seach) return;
                const newList = glb_sv.mrk_stklist
                    .filter(
                        (e) => e.t55.includes(seach) || seach.toUpperCase().includes(e.t55)
                    )
                    .slice(0, 30)
                    .sort(function (a, b) {
                        if (a.t55 > b.t55) return -1;
                        if (a.t55 < b.t55) return 1;
                        return 0;
                    });
                const newList1 = [];
                newList.forEach((element) => {
                    const obj = glb_sv.mrk_stklist.find((e) => e.t55 === element.t55);
                    if (obj) {
                        const cloneObj = { ...obj };
                        glb_sv.allListFav.forEach((item) => {
                            if (item.ListStock.some((temp) => temp === cloneObj.t55)) {
                                cloneObj.isFav = true;
                                if (cloneObj.ExistNameFav) {
                                    cloneObj.ExistNameFav += ", " + item.c2;
                                } else cloneObj.ExistNameFav = item.c2;
                            }
                        });
                        newList1.push(cloneObj);
                    }
                });
                setListStock(newList1);
            }
            if (msg.type === eventList.REQ_GET_MKT_INF_FIRST) {
                if (glb_sv.mrk_stklist.length) {
                    if (glb_sv.mrk_stklist_lang !== language) loadStkList();
                    else getListRencent();
                } else {
                    loadStkList();
                }
            }
        });
        const eventMarket = glb_sv.eventMarket.subscribe((msg) => {
            if (msg.type === eventList.REQ_RE_GET_MKT_INF) {
                if (glb_sv.mrk_stklist.length) {
                    if (glb_sv.mrk_stklist_lang !== language) loadStkList();
                    else getListRencent();
                } else {
                    loadStkList();
                }
            }
        });
        return () => {
            commonEvent.unsubscribe();
            eventMarket.unsubscribe();
        };
    }, []);

    const loadStkList = async () => {
        const domain = socket_sv.url_public[socket_sv.url_public_index];
        const url = [
            domain +
            "/assets/STOCK_INFO.OTS.01" +
            (language === "VI" ? "" : "." + language) +
            ".json",
            domain +
            "/assets/STOCK_INFO.OTS.03" +
            (language === "VI" ? "" : "." + language) +
            ".json",
            domain +
            "/assets/STOCK_INFO.OTS.05" +
            (language === "VI" ? "" : "." + language) +
            ".json",
        ];
        const promises = url.map(async (pageNo) => {
            const req = await fetch(pageNo, {
                headers: {
                    "Content-Type": "application/json",
                    Accept: "application/json",
                },
            });
            return req.json();
        });
        const data = await Promise.all(promises).catch(function (err) {
            console.log("err loadStkList", err);
        });
        if (data && data.length === 3) {
            glb_sv.IndexMarket["HSX|I|HSXIndex"].STOCK_INFO = data[0];
            glb_sv.IndexMarket["HNX|I|HNXIndex"].STOCK_INFO = data[1];
            glb_sv.IndexMarket["HNX|I|HNXUpcomIndex"].STOCK_INFO = data[2];
            glb_sv.mrk_stklist = data[0].concat(data[1].concat(data[2]));
            glb_sv.mrk_stklist_lang = language;
            getListRencent();
            getListStock(seach);
        } else {
            console.log("err loadStkList");
        }
    };

    const getListRencent = () => {
        const newList = [];
        recentStkList.forEach((element) => {
            const obj = glb_sv.mrk_stklist.find((e) => e.t55 === element);
            if (obj) {
                const cloneObj = { ...obj };
                glb_sv.allListFav.forEach((item) => {
                    if (item.ListStock.some((temp) => temp === cloneObj.t55)) {
                        cloneObj.isFav = true;
                        if (cloneObj.ExistNameFav) {
                            cloneObj.ExistNameFav += ", " + item.c2;
                        } else cloneObj.ExistNameFav = item.c2;
                    }
                });
                newList.push(cloneObj);
            }
        });
        setRecentList(newList);
        return newList;
    };

    const getListStock = (namee) => {
        const name = namee.toUpperCase();
        setSearch(name);
        if (name === "") {
            if (timeOutSearch.current) clearTimeout(timeOutSearch.current);
            setListStock([]);
            return;
        }
        if (timeOutSearch.current) clearTimeout(timeOutSearch.current);
        timeOutSearch.current = setTimeout(() => {
            const newList = glb_sv.mrk_stklist
                .filter(
                    (e) => e.t55.includes(name) || name.toUpperCase().includes(e.t55)
                )
                .slice(0, 30)
                .sort(function (a, b) {
                    if (a.t55 > b.t55) return -1;
                    if (a.t55 < b.t55) return 1;
                    return 0;
                });

            const newList1 = [];
            newList.forEach((element) => {
                const obj = glb_sv.mrk_stklist.find((e) => e.t55 === element.t55);
                if (obj) {
                    const cloneObj = { ...obj };
                    console.log('glb_sv.allListFav', glb_sv.allListFav)
                    glb_sv.allListFav.forEach((item) => {
                        if (item.ListStock.some((temp) => temp === cloneObj.t55)) {
                            cloneObj.isFav = true;
                            if (cloneObj.ExistNameFav) {
                                cloneObj.ExistNameFav += ", " + item.c2;
                            } else cloneObj.ExistNameFav = item.c2;
                        }
                    });
                    newList1.push(cloneObj);
                }
            });
            setListStock(newList1);
        }, 300);
    };

    const StockItem = ({ item }) => {
        return (
            <div
                className="flex list"
                onClick={() => handleStock(item)}
            >
                <div className="flex flex-direction-column justify-content-start flex-2">
                    <span
                        className="flex fs__medium fw__medium"
                        style={{ color: "var(--PRIMARY__CONTENT__COLOR)" }}
                    >
                        {item.t55}&ensp;&ensp;
            <span
                            className="fs-normal fw__normal"
                            style={{
                                color: "var(--SECOND__CONTENT__COLOR)",
                                lineHeight: fontSizes.medium + "px",
                            }}
                        >
                            {getExchange(item.U10)}
                        </span>
                    </span>
                    <span
                        className="fs-normal"
                        style={{ color: "var(--SECOND__CONTENT__COLOR)", overflow: 'hidden', textOverflow: 'ellipsis', width: 'calc(75vw - 9px)', whiteSpace: 'nowrap' }}
                    >
                        {item.U9}
                    </span>
                </div>
                <div
                    onClick={() => onPressAddStockToList(item.t55)}
                    className="flex flex-1 flex-direction-column align-items-end justify-content-center"
                >
                    {!item.isFav ? (
                        <Button
                            type="ghost"
                            inline
                            className="am-button-borderfix"
                            style={{
                                borderRadius: "50px",
                                border: "1px solid #06923D",
                                color: "#06923D",
                                width: "86px"
                            }}
                        >
                            {t('add')}
                        </Button>
                    ) : (
                        // <Button className="am-button-borderfix" type="primary" size="middle">Thêm</Button>
                        // <PlusCircleFilled
                        //   className='flex align-self-end'
                        //   style={{ color: 'var(--SECOND__CONTENT__COLOR)', fontSize: 30 }} />
                        <Button
                            type="primary"
                            size="middle"
                            inline
                            style={{
                                borderRadius: "50px",
                                border: "1px solid #06923D",
                                color: "#FFFFFF",
                                background: "#06923D",
                                width: "86px"
                            }}
                        >
                            {t('added')}
                        </Button>
                        // <CheckCircleFilled
                        //   className='flex align-self-end'
                        //   style={{ color: 'var(--SECOND__CONTENT__COLOR)', fontSize: 30 }} />
                    )}
                    {/* {item.isFav ? (
            <span
              className='fs__tiny text-right'
              style={{
                color: 'var(--SECOND__CONTENT__COLOR)',
              }}>
              {t('exists')}
            </span>
          ) : null} */}
                </div>
            </div>
        );
    };

    const handleStock = (item) => {
        const newList = recentStkList.filter((e) => e !== item.t55);
        if (newList.length >= 5) {
            newList.pop();
        }
        newList.unshift(item.t55);
        localStorage.setItem(STORE_KEY.RECENT_STOCK_LIST, JSON.stringify(newList));
        setRecentStkList(newList);

        const newList1 = [];
        newList.forEach((element) => {
            const obj = glb_sv.mrk_stklist.find((e) => e.t55 === element);
            if (obj) {
                const cloneObj = { ...obj };
                glb_sv.allListFav.forEach((item) => {
                    if (item.ListStock.some((temp) => temp === cloneObj.t55)) {
                        cloneObj.isFav = true;
                        if (cloneObj.ExistNameFav) {
                            cloneObj.ExistNameFav += ", " + item.c2;
                        } else cloneObj.ExistNameFav = item.c2;
                    }
                });
                newList1.push(cloneObj);
            }
        });
        setRecentList(newList1);

        if (actionType === "PLACE_ORDER") {
            // changeStock(item.t55);
            history.push(routes.PLACE_ORDER, { actionType: "SEARCH_STOCK", stockValue: item.t55 });
            return;
        }
        history.push(routes.STOCK_INFO, { stockCode: item.t55 });
    };

    const getExchange = (type) => {
        if (type === "01") return "HOSE";
        if (type === "03") return "HNX";
        if (type === "05") return "UPCOM";
    };

    const resetData = () => {
        setSearch("");
        setListStock([]);
    };

    const ListEmpty = () => {
        return (
            <div style={{ justifyContent: "center", flex: 1, margin: 10 }}>
                <span
                    style={{
                        textAlign: "center",
                        color: "var(--PRIMARY__CONTENT__COLOR)",
                    }}
                >
                    {t("no_match_result")}
                </span>
            </div>
        );
    };

    const onPressAddStockToList = (stk) => {
        glb_sv.commonEvent.next({ type: eventList.MSG_ADD_NEW_STK_2FAV, data: { stk, keyFlv: activeList?.c1 } })
        setSelectStock(stk);
        history.goBack();
        // setModal(true);
        return;
    };

    const onChangeStatus = () => {
        getListRencent();

        if (!seach) return;
        const newList = glb_sv.mrk_stklist
            .filter(
                (e) => e.t55.includes(seach) || seach.toUpperCase().includes(e.t55)
            )
            .slice(0, 30)
            .sort(function (a, b) {
                if (a.t55 > b.t55) return -1;
                if (a.t55 < b.t55) return 1;
                return 0;
            });
        const newList1 = [];
        newList.forEach((element) => {
            const obj = glb_sv.mrk_stklist.find((e) => e.t55 === element.t55);
            if (obj) {
                const cloneObj = { ...obj };
                glb_sv.allListFav.forEach((item) => {
                    if (item.ListStock.some((temp) => temp === cloneObj.t55)) {
                        cloneObj.isFav = true;
                        if (cloneObj.ExistNameFav) {
                            cloneObj.ExistNameFav += ", " + item.c2;
                        } else cloneObj.ExistNameFav = item.c2;
                    }
                });
                newList1.push(cloneObj);
            }
        });
        setListStock(newList1);
    };

    return (
        <div className="flex flex-1 flex-direction-column">
            <div
                className="flex width_full"
                id="search_code_ck"
                style={{ position: "fixed", marginTop: 10, width: '100%' }}
            >
                <SearchBar
                    autoFocus={true}
                    focused={true}
                    ref={searchRef}
                    value={seach}
                    placeholder={t('input_symbol')}
                    onSubmit={(value) => getListStock(value)}
                    onClear={(value) => resetData()}
                    onCancel={() => history.goBack()}
                    showCancelButton
                    cancelText={t('common_Cancel')}
                    cancelText={
                        <div style={{ color: 'var(--PRIMARY__CONTENT__COLOR)', fontWeight: "400" }}>{t('common_accomplished')}</div>
                    }
                    onChange={(value) => getListStock(value)}
                    style={{
                        backgroundColor: "var(--PRIMARY__BG__COLOR)",
                        width: "100%",
                    }}
                />
            </div>

            {listStock.length === 0 && seach.length === 0 ? (
                recentList.length ? (
                    <div
                        style={{
                            marginTop: "60px",
                            paddingRight: dimensions.halfIndent,
                            paddingLeft: dimensions.halfIndent,
                        }}
                    >
                        <span
                            style={{
                                color: "var(--HEADER__CONTENT__COLOR)",
                                paddingHorizontal: 5,
                            }}
                        >
                            {t("search_recent")}
                        </span>
                        {recentList.map((item) => (
                            <StockItem item={item} key={item.t55} />
                        ))}
                    </div>
                ) : null
            ) : (
                // <FlatList
                //   data={listStock}
                //   renderItem={StockItem}
                //   keyExtractor={(item) => item.t55}
                //   ListEmptyComponent={ListEmpty}
                //   keyboardShouldPersistTaps="always"
                //   style={{ marginBottom: 10 }}
                // />
                <div className="flex flex-1 flex-direction-column" style={UI.view_list}>
                    {listStock.map((item) => {
                        return <StockItem key={item.t55} item={item} key={item.t55} />;
                    })}
                </div>
            )}

            <ModalAddStock2FavList
                isModalVisible={isModalVisible}
                setModal={setModal}
                selectStock={selectStock}
                onChangeStatus={onChangeStatus}
            />
        </div>
    );
}
const UI = {
    view_list: {
        padding: dimensions.halfIndent,
        marginTop: 45,
    },
};
