import React, { useContext, memo } from 'react'
import { StoreContext } from '../../store';
import { dimensions } from '../../styles';
import { FormatNumber, glb_sv, FormatNumberAC } from '../../utils';
import { useTranslation } from 'react-i18next';
import dayjs from 'dayjs';

const Item = ({ item, stock, changeTimeMatch }) => {
    const { fractionPrice, fractionQty } = useContext(StoreContext);
    return (
        <div className='flex flex-direction-row align-items-center' style={UI.title} onClick={changeTimeMatch}>
            <span className='fs-small' style={{ width: '33.33%', color: 'var(--SECOND__CONTENT__COLOR)' }}>
                {dayjs(item.t52).format('HH:mm:ss')}
            </span>
            <span className='text-right fs-small' style={{ width: '33.33%', color: glb_sv.getColor(item.t31, stock) }}>
                {fractionPrice ? FormatNumber(item.t31 / 1000, 2) : FormatNumber(item.t31)}
            </span>
            <span className='text-right fs-small' style={{ width: '33.33%', color: 'var(--PRIMARY__CONTENT__COLOR)' }}>
                {fractionQty ? FormatNumberAC(item.t32, 2, 0) : FormatNumber(item.t32, 0, 0, 'short')}
            </span>
        </div>
    );
}

const MatchList = ({ stock, changeTimeMatch }) => {
    const { t } = useTranslation()
    return (
        <div className='flex flex-1 flex-direction-column' style={{ borderRightColor: 'var(--DIVIDER__COLOR)', borderRightWidth: 1, paddingRight: 5 }}>
            <span style={{ color: 'var(--PRIMARY__CONTENT__COLOR)', marginBottom: dimensions.halfIndent }}>{t('time_sales')}</span>

            <span className='fs-small ' style={{ color: 'var(--SECOND__CONTENT__COLOR)' }}>{t('tick_by_tick')}</span>

            {stock.EP.slice(-12).sort(function (a, b) {
                if (a.t52 < b.t52) return 1
                if (a.t52 > b.t52) return -1
                if (a.t52 === b.t52) {
                    if (a.subseq < b.subseq) return 1
                    if (a.subseq > b.subseq) return -1
                }
                return 0
            }).map(item =>
                <Item
                    key={item.t52 + item.seq + item.subseq}
                    item={item}
                    stock={stock}
                    changeTimeMatch={changeTimeMatch} />)}
        </div>
    );
}

const UI = {
    container: {
        paddingHorizontal: dimensions.halfIndent,
        marginVertical: dimensions.halfIndent,
        flexDirection: 'row'
    },
    title: {
        paddingTop: 6,
        paddingBottom: 6
    },
    header: {
        paddingTop: 6,
        paddingBottom: 6
    }
};

export default memo(MatchList)