import moment from 'moment';
import { memo, useState, useEffect, useRef, useContext } from 'react';
import { useTranslation } from 'react-i18next';
import { StoreTrading } from 'store-trading';
import { fontSizes } from 'styles';
import { FormatNumber, glb_sv, reqFunct, sendRequest, eventList } from 'utils';
import IC_FOLDER_NULL from 'assets/images/common/ic_folder_null.png';

const ServiceInfo = {
    GET_ASSETS_GUAR_INFO: {
        reqFunct: reqFunct.GET_ASSETS_GUAR_INFO,
        WorkerName: 'FOSqMargin',
        ServiceName: 'FOSqLendingMargin_Online_1450_1',
        Operation: 'Q',
        ClientSentTime: '0'
    },
    GET_CUR_MARG_CONT_LIST: {
        reqFunct: reqFunct.GET_CUR_MARG_CONT_LIST,
        WorkerName: 'FOSqMargin',
        ServiceName: 'FOSqMargin_1402_1',
        ClientSentTime: '0',
        Operation: 'Q'
    },
}

const DebtInfo = ({ debtInfo }) => {
    const { t } = useTranslation();
    const { userInfo } = useContext(StoreTrading);
    const [data, setData] = useState({})
    const mounted = useRef(true)

    const [listCurMarginContract, setListCurMarginContract] = useState([]);

    const [totalObject, setTotalObject] = useState({
        loan_expide_dt: 0,
        due_debt: 0,
        undue_debt: 0,
        interest: 0
    })

    useEffect(() => {
        mounted.current = true;
        getAssetsGuarInfo();
        getListCurrentMarginContractList();

        const commonEvent = glb_sv.commonEvent.subscribe(msg => {
            if (msg.type === eventList.LOGIN_SUCCESS) {
                getAssetsGuarInfo();
                getListCurrentMarginContractList();
            }
        })

        return () => {
            mounted.current = false;
            commonEvent.unsubscribe();
        }
    }, [userInfo])

    const getAssetsGuarInfo = () => {
        const { actn_curr, sub_curr } = userInfo;
        const InputParams = [actn_curr, sub_curr];
        sendRequest(ServiceInfo.GET_ASSETS_GUAR_INFO, InputParams, getAssetsGuarInfoResult, true, getAssetsGuarInfoTimeout)
        setData({})
    }

    const getAssetsGuarInfoTimeout = () => {
    }

    const getAssetsGuarInfoResult = (reqInfoMap, message) => {
        if (!mounted.current) return;
        if (Number(message['Result']) === 0) {
            return
        } else {
            try {
                let jsondata = message['Data'] ? JSON.parse(message['Data'])[0] : {}
                setData(jsondata)
            } catch (err) {
                console.log('getAssetsGuarInfoResult', err)
            }
        }
    }

    const getListCurrentMarginContractList = () => {
        const inputParams = ['OTSRPY', userInfo.actn_curr, userInfo.sub_curr, '6', '%', '0', 'z', ''];
        sendRequest(ServiceInfo.GET_CUR_MARG_CONT_LIST, inputParams, handleGetListCurrentMarginContractList)
    }

    const handleGetListCurrentMarginContractList = (reqInfoMap, message) => {
        if (Number(message['Result']) === 0) {
            return;
        } else {
            let jsondata;
            try {
                jsondata = JSON.parse(message['Data']);
            } catch (err) {
                jsondata = [];
            }
            if (Number(message['Packet']) <= 0) {
                setListCurMarginContract(jsondata)
                const loan_expide_dt = jsondata.reduce(function (a, b) {
                    return a + 0;
                }, 0);
                const due_debt = jsondata.reduce(function (a, b) {
                    return a + 0;
                }, 0);
                const undue_debt = jsondata.reduce(function (a, b) {
                    return a + 0;
                }, 0);
                const interest = jsondata.reduce(function (a, b) {
                    return a + Number(b.c21)
                }, 0);
                setTotalObject({
                    loan_expide_dt,
                    due_debt,
                    undue_debt,
                    interest
                })
            }
        }
    }

    const marginPayback = item => {
        glb_sv.commonEvent.next({
            type: eventList.OPEN_MAJOR_FUNCTION,
            value: 'marginPayback',
            title: 'title_margin_payback',
            data: item
        })
    }

    const marginExtension = item => {
        glb_sv.commonEvent.next({
            type: eventList.OPEN_MAJOR_FUNCTION,
            value: 'marginExtension',
            title: 'title_margin_extension',
            data: item
        })
    }

    return (
        <div className='flex flex-direction-column' style={{ fontSize: fontSizes.small }}>
            <div className='flex flex-direction-row mt-1 mb-1'>
                <div className='flex-2'>{t('advance_deb')}</div>
                <div className='flex-2 text-right fw-bold'>{FormatNumber(data?.c18, 0)}</div>
            </div>
            <div className='flex flex-direction-row mt-1 mb-1'>
                <div className='flex-2'>{t('advance_debt_fee')}</div>
                <div className='flex-2 text-right fw-bold'>{FormatNumber(data?.c19, 0)}</div>
            </div>
            <div className='flex flex-direction-row mt-1 mb-1'>
                <div className='flex-2'>{t('margin_debt')}</div>
                <div className='flex-2 text-right fw-bold'>{FormatNumber(data?.c16, 0)}</div>
            </div>
            <div className='flex flex-direction-row mt-1 mb-1'>
                <div className='flex-2'>{t('margin_debt_fee')}</div>
                <div className='flex-2 text-right fw-bold'>{FormatNumber(data?.c17, 0)}</div>
            </div>
            <div className='flex flex-direction-row mt-1 mb-1'>
                <div className='flex-2'>{t('temporary_loan')}</div>
                <div className='flex-2 text-right fw-bold'>{FormatNumber(data?.c34, 0)}</div>
            </div>
            <div className='flex flex-direction-row mt-1 mb-1'>
                <div className='flex-2'>{t('loan_expide_dt')}</div>
                <div className='flex-2 text-right fw-bold'>{FormatNumber(data?.c33, 0)}</div>
            </div>
            <div className='flex flex-direction-row mt-1 mb-1'>
                <div className='flex-2'>{t('total_current_loand')}</div>
                <div className='flex-2 text-right fw-bold'>{FormatNumber(data?.c31, 0)}</div>
            </div>
            <div className='flex flex-direction-row mt-1 mb-1'>
                <div className='flex-2'>RTT{/* {t('total_current_loand')} */}</div>
                <div className='flex-2 text-right fw-bold'>{FormatNumber(data?.c2, 2, 0)} %</div>
            </div>
            <div className='flex flex-direction-row mt-1 mb-1'>
                <div className='flex-2'>{t('adding_amount')}</div>
                <div className='flex-2 text-right fw-bold'>{FormatNumber(data?.c49, 0)}</div>
            </div>
            <div className='flex flex-direction-row mt-1 mb-1'>
                <div className='flex-2'>{t('remain_margin_amount')}</div>
                <div className='flex-2 text-right fw-bold'>{FormatNumber(data?.c56, 0)}</div>
            </div>
            <div className='ant-divider' role='separator' style={{ backgroundColor: 'var(--DIVIDER__COLOR)', margin: '5px 0' }}></div>
            {/* list balances */}
            <div className='flex flex-direction-row mt-1 mb-1'>
                <div className='flex-4 fw-bold' style={{ fontSize: fontSizes.medium }}>
                    {t('list_debt')}
                </div>
                <div className='flex-2 text-right'></div>
            </div>
            <div className='list-balances flex flex-direction-column'>
                <div className='flex flex-direction-row'>
                    <div className='flex-2 fw-bold flex-direction-column justify-content-start'>
                        <div className='flex justify-content-start'>{t('loan_date')}</div>
                        <div className='flex justify-content-start'>{t('date_due')}</div>
                    </div>
                    <div className='flex-4 fw-bold flex-direction-column justify-content-end'>
                        <div className='flex justify-content-end'>{t('loan_amount')}</div>
                        <div className='flex justify-content-end text-right'>{t('loan_current')}</div>
                    </div>
                    <div className='flex-4 fw-bold text-right'>{t('progress_status')}</div>
                </div>
                {
                    listCurMarginContract.length > 0 ? (
                        listCurMarginContract.map((item, index) => (
                            <div key={index} className='flex flex-direction-row' style={{ fontSize: fontSizes.verySmall }}>
                                <div className='flex-2 flex-direction-column justify-content-start'>
                                    <div className='flex justify-content-start'>{moment(item?.c1, 'DDMMYYYY').format('DD/MM/YYYY')}</div>
                                    <div className='flex justify-content-start'>{moment(item?.c5, 'DDMMYYYY').format('DD/MM/YYYY')}</div>
                                </div>
                                <div className='flex-4 flex-direction-column justify-content-end'>
                                    <div className='flex justify-content-end'>{FormatNumber(item?.c8)}</div>
                                    <div className='flex justify-content-end'>{FormatNumber(item?.c9)}</div>
                                </div>
                                <div className='flex-4 text-right'>{item?.c19}</div>
                            </div>
                        ))
                    ) : (
                        <div>
                            <div className='flex justify-content-center'>
                                <img src={IC_FOLDER_NULL} />
                            </div>
                            <div style={{ textAlign: 'center', lineHeight: '160%', marginTop: '30px' }}>
                                <div style={{ fontSize: '15px', lineHeight: '20px', fontWeight: '500' }}>{t('empty_list')}</div>
                            </div>
                        </div>
                    )
                    // </div>
                    // <div style={{ paddingTop: 30, paddingBottom: 30 }} className='flex flex-direction-row justify-content-center'>
                    //     {t('empty_list')}
                    // </div>
                }
            </div>
        </div>
    )
}

export default memo(DebtInfo);
