import React, { Component } from 'react'
import { dimensions, fontSizes } from '../../../styles';
import { glb_sv } from '../../../utils';
import F2 from '@antv/f2';

export default class ColumnChartPrice extends Component {
    constructor(props) {
        super(props);
    }

    componentDidMount() {
        console.log('props: ', this.props.style)

        this.chart = new F2.Chart({
            id: 'chart-column-time-matching',
            pixelRatio: window.devicePixelRatio,
            height: 300,
            width: dimensions.WIDTH / 2
        });
        this.chart.source([], {
            price: {
                range: [0, 1]
            },
            qty: {
                tickCount: 3
            },
            grid: (text, index, total) => {
                return {
                    stroke: '#cacaca'
                }
            },
        });
        this.chart.legend(false)
        this.chart.tooltip(false);
        this.chart.coord({
            transposed: true
        });
        this.chart.axis('price', {
            label: function label(text) {
                return {
                    text: formatNumber(text * 1),
                    fill: '#cacaca'
                };
            }
        });

        this.chart.axis('qty', {
            position: 'right',
            label: function label(text) {
                return {
                    text: nFormatter(text * 1, 2),
                    fill: '#cacaca'
                };
            },
            grid: function grid(text) {
                return {
                    stroke: '#cacaca80'
                }
            },
            line: {
                stroke: 'transparent',
                lineWidth: 0
            }
        });
        this.chart.interval().position('price*qty').color('color', function (val) {
            return val;
        });
        this.chart.render();

        if (!this.props.EP.length) return;
        const stock = glb_sv.StockMarket[this.props.stockCode];
        if (!stock) return;
        const arr = this.props.EP.reduce((acc, curr) => {
            if (!acc.length) {
                acc.push({
                    qty: curr.t32,
                    price: curr.t31,
                })
            } else {
                const dupPrice = acc.find(e => e.price === curr.t31)
                if (dupPrice) {
                    dupPrice.qty += curr.t32
                } else {
                    acc.push({
                        qty: curr.t32,
                        price: curr.t31,
                    })
                }
            }
            return acc
        }, []);
        arr.forEach(element => {
            element.color = glb_sv.getColor(element.price, stock, this.props.style, 't31');
        });
        this.chart.changeData(arr);
    }

    UNSAFE_componentWillReceiveProps(nextProps) {
        if (nextProps.EP.length !== this.props.EP.length) {
            if (!nextProps.EP.length) return;
            const stock = glb_sv.StockMarket[nextProps.stockCode];
            if (!stock) return;
            const arr = nextProps.EP.reduce((acc, curr) => {
                if (!acc.length) {
                    acc.push({
                        qty: curr.t32,
                        price: curr.t31,
                    })
                } else {
                    const dupPrice = acc.find(e => e.price === curr.t31)
                    if (dupPrice) {
                        dupPrice.qty += curr.t32
                    } else {
                        acc.push({
                            qty: curr.t32,
                            price: curr.t31,
                        })
                    }
                }
                return acc
            }, []);
            arr.forEach(element => {
                element.color = glb_sv.getColor(element.price, stock, this.props.style, 't31');
            });
            this.chart.changeData(arr);
        }
    }

    render() {
        const { t } = this.props;
        return (
            <div className='flex flex-direction-column' style={{ paddingLeft: 5 }}>
                <span style={{ marginBottom: dimensions.halfIndent, height: 19 }}>{'  '}</span>
                <span className='fs-small' style={{ color: 'var(--SECOND__CONTENT__COLOR)' }}>{t('chart_rate_match_today')}</span>

                <div style={{ height: 300, width: dimensions.WIDTH / 2, backgroundColor: 'var(--PRIMARY__BG__COLOR)' }}>
                    <canvas id='chart-column-time-matching' />
                </div>
                {/* <div style={{ height: 20 }} /> */}
            </div>
        )
    }
}

function formatNumber(n) {
    return String(Math.round(n * 100) / 100).replace(/\B(?=(\d{3})+(?!\d))/g, ',');
}
function nFormatter(num, digits) {
    const si = [
        { value: 1, symbol: "" },
        { value: 1E3, symbol: "k" },
        { value: 1E6, symbol: "M" },
        { value: 1E9, symbol: "B" },
        { value: 1E12, symbol: "T" }
    ]
    const rx = /\.0+$|(\.[0-9]*[1-9])0+$/;
    let i;
    for (i = si.length - 1; i > 0; i--) {
        if (num >= si[i].value) {
            break;
        }
    }
    return (num / si[i].value).toFixed(digits).replace(rx, "$1") + si[i].symbol;
}