import React, { memo } from 'react'
import { useTranslation } from 'react-i18next';
import { dimensions, fontSizes } from '../../styles';
import dayjs from 'dayjs';
import Text from 'antd/lib/typography/Text';
import { StoreContext } from '../../store';
import moment from 'moment';

const HistoryChangeInfo = ({ tradStatus, timeIndex }) => {
    const { styles } = React.useContext(StoreContext)

    const { t } = useTranslation();

    return (
        <div className='flex' style={{ paddingLeft: dimensions.moderate(16), paddingRight: dimensions.moderate(16) }}>
            <svg
                width={16}
                height={16}
                viewBox="0 0 16 16"
                xmlns="http://www.w3.org/2000/svg"
            >
                <path
                    d="M8 1.5a6.5 6.5 0 100 13 6.5 6.5 0 000-13zM11 9H8a.5.5 0 01-.5-.5V4a.5.5 0 111 0v4H11a.5.5 0 010 1z"
                    fill={styles.PLACEHODLER__COLOR}
                />
            </svg>
            <Text style={{ fontSize: fontSizes.tiny, color: 'var(--HEADER__CONTENT__COLOR)' }}> {!!tradStatus && !!tradStatus.trim() ? t(tradStatus) : t('priceboard_Not_traded')}</Text>
            {timeIndex && tradStatus !== 'priceboard_Not_traded' ?
                <Text style={{ marginLeft: 3, fontSize: fontSizes.tiny, color: 'var(--HEADER__CONTENT__COLOR)' }}>
                    {moment(timeIndex, 'YYYYMMDD-HH:mm:ss').format('HH:mm DD/MM')}
                </Text> : null}
        </div>
    )
}

export default memo(HistoryChangeInfo)