import {
    EyeInvisibleOutlined, EyeOutlined
} from '@ant-design/icons';
import { Button } from 'antd';
import { Modal } from 'antd-mobile'
import dayjs from 'dayjs';
import React, { memo, useContext, useEffect, useRef, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { ReactComponent as ClockIcon } from '../../assets/images/common/ic_clock.svg';
import { StoreTrading } from '../../store-trading';
import { dimensions, fontSizes, fontWeights } from '../../styles';
import { dataCryption, glb_sv, reqFunct, sendRequest, socket_sv } from '../../utils';
import { InputFloatingLabel } from 'basic-components';


const ServiceInfo = {
    LOGIN_FUNCTION: {
        reqFunct: reqFunct.LOGIN_FUNCTION,
        WorkerName: 'FOSxID02',
        ServiceName: 'FOSxID02_Login',
        ClientSentTime: '0',
        Operation: 'U',
    },
    AUTHEN_OTP: {
        reqFunct: reqFunct.AUTHEN_OTP,
        WorkerName: 'FOSxID01',
        ServiceName: 'FOSxID01_Management',
        ClientSentTime: '0',
        Operation: 'Q',
    },
    GET_NEW_OTP: {
        reqFunct: reqFunct.GET_NEW_OTP,
        WorkerName: 'FOSxID01',
        ServiceName: 'FOSxID01_Management',
        ClientSentTime: '0',
        Operation: 'I',
    }
}

function ModalAuthenOTP({
    time,
    visible,
    setVisible,
    callBackLogin,
    type,
    typeSendOTP,
    new_pass,
    callbackAfterChangePassword,
    callbackResetPassword }) {

    const { t } = useTranslation();
    const { setUserInfo, setAuth } = useContext(StoreTrading)

    const [code, setCode] = useState('')
    const [timeOTP, setTimeOTP] = useState(time === 'get_new_otp' ? 0 : time)
    const timeOTPRef = useRef(time === 'get_new_otp' ? 0 : time)

    const [pass, setPass] = useState(new_pass)
    const [cfmPass, setCfmPass] = useState(new_pass)

    const [showPass, setShowPass] = useState(false)
    const [showCfmPass, setShowCfmPass] = useState(false)

    const flag_sendAuthenOTP = useRef(null);

    const otpRef = useRef(null);
    const passRef = useRef(null);
    const cfmPassRef = useRef(null);

    const timeoutTimingOTP = useRef(null)

    const [error, setError] = useState('')

    const [loading, setLoading] = useState(false)

    useEffect(() => {
        setTimeOTP(time === 'get_new_otp' ? 0 : time)
        timeOTPRef.current = time === 'get_new_otp' ? 0 : time;
        if (time === 'get_new_otp') {
            resendAuthenOTP();
        } else {
            glb_sv.timeAuthenOtp = {
                timeGetOTP: dayjs(),
                numberOTP: timeOTPRef.current
            }
            timingOTP();
            setTimeout(() => {
                otpRef.current?.focus();
            }, 200);
        }
        return () => {
            if (timeoutTimingOTP.current) clearTimeout(timeoutTimingOTP.current)
        }
    }, [time])

    const timingOTP = () => {
        if (timeOTPRef.current > 0) {
            if (timeoutTimingOTP.current) clearTimeout(timeoutTimingOTP.current)
            timeoutTimingOTP.current = setTimeout(() => {
                const remainTimeOTP = Math.max(0, glb_sv.timeAuthenOtp.numberOTP - Math.round((dayjs() - glb_sv.timeAuthenOtp.timeGetOTP) / 1000))
                timeOTPRef.current = remainTimeOTP
                setTimeOTP(remainTimeOTP)
                timingOTP();
            }, 1000);
        }
    }

    const autoLogin = (username, pass, typee) => {
        const password = dataCryption.encryptString(pass);
        const InputParams = ['login', username, typee ? password : pass, glb_sv.notifyOnesignal.userId || '', glb_sv.notifyOnesignal.userId || '', 'Y'];
        sendRequest(ServiceInfo.LOGIN_FUNCTION, InputParams, loginResultProc, true, loginProccessTimeout)
    }

    const loginProccessTimeout = () => null

    const loginResultProc = (reqInfoMap, message) => {
        if (Number(message['Result']) === 0) {
        } else {
            let dataArr = {};
            try {
                let strdata = message['Data']
                dataArr = JSON.parse(strdata)[0]
            } catch (error) {
                return
            }
            glb_sv.objShareGlb['userInfo'] = dataArr;
            glb_sv.objShareGlb.otpType = Number(dataArr.c6);
            storeAccountInfo(dataArr)

            glb_sv.objShareGlb['sessionInfo'] = {
                sessionId: dataArr.c0,
                userID: dataArr.c1,
                passID: reqInfoMap.inputParam[2],
                remOrdPass: false,
                orderPass: null,
                Otp: '',
            }

            setTimeout(() => {
                glb_sv.sendAuthReqSocketStream({
                    LoginID: dataArr.c1,
                    MdmTp: '02',
                    Token: dataArr.c0
                })
            }, 2000);

            socket_sv.establishSocketConnect('authen_success', {})

            glb_sv.credentials.username = reqInfoMap.inputParam[1];
            glb_sv.credentials.password = reqInfoMap.inputParam[2];

            hideModal();
            callBackLogin(message.Message)
        }
    }

    const storeAccountInfo = userInfo => {
        const accountInfo = userInfo.c12
        if (!accountInfo) {
            setAuth(true)
            glb_sv.authFlag = true
            return
        }
        const array_acnt = accountInfo.split('|')
        const js_acntInfo = []
        const js_acntList = []
        // const js_acntListAll = []
        let acntMain = '',
            acntMainName = ''
        for (let i = 0; i < array_acnt.length; i++) {
            const acntInfo = array_acnt[i]
            const arr_info = acntInfo.split(',')
            // if (i === 0) {
            //     const ls_acntObjsAll = {
            //         id: arr_info[0] + '.%',
            //         name: arr_info[0] + '.% - ' + arr_info[2],
            //     }
            //     js_acntListAll.push(ls_acntObjsAll)
            // }
            if (arr_info[4] === 'Y' && arr_info[1] === '00') {
                acntMain = arr_info[0]
                acntMainName = arr_info[2]
            }
            const js_acnt = {
                AcntNo: arr_info[0],
                SubNo: arr_info[1],
                AcntNm: arr_info[2],
                IsOwnAcnt: arr_info[4],
                MarginYN: arr_info[3] === '2',
                ActType: arr_info[3],
                // Broker: arr_info[20],
                // BrokerBranch: arr_info[21],
                // BrokerAgent: arr_info[22],
                // TradingFee: arr_info[23],
            }
            const js_acntObj = {
                value: arr_info[0] + '.' + arr_info[1],
                label: arr_info[0] + '.' + arr_info[1] + ' - ' + arr_info[2],
            }
            js_acntInfo.push(js_acnt)
            js_acntList.push(js_acntObj)
            // js_acntListAll.push(js_acntObj)
        }

        glb_sv.objShareGlb['acntNoInfo'] = js_acntInfo
        glb_sv.objShareGlb['acntNoList'] = js_acntList
        // glb_sv.objShareGlb['acntNoListAll'] = js_acntListAll
        // --------------------
        glb_sv.objShareGlb['AcntMain'] = acntMain // -- chủ tài khoản lưu ký
        glb_sv.objShareGlb['AcntMainNm'] = acntMainName // -- Tên chủ tài khoản
        glb_sv.objShareGlb['workDate'] = userInfo.c16 // -- Ngày làm việc
        // glb_sv.objShareGlb['brokerId'] = userInfo['c24'] // -- ID nhân viên môi giới
        // glb_sv.objShareGlb['brokerNm'] = userInfo['c25'] // -- Tên nhân viên môi giới
        // glb_sv.objShareGlb['brokerEmail'] = userInfo['c26'] // -- Email nhân viên môi giới
        // glb_sv.objShareGlb['brokerMobile'] = userInfo['c27'] // -- Mobile nhân viên môi giới
        // glb_sv.objShareGlb['secCode'] = userInfo['c9'] // -- Tên nhân viên môi giới
        // glb_sv.objShareGlb['prdYN'] = userInfo['c31'] // -- User trình chiếu bảng điện hay không
        glb_sv.objShareGlb['verify'] = Number(userInfo['c37']) || 0 // -- verify > 0 => Phải xác thực chứng chỉ số
        glb_sv.objShareGlb['serialnum'] = userInfo['c38'] // -- serialNumber

        const sub_list = [];
        const actn_list = [];
        js_acntInfo.forEach(e => {
            if (e.AcntNo === acntMain) sub_list.push(e.SubNo)
            if (!actn_list.find(temp => temp.AcntNo === e.AcntNo))
                actn_list.push({
                    AcntNo: e.AcntNo,
                    actn_name: e.AcntNm,
                    key: e.AcntNo,
                    label: e.AcntNo + ' (' + e.AcntNm + ')'
                })
        })
        const isMargin = glb_sv.objShareGlb['acntNoInfo'].find(e => e.AcntNo === acntMain && e.SubNo === sub_list[0])?.MarginYN
        setUserInfo({
            actn_curr: acntMain,
            sub_curr: sub_list[0],
            actn_name: acntMainName,
            sub_list,
            // sub_list: ['00','01','02','03','04','05','06','07','08'],
            actn_list,
            actn_full: acntMain + '.00',
            isMargin: isMargin || false
        });
        setAuth(true)
        glb_sv.authFlag = true
    }

    const sendAuthenOTP = () => {
        if (pass !== cfmPass) {
            setError(t('pass_confirm_not_correct'))
            cfmPassRef.current.focus();
            return;
        }
        if (flag_sendAuthenOTP.current) return;
        const id = glb_sv.credentials.username
        const password = dataCryption.encryptString(pass);
        const codeEncryt = dataCryption.encryptString(code);
        const input2 = typeSendOTP || (validateEmailRegex(id) ? 'email' : 'phone')
        const InputParams = ['authen', id, input2, codeEncryt, password, glb_sv.notifyOnesignal.userId || ''];
        sendRequest(ServiceInfo.AUTHEN_OTP, InputParams, sendAuthenOTPResult, true, sendAuthenOTPResultTimeout)
        flag_sendAuthenOTP.current = true;
        setLoading(true);
    }

    const sendAuthenOTPResultTimeout = () => {
        flag_sendAuthenOTP.current = false;
        setLoading(false);
    }

    const sendAuthenOTPResult = (reqInfoMap, message) => {
        console.log("sendAuthenOTPResult -> message", message)
        flag_sendAuthenOTP.current = false;
        setLoading(false);
        if (Number(message['Result']) === 0) {
            setError(message.Message || t('notify_otp_error'))
            otpRef.current?.focus();
            if (message.Code === '010013') {
                if (timeoutTimingOTP.current) clearTimeout(timeoutTimingOTP.current)
                setTimeOTP(0);
                timeOTPRef.current = 0;
            }
        } else {
            if (type === 'change_password') {
                setVisible(false);
                callbackAfterChangePassword()
            } else if (type === 'reset_password') {
                setVisible(false);
                callbackResetPassword()
            } else {
                glb_sv.credentials.password = reqInfoMap.inputParam[4];
                autoLogin(glb_sv.credentials.username, glb_sv.credentials.password);
            }
        }
    }

    const resendAuthenOTP = () => {
        if (timeOTPRef.current) return;
        if (flag_sendAuthenOTP.current) return;
        const InputParams = ['newopnotp', validateEmailRegex(glb_sv.credentials.username) ? 'email' : 'phone', glb_sv.credentials.username];
        sendRequest(ServiceInfo.GET_NEW_OTP, InputParams, resendAuthenOTPResult, true, resendAuthenOTPResultTimeout)
        flag_sendAuthenOTP.current = true
    }

    const resendAuthenOTPResultTimeout = () => flag_sendAuthenOTP.current = false

    const resendAuthenOTPResult = (reqInfoMap, message) => {
        console.log("resendAuthenOTPResult -> message", message)
        flag_sendAuthenOTP.current = false;
        if (Number(message['Result']) === 0) {
        } else {
            let jsondata = {}
            try {
                if (message['Data']) {
                    jsondata = JSON.parse(message['Data'])[0]
                }
            } catch (err) {
                console.error('resendAuthenOTPResult error', err)
                return
            }
            setTimeOTP(Number(jsondata.c0));
            setCode('')
            timeOTPRef.current = Number(jsondata.c0);
            glb_sv.timeAuthenOtp = {
                timeGetOTP: dayjs(),
                numberOTP: timeOTPRef.current
            }
            timingOTP();
            otpRef.current?.focus();
        }
    }

    const validateInput = () => {
        if (!code) return false
        if (!pass || pass.length < 6 || pass.length > 20) return false
        if (!cfmPass || cfmPass.length < 6 || cfmPass.length > 20) return false
        return true
    }

    const hideModal = () => {
        setVisible(false);
        setCode('');
        if (type !== 'change_password') {
            setPass('');
            setCfmPass('');
        }
        setError('');
        if (timeoutTimingOTP.current) clearTimeout(timeoutTimingOTP.current)
    }

    const onKeyDown = (e) => {
        if (e.key === 'Enter') {
            if (e.target.id === 'id_otp_authen') {
                if (type === 'change_password') {
                    validateInput() && sendAuthenOTP();
                }
            }
            if (e.target.id === 'id_cfm_pass_authen') {
                validateInput() && sendAuthenOTP();
            }
        }

    }

    return (
        <Modal
            visible={visible}
            onCancel={hideModal}
            transparent
            style={{width: '85vw'}}
        >
            <div className='flex flex-1 flex-direction-column justify-content-center align-items-center' style={UI.container}>
                <ClockIcon />
                <span style={{ fontSize: 'var(--big)', color: 'var(--TEXT__1)', marginTop: dimensions.vertical(12), marginBottom: dimensions.vertical(12) }}>
                    {type === 'change_password' ? t('title_authen_change_pass') : t('title_authen_account')}
                </span>

                <InputFloatingLabel
                    refs={otpRef}
                    id="id_otp_authen"
                    label={t('label_code_otp')}
                    className='input__component'
                    onChange={e => {
                        setCode(e.target.value)
                        setError('')
                        if (e.target.value.length > 5) {
                            glb_sv.focusInput('id_new_pass_authen')
                        }
                    }}
                    rightComponent={
                        <div className='flex' style={{ position: 'absolute', right: dimensions.halfIndent }}>
                            <span style={{ color: 'var(--TEXT__2)', fontSize: fontSizes.normal, padding: 0 }}>
                                {timeOTP}s
                                </span>
                        </div>}
                    value={code}
                    onKeyDown={onKeyDown}
                    type='text'
                    autocomplete="off"
                />

                {type === 'change_password' ? null :
                    <InputFloatingLabel
                        id="id_new_pass_authen"
                        label={t('label_new_pass')}
                        className='input__component'
                        onChange={e => {
                            setPass(e.target.value)
                            setError('')
                        }}
                        type={!showPass ? 'password' : 'text'}
                        maxlength="30"
                        rightComponent={
                            <div className='flex' style={{ position: 'absolute', right: dimensions.halfIndent }}>
                                {showPass ? <EyeOutlined
                                    className='icon_class'
                                    onClick={() => setShowPass(value => !value)}
                                /> :
                                    <EyeInvisibleOutlined
                                        className='icon_class'
                                        onClick={() => setShowPass(value => !value)}
                                    />}
                            </div>}
                        value={pass}
                        autocomplete="off"
                    />}

                {type === 'change_password' ? null :
                    <InputFloatingLabel
                        id="id_cfm_pass_authen"
                        label={t('label_cfm_pass')}
                        className='input__component'
                        onChange={e => {
                            setCfmPass(e.target.value)
                            setError('')
                        }}
                        type={!showCfmPass ? 'password' : 'text'}
                        maxlength="30"
                        rightComponent={
                            <div className='flex' style={{ position: 'absolute', right: dimensions.halfIndent }}>
                                {showCfmPass ? <EyeOutlined
                                    className='icon_class'
                                    onClick={() => setShowCfmPass(value => !value)}
                                /> :
                                    <EyeInvisibleOutlined
                                        className='icon_class'
                                        onClick={() => setShowCfmPass(value => !value)}
                                    />}
                            </div>}
                        value={cfmPass}
                        onKeyDown={onKeyDown}
                        autocomplete="off"
                    />}

                <span style={{ fontSize: 'var(--small)', color: 'var(--ERROR__COLOR)' }}>
                    {error}
                </span>

                <span
                    onClick={resendAuthenOTP}
                    className='text-center cursor-pointer'
                    style={{ fontSize: 'var(--medium)', color: 'var(--PRIMARY)', paddingTop: 12, paddingBottom: 12, opacity: timeOTP ? 0 : 1 }}>
                    {t('resend_authen_account')}
                </span>

                <Button
                    onClick={sendAuthenOTP}
                    disabled={!validateInput()}
                    loading={loading}
                    block
                    type="ghost"
                    shape="round"
                    size='large'
                    style={{ backgroundColor: !validateInput() ? 'var(--PRIMARY__DISABLED)' : 'var(--PRIMARY)' }}
                >
                    <span style={{ color: !validateInput() ? 'var(--TEXT__2)' : '#FFF', fontSize: 'var(--medium)', fontWeight: '500' }}>
                        {t('authen')}
                    </span>
                </Button>

                <Button
                    onClick={hideModal}
                    block
                    type="ghost"
                    size='large'
                    shape="round">
                    <span style={{ color: 'var(--TEXT__1)', fontSize: 'var(--medium)', opacity: 0.34 }}>
                        {t('common_Cancel')}
                    </span>
                </Button>

            </div>

        </Modal>
    );
}
export default memo(ModalAuthenOTP)

const UI = {
    container: {
        borderRadius: 12,
        borderColor: 'rgba(0, 0, 0, 0.1)',
    },
    tab: {
        fontSize: fontSizes.small,
        fontWeight: fontWeights.medium,
        marginLeft: 0,
        marginRight: 0
    },
    row: {
        flexDirection: 'row',
        marginTop: 10,
        paddingHorizontal: dimensions.halfIndent,
        borderRadius: 200
    },
    codeInput: {
        fontSize: fontSizes.normal,
        fontWeight: fontWeights.semiBold,
        flex: 1,
    }
}

function validateEmailRegex(email) {
    const re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(String(email).toLowerCase());
}