import { useContext, useEffect, useState, useRef, memo } from 'react';
import { useTranslation } from 'react-i18next';

import { StoreContext } from '../store';
import MarketLayout from '../layouts/market';
import NewsLayout from '../layouts/news'
import SettingsLayout from '../layouts/settings'
import IMAGE from '../utils/constant/image'

import Screens from '../utils/constant/screens';
import { glb_sv, eventList } from '../utils';
import { TabBar } from 'antd-mobile';
import Asset from 'layouts/asset';
import PlaceOrder from 'layouts/place-order';

import { ReactComponent as IC_MARKET_888 } from 'assets/images/common/home.svg'
import { ReactComponent as IC_ORDER_888 } from 'assets/images/common/place_order.svg'
import { ReactComponent as IC_ASSETS_888 } from 'assets/images/common/assets.svg'
import { ReactComponent as IC_NEWS_888 } from 'assets/images/common/news.svg'
import { ReactComponent as IC_SERVICE_888 } from 'assets/images/common/service.svg'


const { MARKET, PORTFOLIO, NEWS, SETTINGS, MARKET_TEST, ASSETS_TAB, PLACE_ORDER } = Screens;

const MarketLayoutMemo = memo(MarketLayout);
const PlaceOrderLayout = memo(PlaceOrder)
const NewsLayoutMemo = memo(NewsLayout)
const SettingsLayoutMemo = memo(SettingsLayout)

function HomeNavigator({ navigation, tabname }) {
  const { styles, activeCode } = useContext(StoreContext)
  const { t } = useTranslation();

  const tabRef = useRef('');
  const [selectedTab, setTab] = useState(glb_sv.tabbarName);

  const [showTab1, setShowTab1] = useState(glb_sv.tabbarName === MARKET)
  const [showTab2, setShowTab2] = useState(glb_sv.tabbarName === PORTFOLIO)
  const [showTab3, setShowTab3] = useState(glb_sv.tabbarName === NEWS)
  const [showTab4, setShowTab4] = useState(glb_sv.tabbarName === PLACE_ORDER)
  const [showTab5, setShowTab5] = useState(glb_sv.tabbarName === SETTINGS)
  const [showTab6, setShowTab6] = useState(glb_sv.tabbarName === MARKET_TEST)
  const [showTabAsset, setShowTabAsset] = useState(glb_sv.tabbarName === ASSETS_TAB)

  useEffect(() => {
    // glb_sv.isRefreshApp = false

    const commonEvent = glb_sv.commonEvent.subscribe(msg => {
      if (msg.type === eventList.RES_COMMON_MSG && msg.data.Code === '080128') {

        glb_sv.objShareGlb['sessionInfo']['Otp'] = ''

        let data
        try {
          data = JSON.parse(msg.data['Data'])[0]
        } catch (error) {
          console.log("HomeNavigator -> error", error)
          return
        }
        const otp_Type = Number(data.c1);
        let reqOtpMessage = 'OTP'
        if (data['c1'] === undefined || Number(data['c1']) === 1) {
          // OTP bị động -> Server tự sinh OTP
        } else if (Number(data['c1']) === 2) { //-- dạng thẻ ma trận
          if (activeCode === '102' || activeCode === '061') {
            reqOtpMessage = 'OTP ' + data['c2']
          } else {
            reqOtpMessage = 'OTP ' + data['c3'] + data['c2']
          }
        }
        const expTimeOtp = otp_Type === 3 ? 0 : Number(data['c0'])
        // navigation.navigate(OTP_MODAL, { time: expTimeOtp, type: reqOtpMessage, hasOTP: otp_Type === 3 ? false : true, otp_Type });
        window.location.reload()
      }
      if (msg.type === eventList.MESSAGE_SYSTEM) {
        const message = msg.data;
        if (message['Code'] === 'XXXXX6') {
          const ls_msg = 'common_login_expire_bc_newsession'
          // navigation.navigate(ALERT_MODAL, {
          //   title: t('common_notify'),
          //   content: t(ls_msg),
          //   typeColor: styles.WARN__COLOR,
          //   linkCallback: () => window.location.reload()
          // })
          window.location.reload()
        }
        if (message.Code === '080001' || message.Code === 'XXXXX7') {
          // navigation.navigate(Screens.ALERT_MODAL, {
          //   title: t('common_notify'),
          //   content: t('Your_session_time_out_System_will_disconnected_this_session_now'),
          //   typeColor: styles.WARN__COLOR,
          //   linkCallback: () => window.location.reload()
          // })
          window.location.reload()
        }
      }
      if (msg.type === eventList.CHANGE_TAB) {
        if (msg.data?.screen !== tabRef.current) {
          changeNewTab(msg.data.screen);
        }
      }
    });
    return () => {
      commonEvent.unsubscribe();
    }
  }, []);

  useEffect(() => {
    if (tabRef.current !== glb_sv.tabbarName) {
      changeNewTab(glb_sv.tabbarName);
    }
  }, [glb_sv.tabbarName])

  const changeNewTab = (screen) => {
    tabRef.current = screen;
    setTab(screen);
    glb_sv.tabbarName = screen;
    if (screen === MARKET && !showTab1) setShowTab1(true)
    if (screen === PORTFOLIO && !showTab2) setShowTab2(true)
    if (screen === NEWS && !showTab3) setShowTab3(true)
    if (screen === PLACE_ORDER && !showTab4) setShowTab4(true)
    if (screen === SETTINGS && !showTab5) setShowTab5(true)
    if (screen === MARKET_TEST && !showTab6) setShowTab6(true)
    if (screen === ASSETS_TAB && !showTabAsset) setShowTabAsset(true)
  }

  return (
    <div style={{ position: 'fixed', height: '100%', width: '100%', top: 0 }}>
      <TabBar
        unselectedTintColor="#949494"
        barTintColor={styles.TABBAR__BG__COLOR}
        // style={{
        //   boxShadow: `0px -5px ${styles.TABBAR__SHADOW__COLOR}`
        // }}
        tintColor={'var(--UP__COLOR'}
      >
        <TabBar.Item

          title={t('market_info_tab')}
          key="MARKET"
          icon={IMAGE[activeCode].MARKET_TABBAR}
          selectedIcon={<IC_MARKET_888 fill={'var(--UP__COLOR'} />}
          selected={selectedTab === MARKET}
          onPress={() => {
            if (selectedTab !== MARKET) changeNewTab(MARKET)
          }}
          data-seed="logId"
        >
          {showTab1 && <MarketLayoutMemo />}
        </TabBar.Item>
        <TabBar.Item
          icon={IMAGE[activeCode].ASSETS_TABBAR}
          selectedIcon={<IC_ASSETS_888 fill={'var(--UP__COLOR'} />}
          title={t('asset_tab')}
          key="ASSETS_TAB"
          selected={selectedTab === ASSETS_TAB}
          onPress={() => {

            if (selectedTab !== ASSETS_TAB) changeNewTab(ASSETS_TAB)
          }}
        >
          {showTabAsset && <Asset />}
        </TabBar.Item>
        <TabBar.Item
          icon={IMAGE[activeCode].ORDER_TABBAR}
          selectedIcon={<IC_ORDER_888 fill={'var(--UP__COLOR'} />}
          title={t('order')}
          key="PLACE_ORDER"
          selected={selectedTab === PLACE_ORDER}
          onPress={() => {
            if (selectedTab !== PLACE_ORDER) changeNewTab(PLACE_ORDER)
          }}
        >
          {showTab4 && <PlaceOrderLayout />}
        </TabBar.Item>

        <TabBar.Item
          icon={IMAGE[activeCode].NEWS_TABBAR}
          selectedIcon={<IC_NEWS_888 fill={'var(--UP__COLOR'} />}
          title={t('notify_news')}
          key="NEWS"
          selected={selectedTab === NEWS}
          onPress={() => {
            if (selectedTab !== NEWS) changeNewTab(NEWS)
          }}
        >
          {showTab3 && <NewsLayoutMemo />}
        </TabBar.Item>
        <TabBar.Item
          icon={IMAGE[activeCode].SERVICE_TABBAR}
          selectedIcon={<IC_SERVICE_888 fill={'var(--UP__COLOR'} />}
          title={t('service')}
          key="SETTINGS"
          selected={selectedTab === SETTINGS}
          onPress={() => {
            if (selectedTab !== SETTINGS) changeNewTab(SETTINGS)
          }}
        >
          {showTab5 && <SettingsLayoutMemo />}
        </TabBar.Item>
      </TabBar>
    </div>
  );
}

export default HomeNavigator