import React, { useContext, useState, useEffect } from 'react'
import { Col, List, Switch, Drawer, Button } from 'antd'
import { useHistory } from 'react-router-dom'
import Title from 'antd/lib/typography/Title'
import HeaderComponent from '../../components/header'
import { useTranslation } from 'react-i18next'
import { routes } from 'navigation/routes'
import { glb_sv } from '../../utils'
import { changeTheme } from '../../styles/script'
import { StoreContext } from '../../store'
import Text from 'antd/lib/typography/Text'

const MenuLayout = () => {
    const { t } = useTranslation()
    let history = useHistory()
    const [listUnitDrawer, setListUnitDrawer] = useState([])
    const [isOpenModalConfigUnits, setisOpenModalConfigUnits] = useState()
    const {
        language,
        theme,
        setTheme,
        timeoutConnect,
        fractionPrice,
        setFractionPrice,
        setFractionQty,
        fractionQty,
        fractionPriceOrder,
        setFractionPriceOrder,
    } = useContext(StoreContext)

    const onChange = (checked) => {
        const currentTheme = localStorage.getItem('THEME')
        changeTheme(currentTheme, checked ? 'DARK' : 'LIGHT')
        setTheme(localStorage.getItem('THEME'))
        glb_sv.theme = localStorage.getItem('THEME')
    }

    useEffect(() => {
        const currentTheme = localStorage.getItem('THEME')
        changeTheme('NULL', currentTheme)
    }, [])

    const getLangage = () => {
        if (language === 'VI') return 'Tiếng Việt'
        if (language === 'EN') return 'English'
        if (language === 'CN') return '中文'
        if (language === 'ZH') return '中文'
        if (language === 'KO') return '한국어'
    }

    const listConfigUnitsPrice = [
        { key: 1, label: 'x1 VND', value: false, type: 'exactly', cb: setFractionPrice },
        { key: 2, label: 'x1000 VND', value: true, type: 'per_thousand', cb: setFractionPrice },
    ]

    const listConfigUnitsQty = [
        { key: 1, label: 'x1 ' + t('stock'), value: false, type: 'exactly', cb: setFractionQty },
        { key: 2, label: 'x10 ' + t('stock'), value: true, type: 'per_ten', cb: setFractionQty },
    ]

    const listConfigUnitsPriceInOrder = [
        { key: 1, label: 'x1 VND', value: false, type: 'exactly', cb: setFractionPriceOrder },
        { key: 2, label: 'x1000 VND', value: true, type: 'per_thousand', cb: setFractionPriceOrder },
    ]

    const onClose = () => {
        setisOpenModalConfigUnits(!isOpenModalConfigUnits)
    }

    const onConfigUnits = (listSelect) => {
        setisOpenModalConfigUnits(!isOpenModalConfigUnits)
        console.log('listSelect', listSelect)
        setListUnitDrawer(listSelect)
    }

    return (
        <div className="menu__layout" style={{ backgroundColor: 'var(--PRIMARY__BG__COLOR)', height: '100%' }}>
            <HeaderComponent
                title={t('config')}
            />
            <div>
                <List>
                    <List.Item>
                        <Col span={20}>
                            <Title className="fs__medium" level={5}>
                                {t('theme')}
                            </Title>
                        </Col>
                        <Col span={4} className="center__item">
                            <Switch
                                size="small"
                                checkedChildren="DARK"
                                unCheckedChildren="LIGHT"
                                defaultChecked={theme === 'DARK'}
                                onChange={onChange}
                            />
                        </Col>
                    </List.Item>
                    <List.Item onClick={() => history.push(routes.LANGUAGE.path, { type: 'TEST', content: 'Hello' })}>
                        <Col span={16}>
                            <Title className="fs__medium" level={5}>
                                {t('language')}
                            </Title>
                        </Col>
                        <Col span={8} style={{ textAlign: 'right' }}>
                            <Text>{t(getLangage())}</Text>
                        </Col>
                    </List.Item>
                    <List.Item className="flex-wrap-wrap">
                        <Col span={20}>
                            <Title className="fs__medium" level={5}>
                                {t('config_unit_price_qty')}
                            </Title>
                        </Col>
                        <Col span={4} className="center__item"></Col>
                        <Col span={24}>
                            <Text style={{ color: 'var(--SECOND__CONTENT__COLOR)' }}>{t('config_priceboard')}</Text>
                        </Col>
                        <Col onClick={() => onConfigUnits(listConfigUnitsPrice)}>
                            <Text>
                                {t('price')} {fractionPrice ? 'x1000' : 'x1'} VND
                            </Text>
                        </Col>
                        <Col onClick={() => onConfigUnits(listConfigUnitsQty)}>
                            <Text>
                                {t('qty')} {fractionQty ? 'x10' : 'x1'} {t('share')}
                            </Text>
                        </Col>
                        <Col span={24}>
                            <Text style={{ color: 'var(--SECOND__CONTENT__COLOR)' }}>
                                <Text>{t('config_priceboard')}</Text>
                            </Text>
                        </Col>
                        <Col onClick={() => onConfigUnits(listConfigUnitsPriceInOrder)}>
                            <Text>
                                {t('price')} {fractionPriceOrder ? 'x1000' : 'x1'} VND
                            </Text>
                        </Col>
                        <Col onClick={() => {}}>
                            <Text>
                                {t('qty')} {'x1'} {t('share')}
                            </Text>
                        </Col>
                    </List.Item>
                    <List.Item
                        onClick={() =>
                            history.push(routes.TIMEOUT_CONNECT.path, {
                                type: 'TIMEOUT_CONNECT',
                                content: 'TIMEOUT_CONNECT',
                            })
                        }
                    >
                        <Col span={12}>
                            <Title className="fs__medium" level={5}>
                                {t('time_stay_connected')}
                            </Title>
                        </Col>
                        <Col span={12} style={{ textAlign: 'right' }}>
                            <Text>{t(timeoutConnect)}</Text>
                        </Col>
                    </List.Item>
                </List>
                <Drawer
                    title=""
                    placement={'bottom'}
                    closable={false}
                    onClose={onClose}
                    visible={isOpenModalConfigUnits}
                    key={'bottom'}
                >
                    <List>
                        {listUnitDrawer.map((item) => (
                            <List.Item
                                key={item.label}
                                onClick={() => {
                                    item.cb(item.value)
                                    setisOpenModalConfigUnits(false)
                                }}
                            >
                                <Col span={4} className="center__item"></Col>
                                <Col span={16} className="center__item">
                                    <Title className="fs__medium" level={5}>
                                        {t(item.label)}
                                    </Title>
                                </Col>
                                <Col span={4} className="center__item"></Col>
                            </List.Item>
                        ))}
                        <Button block type="primary" onClick={onClose}>
                            {t('common_Cancel')}
                        </Button>
                    </List>
                </Drawer>
            </div>
        </div>
    )
}

export default MenuLayout
