import React, { useState, useContext, memo, useEffect, useRef, useCallback } from 'react';
// import { RefreshControl } from 'react-native'
// import { Content } from 'native-base';
import { StoreContext } from '../../store';
// import { dimensions } from '../../styles';
import { RowNews } from '../../components/news';
import { reqFunct, InputServiceBody, requestInfo, socket_sv, glb_sv, Screens, sendRequest } from '../../utils';
import dayjs from 'dayjs'
import { Carousel } from 'antd';
import { useHistory } from "react-router-dom";
import { routes } from 'navigation/routes';
import { dimensions, fontSizes } from '../../styles';
import Title from 'antd/lib/typography/Title';

const contentStyle = {
    height: dimensions.indent * 12.5,
    color: 'var(--PRIMARY__CONTENT__COLOR)',
    backgroundSize: 'cover',
};

const ServiceInfo = {
    GET_ORDER_LIST: {
        reqFunct: reqFunct.GET_HOT_NEWS,
        WorkerName: 'FOSqNews',
        ServiceName: 'FOSqNews_NewsInfo',
        Operation: 'Q',
        ClientSentTime: '0'
    },
}
function TopNews(props) {
    let history = useHistory()
    const { styles, language } = useContext(StoreContext);

    const [newsList, setNewsList] = useState(glb_sv.dataHotNews.list)
    const getHotNewsFlag = useRef(false)
    const newsListRef = useRef([])

    const [refreshing, setRefreshing] = useState(false)

    useEffect(() => {
        if (glb_sv.dataHotNews.language !== language) getHotNews();
    }, [language]);

    const getHotNews = () => {
        if (getHotNewsFlag.current) return;
        const InVal = ['01', '0'];
        sendRequest(ServiceInfo.GET_ORDER_LIST, InVal, getHotNewsResultProc, true, timeOutFunct)
        getHotNewsFlag.current = true
        newsListRef.current = []
    }

    const getHotNewsResultProc = (reqInfoMap, message) => {
        console.log("getHotNewsResultProc -> message", message)
        setRefreshing(false);
        if (Number(message['Result']) === 0) {
            return
        } else {
            let jsondata = []
            try {
                if (!message['Data']) {
                } else {
                    jsondata = JSON.parse(glb_sv.filterStrBfParse(message['Data']))
                }
            } catch (err) {
                console.log('getHotNewsResultProc error', err)
                getHotNewsFlag.current = false
                return
            }
            newsListRef.current = newsListRef.current.concat(jsondata)
            if (Number(message['Packet']) <= 0) {
                getHotNewsFlag.current = false
                setNewsList(newsListRef.current)
                glb_sv.dataHotNews.list = newsListRef.current;
                glb_sv.dataHotNews.language = language;
            }
        }
    }

    const timeOutFunct = (reqInfo) => {
        if (reqInfo.reqFunct === reqFunct.GET_HOT_NEWS) {
            getHotNewsFlag.current = false;
        }
    }

    const onRefresh = useCallback(() => {
        setRefreshing(true);
        getHotNews();
    }, []);

    return (
        <>
            {newsList.length > 3 &&
                <Carousel autoplay>
                    {newsList.slice(0, 3).map((news) =>
                        <div key={news.c0} onClick={() => history.push(routes.NEWS_DETAIL.path, { title: news.c4, link: news.c3 })}>
                            <Title level={4} className="center__item" style={{ ...contentStyle, backgroundImage: `url(${news.c7})` }}>
                                {news.c4}
                            </Title>
                        </div>
                    )}
                </Carousel>
            }
            {newsList.length > 3 ? <>
                {newsList.slice(3, newsList.length).map(item =>
                    <RowNews
                        key={item.c0}
                        link={item.c3}
                        styles={styles}
                        title={item.c4}
                        subtitle={item.c9 + ' ∙ ' + dayjs(item.c6, 'YYYYMMDDHHmmss').format('DD/MM HH:mm:ss')}
                        uri={item.c7} />
                )}
            </> : <>
                {newsList.map(item =>
                    <RowNews
                        key={item.c0}
                        link={item.c3}
                        styles={styles}
                        title={item.c4}
                        subtitle={item.c9 + ' ∙ ' + dayjs(item.c6, 'YYYYMMDDHHmmss').format('DD/MM HH:mm:ss')}
                        uri={item.c7} />
                )}
            </>}
        </>
    );
}

export default memo(TopNews)