import React, { memo } from 'react';
import { fontSizes, dimensions } from '../../../styles';
import { useTranslation } from 'react-i18next';

const listTimeHis = ['1W', '1M', '3M', '6M', '1Y', '2Y', '3Y'];

const HeaderTop = ({ typeTop, activeOtp, bgColor }) => {
    const { t } = useTranslation()
    console.log('HeaderTop', typeTop, activeOtp, bgColor)
    return (
        <div className='flex' style={{ ...UI.HeaderTop_view, borderBottomColor: 'var(--DIVIDER__COLOR)', backgroundColor: bgColor }}>
            <div className='flex flex-1 justify-content-start'>
                <span style={{ fontSize: fontSizes.verySmall, color: 'var(--HEADER__CONTENT__COLOR)' }}>
                    {t('short_symbol')}
                </span>
            </div>
            <div className='flex flex-1 justify-content-end'>
                <span style={{ fontSize: fontSizes.verySmall, textAlign: 'right', color: 'var(--HEADER__CONTENT__COLOR)' }}>
                    {t('%change')}
                </span>
            </div>
            <div className='flex flex-1 justify-content-end'>
                <span style={{ fontSize: fontSizes.verySmall, textAlign: 'right', color: 'var(--HEADER__CONTENT__COLOR)' }}>
                    {typeTop === 'FRG_VAL_UP' ? t('priceboard_total_buy_value_trading') :
                        listTimeHis.includes(activeOtp) ? t('common_bottom') : t('open')}
                </span>
            </div>
            <div className='flex flex-1 justify-content-end'>
                <span style={{ fontSize: fontSizes.verySmall, textAlign: 'right', color: 'var(--HEADER__CONTENT__COLOR)' }}>
                    {typeTop === 'FRG_VAL_UP' ? t('priceboard_total_sell_value_trading') :
                        listTimeHis.includes(activeOtp) ? t('common_top') : t('current')}
                </span>
            </div>
            <div className='flex flex-1 justify-content-end'>
                <span style={{ fontSize: fontSizes.verySmall, textAlign: 'right', color: 'var(--HEADER__CONTENT__COLOR)' }}>
                    {typeTop === 'FRG_VAL_UP' ? t('trade_value_net') :
                        typeTop === 'TOP_VAL_UP' ? t('common_values') : t('volume')}
                </span>
            </div>
        </div>
    )
}

export default memo(HeaderTop)

const UI = {
    HeaderTop_view: {
        borderBottomWidth: 1,
        borderBottomStyle: 'solid',
        paddingTop: dimensions.vertical(5),
        paddingBottom: dimensions.vertical(5)
    }
}