                this.IndexMarket[msgKey].indexArr?.push(newIndexNode)
                // console.log('indexArr', glb_sv.timeServer ,newIndexNode)
                this.IndexMarket[msgKey].arrMinutes?.push(newIndexNode)
                this.IndexMarket.setIndexData({
                    key: msgKey,
                    time: '1D',
                    topic: 'INTRADAY_1m',
                    data: newIndexNode,
                    mode: 'append',
                })
