        this.chartConvertIntraday = (data) => {
            const arr = []
            for (let index = 0; index < data.length; index++) {
                const curItem = data[index]
                const prevItem = arr[arr.length - 1]
                const obj = { ...curItem }
                if (!arr.length) {
                    arr.push(obj)
                } else {
                    if (prevItem) {
                        const momentCur = dayjs(curItem.t52, 'YYYYMMDD-HH:mm:ss')
                        const momentPrev = dayjs(prevItem.t52, 'YYYYMMDD-HH:mm:ss')
                        if (momentCur.second() === momentPrev.second() && momentCur.minute() === momentPrev.minute()) {
                            if (index === data.length - 1) {
                                const time = dayjs(obj.t52, 'YYYYMMDD-HH:mm:ss').add(1, 'seconds')
                                obj.t52 = time.format('YYYYMMDD-HH:mm:ss')
                                arr.push(obj)
                            } else prevItem.t32 = prevItem.t32 + curItem.t32
                            continue
                        } else {
                            arr.push(obj)
                        }
                    }
                }
            }
            return arr
        }

