import moment from 'moment'
//import { Button, Col, Container, Content, Row } from 'native-base'
import React, { useContext, useEffect, useState } from 'react'
import { useTranslation } from 'react-i18next'
import Account from '../../../components/account'
import HeaderComponent from '../../../components/header'
import { ColTableData, ModalBottomContent, ModalBottomRowSelect, RowTableData, RowTitleGroup } from '../../../components/trading-component'
//import EmptyView from '../../../components/trading-component/empty-view';
import HeaderList from '../../../components/trading-component/header-list'
import { StoreContext } from '../../../store'
import { StoreTrading } from '../../../store-trading'
//import { dimensions as dm, fontSizes as fs, IconSvg } from '../../../styles';
import { FormatNumber, glb_sv, reqFunct, wait, eventList, sendRequest } from '../../../utils'
import Screens from '../../../utils/constant/screens'
import { Button, notification, Drawer, Input } from 'antd'
import { useHistory } from 'react-router-dom'
import { InputDatePicker } from '../../../basic-components'
import IC_FOLDER_NULL from '../../../assets/images/common/ic_folder_null.png'
// Khai báo ServiceInfo (nếu có)
const ServiceInfo = {
    GET_CONFIRM_ORDER_LIST: {
        reqFunct: reqFunct.GET_CONFIRM_ORDER_LIST,
        WorkerName: 'FOSqOrder01',
        ServiceName: 'FOSqOrder01_Verify_Order',
        ClientSentTime: '0',
        Operation: 'Q',
    },
    CONFIRM_VERIFY_ALL_ORDER: {
        reqFunct: reqFunct.CONFIRM_VERIFY_ALL_ORDER,
        // WorkerName: 'ALTxOrder',
        // ServiceName: 'ALTxOrder_0510_1',
        WorkerName: 'FOSxOrder',
        ServiceName: 'FOSxOrder_0510_1',
        ClientSentTime: '0',
        Operation: 'I',
    },
}

const TransactionList = [
    { index: 0, value: '%', label: 'common_all' },
    { index: 1, value: 'Y', label: 'common_confirmed' },
    { index: 2, value: 'N', label: 'common_not_yet_confirmed' },
]

// Khai báo component
const VerifyOrder = ({}) => {
    const history = useHistory()
    const { styles, theme } = useContext(StoreContext)
    const { userInfo } = useContext(StoreTrading)

    const { t } = useTranslation()
    // All orther state
    const [activeStep, setActiveStep] = useState(0)
    const [isOpenModal, setIsOpenModal] = useState(false)
    const [refreshing, setRefreshing] = React.useState(false)

    // Start define all bussiness state
    const [start_dt, setStart_dt] = useState(moment(glb_sv.objShareGlb['workDate']).subtract(1, 'months').toDate())
    const [from_dt, setFrom_dt] = useState(moment(glb_sv.objShareGlb['workDate']).toDate())

    const [isDatePickerStart, setIsDatePickerStart] = useState(false)
    const [isDatePickerFrom, setIsDatePickerFrom] = useState(false)

    const [listOrderConfirm, setListOrderConfirm] = useState([])

    const [dateRenew, setDateRenew] = useState(null)

    const [visibleTypeTranstation, setVisibleTypeTranstation] = useState(false)
    const [transactionSelect, setTransactionSelect] = useState(TransactionList[0])
    const [checkDraler, setCheckDraler] = useState(false)
    const colSpan = [1, 1, 1, 1]

    // End define form bussiness state
    // -------------------------------------------   Dùng useEffect get các dữ liệu cần thiết còn thiếu
    useEffect(() => {
        getOrderList()

        const commonEvent = glb_sv.commonEvent.subscribe((msg) => {
            if (msg.type === eventList.LOGIN_SUCCESS) {
                // reCall service FOSq
                getOrderList()
            }
        })

        return () => {
            commonEvent.unsubscribe()
        }
    }, [transactionSelect, userInfo.sub_curr, glb_sv.checkLoadHistory])

    // -------------------------------------------   Viết các hàm để set lại bussiness form state. Nên viết đúng một chuẩn duy nhất để dễ control
    // const onRefresh = React.useCallback(() => {
    //     setRefreshing(true)
    //     getOrderList()
    //     wait(100).then(() => setRefreshing(false))
    // }, [])

    const switchStep = {
        goTo: (step) => setActiveStep(step),
        next: () => setActiveStep(activeStep + 1),
        prev: () => {
            setDateRenew(null)
            setActiveStep(activeStep - 1)
        },
        // submit: () => confirmRenewContractMargin(),
        onFinish: () => _checkValueBfSend(),
        getCurrent: () => {
            return activeStep
        },
    }

    const hideModal = () => {
        setIsOpenModal(false)
    }

    // const _onClickBackButton = () => {
    //     if (activeStep > 0) switchStep.prev()
    //     if (activeStep === 0) navigation.goBack()
    // }

    const _checkValueBfSend = () => {
        if (Number(dateRenew) <= 0) {
            hideModal()

            // ToastGlobal.show({
            //     text2: t('note_renew_date'),
            //     type: 'warning',
            // })
            return
        }

        setIsOpenModal(true)
    }

    // -------------------------------------------   Khai báo các hàm gửi (request) lên server

    const getOrderList = (start, from) => {
        const InputParams = [
            userInfo.actn_curr,
            userInfo.sub_curr,
            moment(start || start_dt).format('YYYYMMDD'),
            moment(from || from_dt).format('YYYYMMDD'),
            transactionSelect.value,
        ]
        sendRequest(ServiceInfo.GET_CONFIRM_ORDER_LIST, InputParams, handleGetListOrderConfirmList)
        setRefreshing(false)
    }

    const confirmAllOrders = () => {
        if (preCheck()) {
            notification.warn({
                message: t('common_notify'),
                description: t('empty_list'),
                duration: 4,
            })
            // ToastGlobal.show({
            //     text2: t('empty_list'),
            //     type: 'warning',
            // })
            return
        }
        const InputParams = [
            userInfo.actn_curr,
            userInfo.sub_curr,
            '',
            listOrderConfirm[0]?.c1,
            moment(start_dt).format('YYYYMMDD'),
            moment(from_dt).format('YYYYMMDD'),
            '1',
        ]
        sendRequest(ServiceInfo.CONFIRM_VERIFY_ALL_ORDER, InputParams, handleConfirmAllOrders)
    }

    const preCheck = () => {
        if (listOrderConfirm.length == 0) {
            return true
        }
        return false
    }

    // -------------------------------------------   Khai báo các hàm gửi (request) và nhận (handle respone) dữ liệu từ server
    const handleGetListOrderConfirmList = (reqInfoMap, message) => {
        console.log('handleGetListOrderConfirmList', reqInfoMap, message)
        if (Number(message['Result']) === 0) {
            // ToastGlobal.show({
            //     text2: message['Message'],
            //     type: 'warning',
            // })

            setListOrderConfirm([])
        } else {
            let jsondata = []
            try {
                jsondata = message['Data'] ? JSON.parse(message['Data']) : []
            } catch (err) {}
            setListOrderConfirm(jsondata)
        }
    }

    // const handleConfirmAllOrders = (reqInfoMap, message) => {
    //     console.log('handleConfirmAllOrders', reqInfoMap, message)
    //     if (Number(message['Result']) === 0) {
    //         if (message.Code == 'XXXXX5' || message.Code === '080063' || message.Code === '010012') {
    //             notification.warn({
    //                 message: t('common_notify'),
    //                 description: t('request_hanlde_not_success_try_again'),
    //                 duration: 4,
    //             })
    //         } else if (message['Code'] !== '080128') {
    //             notification.warn({
    //                 message: t('common_notify'),
    //                 description: t('empty_list'),
    //                 duration: 4,
    //             })
    //         }
    //         return
    //     } else {
    //         let jsondata = []
    //         try {
    //             // jsondata = message['Data'] ? JSON.parse(message['Data']) : []
    //             notification.warn({
    //                 message: t('common_notify'),
    //                 description: message['Message'],
    //                 duration: 4,
    //             })
    //         } catch (err) {}
    //         // onRefresh()
    //         getOrderList()
    //     }
    // }
    const handleConfirmAllOrders = (reqInfoMap, message) => {
        // console.log('handleConfirmAllOrders', reqInfoMap, message)
        if (Number(message['Result']) === 0) {
            if (message.Code == 'XXXXX5' || message.Code === '080063' || message.Code === '010012') {
                notification.warn({
                    message: t('common_notify'),
                    description: t('request_hanlde_not_success_try_again'),
                    duration: 4,
                })
            } else if (glb_sv.otpCases.includes(message.Code)) {
                glb_sv.objShareGlb['sessionInfo']['Otp'] = ''

                let data
                try {
                    data = JSON.parse(message.Data)[0]
                } catch (error) {
                    return
                }

                const otp_Type = Number(data.c1)
                const expTimeOtp = Number(data.c2)
                let reqOtpMessage = 'OTP'
                if (Number(data['c1']) === 2) {
                    //-- dạng thẻ ma trận
                    reqOtpMessage = 'OTP ' + data['c3']
                }
                glb_sv.commonEvent.next({
                    type: eventList.OTP_MODAL,
                    time: expTimeOtp,
                    message: reqOtpMessage,
                    messageOTP: message.Message,
                    hasOTP: false,
                    otp_Type,
                })
            } else {
                notification.warn({
                    message: t('common_notify'),
                    description: t('request_hanlde_not_success_try_again'),
                    duration: 4,
                })
            }
        } else {
            try {
                notification.success({
                    message: t('common_notify'),
                    description: message['Message'],
                    duration: 4,
                })
            } catch (err) {}
            // onRefresh()
            getOrderList()
        }
    }

    const getColor = (status) => {
        if (status === 'N') return styles.REF__COLOR
        if (status === 'R' || status === 'D') return styles.DOWN__COLOR
        if (status === 'Y') return styles.UP__COLOR
    }

    const getColorSub = (sub) => {
        const listColor = [styles.UP__COLOR, styles.REF__COLOR, styles.DOWN__COLOR]
        return listColor[Number(sub)] || styles.REF__COLOR
    }

    const ViewLastTransfer = ({ item, index }) => {
        return (
            <>
                <RowTableData
                    type='table'
                    onClick={() => history.push('/stock/detail-verify-order', { data: item })}
                    //  onPress={() => navigation.navigate(Screens.DETAIL_ORDERS, { data: item, onRefresh: onRefresh })}
                >
                    <ColTableData colSpan={6} text={item.c2} typeOrder={item.c3 === '1' ? 'buy' : 'sell'} />
                    <ColTableData colSpan={6} text={FormatNumber(item.c4)} textAlign='center' />
                    <ColTableData colSpan={6} text={FormatNumber(item.c5)} textAlign='center' />
                    <ColTableData
                        colSpan={6}
                        text={item.c9 == 'Y' ? t('common_confirmed') : t('common_not_yet_confirmed')}
                        colorText={getColor(item.c9)}
                        textAlign='right'
                    />
                </RowTableData>
            </>
        )
    }

    const hideDatePickerStart = () => {
        console.log('hide')
        setIsDatePickerStart(false)
    }

    const hideDatePickerFrom = () => {
        setIsDatePickerFrom(false)
    }

    const onSelectTransaction = (value) => {
        setTransactionSelect(value)
        setCheckDraler(false)
        getOrderList()
    }
    const onChangeStartday = (_, dateOutPut) => {
        const formatResult = moment(dateOutPut, 'DD/MM/YYYY').format('YYYYMMDD')
        setStart_dt(formatResult)
        getOrderList(formatResult)
    }
    const onChangeEndDay = (_, dateOutPut) => {
        const formatResult = moment(dateOutPut, 'DD/MM/YYYY').format('YYYYMMDD')
        setFrom_dt(formatResult)
        getOrderList(formatResult)
    }
    return (
        <div>
            {/* <HeaderComponent
                title={t('title_confirm_order')}
                transparent
                titleAlgin='start'
                colorTitle={styles.PRIMARY__CONTENT__COLOR}
                alignChild={['left', 'center', 'center']}
            /> */}
             <HeaderComponent
                title={t('title_confirm_order')}
                transparent
                titleAlign='start'
                alignChild={['left', 'center', 'center']}
            />
            <div style={{ backgroundColor: styles.PRIMARY__BG__COLOR }}>
                <div>
                    <div className='pl-2' style={{ overflow: 'hidden' }}>
                        <Account />
                    </div>
                    <div className='pl-1 pr-1 mt-1 mb-2'>
                        <div className='pr-1 pl-1 mt-2 mb-2 border-none'>
                            <Input
                                suffix={'>'}
                                style={{ padding: '12px 16px', background: 'var(--INPUT__BG)', borderRadius: '8px' }}
                                size='middle'
                                className='input__component'
                                placeholder={t('choose_trans_acc')}
                                onClick={() => {
                                    setCheckDraler(true)
                                }}
                                value={t(`${transactionSelect.label}`)}
                                // value={objSubReceive?.c2}
                            />
                        </div>
                        <div style={UI.GroupFilter}>
                            <div className='flex flex-1'>
                                <InputDatePicker labelStyle={{fontSize:'12px'}} label={t('common_from_date')} value={start_dt} onChange={onChangeStartday} />
                                <InputDatePicker labelStyle={{fontSize:'12px'}} label={t('common_to_date')} value={from_dt} onChange={onChangeEndDay} />
                            </div>
                        </div>
                    </div>
                    <RowTitleGroup hasDivider />
                    <div className=''>
                        <RowTitleGroup
                            // hasDivider
                            text={t('hitory_transaction')}
                            iconRight={
                                <Button
                                    disabled={preCheck()}
                                    full
                                    transparent
                                    onClick={() => {
                                        confirmAllOrders()
                                    }}
                                    style={{
                                        borderRadius: 50,
                                        backgroundColor: styles.PRIMARY,
                                        height: 30,
                                        width: 120,
                                        opacity: preCheck() ? 0.5 : 1,
                                    }}>
                                    <div style={{ color: '#fff', fontSize: 13 }}>{t('confirm_all_order_tickets')}</div>
                                </Button>
                            }
                        />
                    </div>
                    <div className=''>
                        <HeaderList typeHeader='CONFIRM_ORDER' />
                    </div>
                    <div className='ml-2 mr-2'>
                        {' '}
                        {listOrderConfirm.length > 0 ? (
                            listOrderConfirm.map((item, i) => <ViewLastTransfer key={i} item={item} />)
                        ) : (
                            <div style={{ textAlign: 'center', alignSelf: 'center', height: '200px' }}>
                                <img src={IC_FOLDER_NULL} />
                            </div>
                        )}
                    </div>
                </div>
            </div>
            {/* **************************************** Modal submit ******************************************/}
            <div>
                <Drawer
                    onClose={() => {
                        setCheckDraler(false)
                    }}
                    title={<div className='text-center text-primary'>{t('contract_type')}</div>}
                    placement={'bottom'}
                    closable={false}
                    visible={checkDraler}>
                    {TransactionList.map((item, index) => (
                        <ModalBottomRowSelect
                            onClick={() => onSelectTransaction(item)}
                            text={t(item.label)}
                            key={index}
                            checked={transactionSelect.value === item.value}
                        />
                    ))}
                    {/* {listCanTransfer.map((item, index) => (
                        <ModalBottomRowSelect key={index} onClick={() => onSelectActReceive(item)} text={item.c2} checked={objSubReceive?.c2 === item.c2} />
                    ))} */}
                </Drawer>
            </div>
        </div>
    )
}

export default VerifyOrder
