import React, { useState, useContext, useEffect, useRef, memo, useCallback } from 'react'
import { useTranslation } from 'react-i18next'
import { useHistory } from 'react-router-dom'
import { routes } from 'navigation/routes'
import { SearchOutlined, BellFilled } from '@ant-design/icons'
import { Button } from 'antd'
import { fontSizes, fontWeights, dimensions } from '../../../styles'
import { glb_sv, subcribeFunct, eventList, STORE_KEY, RealTimeHandler, reqFunct, sendRequest } from '../../../utils'
import { ViewIndex } from '..'
import { ScrollView } from 'basic-components'
import { StoreContext } from 'store'
import { ReactComponent as Search } from 'assets/images/common/search.svg'
import { ReactComponent as Notification } from 'assets/images/common/notification_bell.svg'
import SearchStockModal from 'layouts/place-order/searchStockModal'

const ServiceInfo = {
    getStockListByIndex: {
        reqFunct: reqFunct.GET_STK_LIST_PRB,
        WorkerName: 'FOSqMkt02',
        ServiceName: 'FOSqMkt02_IndexMgt',
        ClientSentTime: '0',
        Operation: 'Q',
    },
}

const HeaderMarket = ({ activeSelect, setActiveSelect, setIndexCd }) => {
    const IndexMarketView = new RealTimeHandler('MARKET_VIEW')
    const SubscribeStockIndex = new RealTimeHandler('STOCK_INDEX')
    const { styles } = useContext(StoreContext)
    const history = useHistory()
    const { t } = useTranslation()

    const [listIndex, setListIndex] = useState([])
    const [hiddenChart, setHiddenChart] = useState(true)
    const [active, setActive] = useState('')
    const activeRef = useRef('')
    const subListIndex = useRef(true)
    const timeoutRef = useRef(null)
    // const countNotifiRef = useRef('')
    const [countNotifi, setCountNotifi] = useState('')
    const [showSearchModal, setShowSearchModal] = useState(false)

    const searchRef = useRef(null)

    useEffect(() => {
        if (listIndex.length === 0) {
            IndexMarketView.subscribe({
                onTimeout: () => {},
                topic: ['MDDS|I', 'MDDS|BI'],
                value: ['HSXIndex', 'HNXIndex', 'HNXUpcomIndex', 'VN30'],
            })
            const data = glb_sv.listIndex.server.map((e, index) => {
                return { key: String(index), sortIndex: getSortIndex(e), ...glb_sv.IndexMarket[e] } || {}
            })
            const sortList = data.sort((a, b) => a.sortIndex - b.sortIndex)
            setListIndex([...sortList])
        }
        IndexMarketView.listen((msg) => {
            if (msg.type === eventList.GET_INDEXS_DONE) {
                IndexMarketView.unsubscribe({
                    onTimeout: () => {},
                    topic: ['MDDS|I', 'MDDS|BI'],
                    value: ['HSXIndex', 'HNXIndex', 'HNXUpcomIndex', 'VN30'],
                })
                IndexMarketView.subscribe({
                    onTimeout: () => {},
                    topic: ['MDDS|I', 'MDDS|BI'],
                    value: ['HSXIndex', 'HNXIndex', 'HNXUpcomIndex', 'VN30'],
                })
            }

            if (msg.type === eventList.LOADING_FAV) {
                IndexMarketView.unsubscribe({
                    onTimeout: () => {},
                    topic: ['MDDS|I', 'MDDS|BI'],
                    value: ['HSXIndex', 'HNXIndex', 'HNXUpcomIndex', 'VN30'],
                })
                IndexMarketView.subscribe({
                    onTimeout: () => {},
                    topic: ['MDDS|I', 'MDDS|BI'],
                    value: ['HSXIndex', 'HNXIndex', 'HNXUpcomIndex', 'VN30'],
                })
            }

            if (msg.type === eventList.SUB_INDEX) {
                const data = glb_sv.listIndex.server.map((e, index) => {
                    return { key: String(index), sortIndex: getSortIndex(e), ...glb_sv.IndexMarket[e] } || {}
                })
                const sortList = data.sort((a, b) => a.sortIndex - b.sortIndex)
                if (activeRef?.current?.length == 0) {
                    activeRef.current = data[0]?.indexCode
                    setActive(data[0]?.indexCode)
                }
                setListIndex([...sortList])
            }
        })
        return () => {
            IndexMarketView.unsubscribe({
                onTimeout: () => {},
                topic: ['MDDS|I', 'MDDS|BI'],
                value: ['HSXIndex', 'HNXIndex', 'HNXUpcomIndex', 'VN30'],
            })
            IndexMarketView.clearRealTimeListener({ unsubscribe: true })
        }
    }, [])
    useEffect(() => {
        const commonEvent = glb_sv.commonEvent.subscribe((msg) => {
            if (msg.type === 'COUNT_NOTIFI') {
                setCountNotifi(msg.data.length)
            }
        })
        return () => {
            commonEvent.unsubscribe()
        }
    }, [])
    const getSortIndex = (index) => {
        if (index === 'HSXIndex') return 1
        if (index === 'HNXIndex') return 2
        if (index === 'HNXUpcomIndex') return 3
        if (index === 'VN30') return 4
        if (index === 'HNX30') return 5
        return 1000 // Một số lớn k thể vượt qua
    }

    const changeActiveIndex = (value) => {
        if (value === activeRef.current) {
            subListIndex.current = false
            history.push(routes.INDEX_INFO.path, { indexCode: activeRef.current })
            return
        }
        activeRef.current = value
        setActive(value)
        setIndexCd(value)
        // if (timeoutRef.current) clearTimeout(timeoutRef.current)
        // timeoutRef.current = setTimeout((newValue) => {
        //     if (activeRef.current.includes('HNXUpcomIndex')) {
        //         subcribeFunct(null, 'UNSUB', 'UPC', ["MKT_TOP", 'UPC']);
        //     } else if (activeRef.current.includes('HNX')) {
        //         subcribeFunct(null, 'UNSUB', 'HNX', ["MKT_TOP", 'HNX']);
        //     } else subcribeFunct(null, 'UNSUB', 'HSX', ["MKT_TOP", 'HSX']);
        //     activeRef.current = newValue;

        //     if (activeRef.current.includes('HNXUpcomIndex')) {
        //         subcribeFunct(null, 'SUB', 'UPC', ["MKT_TOP", 'UPC']);
        //     } else if (activeRef.current.includes('HNX')) {
        //         subcribeFunct(null, 'SUB', 'HNX', ["MKT_TOP", 'HNX']);
        //     } else subcribeFunct(null, 'SUB', 'HSX', ["MKT_TOP", 'HSX']);

        //     if (glb_sv.LIST_GET_INDEX_MSG.includes(activeRef.current)) {
        //         throttled.current(activeRef.current);
        //     } else subcribeFunct(null, 'INDEX_MSG', activeRef.current);
        // }, 400, value);
    }

    return (
        <div>
            <div
                className="flex align-items-center"
                style={{ height: 40, paddingRight: dimensions.indent, paddingLeft: dimensions.indent }}
            >
                <div
                    className="flex flex-6 flex-direction-column justify-content-center align-items-start"
                    style={{
                        color: 'var(--primary__content__color)',
                        fontSize: fontSizes.xmedium,
                        fontWeight: fontWeights.semiBold,
                        overflowY: 'scroll',
                    }}
                >
                    <ScrollView horizontal>
                        <div className="flex">
                            <Button
                                type="text"
                                className="flex"
                                style={{
                                    backgroundColor: 'var(--PRIMARY__BG__COLOR)',
                                    marginRight: 10,
                                    padding: 0,
                                    fontSize: fontSizes.normal,
                                    color: activeSelect['list_fav'] ? 'var(--PRIMARY)' : 'var(--PLACEHODLER__COLOR)',
                                    borderColor: 'transparent',
                                    fontWeight: 400,
                                }}
                                onClick={() => setActiveSelect({ ['list_fav']: true })}
                            >
                                {t('list_fav')}
                            </Button>
                            <Button
                                type="text"
                                className="flex"
                                style={{
                                    backgroundColor: 'var(--PRIMARY__BG__COLOR)',
                                    marginRight: 10,
                                    padding: 0,
                                    fontSize: fontSizes.normal,
                                    color: activeSelect['market_info_tab']
                                        ? 'var(--PRIMARY)'
                                        : 'var(--PLACEHODLER__COLOR)',
                                    borderColor: 'transparent',
                                    fontWeight: 400,
                                }}
                                onClick={() => setActiveSelect({ ['market_info_tab']: true })}
                            >
                                {t('market_info_tab')}
                            </Button>
                            <Button
                                type="text"
                                className="flex"
                                style={{
                                    backgroundColor: 'var(--PRIMARY__BG__COLOR)',
                                    marginRight: 10,
                                    padding: 0,
                                    fontSize: fontSizes.normal,
                                    color: activeSelect['market_cap'] ? 'var(--PRIMARY)' : 'var(--PLACEHODLER__COLOR)',
                                    borderColor: 'transparent',
                                    fontWeight: 400,
                                }}
                                onClick={() => setActiveSelect({ ['market_cap']: true })}
                            >
                                {t('market_cap')}
                            </Button>
                        </div>
                    </ScrollView>
                </div>
                <div className="flex flex-2 justify-content-end">
                    <span
                        onClick={() => setShowSearchModal(true)}
                        style={{
                            display: 'flex',
                            alignItems: 'center',
                        }}
                    >
                        <Search style={{ color: 'var(--PRIMARY__CONTENT__COLOR)' }} />
                        {/* <SearchOutlined style={{ color: 'var(--ICON__COLOR)', padding: 8, backgroundColor: 'var(--SECOND__BORDER__COLOR)', borderRadius: 50, fontSize: '1rem', marginRight: 10 }} /> */}
                    </span>
                    <span
                        onClick={() => {
                            history.push('/notification')
                            // glb_sv.countNotifycation =
                        }}
                        style={{
                            display: 'flex',
                            alignItems: 'center',
                            marginLeft: '0.25rem',
                        }}
                    >
                        <Notification fillFirst={'var(--PRIMARY__CONTENT__COLOR)'} />
                        {/* {countNotifi} chuong */}
                        {/* <BellFilled style={{ color: 'var(--ICON__COLOR)', padding: 8, backgroundColor: 'var(--SECOND__BORDER__COLOR)', borderRadius: 50, fontSize: '1rem' }} /> */}
                    </span>
                </div>
            </div>
            <div
                style={{
                    marginVertical: dimensions.vertical(16, 0.3),
                    flexDirection: 'row',
                    marginLeft: dimensions.moderate(12, 0.3),
                    marginRight: dimensions.moderate(12, 0.3),
                }}
            >
                <ScrollView horizontal>
                    <div className="flex">
                        {listIndex?.map((item) => (
                            <ViewIndex
                                key={`${item.indexName}-${item.indexValue}`}
                                color={item.indexColor}
                                title={item.indexName}
                                value={item.indexValue}
                                type={item.indexCode}
                                valueChang={item.indexValueChang}
                                ratioValue={item.indexRatioChang}
                                onChange={(e) => changeActiveIndex(e)}
                                styles={styles}
                                hiddenChart={hiddenChart}
                                active={active}
                            />
                        ))}
                    </div>
                </ScrollView>
            </div>
            <SearchStockModal
                onChangeStock={(item) => history.push(routes.STOCK_INFO.path, { stockCode: item })}
                isOnShow={showSearchModal}
                setIsOnShow={setShowSearchModal}
                searchRef={searchRef}
            />
        </div>
    )
}

export default React.memo(HeaderMarket)
