import { DatePicker } from 'antd'
import moment from 'moment'
import { useState } from 'react'
import { ReactComponent as IC_CALENDAR } from '../../assets/images/common/ic_calendar.svg'

const dateFormatList = 'YYYYMMDD'
const { RangePicker } = DatePicker

const RangeDatePicker = ({
    onValueChange = (e) => null,
    placeholder,
    fromDate = moment(),
    toDate = moment(),
    disabledFromDate = moment(),
    suffixIcon = <IC_CALENDAR fill='var(--TEXT__1)' />,
}) => {
    const [dates, setDates] = useState([]);
    const [hackValue, setHackValue] = useState();
    const [value, setValue] = useState([fromDate, toDate]);
    const disabledDate = current => {
        const tooEff = disabledFromDate.diff(current, 'days') > 0;
        return tooEff;
    };

    const onOpenChange = open => {
        if (open) {
            setHackValue([]);
            setDates([]);
        } else {
            setHackValue(undefined);
        }
    };

    return (
        <RangePicker
            value={hackValue || value}
            disabledDate={disabledDate}
            onCalendarChange={val => setDates(val)}
            onChange={val => {
                onValueChange(val)
                setValue(val)
            }}
            // onOpenChange={onOpenChange}
            inputReadOnly={true}
            format={['DD/MM/YYYY']}
            placeholder={placeholder}
            allowClear={false}
        // suffixIcon={suffixIcon}
        />
    );
}

export default RangeDatePicker