import { glb_sv, eventList } from '../../../utils';

function getAllLocalFavList() {
    const data = localStorage.getItem('FAV_LIST_LS');
    return data ? JSON.parse(data) : []
}

function clearAllLocalFavList() {
    localStorage.removeItem('FAV_LIST_LS')
}

function saveNewFavList(listName, accountId) {
    const currentFavList = getAllLocalFavList();
    const curFav = {
        c0: '',               // Tài khoản
        c1: String(Math.floor(Math.random() * 10000)),      // ID
        c2: listName,                   // Name List
        ListStock: []
    }
    currentFavList.push(curFav)
    const sameArr = glb_sv.allListFav.filter(({ c2: name1 }) => !currentFavList.some(({ c2: name2 }) => name1 === name2)); //HNX30 + VN30
    glb_sv.allListFav = sameArr.concat(currentFavList);
    glb_sv.activeList = curFav;
    glb_sv.commonEvent.next({ type: eventList.CHANGE_LIST_ACTIVE })
    localStorage.setItem('FAV_LIST_LS', JSON.stringify(currentFavList))
}

