import { useState, useEffect, useContext } from "react";
import {
    ListFavoriteStock,
    ModalFavoriteList,
} from "../../components/portfolio";
import {
    AddStockToListFav,
    editFavListName,
    delFavList,
} from "../../components/portfolio/favorite-func/favorite.action";
import { dimensions } from "../../styles";
import { glb_sv, eventList, reqFunct, sendRequest } from "../../utils";
import { useTranslation } from 'react-i18next';
import AddNewFavList from '../../components/portfolio/add-favlist-tab'
import {
    PlusOutlined,
    EllipsisOutlined,
} from "@ant-design/icons";
import { routes } from "navigation/routes";
import { Button, Drawer, Modal, Input, Form, notification } from "antd";
import { useHistory } from "react-router-dom";
import IC_UPDATE from "../../assets/images/common/ic_update.svg";
import IC_ADD_CODECK from "../../assets/images/common/ic_add_codeck.svg";
import IC_ADD_CATEGORY from "../../assets/images/common/ic_add_category.svg";
import IC_UPDATE_CATEGORY from "../../assets/images/common/ic_update_category.svg";
import IC_DELETE_CATEGORY from "../../assets/images/common/ic_delete_category.svg";
import { ScrollView } from "basic-components";
import SearchStockModal from '../place-order/searchStockModal';
import { StoreTrading } from "store-trading";

const ServiceInfo = {
    getStockListByIndex: {
        reqFunct: reqFunct.GET_STK_LIST_PRB,
        WorkerName: 'FOSqMkt02',
        ServiceName: 'FOSqMkt02_IndexMgt',
        ClientSentTime: '0',
        Operation: 'Q',
    },
    DeleteFavList: {
        reqFunct: reqFunct.REMOVE_FAV_LIST,
        WorkerName: 'FOSxID01',
        ServiceName: 'FOSxID01_FavoritesMgt',
        ClientSentTime: '0',
        Operation: 'Q',
    },
    EditFavList: {
        reqFunct: reqFunct.EDIT_NAME_FAV_LIST,
        WorkerName: 'FOSxID01',
        ServiceName: 'FOSxID01_FavoritesMgt',
        ClientSentTime: '0',
        Operation: 'U',
    },
}

export default function PortfolioLayout({ getStkList }) {
    const { authFlag } = useContext(StoreTrading);
    const [showAddStock2Fav, setShowAddStock2Fav] = useState(false);
    const [activeList, setActiveList] = useState(glb_sv.activeList)
    const [isModalVisible, setModal] = useState(false);
    const [showRmStkFav, setShowRmStk] = useState(false);

    return (
        <div className="flex flex-1 flex-direction-column width_full"
        // style={authFlag ? { maxHeight: 'calc(100vh - 235px)', overflowY: 'scroll' } : {}}
        >
            <HeaderView
                setShowAddStock2Fav={setShowAddStock2Fav}
                showAddStock2Fav={showAddStock2Fav}
                getStkList={(index) => getStkList(index)}
                setModal={setModal}
                showRmStkFav={showRmStkFav}
                setShowRmStk={setShowRmStk}
                isModalVisible={isModalVisible}
            />
            <div
                className="flex flex-1 flex-direction-column"
                style={{
                    paddingLeft: dimensions.vertical(12),
                    paddingRight: dimensions.vertical(12)
                }}
            >
                <ListFavoriteStock
                    AddStockToListFav={setShowAddStock2Fav}
                    showRmStkFav={showRmStkFav}
                    setShowRmStk={setShowRmStk}
                />

                <ModalFavoriteList
                    isModalVisible={isModalVisible}
                    setModal={setModal}
                />
            </div>
        </div>
    );
}

const HeaderView = ({
    setShowAddStock2Fav,
    showAddStock2Fav,
    getStkList,
    navigation,
    setModal,
    showRmStkFav,
    setShowRmStk,
    isModalVisible,
}) => {
    const history = useHistory();
    const { t } = useTranslation();
    const [activeList, setActiveList] = useState(glb_sv.activeList);
    const [allFavList, setAllFavList] = useState(glb_sv.allListFav);
    const [visible, setVisible] = useState(false);
    const [visibleUpdate, setVisibleUpdate] = useState(false);
    const [visibleDelete, setVisibleDelete] = useState(false);
    const [nameList, setNameList] = useState(glb_sv.activeList.c2);
    const [showAddFav, setShowAddFav] = useState(false);
    const [currentIndex, setCurrentIndex] = useState(glb_sv.currentIndex_FAV)
    const [isShowSearch, setIsShowSearch] = useState(false);

    // useEffect(() => {
    //     console.log('portfolio allListFav change ===> ', JSON.stringify(glb_sv.allListFav))
    //     setAllFavList([...glb_sv.allListFav]);
    // }, [glb_sv.allListFav])

    useEffect(() => {
        getStkList(glb_sv.allListFav[currentIndex]?.c0)
        setNameList(glb_sv.activeList.c2);
    }, [currentIndex])

    useEffect(() => {
        const commonEvent = glb_sv.commonEvent.subscribe((msg) => {
            if (msg.type === eventList.CHANGE_LIST_ACTIVE) {
                setActiveList({ ...glb_sv.activeList });
                setAllFavList([...glb_sv.allListFav])
                setCurrentIndex(glb_sv.allListFav.findIndex(x => x.c1 === glb_sv.activeList.c1));
            }
            if (msg.type === eventList.LOGIN_SUCCESS) {
                setAllFavList([...glb_sv.allListFav]);
            }
        });
        return () => {
            commonEvent.unsubscribe();
        };
    }, []);

    useEffect(() => {
        setShowAddStock2Fav(false);
        if (showAddStock2Fav) {
            handleAddNew();
        }
    }, [showAddStock2Fav])

    const changeName = (event) => {
        setNameList(event.target.value);
    };

    const editFavListNamee = () => {
        if (!nameList) return;
        if (glb_sv.allListFav.some((e) => e.c2 === nameList)) return;
        if (glb_sv.authFlag) {
            const InputParams = ['FAV_MOD', activeList.c1, nameList]
            sendRequest(ServiceInfo.EditFavList, InputParams, handleChangeNameFav)
        } else {
            editFavListName(nameList, activeList.c1);
            const findIndex = glb_sv.allListFav.findIndex(e => e.c1 === activeList.c1);
            glb_sv.allListFav[findIndex].c2 = nameList;
            glb_sv.activeList = glb_sv.allListFav[findIndex];
            setAllFavList([...glb_sv.allListFav]);
            setActiveList({ ...glb_sv.activeList });
            setVisibleUpdate(false)
            glb_sv.commonEvent.next({ type: eventList.CHANGE_LIST_ACTIVE })
        }
    };

    const handleChangeNameFav = (reqInfoMap, message) => {
        setVisibleUpdate(false)
        if (Number(message['Result']) === 0) {
            return
        } else {
            const findIndex = glb_sv.allListFav.findIndex((x) => x.c1 === activeList.c1)
            if (findIndex >= 0) {
                glb_sv.allListFav[findIndex].c2 = nameList.trim()
                setAllFavList([...glb_sv.allListFav])
            }
        }
    }

    const handleButton = () => {
        if (activeList.c1) history.push(routes.SEARCH_STOCK.path, { actionType: 'FAV_ACTION', activeList })
        else {
            history.push(routes.ADD_FAV_LIST.path, { actionType: 'ADD_FAV_LIST' })
        }
    }

    function checkHidenPopupAdd() {
        setShowAddFav(false)
    }

    const deleteFavListName = () => {
        if (glb_sv.authFlag) {
            const InputParams = ['FAV_REMOVE', activeList.c1]
            sendRequest(ServiceInfo.DeleteFavList, InputParams, handleDeleteFavList)
        } else {
            delFavList(activeList.c1);
            if (activeList.c1) {
                const currentIndex = glb_sv.allListFav.findIndex(node => node['c1'] == activeList['c1'])
                glb_sv.allListFav.splice(currentIndex, 1)
                const newActiveList = glb_sv.allListFav[currentIndex - 1];
                glb_sv.activeList = newActiveList ? { ...newActiveList } : { ListStock: [] };
                setActiveList(newActiveList ? { ...newActiveList } : { ListStock: [] });
                setAllFavList([...glb_sv.allListFav])
                glb_sv.commonEvent.next({ type: eventList.CHANGE_LIST_ACTIVE });
            }
        }
        setVisibleDelete(false);
    };

    const handleDeleteFavList = (reqInfoMap, message) => {
        if (Number(message['Result']) === 0) {
            return
        } else {
            const { inputParam } = reqInfoMap
            const find = glb_sv.allListFav.findIndex((node) => node.c1 == inputParam[1])
            glb_sv.allListFav.splice(find, 1)
            glb_sv.activeList = glb_sv.allListFav[find - 1];
            setActiveList(glb_sv.activeList ? { ...glb_sv.activeList } : { ListStock: [] });
            setAllFavList([...glb_sv.allListFav])
            glb_sv.commonEvent.next({ type: eventList.CHANGE_LIST_ACTIVE });
        }
    }

    const footerPopupUpdate = () => {
        return (
            <div className="flex justify-content-end" style={{ padding: "10px", backgroundColor: 'var(--PRIMARY__BG__COLOR)' }}>
                <div className="flex" onClick={() => setVisibleUpdate(false)}>
                    <span style={{ ...UI.btnCancel, marginRight: 15 }}>Hủy</span>
                </div>
                <div className="flex" onClick={editFavListNamee}>
                    <span
                        style={{
                            fontSize: "20px",
                            // fontFamily: "SF pro Display",
                        }}
                    >
                        <Button
                            type="primary"
                            size="middle"
                            inline
                            style={UI.btnUpdateCategory}
                        >
                            {t('save')}
                        </Button>
                    </span>
                </div>
            </div>
        );
    };

    const modalUpdateCategory = () => {
        return (
            <Modal
                visible={visibleUpdate}
                transparent
                closable={false}
                onClose={false}
                bodyStyle={{ ...UI.bodyModal }}
                footer={null}
                width={"80%"}
                id="popup-update-category"
            >
                <div className="flex justify-content-start flex-direction-column">
                    <div style={UI.category}>
                        {/* {t('favorites')} */}
                        {/* {t("list_of_interest")} */}
                        <div>
                            <img src={IC_UPDATE} size={"lg"} />
                        </div>
                        <div>{t('modify_nm_of_fav')}</div>
                    </div>
                    <div className="flex flex-direction-column" style={UI.categoryItem}>
                        {/* ------------------------- */}

                        <Form>
                            <Form.Item
                                label={<div style={{ color: 'var(--PRIMARY__CONTENT__COLOR)' }}>{t('watchlist_name')}</div>}
                            >
                                <Input autoFocus={true}
                                    placeholder={t('watchlist_name')}
                                    size={"small"}
                                    bordered={false}
                                    style={{ backgroundColor: 'var(--INPUT__BG)', fontSize: 18 }}
                                    value={nameList}
                                    onChange={changeName}
                                />
                            </Form.Item>
                        </Form>
                        {/* ----------------------------- */}
                    </div>
                </div>
                <div className="flex justify-content-end" style={{ padding: "10px", backgroundColor: 'var(--PRIMARY__BG__COLOR)' }}>
                    <div className="flex" onClick={() => setVisibleUpdate(false)}>
                        <span style={{ ...UI.btnCancel, marginRight: 15 }}>{t('common_Cancel')}</span>
                    </div>
                    <div className="flex" onClick={editFavListNamee}>
                        <span
                            style={{
                                fontSize: "20px",
                                // fontFamily: "SF pro Display",
                            }}
                        >
                            <Button
                                type="primary"
                                size="middle"
                                inline
                                style={UI.btnUpdateCategory}
                            >
                                {t('save')}
                            </Button>
                        </span>
                    </div>
                </div>
            </Modal>
        );
    };

    const modalDeleteCategory = () => {
        return (
            <Drawer
                id="watchlist_drawer"
                bodyStyle={{ ...UI.bodyModal }}
                // title={<div style={{ fontWeight: "600" }}>Quản lý danh mục</div>}
                placement={"bottom"}
                closable={false}
                onClose={() => setVisibleDelete(!visibleDelete)}
                visible={visibleDelete}
                key={"bottom"}
                height={280}
            >
                <div className="flex justify-content-start flex-direction-column">
                    <div style={UI.category}>
                        {/* {t('favorites')} */}
                        {/* {t("list_of_interest")} */}
                        <div><img src={IC_DELETE_CATEGORY} s /></div>
                        <div>{t('remove_a_favorites')}</div>
                    </div>
                    <div className="flex flex-direction-column" style={UI.categoryItem}>
                        <div
                            style={{ fontSize: "14px", color: "gray", textAlign: "center" }}
                        >
                            <div>{t('delete_fav_note')}</div>
                            <div>{t('cfm_menu_rmv_content')}</div>
                        </div>
                    </div>
                    <div className="flex flex-direction-column" style={UI.categoryItem}>
                        <div style={{ textAlign: "center" }}>
                            <div onClick={deleteFavListName}>
                                <div
                                    style={{
                                        fontSize: "20px",
                                        // fontFamily: "SF pro Display",
                                    }}
                                >
                                    <Button
                                        type="primary"
                                        size="middle"
                                        style={UI.btnDeleteCategory}
                                    >
                                        OK
                  </Button>
                                </div>
                            </div>
                            <div onClick={() => setVisibleDelete(false)}>
                                <div style={UI.btnCancel}>{t('common_button_cancel')}</div>
                            </div>
                        </div>
                    </div>
                </div>
            </Drawer>
        );
    };

    const handleAddNew = () => {
        if (!!glb_sv.activeList && !!glb_sv.activeList.c1 && glb_sv.activeList?.c1 !== 'HNX30' && glb_sv.activeList?.c1 !== 'VN30' && glb_sv.activeList?.c1 !== '00') {
            // history.push(routes.SEARCH_STOCK.path, { actionType: 'FAV_ACTION', activeList })
            setIsShowSearch(true);
        } else {
            setShowAddFav(true);
        }
    }

    const changeIndexFav = (activeFavList, index) => {
        if(activeFavList.c1 === activeList.c1){
            return
        }
        glb_sv.currentIndex_FAV = index;
        glb_sv.activeList = activeFavList;
        setCurrentIndex(index);
        setActiveList(activeFavList);
        glb_sv.commonEvent.next({ type: eventList.CHANGE_LIST_ACTIVE });
    }

    const handleStockSelect = stock => {
        setIsShowSearch(false);
        glb_sv.commonEvent.next({ type: eventList.MSG_ADD_NEW_STK_2FAV, data: { stk: stock, keyFlv: activeList?.c1 } })
    }

    return (
        <div className="flex" style={UI.header}>
            <div className='flex flex-6 flex-direction-column justify-content-center align-items-start' style={{ overflowY: 'scroll' }}>
                <ScrollView horizontal>
                    <div className='flex' >
                        {allFavList.map((item, index) => {
                            return (
                                <Button onClick={() => changeIndexFav(item, index)} key={item.c1}
                                    style={{
                                        color:  currentIndex == index ? 'var(--PRIMARY)' : 'var(--PRIMARY__CONTENT__COLOR)',
                                        // fontWeight:  currentIndex == index ? 'bold' : 'unset',
                                        borderWidth: 1,
                                        borderColor: currentIndex == index ? 'var(--PRIMARY)' : 'var(--PRIMARY__BORDER__COLOR)',
                                        marginRight: 5, backgroundColor: 'var(--BUTTON__SECONDARY)', borderRadius: 5
                                    }}>
                                    {item.c2}
                                </Button>)
                        })}
                    </div>
                </ScrollView>
            </div>
            <div className='flex flex-1 justify-content-end'>
                {/* <span style={{ marginTop: "7px" }}>
                    <Button
                        style={{ background: 'var(--PRIMARY__BG__COLOR)', color: 'var(--PRIMARY__CONTENT__COLOR)' }}
                        shape="circle"
                        icon={<PlusOutlined size={"sm"} />}
                        onClick={handleAddNew}
                    />
                </span> */}
                <span style={{ marginLeft: "10px", marginTop: "7px" }}>
                    <Button
                        style={{ background: 'var(--BUTTON__SECONDARY)', color: 'var(--PRIMARY__CONTENT__COLOR)' }}
                        shape="circle"
                        icon={<EllipsisOutlined />}
                        onClick={() => setVisible(true)}
                    />
                </span>
            </div>
            {/* ///////////////////////// */}
            <Drawer style={{ zIndex: 0 }}
                bodyStyle={{ padding: 10 }}
                id="watchlist_drawer"
                title={<div style={{ fontWeight: "600", color: 'var(--PRIMARY__CONTENT__COLOR)' }}>{t('fav_manage')}</div>}
                placement={"bottom"}
                closable={false}
                onClose={() => setVisible(!visible)}
                visible={visible}
                key={"bottom"}
            >
                <p onClick={() => {
                    if (!!glb_sv.activeList && !!glb_sv.activeList.c1 && glb_sv.activeList?.c1 !== 'HNX30' && glb_sv.activeList?.c1 !== 'VN30' && glb_sv.activeList?.c1 !== '00') {
                        setIsShowSearch(true);
                    } else {
                        notification.warning({
                            message: t('process_fail'),
                            description: t('warn_add_stock_list'),
                            duration: 3,
                        })
                    }
                }}>
                    <span>
                        <img src={IC_ADD_CODECK} />
                    </span>
                    {" "}
                    <span>{t('add_stock')}</span>
                </p>
                <p onClick={() => {
                    setShowAddFav(true)
                }}>
                    <span>
                        <img src={IC_ADD_CATEGORY} size={"sm"} />
                    </span>
                    {" "}
                    <span>{t('add_new_favorites')}</span>
                </p>
                <p
                    onClick={() => {
                        if (!!glb_sv.activeList && !!glb_sv.activeList.c1 && glb_sv.activeList?.c1 !== 'HNX30' && glb_sv.activeList?.c1 !== 'VN30' && glb_sv.activeList?.c1 !== '00') {
                            setVisible(false);
                            setVisibleUpdate(true);
                        } else {
                            notification.warning({
                                message: t('process_fail'),
                                description: t('warn_remove_list'),
                                duration: 3,
                            })
                        }
                    }}
                >
                    <span>
                        <img src={IC_UPDATE_CATEGORY} size={"sm"} />
                    </span>
                    {" "}
                    <span>{t('modify_nm_of_fav')}</span>
                </p>
                <p
                    onClick={() => {
                        if (!!glb_sv.activeList && !!glb_sv.activeList.c1 && glb_sv.activeList?.c1 !== 'HNX30' && glb_sv.activeList?.c1 !== 'VN30' && glb_sv.activeList?.c1 !== '00') {
                            setVisible(false);
                            setVisibleDelete(true);
                        } else {
                            notification.warning({
                                message: t('process_fail'),
                                description: t('warn_remove_list'),
                                duration: 3,
                            })
                        }
                    }}
                >
                    <span>
                        <img src={IC_DELETE_CATEGORY} size={"sm"} />
                    </span>
                    {" "}
                    <span>{t('remove_a_favorites')}</span>
                </p>
            </Drawer>
            <div>
                <AddNewFavList checkPopupAdd={showAddFav} checkHidenPopupAdd={checkHidenPopupAdd} />
            </div>
            <div>{modalUpdateCategory()}</div>
            <div>{modalDeleteCategory()}</div>
            <SearchStockModal onChangeStock={handleStockSelect} isOnShow={isShowSearch} setIsOnShow={setIsShowSearch} />
        </div >
    );
};

const UI = {
    header: {
        height: 50,
        paddingRight: dimensions.vertical(12),
        paddingLeft: dimensions.vertical(12),
        paddingTop: 5,
        backgroundColor: 'var(--PRIMARY__BG__COLOR)',
        position: 'sticky',
        top: 0,
        zIndex: 9999
    },
    button: {
        height: 25,
        paddingRight: 6,
        paddingLeft: 6,
        border: "none",
        borderRadius: "50%",
        background: "#f8f8f8",
    },
    category: {
        // backgroundColor: 'var(--PRIMARY__BG__COLOR)',
        color: 'var(--PRIMARY__CONTENT__COLOR)',
        marginBottom: 5,
        fontSize: "19px",
        textAlign: "center",
        fontWeight: 600,
        paddingTop: 20,
    },
    categoryItem: { marginTop: 15 },
    btnUpdateCategory: {
        borderRadius: "10px",
        border: "1px solid #06923D",
        color: "#FFFFFF",
        background: "#06923D",
        width: "86px",
        fontSize: "14px",
        lineHeight: "25px",
    },
    btnDeleteCategory: {
        borderRadius: "8px",
        border: "1px solid red",
        color: "#FFFFFF",
        background: "red",
        width: "170px",
        height: "40px",
        fontSize: "14px",
        lineHeight: "25px",
    },
    btnCancel: {
        // color: "#06923D",
        fontSize: "18px",
        // fontFamily: "SF pro Display",
        // marginRight: "15px",
        marginTop: "3px",
        color: "gray",
    },
    bodyModal: {
        borderRadius: 5,
        backgroundColor: 'var(--PRIMARY__BG__COLOR)',
        color: 'var(--PRIMARY__CONTENT__COLOR)',
    }
};
