import React, { useContext, memo } from 'react';
import { StoreContext } from '../../store';
import { useTranslation } from 'react-i18next';
import { FormatNumber } from '../../utils';

import { fontSizes, dimensions, fontWeights, IconSvg } from '../../styles';
import { Modal, Button } from 'antd';
import Text from 'antd/lib/typography/Text';

function ModalOrderConfirm({
	modalOrderCfm,
	confirmOrder,
	sellbuy_tp,
	orderTp,
	nameSessionTp,
	from_dt,
	to_dt,
	price,
	qty,
	stockCode }) {

	const { styles } = useContext(StoreContext);
	const { t } = useTranslation();

	const translateOrderTp = (value) => {
		if (value === '01') {
			return 'LO'
		} else if (value === '02') {
			return 'MP'
		} else if (value === '03') {
			return 'ATO'
		} else if (value === '04') {
			return 'ATC'
		} else if (value === '06') {
			return 'MOK'
		} else if (value === '07') {
			return 'MAK'
		} else if (value === '08') {
			return 'MTL'
		} else if (value === '15') {
			return 'PLO'
		}
	}

	return (
        <Modal
            bodyStyle={{ backgroundColor: 'var(--HEADER__BG__COLOR)', borderRadius: '1rem' }}
            visible={modalOrderCfm}
            footer={null}
            onClose={() => confirmOrder('N')}>
            <div
                style={{
                    backgroundColor: 'var(--HEADER__BG__COLOR)',
                    paddingHorizontal: 20,
                    paddingTop: 32,
                    paddingBottom: 8,
                    justifyContent: 'flex-start',
                    borderRadius: 4,
                    borderColor: 'rgba(0, 0, 0, 0.1)',
                }}>
                <div style={{ alignItems: 'center' }}>{/* <IconSvg.CartIcon color={sellbuy_tp === 'buy' ? styles.UP__COLOR : styles.DOWN__COLOR} /> */}</div>
                <Text
                    style={{
                        marginVertical: 22,
                        fontSize: fontSizes.xmedium,
                        textAlign: 'center',
                        fontWeight: fontWeights.semiBold,
                        color: sellbuy_tp === 'buy' ? styles.UP__COLOR : styles.DOWN__COLOR,
                    }}>
                    {sellbuy_tp === 'buy' ? t('screen_confirm_advOrder_order_buy') : t('screen_confirm_advOrder_order_sell')}
                </Text>

                {/* <AccountModal /> */}

                <div className='flex' style={{ flexDirection: 'row', padding: '0.25rem 0' }}>
                    <div style={{ color: styles.SECOND__CONTENT__COLOR, fontSize: fontSizes.small, flex: 1 }}>{t('short_symbol')}</div>
                    <div style={{ color: styles.PRIMARY__CONTENT__COLOR, fontSize: fontSizes.normal, flex: 1 }}>{stockCode}</div>
                </div>

                <div className='flex' style={{ flexDirection: 'row', padding: '0.25rem 0' }}>
                    <div style={{ color: styles.SECOND__CONTENT__COLOR, fontSize: fontSizes.small, flex: 1 }}>{t('order_tp')}</div>
                    <div style={{ color: styles.PRIMARY__CONTENT__COLOR, fontSize: fontSizes.normal, flex: 1 }}>{translateOrderTp(orderTp)}</div>
                </div>
                {/* <View className='flex' style={{ flexDirection: 'row', padding: '0.25rem 0' }}>
                    <div style={{ color: styles.SECOND__CONTENT__COLOR, fontSize: fontSizes.small, flex: 1 }}>{t('session')}</div>
                    <div style={{ color: styles.PRIMARY__CONTENT__COLOR, fontSize: fontSizes.normal, flex: 1 }}>{nameSessionTp}</div>
                </View> */}
                <div className='flex' style={{ flexDirection: 'row', padding: '0.25rem 0' }}>
                    <div style={{ color: styles.SECOND__CONTENT__COLOR, fontSize: fontSizes.small, flex: 1 }}>{t('qty')}</div>
                    <div style={{ color: styles.PRIMARY__CONTENT__COLOR, fontSize: fontSizes.normal, flex: 1 }}>{FormatNumber(qty)}</div>
                </div>
                {orderTp === '01' ? (
                    <div className='flex' style={{ flexDirection: 'row', padding: '0.25rem 0' }}>
                        <div style={{ color: styles.SECOND__CONTENT__COLOR, fontSize: fontSizes.small, flex: 1 }}>{t('price')}</div>
                        <div style={{ color: styles.PRIMARY__CONTENT__COLOR, fontSize: fontSizes.normal, flex: 1 }}>
                            {orderTp === '01' ? FormatNumber(price) : translateOrderTp(orderTp)}
                        </div>
                    </div>
                ) : null}
                {orderTp === '01' ? (
                    <div className='flex' style={{ flexDirection: 'row', padding: '0.25rem 0' }}>
                        <div style={{ color: styles.SECOND__CONTENT__COLOR, fontSize: fontSizes.small, flex: 1 }}>{t('trading_amt')}</div>
                        <div style={{ color: styles.PRIMARY__CONTENT__COLOR, fontSize: fontSizes.normal, flex: 1 }}>{FormatNumber(price * qty)}</div>
                    </div>
                ) : null}
                <div className='flex' style={{ flexDirection: 'row', padding: '0.25rem 0' }}>
                    <div style={{ color: styles.SECOND__CONTENT__COLOR, fontSize: fontSizes.small, flex: 1 }}>{t('effect_date')}</div>
                    <div style={{ color: styles.PRIMARY__CONTENT__COLOR, fontSize: fontSizes.normal, flex: 1 }}>{from_dt}</div>
                </div>
                <div className='flex' style={{ flexDirection: 'row', padding: '0.25rem 0' }}>
                    <div style={{ color: styles.SECOND__CONTENT__COLOR, fontSize: fontSizes.small, flex: 1 }}>{t('expire_date')}</div>
                    <div style={{ color: styles.PRIMARY__CONTENT__COLOR, fontSize: fontSizes.normal, flex: 1 }}>{to_dt}</div>
                </div>

                <Button
                    onClick={() => confirmOrder('Y')}
                    style={{
                        backgroundColor: sellbuy_tp === 'buy' ? styles.UP__COLOR : styles.DOWN__COLOR,
                        justifyContent: 'center',
                        alignSelf: 'center',
                        marginTop: dimensions.vertical(25),
                        borderRadius: 8,
                        padding: '0.5rem',
                        width: '100%',
                        height: 'unset',
                    }}>
                    <Text style={{ color: '#FFF', fontSize: fontSizes.small }}>{t('common_Ok')}</Text>
                </Button>
                <Button
                    onClick={() => confirmOrder('N')}
                    style={{
                        justifyContent: 'center',
                        alignSelf: 'center',
                        width: '100%',
                        padding: '0.5rem',
                        backgroundColor: 'transparent',
                        borderWidth: 0,
                        height: 'unset',
                    }}>
                    <Text style={{ color: styles.PRIMARY__CONTENT__COLOR, fontSize: fontSizes.small, opacity: 0.34 }}>{t('common_Cancel')}</Text>
                </Button>
            </div>
        </Modal>
    )
}

export default memo(ModalOrderConfirm, areEqual)

function areEqual(prev, next) {
	if (prev.modalOrderCfm === next.modalOrderCfm) return true
	return false
}