import { useState, useContext, memo, useEffect, useRef, useCallback } from 'react';
import { StoreContext } from '../../store';
import { reqFunct, glb_sv, sendRequest, FormatNumber, RealTimeHandler, eventList } from '../../utils';
import moment from 'moment';
import { useTranslation } from 'react-i18next';
import { dimensions, fontSizes } from 'styles';

const ServiceInfo = {
    getStockFromRecommendations: {
        reqFunct: reqFunct.GET_STK_LIST_RECM,
        WorkerName: 'FOSqNews',
        ServiceName: 'FOSqNews_Suggest',
        ClientSentTime: '0',
        Operation: 'Q',
    }
}
function MarketReports(props) {
    const { language } = useContext(StoreContext);
    const { t } = useTranslation();
    const subscribeStockIndex = new RealTimeHandler('STOCK_INDEX_RECOMMEND');

    const [recommandNews, setRecommandNews] = useState(glb_sv.dataRecommend.list)
    const getRecommendFlag = useRef(false)
    const recommendListRef = useRef([])
    const recommendStockListRef = useRef([])

    const [refreshing, setRefreshing] = useState(false)

    useEffect(() => {
        if (glb_sv.dataRecommend.language !== language) getRecommendation();
        subscribeStockIndex.listen(msg => {
            if (msg.type === eventList.SUB_STOCK && recommendStockListRef.current.includes(msg.msgKey)) {
                setRecommandNews([...new Set([...recommandNews, ...glb_sv.dataRecommend.list])])
            }
        })
        return (() => {
            subscribeStockIndex.clearRealTimeListener({ unsubscribe: true });
        })
    }, [language]);

    const getRecommendation = () => {
        if (getRecommendFlag.current) return;
        const inputParams = ['04'];
        sendRequest(ServiceInfo.getStockFromRecommendations, inputParams, handleGetRecommendationResult);
        getRecommendFlag.current = true
    }

    const handleGetRecommendationResult = (reqInfoMap, message) => {
        if (Number(message['Result']) === 0) {
            return
        } else {
            let jsondata = []
            try {
                jsondata = message['Data'] ? JSON.parse(message['Data']) : [];
                console.log('jsondata: ', jsondata);
            } catch (err) {
                return
            }
            const newDataConvert = jsondata.map(item => ({
                t55: item.c1,
                U10: item.c0,
                price_recm: item.c4,
                price_target: item.c5,
                recm_tp: item.c3,
                recm_dt: item.c10,
                link_rpt: item.c11,
                number_start: Number(item.c9),
                group_id: item.c12
            }))
            getRecommendFlag.current = false
            recommendListRef.current = recommendListRef.current.concat(newDataConvert)
            recommendStockListRef.current = recommendListRef.current.map(item => item.t55)
            subscribeStockIndex.subscribe({
                topic: ['MDDS|SI', 'MDDS|TP'],
                value: recommendStockListRef.current
            })
            if (Number(message['Packet']) <= 0) {
                getRecommendFlag.current = false
                setRecommandNews(recommendListRef.current);
                glb_sv.dataRecommend.list = recommendListRef.current;
                glb_sv.dataRecommend.language = language;
            }
        }
    }

    const timeOutFunct = (reqInfo) => {
        if (reqInfo.reqFunct === reqFunct.GET_HOT_NEWS) {
            getRecommendFlag.current = false;
        }
    }

    const onRefresh = useCallback(() => {
        setRefreshing(true);
        getRecommendation();
    }, []);

    // return class đổi màu color
    const colorCell = (cellNm, rowData) => {
        if (cellNm === 't55' || cellNm === 't31' || cellNm === 't31_incr_per' || cellNm === 't31_incr')
            return rowData['t31_color']
        if (cellNm === 'price_recm' || cellNm === 'price_target') {
            if (rowData[cellNm] > rowData.t333 && rowData[cellNm] < rowData.t260) return 'price-down'
            if (rowData[cellNm] > rowData.t260 && rowData[cellNm] < rowData.t332) return 'price-up'
            if (rowData[cellNm] === rowData.t260) return 'ref_color'
            if (rowData[cellNm] >= rowData.t332) return 'ceil_color'
            if (rowData[cellNm] <= rowData.t333) return 'floor_color'
        }

        return ''
    }

    return (
        <>
            <div className='list-report flex flex-direction-column' style={{ paddingRight: dimensions.indent, paddingLeft: dimensions.indent, paddingTop: 10 }}>
                <div className='flex flex-direction-row' style={{ fontSize: fontSizes.small }}>
                    <div className='flex-2 fw-bold'>
                        {t('symbol_short')}
                    </div>
                    <div className='flex-5 fw-bold flex-direction-column justify-content-end'>
                        <div className='flex justify-content-end'>
                            {t('recommendations')}
                        </div>
                        <div className='flex justify-content-end'>
                            {t('date')}
                        </div>
                    </div>
                    <div className='flex-5 fw-bold flex-direction-column text-right'>
                        <div className='flex justify-content-end'>
                            {t('price_target')}
                        </div>
                        <div className='flex justify-content-end'>
                            {t('price_recom')}
                        </div>
                    </div>
                    <div className='flex-5 fw-bold flex-direction-column text-right'>
                        <div className='flex justify-content-end'>
                            {t('last_price')}
                        </div>
                        <div className='flex justify-content-end'>
                            %
                        </div>
                    </div>
                </div>
                {recommandNews.length > 0 &&
                    recommandNews.map((data, index) =>
                        <div key={index} className='flex flex-direction-row' style={{ borderBottom: '1px solid var(--SECOND__CONTENT__COLOR)' }}>
                            <div className='flex-2 flex align-items-center' style={{ fontSize: fontSizes.small, color: 'var(--PRIMARY__CONTENT__COLOR)' }}>
                                <a href={data['link_rpt']} target='_blank' style={{ fontSize: fontSizes.small, color: glb_sv.StockMarket[data.t55].t31 == 0 ? 'var(--REF__COLOR)' : Math.round((glb_sv.StockMarket[data.t55].t31 - Number(data['price_recm'])) / data['price_recm'] * 100) > 0 ? 'var(--UP__COLOR)' : 'var(--DOWN__COLOR)' }}>
                                    {data.t55}
                                </a>
                            </div>
                            <div className='flex-5 flex-direction-column justify-content-end'>
                                <div className='flex justify-content-end' style={{ fontSize: fontSizes.small, color: 'var(--PRIMARY__CONTENT__COLOR)' }}>
                                    {data['recm_tp'] === 1 ? t('common_buy') : t('common_sell')}
                                </div>
                                <div className='flex justify-content-end' style={{ fontSize: fontSizes.small, color: 'var(--SECOND__CONTENT__COLOR)' }}>
                                    {moment(data['recm_dt'], 'YYYYMMDD').format('DD/MM/YYYY')}
                                </div>
                            </div>
                            <div className='flex-5 flex-direction-column text-right'>
                                <div className='flex justify-content-end' style={{ fontSize: fontSizes.small, color: 'var(--PRIMARY__CONTENT__COLOR)' }}>
                                    {FormatNumber(data['price_target'] / 1000, 2, 1)}
                                </div>
                                <div className='flex justify-content-end' style={{ fontSize: fontSizes.small, color: 'var(--SECOND__CONTENT__COLOR)' }}>
                                    {FormatNumber(data['price_recm'] / 1000, 2, 1)}
                                </div>
                            </div>
                            <div className='flex-5 flex-direction-column text-right'>
                                <div className='flex justify-content-end' style={{ fontSize: fontSizes.small, color: (glb_sv.StockMarket[data.t55].t31 == 0 || Math.round((glb_sv.StockMarket[data.t55].t31 - Number(data['price_recm'])) / data['price_recm'] * 100) == 0) ? 'var(--REF__COLOR)' : Math.round((glb_sv.StockMarket[data.t55].t31 - Number(data['price_recm'])) / data['price_recm'] * 100) > 0 ? 'var(--UP__COLOR)' : 'var(--DOWN__COLOR)' }}>
                                    {glb_sv.StockMarket[data.t55].t31 < Number(data['price_recm']) ? '-' : ''}
                                    {FormatNumber(glb_sv.StockMarket[data.t55].t31, 0)}
                                </div>
                                <div className='flex justify-content-end' style={{ fontSize: fontSizes.small, color: (glb_sv.StockMarket[data.t55].t31 == 0 || Math.round((glb_sv.StockMarket[data.t55].t31 - Number(data['price_recm'])) / data['price_recm'] * 100) == 0) ? 'var(--REF__COLOR)' : Math.round((glb_sv.StockMarket[data.t55].t31 - Number(data['price_recm'])) / data['price_recm'] * 100) > 0 ? 'var(--UP__COLOR)' : 'var(--DOWN__COLOR)' }}>
                                    {glb_sv.StockMarket[data.t55].t31 == 0 ? 0 : FormatNumber((glb_sv.StockMarket[data.t55].t31 - Number(data['price_recm'])) / data['price_recm'] * 100, 2)}%
                                </div>
                            </div>
                        </div>
                    )
                }
            </div>
        </>
    );
}

export default memo(MarketReports)