import React, { useState, useContext, useEffect, memo, useRef } from 'react';
import { useTranslation } from 'react-i18next';
import { StoreContext } from '../../../store';
import HeaderTop from './HeaderTop';
import OptionsTop from './OptionsTop';
import RowTop from './RowTop';
import { dimensions, fontSizes } from '../../../styles';
import { glb_sv, FormatNumber, eventList, reqFunct, sendRequest, STORE_KEY, RealTimeHandler } from '../../../utils';
import { VerticalAlignTopOutlined } from '@ant-design/icons';
import { Tabs, Affix } from 'antd'
import { ScrollView } from '../../../basic-components'

const ServiceInfo = {
    GET_MARKET_TOP: {
        reqFunct: reqFunct.GET_MARKET_TOP,
        WorkerName: 'ALTqMktInfo01',
        ServiceName: 'ALTqMktInfo01_MktInfo',
        Operation: 'Q',
        ClientSentTime: '0'
    }
}

const listTimeHis = ['1W', '1M', '3M', '6M', '1Y', '2Y', '3Y'];

function getInitPage() {
    if (glb_sv.TYPE_TOP_MARKET === 'TOP_QTY_UP') return 1;
    if (glb_sv.TYPE_TOP_MARKET === 'TOP_VAL_UP') return 2;
    if (glb_sv.TYPE_TOP_MARKET === 'TOP_PRI_DWN') return 3;
    return 0;
}

function TopMaket({ active, navigateStockInfo, scrollY, height }) {

    const { styles, fractionPrice } = useContext(StoreContext);
    const { t } = useTranslation();
    const [data, setData] = useState([]);
    const dataRef = useRef([])
    const [type, setType] = useState(glb_sv.TYPE_TOP_MARKET || 'TOP_PRI_UP')
    const [page, setPage] = useState(getInitPage)

    const [activeOtp, setActiveOtp] = useState('1D')
    const activeOtpRef = useRef('1D')
    const [loading, setLoading] = useState(true)

    const timeoutChangeTabRef = useRef(null);

    useEffect(() => {
        // if (activeOtpRef.current === '1D') {
        //     const topMktt = glb_sv.listViewableTopMrk.find(e => e.item === type);
        //     if (active.includes('HSX')) {
        //         setData(topMktt.data || [])
        //     } else if (active.includes('HNXUpcomIndex')) {
        //         setData(topMktt.data || [])
        //     } else if (active.includes('HNX')) {
        //         setData(topMktt.data || [])
        //     }
        //     setLoading(false)
        // } else {
        //     setData([]);
        //     if (listTimeHis.includes(activeOtpRef.current)) {
        //         const index = active.includes('HSX') ? '01' : active.includes('HNXUpcomIndex') ? '05' : '03';
        //         let typee = 'TU';
        //         if (type === 'TOP_PRI_DWN') typee = 'TD';
        //         if (type === 'TOP_QTY_UP') typee = 'TV';
        //         if (type === 'TOP_VAL_UP') typee = 'TA';
        //         if (glb_sv.dataHisMrktop[activeOtpRef.current][type + index]) {
        //             const newData = glb_sv.dataHisMrktop[activeOtpRef.current][type + index].map(item => {
        //                 return {
        //                     StkCd: item.c3,
        //                     CurrPri: item.c6 * 1000,
        //                     ChgRatio: item.c11,
        //                     TradVol: item.c8,
        //                     TradVal: item.c9,
        //                     OpenPri: item.c4 * 1000
        //                 }
        //             })
        //             setData(newData);
        //             setLoading(false)
        //             return;
        //         }
        //         const inval = ['1', index, activeOtpRef.current, typee, '-1'];
        //         sendRequest(ServiceInfo.GET_MARKET_TOP, inval, handleGetMarketTop, false)
        //         dataRef.current = [];
        //     }
        // }

        const eventMarket = glb_sv.eventMarket.subscribe(msg => {
            if (msg.type === eventList.MKT_TOP) {
                const topMkt = glb_sv.listViewableTopMrk.find(e => e.item === type);

                let dataa = [];
                try {
                    dataa = msg.Data ? JSON.parse(msg.Data) : topMkt.data;
                }
                catch (err) {
                    console.error('OverviewMarket parse msg', err);
                }
                const obj = glb_sv.listViewableTopMrk.find(e => msg.MsgKey.includes(e.item));
                if (obj) obj.data = dataa;

                if (activeOtpRef.current !== '1D') return;

                if (active.includes('HSX') && msg.MsgKey.includes(type)) {
                    setData(dataa)
                } else if (active.includes('HNXUpcomIndex') && msg.MsgKey.includes(type)) {
                    setData(dataa)
                } else if (active.includes('HNX') && msg.MsgKey.includes(type)) {
                    setData(dataa)
                }
            }
            if (msg.type === eventList.RECONNECT_MARKET) {
                if (activeOtpRef.current === '1D') {
                    const topMktt = glb_sv.listViewableTopMrk.find(e => e.item === type)
                    if (active.includes('HSX')) {
                        setData(topMktt.data || [])
                    } else if (active.includes('HNXUpcomIndex')) {
                        setData(topMktt.data || [])
                    } else if (active.includes('HNX')) {
                        setData(topMktt.data || [])
                    }
                } else {
                    setData([]);
                    if (listTimeHis.includes(activeOtpRef.current)) {
                        const index = active.includes('HSX') ? '01' : active.includes('HNXUpcomIndex') ? '05' : '03';
                        let typee = 'TU';
                        if (type === 'TOP_PRI_DWN') typee = 'TD';
                        if (type === 'TOP_QTY_UP') typee = 'TV';
                        if (type === 'TOP_VAL_UP') typee = 'TA';
                        if (glb_sv.dataHisMrktop[activeOtpRef.current][type + index]) {
                            const newData = glb_sv.dataHisMrktop[activeOtpRef.current][type + index].map(item => {
                                return {
                                    StkCd: item.c3,
                                    CurrPri: item.c6 * 1000,
                                    ChgRatio: item.c11,
                                    TradVol: item.c8,
                                    TradVal: item.c9,
                                    OpenPri: item.c4 * 1000
                                }
                            })
                            setData(newData)
                            return;
                        }
                        const inval = ['1', index, activeOtpRef.current, typee, '-1'];
                        sendRequest(ServiceInfo.GET_MARKET_TOP, inval, handleGetMarketTop, true, handleGetMarketTopTimeout)
                        dataRef.current = [];
                    }
                }

            }
        });
        return () => {
        };
    }, [active])

    // let title = '';
    // if (type === 'TOP_PRI_UP') title = 'top_10_stocks_incre'
    // if (type === 'TOP_PRI_DWN') title = 'top_10_stocks_decre'
    // if (type === 'TOP_QTY_UP') title = 'top_10_stocks_shares_trade'
    // if (type === 'TOP_VAL_UP') title = 'top_10_stocks_value_trade'

    // let name = '';
    // if (active.includes('HNXUpcomIndex')) name = glb_sv.IndexMarket['HNX|I|HNXUpcomIndex'] ? (glb_sv.IndexMarket['HNX|I|HNXUpcomIndex'].t18 || 'UPCOM') : 'UPCOM'
    // else if (active.includes('HNX')) name = glb_sv.IndexMarket['HNX|I|HNXIndex'] ? (glb_sv.IndexMarket['HNX|I|HNXIndex'].t18 || 'HNX') : 'HNX'
    // else name = glb_sv.IndexMarket['HSX|I|HSXIndex'] ? (glb_sv.IndexMarket['HSX|I|HSXIndex'].t18 || 'HOSE') : 'HOSE'


    const changeOtpion = (value) => {
        setActiveOtp(value);
        setData([]);
        setLoading(true)
        activeOtpRef.current = value;
        if (timeoutChangeTabRef.current) clearTimeout(timeoutChangeTabRef.current)
        timeoutChangeTabRef.current = setTimeout(() => {
            if (value === '1D') {
                const topMkt = glb_sv.listViewableTopMrk.find(e => e.item === type)
                if (active.includes('HSX')) {
                    setData(topMkt.data)
                } else if (active.includes('HNXUpcomIndex')) {
                    setData(topMkt.data)
                } else if (active.includes('HNX')) {
                    setData(topMkt.data)
                }
                setLoading(false)
            } else {
                const index = active.includes('HSX') ? '01' : active.includes('HNXUpcomIndex') ? '05' : '03';
                let typee = 'TU';
                if (type === 'TOP_PRI_DWN') typee = 'TD';
                if (type === 'TOP_QTY_UP') typee = 'TV';
                if (type === 'TOP_VAL_UP') typee = 'TA';
                if (glb_sv.dataHisMrktop[value][type + index]) {
                    const newData = glb_sv.dataHisMrktop[value][type + index].map(item => {
                        return {
                            StkCd: item.c3,
                            CurrPri: item.c6 * 1000,
                            ChgRatio: item.c11,
                            TradVol: item.c8,
                            TradVal: item.c9,
                            OpenPri: item.c4 * 1000
                        }
                    })
                    setData(newData)
                    return;
                }
                const inval = ['1', index, value, typee, '-1'];
                sendRequest(ServiceInfo.GET_MARKET_TOP, inval, handleGetMarketTop, true, handleGetMarketTopTimeout)
                dataRef.current = [];
            }
        }, 500);
    }
    const handleGetMarketTopTimeout = () => {
        setLoading(false);
    }

    const handleGetMarketTop = (reqInfoMap, message) => {
        setLoading(false)
        if (Number(message['Result']) === 0) {
            return;
        } else {
            let jsondata = [];
            try {
                jsondata = message['Data'] ? JSON.parse(message['Data']) : [];
            } catch (err) {
            }
            dataRef.current = dataRef.current.concat(jsondata);
            if (Number(message['Packet']) <= 0) {
                const newData = dataRef.current.map(item => {
                    return {
                        StkCd: item.c3,
                        CurrPri: item.c6 * 1000,
                        ChgRatio: item.c11,
                        TradVol: item.c8,
                        TradVal: item.c9,
                        OpenPri: item.c4 * 1000
                    }
                })
                setData(newData)
                const index = active.includes('HSX') ? '01' : active.includes('HNXUpcomIndex') ? '05' : '03';
                glb_sv.dataHisMrktop[activeOtpRef.current][type + index] = dataRef.current.slice();
            }
        }
    }
    const changeTab = (i) => {
        // console.log("TOP_PRI_UP", i, typeof(i));
        let tab = 'TOP_PRI_UP';
        if (Number(i) === 1) tab = 'TOP_QTY_UP';
        if (Number(i) === 2) tab = 'TOP_VAL_UP';
        if (Number(i) === 3) tab = 'TOP_PRI_DWN';

        if (glb_sv.TYPE_TOP_MARKET === tab) return;
        glb_sv.TYPE_TOP_MARKET = tab;
        setType(tab);
        setData([]);
        setLoading(true)
        localStorage.setItem(STORE_KEY.TYPE_TOP_MARKET, tab);

        if (timeoutChangeTabRef.current) clearTimeout(timeoutChangeTabRef.current)
        timeoutChangeTabRef.current = setTimeout(() => {
            if (activeOtpRef.current === '1D') {
                const topMktt = glb_sv.listViewableTopMrk.find(e => e.item === tab);
                if (active.includes('HSX')) {
                    setData(topMktt.data || [])
                } else if (active.includes('HNXUpcomIndex')) {
                    setData(topMktt.data || [])
                } else if (active.includes('HNX')) {
                    setData(topMktt.data || [])
                }
                setLoading(false)
            } else {
                setData([]);
                if (listTimeHis.includes(activeOtpRef.current)) {
                    const index = active.includes('HSX') ? '01' : active.includes('HNXUpcomIndex') ? '05' : '03';
                    let typee = 'TU';
                    if (tab === 'TOP_PRI_DWN') typee = 'TD';
                    if (tab === 'TOP_QTY_UP') typee = 'TV';
                    if (tab === 'TOP_VAL_UP') typee = 'TA';
                    if (glb_sv.dataHisMrktop[activeOtpRef.current][tab + index]) {
                        const newData = glb_sv.dataHisMrktop[activeOtpRef.current][tab + index].map(item => {
                            return {
                                StkCd: item.c3,
                                CurrPri: item.c6 * 1000,
                                ChgRatio: item.c11,
                                TradVol: item.c8,
                                TradVal: item.c9,
                                OpenPri: item.c4 * 1000
                            }
                        })
                        setData(newData)
                        setLoading(false)
                        return;
                    }
                    const inval = ['1', index, activeOtpRef.current, typee, '-1'];
                    sendRequest(ServiceInfo.GET_MARKET_TOP, inval, handleGetMarketTop, true, handleGetMarketTopTimeout)
                    dataRef.current = [];
                }
            }
        }, 500);
    }
    return (
        <div style={{ paddingRight: dimensions.indent, paddingLeft: dimensions.indent, marginBottom: dimensions.halfVerticalIndent }}>
            <Affix 
                offsetTop={glb_sv.isTablet ? 60 : 50} 
                // target={() => document.body}
                onChange={(affixed) => console.log("onChange", affixed)}>
                <div style={{ backgroundColor: 'var(--PRIMARY__BG__COLOR)' }}>
                <Tabs className="market__top--tab-view" defaultActiveKey="1" onChange={changeTab}
                    // tabBarStyle={{ color: 'var(--PRIMARY__CONTENT__COLOR)', borderColor: 'var(--PRIMARY__CONTENT__COLOR)'}}
                    >
                    <Tabs.TabPane tab={t('top_10_stocks_incre')} key="1"></Tabs.TabPane>
                    <Tabs.TabPane tab={t('top_10_stocks_shares_trade')} key="2"></Tabs.TabPane>
                    <Tabs.TabPane tab={t('top_10_stocks_value_trade')} key="3"></Tabs.TabPane>
                    <Tabs.TabPane tab={t('top_10_stocks_decre')} key="4"></Tabs.TabPane>
                </Tabs>
                <ScrollView horizontal style={{ marginTop: dimensions.vertical(4, 0.3), marginBottom: dimensions.vertical(4), overflow: 'scroll' }}>
                    <OptionsTop onChange={() => changeOtpion('1D')} title={'1 ' + t('day')} active styles={styles} active={activeOtp === '1D'} />
                    <OptionsTop onChange={() => changeOtpion('1W')} title={'5 ' + t('day')} styles={styles} active={activeOtp === '1W'} />
                    <OptionsTop onChange={() => changeOtpion('1M')} title={t('one_month')} styles={styles} active={activeOtp === '1M'} />
                    <OptionsTop onChange={() => changeOtpion('3M')} title={t('three_month')} styles={styles} active={activeOtp === '3M'} />
                    <OptionsTop onChange={() => changeOtpion('6M')} title={t('six_month')} styles={styles} active={activeOtp === '6M'} />
                    <OptionsTop onChange={() => changeOtpion('1Y')} title={'52 ' + t('weeks')} styles={styles} active={activeOtp === '1Y'} />
                    <OptionsTop onChange={() => changeOtpion('2Y')} title={'2 ' + t('year')} styles={styles} active={activeOtp === '2Y'} />
                    <OptionsTop isLast={true} onChange={() => changeOtpion('3Y')} title={'3 ' + t('year')} styles={styles} active={activeOtp === '3Y'} />
                </ScrollView>
                <HeaderTop type={type} activeOtp={activeOtp} />
                </div>
            </Affix>
            {data.map(item =>
                <RowTop
                    key={item.StkCd}
                    item={item}
                    title={item.StkCd}
                    activeOtp={activeOtp}
                    subtitle='No name'
                    navigateStockInfo={navigateStockInfo}
                    ratioValue={FormatNumber(item.ChgRatio, 2)}
                    value_market={type !== 'TOP_VAL_UP'
                        ? FormatNumber(item.TradVol, 0, 1, 'short')
                        : FormatNumber(item.TradVal * (10 ** 9), 0, 1, 'short')}
                    valueCol3={fractionPrice ? FormatNumber(item.OpenPri / 1000, 2, 1) : FormatNumber(item.OpenPri, 0, 1)}
                    value={item.CurrPri === 777777710000 ? 'ATO' :
                        item.CurrPri === 777777720000 ? 'ATC' :
                            (fractionPrice ? FormatNumber(item.CurrPri / 1000, 2, 1) : FormatNumber(item.CurrPri, 0, 1))}
                    styles={styles} />)}
            {/* <div onClick={() => navigateAllTopMarket(t(title) + ' - ' + name, type, active)} style={{ paddingVertical: 10 }}>
                <span style={{ color: 'var(--SECOND__CONTENT__COLOR)', fontSize: fontSizes.normal }}>{t('View All') + ' >'}</span>
            </div> */}
        </div>
    );
}
export default memo(TopMaket)