import { useState, useContext, memo, useEffect, useRef } from 'react';
import { useTranslation } from 'react-i18next';
import { Modal } from 'antd-mobile';
import { Button } from 'antd';
import moment from 'moment';
import { StoreTrading } from 'store-trading';
import { InputSelect } from '../../basic-components/input-select';
import { reqFunct, glb_sv, sendRequest, FormatNumber } from '../../utils';
import IC_FOLDER_NULL from 'assets/images/common/ic_folder_null.png';
import { fontSizes } from 'styles';

const ServiceInfo = {
    GET_LIST_RIGHT_TYPE: {
        reqFunct: reqFunct.GET_LIST_RIGHT_TYPE, //Lấy DS {t('right_type')}
        WorkerName: 'FOSqRights',
        ServiceName: 'FOSqRights_Common',
        ClientSentTime: '0',
        Operation: 'Q',
    },
    GET_LIST_RIGHT_TYPE_REGISTER: {
        reqFunct: reqFunct.GET_LIST_RIGHT_TYPE_REGISTER, //Lấy DS Kết quả
        WorkerName: 'FOSqRights',
        ServiceName: 'FOSqRights_OnlineRightsInfo_1',
        ClientSentTime: '0',
        Operation: 'Q',
    },
    GET_LIST_RIGHT: {
        reqFunct: reqFunct.GET_LIST_RIGHT,
        WorkerName: 'FOSqRights',
        ServiceName: 'FOSqRights_OnlineRightsInfo_2',
        ClientSentTime: '0',
        Operation: 'Q',
    }
}

function EventTabs({ }) {
    const { t } = useTranslation()
    const { userInfo } = useContext(StoreTrading)
    const [listRight, setListRight] = useState([])
    const [valueBankStock, setValueBankStock] = useState('')
    const [valueRightType, setValueRightType] = useState('%')
    const [listRightToBuy, setListRightToBuy] = useState([])
    const [nameStockCode, setNameStockCode] = useState(t('choose_symbol_trading'))
    const [checkIcon, setCheckIcon] = useState(false)
    const selectStockCodeRef = useRef(null)
    const jsonListRightType = useRef([])
    const jsonListRightToBuy = useRef([])
    const [dataSelect, setDataSelect] = useState({});
    const [shouldShowModal, setShouldShowModal] = useState(false);
    const [defaultDate, setDefaultDate] = useState('0');
    const recordNumberDefault = useRef('20');

    useEffect(() => {
        getListRight();
    }, [userInfo.actn_curr])

    useEffect(() => {
        getListRight()
    }, [defaultDate])

    const getExchanges = (U10) => {
        switch (U10) {
            case '01':
                return 'HOSE'
                break
            case '03':
                return 'HNX'
                break
            case '05':
                return 'UPCOM'
                break
            default:
                break
        }
    }

    const getListRight = () => {
        const inputParams = ['01', '%', '%', defaultDate, '1', recordNumberDefault.current]
        sendRequest(ServiceInfo.GET_LIST_RIGHT, inputParams, handleGetListRight)
    }

    const handleGetListRight = (reqInfoMap, message) => {
        if (Number(message['Result']) === 0) {
            setListRight([])
            return
        } else {
            let jsondata = []
            try {
                jsondata = JSON.parse(message['Data'])
            } catch (err) {
                setListRight([])
                return
            }
            if (Number(message['Packet']) <= 0) {
                setListRight(jsondata)
            }
        }
    }
    const formatTimeNull = (timeData) => {
        return !timeData.trim() ? null : moment(timeData, 'DDMMYYYY').format('DD/MM/YYYY')
    }
    const getListRightRegister = (valueBankStock = '', valueRightType = '%') => {
        const inputParams = [valueBankStock, valueRightType]
        sendRequest(ServiceInfo.GET_LIST_RIGHT_TYPE_REGISTER, inputParams, handleGetListRightRegister)
    }

    const handleGetListRightRegister = (reqInfoMap, message) => {
        jsonListRightToBuy.current = []
        if (Number(message['Result']) === 0) {
            setListRightToBuy([])
            return
        } else {
            let jsondata = []
            try {
                jsondata = message['Data'] ? JSON.parse(message['Data']) : []
            } catch (err) {
                jsonListRightToBuy.current = null
                setListRightToBuy([])
                return
            }
            jsonListRightToBuy.current = jsonListRightToBuy.current.concat(jsondata)
            if (Number(message['Packet']) <= 0) {
                setTimeout(() => {
                    setCheckIcon(false)
                }, 500)
                setListRightToBuy(jsonListRightToBuy.current)
            }
        }
    }

    const setShowModal = value => {
        setShouldShowModal(value);
    }

    const openModalDetail = data => {
        setDataSelect(data);
        setShouldShowModal(true);
    }

    return (
        <div className='flex flex-direction-column' style={{ position: 'relative', marginRight: 16, marginLeft: 16 }}>
            <div className='' style={{ display: 'block', width: '100vw', position: 'sticky', top: 0, backgroundColor: 'var(--PRIMARY__BG__COLOR)' }}>
                <div className='flex flex-direction-row justify-content-center'>
                    <Button style={{ borderColor: defaultDate === '-7' ? 'var(--PRIMARY)' : 'var(--PRIMARY__BORDER__COLOR)', borderWidth: 'thin', color: defaultDate === '-7' ? 'var(--PRIMARY)' : 'var(--SECOND__CONTENT__COLOR)', marginRight: 5, backgroundColor: 'var(--BUTTON__SECONDARY)', borderRadius: 5 }} className='flex mt-2 mb-2 mr-1 ml-1' onClick={() => setDefaultDate('-7')}>{t('history')}</Button>
                    <Button style={{ borderColor: defaultDate === '0' ? 'var(--PRIMARY)' : 'var(--PRIMARY__BORDER__COLOR)', borderWidth: 'thin', color: defaultDate === '0' ? 'var(--PRIMARY)' : 'var(--SECOND__CONTENT__COLOR)', marginRight: 5, backgroundColor: 'var(--BUTTON__SECONDARY)', borderRadius: 5 }} className='flex mt-2 mb-2 mr-1 ml-1' onClick={() => setDefaultDate('0')}>{t('current')}</Button>
                    <Button style={{ borderColor: defaultDate === '7' ? 'var(--PRIMARY)' : 'var(--PRIMARY__BORDER__COLOR)', borderWidth: 'thin', color: defaultDate === '7' ? 'var(--PRIMARY)' : 'var(--SECOND__CONTENT__COLOR)', marginRight: 5, backgroundColor: 'var(--BUTTON__SECONDARY)', borderRadius: 5 }} className='flex mt-2 mb-2 mr-1 ml-1' onClick={() => setDefaultDate('7')}>{t('1_week')}</Button>
                </div>
            </div>
            <div className='flex flex-direction-column'>
                {listRight.length > 0 &&
                    listRight.map((data, index) =>
                        <div key={index} className='flex flex-direction-row' style={{ borderBottom: '1px solid var(--SECOND__CONTENT__COLOR)' }}>
                            <div className='flex-2 flex align-items-center' style={{ color: 'var(--PRIMARY__CONTENT__COLOR)' }}>{data.c1}</div>
                            <div className='flex-5 flex-direction-column justify-content-start'>
                                <div className='flex justify-content-start' style={{ fontSize: fontSizes.small, color: 'var(--PRIMARY__CONTENT__COLOR)' }}>
                                    {data.c8}
                                </div>
                                <div className='flex justify-content-start' style={{ fontSize: fontSizes.verySmall, color: 'var(--SECOND__CONTENT__COLOR)' }}>
                                    {data.c9}
                                </div>
                            </div>
                            <div className='flex-3 flex justify-content-end align-items-center' style={{ fontSize: fontSizes.small }}>
                                {moment(data.c2, 'DDMMYYYY').format('DD/MM/YYYY')}
                            </div>
                        </div>
                    )
                }
            </div>
            {/* <ModalDetail data={dataSelect} isModalVisible={shouldShowModal} setModal={setShowModal} /> */}
        </div>
    )
}

const ModalDetail = ({ data, isModalVisible, setModal }) => {
    const { t } = useTranslation()

    const formatTimeNull = (timeData) => {
        return timeData !== undefined ? !timeData.trim() ? null : moment(timeData, 'DDMMYYYY').format('DD/MM/YYYY') : ''
    }

    return (
        <Modal
            visible={isModalVisible}
            transparent
            maskClosable={false}
            onClose={() => setModal(false)}
            wrapClassName='modal-add-fav-stock'
        >
            <div className='detail-modal-content flex justify-content-start flex-direction-column p-3'>
                <div className='justify-content-center flex fs-medium ' style={{ marginBottom: 10 }}>{t('right_info_detail')}</div>
                <div className='ant-divider' role='separator'></div>
                <div className='content-right'>
                    <div className='flex flex-1'>
                        <div className='flex flex-2'>{t('right_code')}</div>
                        <div className='flex flex-2 justify-content-end' style={{ color: 'var(--SECOND__CONTENT__COLOR)' }}>
                            {data.c0}
                        </div>
                    </div>
                    <div className='flex flex-1'>
                        <div className='flex flex-2'>{t('short_symbol')}</div>
                        <div className='flex flex-2 justify-content-end' style={{ color: 'var(--SECOND__CONTENT__COLOR)' }}>
                            {data.c1}
                        </div>
                    </div>
                    <div className='flex flex-1'>
                        <div className='flex flex-2'>{t('last_regist_date')}</div>
                        <div className='flex flex-2 justify-content-end' style={{ color: 'var(--SECOND__CONTENT__COLOR)' }}>
                            {formatTimeNull(data.c29)}
                        </div>
                    </div>
                    <div className='flex flex-1'>
                        <div className='flex flex-2'>{t('record_date')}</div>
                        <div className='flex flex-2 justify-content-end' style={{ color: 'var(--SECOND__CONTENT__COLOR)' }}>
                            {formatTimeNull(data.c2)}
                        </div>
                    </div>
                    <div className='flex flex-1'>
                        <div className='flex flex-2'>{t('exercise_date')}</div>
                        <div className='flex flex-2 justify-content-end' style={{ color: 'var(--SECOND__CONTENT__COLOR)' }}>
                            {(Number(data?.c21?.slice(4, 8)) !== 3000 && !!formatTimeNull(data.c21)) ? formatTimeNull(data.c21) : (Number(data?.c24?.slice(4, 8)) !== 3000 && !!formatTimeNull(data.c24)) ? formatTimeNull(data.c24) : formatTimeNull(data.c2)}
                        </div>
                    </div>
                    <div className='flex flex-1'>
                        <div className='flex flex-2'>{t('right_type')}</div>
                        <div className='flex flex-2 justify-content-end' style={{ color: 'var(--SECOND__CONTENT__COLOR)' }}>
                            {data.c33}
                        </div>
                    </div>
                    <div className='flex flex-1'>
                        <div className='flex flex-2'>{t('right_type')}</div>
                        <div className='flex flex-2 justify-content-end' style={{ color: 'var(--SECOND__CONTENT__COLOR)' }}>
                            {data.c34}
                        </div>
                    </div>
                    <div className='flex flex-1'>
                        <div className='flex flex-2'>{t('date_trading_not_right')}</div>
                        <div className='flex flex-2 justify-content-end' style={{ color: 'var(--SECOND__CONTENT__COLOR)' }}>
                            {formatTimeNull(data.c29)}
                        </div>
                    </div>
                    <div className='flex flex-1'>
                        <div className='flex flex-2'>{t('content')}</div>
                    </div>
                    <div className='flex flex-1'>
                        {data.c31}
                    </div>
                </div>
                <div className='ant-divider' role='separator'></div>
                <div className='flex justify-content-between' style={{ marginTop: 10 }}>
                    <div className='flex'></div>
                    <div className='flex justify-content-end' onClick={() => setModal(false)}>
                        <span style={{}}>{t('common_Close')}</span>
                    </div>
                </div>
            </div>
        </Modal>
    )
}

export default memo(EventTabs)