import React, { useContext, memo, useState } from 'react'
// import { div, Text, StyleSheet, ScrollView } from 'react-native';
// import { Button } from 'native-base';
import { dimensions, fontSizes } from '../../styles'
import { StoreContext } from '../../store'
// import AntDesign from 'react-native-vector-icons/AntDesign';
// import { ModalSelector } from '../../basic-components'
import { glb_sv } from '../../utils'
import { useTranslation } from 'react-i18next'
import { ScrollView } from '../../basic-components'
import { Button, Avatar, Row, Col, Tooltip, Dropdown, Menu } from 'antd'
import Text from 'antd/lib/typography/Text'
import { StoreTrading } from 'store-trading'
import { ReactComponent as IconArrowRight } from '../../assets/images/common/ic_arrow_right.svg'
import { Modal } from 'antd-mobile'
const Account = ({ showSubAll, isAll, changeAllSub }) => {
    const { styles } = useContext(StoreContext)
    const { userInfo, setUserInfo } = useContext(StoreTrading)
    const { t } = useTranslation()

    const [visibleModalAct, setVisibleModalAct] = useState(false)

    const changeSubAct = (item) => {
        // if (item === userInfo.sub_curr) return
        // setUserInfo({ ...userInfo, sub_curr: item })
        changeAllSub && changeAllSub(false)
        if (item === userInfo.sub_curr) return
        const isMargin = glb_sv.objShareGlb['acntNoInfo'].find((e) => e.AcntNo === userInfo.actn_curr && e.SubNo === item)?.MarginYN
        setUserInfo({ ...userInfo, sub_curr: item, isMargin: isMargin || false })
        glb_sv.userInfo.sub_curr = item
    }

    const changeActMain = (item) => {
        const js_acntInfo = glb_sv.objShareGlb['acntNoInfo']
        const sub_list = []
        js_acntInfo.forEach((e) => {
            if (e.AcntNo === item.AcntNo) sub_list.push(e.SubNo)
        })
        setUserInfo({
            ...userInfo,
            actn_curr: item.AcntNo,
            sub_curr: sub_list[0],
            actn_name: item.actn_name,
            sub_list,
        })
        setVisibleModalAct(false)
    }

    const initValue = userInfo.actn_list.find((e) => e.AcntNo === userInfo.actn_curr)
        ? userInfo.actn_list.find((e) => e.AcntNo === userInfo.actn_curr).label
        : ''

    const IconAvatar = () => {
        return (
            <svg width='61' height='60' viewBox='0 0 61 60' fill='none' xmlns='http://www.w3.org/2000/svg'>
                <g clipPath='url(#clip0)'>
                    <path
                        opacity='0.12'
                        d='M49.25 60H11.75C5.53625 60 0.5 54.9637 0.5 48.75V11.25C0.5 5.03625 5.53625 0 11.75 0H49.25C55.4637 0 60.5 5.03625 60.5 11.25V48.75C60.5 54.9637 55.4637 60 49.25 60Z'
                        fill='#24253D'
                    />
                    <path
                        d='M31 29C26.8645 29 23.5 25.6355 23.5 21.5C23.5 17.3645 26.8645 14 31 14C35.1355 14 38.5 17.3645 38.5 21.5C38.5 25.6355 35.1355 29 31 29Z'
                        fill='#24253D'
                    />
                    <path
                        d='M44.125 46H17.875C17.3572 46 16.9375 45.5802 16.9375 45.0625C16.9375 37.3013 23.2162 30.875 31 30.875C38.7859 30.875 45.0625 37.3037 45.0625 45.0625C45.0625 45.5802 44.6427 46 44.125 46Z'
                        fill='#24253D'
                    />
                </g>
                <defs>
                    <clipPath id='clip0'>
                        <rect width='60' height='60' fill='white' transform='translate(0.5)' />
                    </clipPath>
                </defs>
            </svg>
        )
    }
    // const menu = (
    //     // <Modal visible={true} style={{ width: '85vw' }} transparent maskClosable={false}>
    //     <Menu style={{ background: 'var(--PRIMARY__BG__COLOR)' }}>
    //         {userInfo.actn_list.map((e, index) => (
    //             <Menu.Item key={String(index)} onClick={() => changeActMain(e)} style={{ color: e.AcntNo === userInfo.actn_curr ? 'var(--PRIMARY)' : '' }}>
    //                 {e.label}
    //             </Menu.Item>
    //         ))}
    //     </Menu>
    //     // </Modal>
    // )
    const changeAll = () => {
        changeAllSub && changeAllSub(true)
    }
    return (
        <div className='flex' style={{ width: '100vw', alignItems: 'center' }}>
            <div className='flex-2 mr-2' style={{ width: '140px' }}>
                {userInfo.actn_list.length > 1 ? (
                    // <Dropdown overlay={menu} trigger={['click']} >
                    <span
                        className='flex align-items-center cursor-pointer text-primary'
                        onClick={() => {
                            setVisibleModalAct(true)
                        }}>
                        {userInfo.actn_curr}
                        <svg width={16} height={16} viewBox='0 0 16 16' style={{ marginLeft: 4, fill: 'var(--PRIMARY__CONTENT__COLOR)' }}>
                            <path d='M4 6l4 4 4-4' stroke='var(--TEXT__1)' strokeWidth={1.333} strokeLinecap='round' strokeLinejoin='round' />
                        </svg>
                    </span>
                ) : (
                    // </Dropdown>
                    <span className='flex align-items-center text-primary fw-600 fs-13'>{userInfo.actn_curr}</span>
                )}

                <div className='center__item' style={{ justifyContent: 'left' }}>
                    <span
                        // onClick={() => userInfo.actn_list.length > 1 ? setVisibleModalAct(false) : null}
                        style={{ color: 'var(--PRIMARY__CONTENT__COLOR)', fontSize: fontSizes.small, opacity: 0.3, whiteSpace: 'pre' }}>
                        {userInfo.actn_name}
                    </span>
                </div>
            </div>
            <div className='' style={{ width: 'calc(100% - 128px)' }}>
                <div className='flex' style={{ overflowX: 'auto', marginRight: '1rem', paddingBottom: '0.6rem' }}>
                    {showSubAll && (
                        <Tooltip placement='top' title={t('all_sub_account')}>
                            <Button
                                onClick={changeAll}
                                className='flex justify-content-center'
                                style={{
                                    backgroundColor: isAll ? 'var(--PRIMARY)' : 'var(--INPUT__BORDER)',
                                    color: isAll ? '#FFF' : '#fff',
                                    border: '1px solid var(--INPUT__BG)',
                                    fontWeight: isAll ? '600' : '',
                                    padding: dimensions.moderate(3),
                                    marginLeft: dimensions.moderate(2),
                                    minHeight: dimensions.moderate(35),
                                    minWidth: dimensions.moderate(35),
                                    borderRadius: 8,
                                    marginRight: '8px',
                                    alignItems: 'center',
                                    fontSize: fontSizes.verySmall,
                                }}>
                                ALL
                            </Button>
                        </Tooltip>
                    )}
                    {userInfo.sub_list.map((item, index) => (
                        <Button
                            onClick={() => changeSubAct(item)}
                            key={item}
                            style={{
                                // backgroundColor: item === userInfo.sub_curr ? 'var(--PRIMARY)' : 'var(--PRIMARY__CONTENT__COLOR)',
                                backgroundColor: !isAll && item === userInfo.sub_curr ? 'var(--PRIMARY)' : 'var(--INPUT__BORDER)',
                                fontWeight: !isAll && item === userInfo.sub_curr ? '600' : '',
                                // backgroundColor: item === userInfo.sub_curr ? 'var(--PRIMARY)' : 'var(--PRIMARY__BG__COLOR)',
                                padding: dimensions.moderate(3),
                                marginLeft: dimensions.moderate(2),
                                minHeight: dimensions.moderate(32),
                                minWidth: dimensions.moderate(32),
                                borderRadius: 8,
                                marginLeft: index !== 0 && dimensions.halfIndent,
                                border: '1px solid var(--INPUT__BG)',
                                // display:'flex',
                                // justifyContent:'center',
                                // className="flex justify-content-center text-primary"
                            }}>
                            <Text
                                style={{
                                    color: !isAll && item === userInfo.sub_curr ? '#FFF' : '',
                                    // color: item === userInfo.sub_curr ? 'white' : 'var(--PRIMARY_CONTENT_COLOR)',
                                    fontSize: fontSizes.verySmall,
                                }}>
                                {item}
                            </Text>
                        </Button>
                    ))}
                </div>
            </div>
            <Modal visible={visibleModalAct} style={{ width: '85vw' }} transparent maskClosable={false}>
                {userInfo.actn_list.map((e, index) => (
                    <div
                        className='mt-1'
                        key={String(index)}
                        onClick={() => changeActMain(e)}
                        style={{ color: e.AcntNo === userInfo.actn_curr ? 'var(--PRIMARY)' : '' }}>
                        {e.label}
                    </div>
                ))}
                <div
                    className='mt-3 fw-bold'
                    onClick={() => {
                        setVisibleModalAct(false)
                    }}>{t('common_button_cancel')}</div>
            </Modal>
        </div>

        // </div>
    )
}

export default memo(Account)
