import React, { useContext, memo } from 'react';
// import { View, ScrollView, StyleSheet, Text } from 'react-native';
import { StoreContext } from '../../store';
import { useTranslation } from 'react-i18next';
import { fontSizes, dimensions, fontWeights } from '../../styles';
import { Row } from 'antd';
import { Button } from 'antd-mobile';
import Text from 'antd/lib/typography/Text';
import { ScrollView } from 'basic-components';

const RowOrderType = ({ orderTps, orderTp, onChangeOrderTp }) => {
    const { styles } = useContext(StoreContext);
    const { t } = useTranslation();
    return (
        <Row style={UI.margin_top_row}>
            <div style={UI.left_row}>
                <Text style={{ color: 'var(--PRIMARY__CONTENT__COLOR)', fontWeight: fontWeights.light }}>{t('order_tp')}</Text>
            </div>
            <div style={{ flex: 8, flexDirection: 'row' }}>
                <ScrollView horizontal keyboardShouldPersistTaps='handled'>
                    {orderTps.map(item =>
                        <Button
                            onClick={() => onChangeOrderTp(item)}
                            key={item.key}
                            small
                            style={{ backgroundColor: item.key === orderTp ? 'var(--PRIMARY)' : styles.BUTTON__SECONDARY, padding: 3, marginLeft: 2 }}>
                            <Text style={{ color: item.key === orderTp ? 'white' : 'var(--PRIMARY__CONTENT__COLOR)', fontSize: fontSizes.verySmall, paddingHorizontal: 5 }}>
                                {item.name}
                            </Text>
                        </Button>
                    )}
                </ScrollView>
            </div>
        </Row>
    )
};
export default memo(RowOrderType, areEqualRowOrderTypeMemo)

function areEqualRowOrderTypeMemo(prev, next) {
    if (prev.orderTps === next.orderTps && prev.orderTp === next.orderTp) return true
    else return false
}

const UI = {
    margin_top_row: {
        marginTop: dimensions.halfIndent
    },
    left_row: {
        flex: 4,
        flexDirection: 'row',
        paddingVertical: 5
    }
}