import React, { useContext } from "react";
import { StoreContext } from '../../../store';
import { fontSizes as fs, dimensions as dm, fontWeights as fw } from '../../../styles';

const ModalContent = ({
    children,
    title = '',             // Nội dung modal
    title2,
    type = 'confirm',           // type của button
    iconComponent,
}) => {
    const { styles } = useContext(StoreContext);

    return (
        <div className="" style={{
            // backgroundColor: styles.HEADER__BG__COLOR,
            justifyContent: 'flex-start',
            borderRadius: 16,
        }}>
            <div
                style={{
                    flex: 1,
                    justifyContent: 'center',
                    alignSelf: 'center',
                    paddingLeft: iconComponent ? dm.vertical(32) : dm.vertical(8),
                    paddingRight: iconComponent ? dm.vertical(32) : dm.vertical(8),
                    textAlign: 'center',
                }}>
                {iconComponent}
            </div>
            <div
                style={{
                    textAlign: 'center',
                    fontSize: fs.xmedium,
                    color: type === 'confirm' ? styles.PRIMARY : styles.SELL__BG,
                    fontWeight: 'bold',
                }}>
                {title}
            </div>
            {title2 ? (
                <div
                    style={{
                        textAlign: 'center',
                        marginVertical: dm.halfIndent,
                        marginHorizontal: dm.moderate(16),
                        fontWeight: 'bold',
                        color: 'var(--PRIMARY__CONTENT__COLOR)',
                    }}>
                    {title2}
                </div>
            ) : null}
            {children}
        </div>
    )
}

export default ModalContent