import React, { useContext, useState, memo, useEffect, useLayoutEffect, useRef } from 'react';
import { StoreContext } from '../../store';
import { useTranslation } from 'react-i18next';
import { FormatNumber, glb_sv, eventList, subcribeFunct } from '../../utils';
import throttle from 'lodash/throttle'
import { dimensions, fontSizes, fontWeights } from '../../styles';
import Text from 'antd/lib/typography/Text';
import HeaderTop from '../market-tab/TopMarket/HeaderTop'
import { Button } from 'antd';

const StockItem = ({ item, onChangeStkInfo }) => {

    return (
        <div className='flex flex-direction-row' style={UI.HeaderTop_view} onClick={() => onChangeStkInfo(item)}>
            <div className='flex flex-2 --fs__normal justify-content-start flex-direction-column'>
                <Text style={{ color: 'var(--PRIMARY__CONTENT__COLOR)', fontSize: item.t55.length > 3 ? fontSizes.verySmall : fontSizes.small }}>
                    {item.t55}
                </Text>
            </div>
            <div className='flex flex-1 justify-content-center'>
                {item.t31 ? <Text style={{ color: glb_sv.getColor(item.t31, item), fontSize: fontSizes.verySmall, textAlign: 'right' }}>
                    {item.t31_incr_per > 0 ? '+' : ''}{FormatNumber(item.t31_incr_per, 2)}%
                </Text> : null}
            </div>
            <div className='flex flex-1 justify-content-center'>
                <Text style={{ color: glb_sv.getColor(item.t260, item), fontSize: fontSizes.verySmall, textAlign: 'right' }}>
                    {FormatNumber(item.t137, 0, 1)}
                </Text>
            </div>
            <div className='flex flex-1 justify-content-center'>
                <Text style={{ color: glb_sv.getColor(item.t31, item), fontSize: fontSizes.verySmall, textAlign: 'right' }}>
                    {FormatNumber(item.t31, 0, 1)}
                </Text>
            </div>
            <div className='flex flex-1 justify-content-center'>
                <Text style={{ color: glb_sv.getColor(item.t31, item), fontSize: fontSizes.verySmall, textAlign: 'right' }}>
                    {FormatNumber(item.t387, 0, 1, 'short')}
                </Text>
            </div>
        </div>
    )
};

const FooterComponent = memo(({ showAllStk, type, t }) => {
    const { styles } = useContext(StoreContext)
    return (
        <div className='flex flex-direction-row center__item' style={{ paddingTop: dimensions.halfVerticalIndent, paddingBottom: dimensions.halfVerticalIndent }} onClick={() => showAllStk(type)}>
            <Button style={UI.Button_all}>
                <Text style={{ color: 'white' }}>{t('view_all')}</Text>
            </Button>
        </div>
    )
}, () => true);

function ListStock({ listStock, showAllStk, onChangeStkInfo, maxList, type }) {

    const { styles } = useContext(StoreContext)
    const [list, setList] = useState(listStock);
    const listStockRef = useRef(listStock)
    const { t } = useTranslation()

    const throttled = useRef(throttle(() => {
        const newList = listStockRef.current.map(e => {
            return glb_sv.StockMarket[e.t55] ? glb_sv.StockMarket[e.t55] : { ...e }
        })
        setList(newList)
    }, 500, { trailing: true }))

    useEffect(() => {
        listStockRef.current = listStock;
        const eventMarket = glb_sv.eventMarket.subscribe(msg => {
            if (msg.type === eventList.SUB_STOCK && msg.message === 'SI') {
                throttled.current();
            }
        });
        const newList = listStockRef.current.map(e => {
            return glb_sv.StockMarket[e.t55] ? glb_sv.StockMarket[e.t55] : { ...e }
        })
        setList(newList)
        return () => {
            eventMarket.unsubscribe();
            throttled.current.cancel();
        };
    }, [listStock]);

    if (!list.length) {
        return(
            <div className='flex justify-content-center align-items-center' style={{ height: 200 }}>
                <span style={{ color: 'var(--SECOND__CONTENT__COLOR)' }}>{t('common_NoDataFound')}</span>
            </div>
        )
    }

    return (<>
        <HeaderTop type='TOP_PRI_UP' activeOtp='1D' />
        {list.map(item => <StockItem key={item.t55} item={item} styles={styles} onChangeStkInfo={onChangeStkInfo} />)}
        {maxList > 30 ? <FooterComponent showAllStk={showAllStk} type={type} t={t}/> : null}
    </>);
}

const UI = {
    HeaderTop_view: {
        paddingTop: dimensions.vertical(12),
        paddingBottom: dimensions.vertical(12),
        borderBottom: '1px solid var(--DIVIDER__COLOR)'
    },
    Button_all: {
        backgroundColor: 'var(--PRIMARY)',
        borderRadius: 8,
        width: dimensions.indent * 8,
        height: dimensions.verticalIndent * 2.5,
        fontWeight: fontWeights.medium
    }
};

export default memo(ListStock, areEqual);
function areEqual(prev, next) {
    if (prev.listStock === next.listStock) return true
    return false
}