import { Text } from 'antd-mobile'
import { Spin } from 'antd'
import Account from 'components/account'
import { routes } from 'navigation/routes'
import React, { useState, useEffect, useCallback, useRef, useContext, memo } from 'react'
import { useTranslation } from 'react-i18next'
import { StoreContext } from 'store'
import { StoreTrading } from 'store-trading'
import { dimensions, fontSizes, fontWeights } from 'styles'
import { eventList, FormatNumber, glb_sv, reqFunct, sendRequest } from 'utils'
import { useHistory } from 'react-router-dom'
import IC_FOLDER_NULL from 'assets/images/common/ic_folder_null.png';
import ModalOtp from './modal-otp'

const ServiceInfo = {
	GET_ORDER_LIST: {
		reqFunct: reqFunct.GET_ORDER_LIST,
		WorkerName: 'FOSqOrder01',
		ServiceName: 'FOSqOrder01_OrderBook',
		Operation: 'Q',
		ClientSentTime: '0'
	},
	CANCEL_ORDER: {
		reqFunct: reqFunct.CANCEL_ORDER,
		WorkerName: 'FOSxCancelOrder',
		ServiceName: 'FOSxCancelOrder',
		Operation: 'I',
		ClientSentTime: '0'
	},
	MODIFY_ORDER: {
		reqFunct: reqFunct.MODIFY_ORDER,
		WorkerName: 'FOSxModifyOrder',
		ServiceName: 'FOSxModifyOrder',
		Operation: 'I',
		ClientSentTime: '0'
	},
}

const translateOrderTp = value => {
	if (value === '01') {
		return 'LO'
	} else if (value === '02') {
		return 'MP'
	} else if (value === '03') {
		return 'ATO'
	} else if (value === '04') {
		return 'ATC'
	} else if (value === '05') {
		return 'PT'
	} else if (value === '06') {
		return 'MOK'
	} else if (value === '07') {
		return 'MAK'
	} else if (value === '08') {
		return 'MTL'
	} else if (value === '15') {
		return 'PLO'
	}
}

const transOrderStatus = value => {
	if (value === '0') return 'in_core'
	else if (value === '1') return 'send_to_exchange'
	else if (value === '3') return 'wait_to_match'
	else if (value === '4') return 'match_all'
	else if (value === '5') return 'match_a_pieces'
	else if (value === '6') return 'wait_to_cancel_cfm'
	else if (value === '7') return 'confirm_cancel'
	else if (value === '8') return 'confirm_stop_order'
	else if (value === '9') return 'confirm_ice_order'
	else if (value === 'X') return 'reject'
}


const OrderList = () => {
	const { t } = useTranslation()
	const { styles } = useContext(StoreContext);
	const { userInfo } = useContext(StoreTrading);
	const [newsList, setNewsList] = useState([])
	const getOrderListFlag = useRef(false)
	const histOrdListTemple = useRef([])
	const history = useHistory()

	// handle cancel, modify order
	const [data, setData] = useState({})
	const [modalCancelOrder, setModalCancelOrder] = useState(false)
	const sendCancelOrderFlag = useRef(false)
	const showModalCancelRef = useRef(false)

	const [modalModifyOrder, setModalModifyOrder] = useState(false)
	const [objEdit, setObjEdit] = useState({ price: 0, qty: 0 })
	const sendModifyOrderFlag = useRef(false)

	const timeoutGetListOrder = useRef(null)

	const [spinner, setSpinner] = useState(false)

	const [refreshing, setRefresh] = useState(false)

	const itemAction = useRef(null)

	const [showErrorView, setShowErrorView] = useState(false)

	const [isOpenOTP, setIsOpenOTP] = useState(false)
	const [OTP_Data, setOTP_Data] = useState({})


	useEffect(() => {
		if (timeoutGetListOrder.current) clearTimeout(timeoutGetListOrder.current)
		timeoutGetListOrder.current = setTimeout(() => {
			setNewsList([])
			getOrderList()
		}, 1000);
		const commonEvent = glb_sv.commonEvent.subscribe(msg => {
			if (msg.type === eventList.NOTIFY_SERVER) {
				const message = msg.data
				if (
					message['MsgTp'] === 'ORD_NEW' ||
					message['MsgTp'] === 'ORD_REJ' ||
					message['MsgTp'] === 'ORD_MTH' ||
					message['MsgTp'] === 'ORD_CNL' ||
					message['MsgTp'] === 'ORD_MOD' ||
					message['MsgTp'] === 'ORD_ADV' ||
					message['MsgTp'] === 'ORD_REJ8' ||
					message['MsgTp'] === 'MSS_ORD_OK' ||
					message['MsgTp'] === 'MSS_ORD_ERR'
				) {
					if (timeoutGetListOrder.current) clearTimeout(timeoutGetListOrder.current)
					timeoutGetListOrder.current = setTimeout(() => {
						setNewsList([])
						getOrderList()
					}, 2000)
				}
			}
			if (msg.type === eventList.LOGIN_SUCCESS) {
				setNewsList([])
				getOrderList()
			}
			if (msg.type === eventList.REFRESH_ORDER_LIST) {
				if (msg.notGetList) {
					if (timeoutGetListOrder.current) clearTimeout(timeoutGetListOrder.current)
					return;
				}
				setRefresh(true)
				if (timeoutGetListOrder.current) clearTimeout(timeoutGetListOrder.current)
				timeoutGetListOrder.current = setTimeout(() => {
					getOrderList()
				}, 1000)
			}
		})

		return () => {
			commonEvent.unsubscribe();
		}
	}, [userInfo.actn_curr, userInfo.sub_curr])

	const getOrderList = () => {
		setSpinner(true)
		setRefresh(true)
		histOrdListTemple.current = [];
		const InputParams = ['3', userInfo.actn_curr, userInfo.sub_curr]
		sendRequest(ServiceInfo.GET_ORDER_LIST, InputParams, getOrderListResultProc, true, getOrderListTimeout)
	}

	const getOrderListTimeout = () => {
		getOrderListFlag.current = false;
		setRefresh(false);
		setShowErrorView(true)
		setSpinner(false);
	}

	const getOrderListResultProc = (reqInfoMap, message) => {
		console.log('getOrderListResultProc', reqInfoMap, message)
		setTimeout(() => {
			setRefresh(false)
		}, 500);
		if (Number(message['Result']) === 0) {
			setShowErrorView(true)
			setSpinner(false);
			return
		} else {
			let jsondata = []
			try {
				if (!message['Data']) {
				} else {
					jsondata = JSON.parse(glb_sv.filterStrBfParse(message['Data']))
				}
			} catch (err) {
				console.log('getHotNewsResultProc error', err)
				getOrderListFlag.current = false
				return
			}
			histOrdListTemple.current = histOrdListTemple.current.concat(jsondata)
			if (Number(message['Packet']) <= 0) {
				getOrderListFlag.current = false
				setNewsList([...histOrdListTemple.current])
				setSpinner(false);
			}
		}
	}

	const colorOrderStatus = (value) => {
		if (value === '0') return 'var(--SEND_TO_EXCHANGE_COLOR)';
		else if (value === '1') return 'var(--SEND_TO_EXCHANGE_COLOR)';
		else if (value === '3') return 'var(--WAIT_TO_MATCH_COLOR)';
		else if (value === '4') return 'var(--MATCH_A_PIECES_COLOR)';
		else if (value === '5') return 'var(--MATCH_A_PIECES_COLOR)';
		else if (value === '6') return '';
		else if (value === '7') return 'var(--REJECT_COLOR)';
		else if (value === '8') return '';
		else if (value === '9') return '';
		else if (value === 'X') return 'var(--SEND_TO_EXCHANGE_COLOR)';
	}

	const sendCancelOrder = () => {
		if (sendCancelOrderFlag.current) return;
		sendCancelOrderFlag.current = true
		setModalCancelOrder(false)
		setSpinner(true);
		const InputParams = [data.c13, data.c16, data.c17, '']
		sendRequest(ServiceInfo.CANCEL_ORDER, InputParams, handleSendCancelOrder, true, sendCancelOrderTimeout)
	}

	const sendCancelOrderTimeout = () => {
		sendCancelOrderFlag.current = false;
		setSpinner(false);
	}

	const handleSendCancelOrder = (reqInfoMap, message) => {
		sendCancelOrderFlag.current = false;
		setSpinner(false);
		if (Number(message['Result']) === 0) {
			if (message.Code == 'XXXXX5' || message.Code === '080063' || message.Code === '010012') {
				// navigation.navigate(Screens.ALERT_MODAL, {
				// 	icon: <IconSvg.ErrorIcon color={styles.WARN__COLOR} />,
				// 	title: t('common_notify'),
				// 	content: t('request_hanlde_not_success_try_again'),
				// 	typeColor: styles.WARN__COLOR
				// })
			}
			else if (glb_sv.otpCases.includes(message.Code)) {
				glb_sv.objShareGlb['sessionInfo']['Otp'] = ''

				let data
				try {
					data = JSON.parse(message.Data)[0]
				} catch (error) {
					return
				}

				const otp_Type = Number(data.c1);
				const expTimeOtp = Number(data.c2)
				let reqOtpMessage = 'OTP';
				if (Number(data['c1']) === 2) { //-- dạng thẻ ma trận
					reqOtpMessage = 'OTP ' + data['c3']
				}
				// navigation.navigate(Screens.OTP_MODAL,
				// 	{
				// 		time: expTimeOtp,
				// 		type: reqOtpMessage,
				// 		hasOTP: false,
				// 		otp_Type,
				// 		msg: message.Message,
				// 		functCallback: showModalCancel
				// 	});

				setIsOpenOTP(true);
				setOTP_Data({
					time: expTimeOtp,
					type: reqOtpMessage,
					hasOTP: false,
					otp_Type,
					msg: message.Message,
					functCallback: handleShowModalModify
				})

			}
			else {
				// navigation.navigate(Screens.ALERT_MODAL, {
				// 	title: t('common_notify'),
				// 	content: message.Message,
				// 	typeColor: styles.WARN__COLOR
				// })
			}
		}
		else {
			// navigation.navigate(Screens.ALERT_MODAL, {
			// 	title: t('common_notify'),
			// 	content: message.Message,
			// 	typeColor: styles.SUCCESS__COLOR
			// });
			if (timeoutGetListOrder.current) clearTimeout(timeoutGetListOrder.current)
			timeoutGetListOrder.current = setTimeout(() => {
				getOrderList();
			}, 1000);
		}
	}

	const handleShowModalModify = item => {
		if (item) itemAction.current = item;
		if (!itemAction.current) return;
		// if (!glb_sv.checkOtp(navigation, handleShowModalModify)) {
		// 	return
		// }
		setData(itemAction.current)
		setObjEdit({
			price: itemAction.current.c6,
			qty: itemAction.current.c5
		})
		setModalModifyOrder(true)
	}

	const sendModifyOrder = () => {
		if (!objEdit.qty || !objEdit.price) return;
		if (sendModifyOrderFlag.current) return
		sendModifyOrderFlag.current = true
		setModalModifyOrder(false)
		setSpinner(true);
		const InputParams = [data.c13, data.c16, data.c17, '', objEdit.qty + '', objEdit.price + '']
		sendRequest(ServiceInfo.MODIFY_ORDER, InputParams, handleSendModifyOrder, true, sendModifyOrderTimeout)
	}


	const sendModifyOrderTimeout = () => {
		sendModifyOrderFlag.current = false;
		setSpinner(false);
	}

	const handleSendModifyOrder = (reqInfoMap, message) => {
		sendModifyOrderFlag.current = false;
		setSpinner(false);
		if (Number(message['Result']) === 0) {
			if (message.Code == 'XXXXX5' || message.Code === '080063' || message.Code === '010012') {
				// navigation.navigate(Screens.ALERT_MODAL, {
				// 	icon: <IconSvg.ErrorIcon color={styles.WARN__COLOR} />,
				// 	title: t('common_notify'),
				// 	content: t('request_hanlde_not_success_try_again'),
				// 	typeColor: styles.WARN__COLOR
				// })
			}
			else if (glb_sv.otpCases.includes(message.Code)) {
				glb_sv.objShareGlb['sessionInfo']['Otp'] = ''

				let data
				try {
					data = JSON.parse(message.Data)[0]
				} catch (error) {
					return
				}

				const otp_Type = Number(data.c1);
				const expTimeOtp = Number(data.c2)
				let reqOtpMessage = 'OTP';
				if (Number(data['c1']) === 2) { //-- dạng thẻ ma trận
					reqOtpMessage = 'OTP ' + data['c3']
				}
				// navigation.navigate(Screens.OTP_MODAL,
				// 	{
				// 		time: expTimeOtp,
				// 		type: reqOtpMessage,
				// 		hasOTP: false,
				// 		otp_Type,
				// 		msg: message.Message,
				// 		functCallback: handleShowModalModify
				// 	});
				setIsOpenOTP(true);
				setOTP_Data({
					time: expTimeOtp,
					type: reqOtpMessage,
					hasOTP: false,
					otp_Type,
					msg: message.Message,
					functCallback: handleShowModalModify
				})
			}
			else {
				// navigation.navigate(Screens.ALERT_MODAL, {
				// 	title: t('common_notify'),
				// 	content: message.Message,
				// 	typeColor: styles.WARN__COLOR
				// })
			}
		}
		else {
			// navigation.navigate(Screens.ALERT_MODAL, {
			// 	title: t('common_notify'),
			// 	content: message.Message,
			// 	typeColor: styles.SUCCESS__COLOR
			// });
			if (timeoutGetListOrder.current) clearTimeout(timeoutGetListOrder.current)
			timeoutGetListOrder.current = setTimeout(() => {
				getOrderList();
			}, 1000);
		}
	}

	const showModalCancel = (item) => {
		if (item) itemAction.current = item;
		if (!itemAction.current) return
		// if (!glb_sv.checkOtp(navigation, showModalCancel)) {
		// 	return
		// }
		showModalCancelRef.current = true;
		setModalCancelOrder(true);
		setData(itemAction.current);
	}

	const hideModalCancelOrder = () => {
		setTimeout(() => {
			showModalCancelRef.current = false;
		}, 1000);
		setModalCancelOrder(false)
	}

	const RenderItem = ({ item }) => {
		return (
			<RowItem
				changeOrderDetail={() => changeOrderDetail(item)}
				PRIMARY__BG__COLOR={styles.PRIMARY__BG__COLOR}
				UP__COLOR={styles.UP__COLOR}
				DOWN__COLOR={styles.DOWN__COLOR}
				PRIMARY__CONTENT__COLOR={styles.PRIMARY__CONTENT__COLOR}
				DIVIDER__COLOR={styles.DIVIDER__COLOR}
				styles={styles}
				c0={item.c0}
				c4={item.c4}
				c5={item.c5}
				c6={item.c6}
				c8={item.c8}
				c13={item.c13}
				c2={item.c2}
				c11={t(transOrderStatus(item.c11))}
				colorC11={colorOrderStatus(item.c11)}
				t={t}
			/>
		)
	};

	const changeOrderDetail = useCallback(item => {
		// navigation.navigate(Screens.ORDER_DETAIL, { stockCode: item.c4, orderID: item.c0 })
		history.push(routes.ORDER_DETAIL.path, { stockCode: item.c4, orderID: item.c0 })
	}, []);

	const onRefresh = () => {
		if (refreshing) return;
		setRefresh(true);
		setShowErrorView(false);
		setTimeout(() => {
			setNewsList([])
			getOrderList();
		}, 1000);
	}


	return <div>
		<Account />
		<Spin spinning={spinner}>
			<div className='flex' style={{ ...UI.HeaderTop_view, borderBottom: 0 }}>
				<div className='flex flex-1 justify-content-start'>
					<span style={{ fontSize: fontSizes.verySmall, color: 'var(--HEADER__CONTENT__COLOR)' }}>
						{t('buy_short')}{'/'}{t('sell_short')}
					</span>
				</div>
				<div className='flex flex-1'>
					<span style={{ fontSize: fontSizes.verySmall, textAlign: 'right', color: 'var(--HEADER__CONTENT__COLOR)' }}>
						{t('stock_symbol_short')}
					</span>
				</div>
				<div className='flex flex-1'>
					<span style={{ fontSize: fontSizes.verySmall, textAlign: 'right', color: 'var(--HEADER__CONTENT__COLOR)' }}>
						{t('volume_evaluation')}
					</span>
				</div>
				<div className='flex flex-1'>
					<span style={{ fontSize: fontSizes.verySmall, textAlign: 'right', color: 'var(--HEADER__CONTENT__COLOR)' }}>
						{t('last_price')}
					</span>
				</div>
				<div className='flex flex-1'>
					<span style={{ fontSize: fontSizes.verySmall, textAlign: 'right', color: 'var(--HEADER__CONTENT__COLOR)' }}>
						{t('price')}
					</span>
				</div>
				<div className='flex flex-1'>
					<span style={{ fontSize: fontSizes.verySmall, textAlign: 'right', color: 'var(--HEADER__CONTENT__COLOR)' }}>
						{t('order_status')}
					</span>
				</div>
			</div>
			<div>
				{newsList.length ? newsList.map(item => {
					return <RenderItem item={item} />
				}) :
					<div>
						<div className='flex justify-content-center'>
							<img src={IC_FOLDER_NULL} />
						</div>
						<div style={{ textAlign: "center", lineHeight: "160%", marginTop: "30px" }}>
							<div style={{ fontSize: "15px", lineHeight: "20px", fontWeight: "500" }}>{t('empty_list')}</div>
						</div>
					</div>}
			</div>

			{
				isOpenOTP && <ModalOtp
					isOpenOTP={isOpenOTP}
					setIsOpenOTP={setIsOpenOTP}
					OTP_Data={OTP_Data}
				/>
			}
		</Spin>
	</div>
}

const RowItem = memo(({ PRIMARY__BG__COLOR, UP__COLOR, DOWN__COLOR, PRIMARY__CONTENT__COLOR, changeOrderDetail, colorC11, c4, c5, c8, c6, c11, c0, c2, t }) => {
	return (
		<div style={{ display: 'flex', paddingBottom: '0.5rem' }} onClick={changeOrderDetail}>
			<div style={{ flexDirection: 'row', flex: 0.4 }}>
				<div style={{ flex: 0.4, width: 18, height: 18, borderRadius: 9, backgroundColor: c2 === '1' ? UP__COLOR : DOWN__COLOR, alignItems: 'center', justifyContent: 'center', position: 'absolute', display: 'flex' }}>
					<Text style={{ color: '#FFF', fontSize: fontSizes.verySmall, fontWeight: fontWeights.medium, marginLeft: 1.8 }}>
						{c2 === '1' ? t('buy_short') : t('sell_short')}
					</Text>
				</div>
			</div>

			<Text
				style={{ flex: 0.7, textAlign: 'right', fontSize: fontSizes.verySmall, fontWeight: fontWeights.medium }}>
				{c4}
			</Text>

			<Text style={{ flex: 1, textAlign: 'right', color: PRIMARY__CONTENT__COLOR, fontSize: fontSizes.verySmall, fontWeight: fontWeights.medium }}>
				{FormatNumber(c5)}
			</Text>
			<Text style={{ flex: 1, textAlign: 'right', color: PRIMARY__CONTENT__COLOR, fontSize: fontSizes.verySmall, fontWeight: fontWeights.medium }}>
				{FormatNumber(c8)}
			</Text>
			<Text style={{ flex: 1, textAlign: 'right', color: PRIMARY__CONTENT__COLOR, fontSize: fontSizes.verySmall, fontWeight: fontWeights.medium }}>
				{FormatNumber(c6)}
			</Text>
			<Text style={{ flex: 1.3, textAlign: 'right', color: colorC11, fontSize: fontSizes.verySmall, fontWeight: fontWeights.medium }}>
				{c11}
			</Text>
		</div >
	);
}, propsAreEqual);

function propsAreEqual(prev, next) {
	return prev.c0 === next.c0 && prev.c4 === next.c4 && prev.c5 === next.c5 && prev.c6 === next.c6 && prev.c8 === next.c8 && prev.c11 === next.c11 && prev.c2 === next.c2;
}

function areEqual(prev, next) {
	return true;
}

const UI = {
	HeaderTop_view: {
		borderBottomWidth: 1,
		borderBottomStyle: 'solid',
		paddingTop: dimensions.vertical(16),
		paddingBottom: dimensions.vertical(16)
	}
}

export default OrderList