import moment from 'moment'
//import { Container, Content } from 'native-base';
import React, { useContext, useState } from 'react'
import { useTranslation } from 'react-i18next'
import HeaderComponent from '../../../../components/header'
import { ButtonCustom, ModalContent, RowData, RowDataModal } from '../../../../components/trading-component'
import { StoreContext } from '../../../../store'
import { StoreTrading } from '../../../../store-trading'
import { FormatNumber, reqFunct, sendRequest, Screens, glb_sv, eventList } from '../../../../utils'
import { Modal } from 'antd-mobile'
import { useLocation, useHistory } from 'react-router-dom'
import { notification } from 'antd'
const ServiceInfo = {
    CANCEL_LENDING_ADVANCE: {
        // Hủy ứng trước tiền bán
        reqFunct: reqFunct.CANCEL_LENDING_ADVANCE,
        // WorkerName: 'ALTxLendingAdvance',
        // ServiceName: 'ALTxLendingAdvance_1302_1',
        WorkerName: 'FOSxPIA',
        ServiceName: 'FOSxPIA_1302_1',
        ClientSentTime: '0',
        Operation: 'D',
        AprStat: 'D',
        AprSeq: '', //aprSeq,
        MakerDt: '', //aprDt,
        // TestInput: []
    },
}

// Khai báo component
export default function DetailAdvanceLendingLayout({}) {
    const { styles } = useContext(StoreContext)
    const history = useHistory()
    const location = useLocation()
    let { data } = location.state
    const { userInfo } = useContext(StoreTrading)

    const { t } = useTranslation()
    const [isOpenModal, setIsOpenModal] = useState(false)
    const onCancelAdvanceLending = (itemLending) => {
        ServiceInfo.CANCEL_LENDING_ADVANCE.AprSeq = data.c14 || ''
        ServiceInfo.CANCEL_LENDING_ADVANCE.MakerDt = moment(data.c13, 'DDMMYYYY').format('YYYYMMDD') || ''

        const inputParams = [itemLending.c2, userInfo.actn_curr, userInfo.sub_curr]
        sendRequest(ServiceInfo.CANCEL_LENDING_ADVANCE, inputParams, handleonCancelAdvanceLending)
    }
    // // -------------------------------------------   Khai báo các hàm gửi (request) và nhận (handle respone) dữ liệu từ server
    // const handleonCancelAdvanceLending = (reqInfoMap, message) => {
    //     if (Number(message['Result']) === 0) {
    //         if (message.Code == 'XXXXX5' || message.Code === '080063' || message.Code === '010012') {
    //             notification.warning({
    //                 message: t('common_notify'),
    //                 description: t('request_hanlde_not_success_try_again'),
    //                 duration: 4,
    //             })
    //         } else if (message['Code'] !== '080128') {
    //             notification.warning({
    //                 message: t('common_notify'),
    //                 description: message['Message'],
    //                 duration: 4,
    //             })
    //         }
    //         return
    //     } else {
    //         notification.success({
    //             message: t('common_notify'),
    //             description: message['Message'],
    //             duration: 4,
    //         })
    //     }
    // }

    const handleonCancelAdvanceLending = (reqInfoMap, message) => {
        // -- process after get result --
        if (Number(message['Result']) === 0) {
            if (message.Code == 'XXXXX5' || message.Code === '080063' || message.Code === '010012') {
                // navigation.navigate(Screens.ALERT_MODAL, {
                //     icon: <IconSvg.ErrorIcon color={styles.WARN__COLOR} />,
                //     title: t('common_notify'),
                //     content: t('request_hanlde_not_success_try_again'),
                //     typeColor: styles.WARN__COLOR
                // })

                notification.warning({
                    message: t('common_notify'),
                    description: t('request_hanlde_not_success_try_again'),
                    duration: 4,
                })
            } else if (glb_sv.otpCases.includes(message.Code)) {
                glb_sv.objShareGlb['sessionInfo']['Otp'] = ''

                let data
                try {
                    data = JSON.parse(message.Data)[0]
                } catch (error) {
                    return
                }

                const otp_Type = Number(data.c1)
                const expTimeOtp = Number(data.c2)
                let reqOtpMessage = 'OTP'
                if (Number(data['c1']) === 2) {
                    //-- dạng thẻ ma trận
                    reqOtpMessage = 'OTP ' + data['c3']
                }
                glb_sv.commonEvent.next({
                    type: eventList.OTP_MODAL,
                    time: expTimeOtp,
                    message: reqOtpMessage,
                    messageOTP: message.Message,
                    hasOTP: false,
                    otp_Type,
                })
            } else {
                notification.warning({
                    message: t('common_notify'),
                    description: message['Message'],
                    duration: 4,
                })
            }
        } else {
            notification.success({
                message: t('common_notify'),
                description: message['Message'],
                duration: 4,
            })
        }
    }
    const getColor = (status) => {
        if (status === 'N') return styles.REF__COLOR
        if (status === 'R' || status === 'D') return styles.DOWN__COLOR
        if (status === 'Y') return styles.UP__COLOR
    }

    return (
        <div style={{ backgroundColor: styles.PRIMARY__BG__COLOR }}>
            <HeaderComponent
                transparent
                titleAlgin='start'
                title={t('contract_detail_info')}
                colorTitle={styles.PRIMARY__CONTENT__COLOR}
                alignChild={['left', 'left', 'center']}
            />
            <div>
                <RowData textLeft={t('acnt_no')} dataSub={[data.c0, data.c1]} />
                <RowData textLeft={t('contract_no')} textRight={data.c2} />
                <RowData textLeft={t('settlement_date')} textRight={moment(data.c3, 'DDMMYYYY').format('DD/MM/YYYY')} />
                <RowData textLeft={t('ratio_fee')} textRight={FormatNumber(data.c4, 4) + '%'} />
                <RowData textLeft={t('amount_fee_repay')} textRight={FormatNumber(data.c5)} />
                <RowData textLeft={t('common_chanel')} textRight={data.c15} />
                <RowData textLeft={t('pia_type')} textRight={data.c16} />
                <RowData textLeft={t('order_status')} textRight={data.c17} rightColor={getColor(data.c10)} />
                <RowData textLeft={t('approve_time')} textRight={moment(data.c11, 'DDMMYYYYHHmmss').format('HH:mm:ss DD/MM/YYYY')} last />
                <div className='pl-2 pr-2'>
                    <ButtonCustom
                        disable={data.c10 !== 'N'}
                        visible={data.c10 === 'N'} // Có thể hủy
                        text={t('common_Cancel')}
                        onClick={() => setIsOpenModal(true)}
                    />
                </div>
            </div>
            <Modal visible={isOpenModal} style={{ width: '90vw' }} transparent maskClosable={false}>
                <ModalContent
                    title={t('confirm_cancel')}
                    type='cancel'
                    iconComponent={
                        <svg width={48} height={48} viewBox='0 0 32 32' fill='none' xmlns='http://www.w3.org/2000/svg'>
                            <path
                                d='M7.148 12.624l2.237 17.007a1 1 0 00.992.869h11.246a1 1 0 00.991-.87l1.728-13.13.658-5H8.272l-1.124 1.124z'
                                fill={styles.SELL__BG}
                            />
                            <path
                                d='M15 15.5v11a1 1 0 102 0v-11a1 1 0 10-2 0zM11 15.5v11a1 1 0 102 0v-11a1 1 0 10-2 0zM19 15.5v11a1 1 0 102 0v-11a1 1 0 10-2 0z'
                                fill='#F2F8FF'
                            />
                            <path
                                d='M15 8.5l2 1h2.5l-1-2.5-2-.5-1.5 2zM24 5l-2.5.5v1l.5 2 4-.5-1.5-2-.5-1zM15.086 1.858a1 1 0 00-1.414 0L2.358 13.172a1 1 0 000 1.414L3.772 16 16.5 3.272l-1.414-1.414z'
                                fill={styles.SELL__BG}
                            />
                            <path
                                opacity={0.12}
                                d='M22.614 29.63l.196-1.486c-6.417-2.253-10.844-8.531-10.448-15.652l.054-.992H8.272l-1.124 1.124 2.237 17.007a1 1 0 00.992.869h11.246a1 1 0 00.991-.87z'
                                fill='#303030'
                            />
                            <path
                                opacity={0.12}
                                d='M13.515 4.486L2.886 15.114l.886.886L16.5 3.272l-1.414-1.414a1 1 0 00-1.414 0l-.157.157a1.747 1.747 0 010 2.47zM24.5 6L24 5l-.909.182.41.818L25 8l-3.031.379.03.121 4-.5-1.5-2zM17.5 7.5l.8 2h1.2l-1-2.5-2-.5-.474.631L17.5 7.5z'
                                fill='#303030'
                            />
                            <path
                                d='M5.186 10.343l-1.06-1.06a1.502 1.502 0 010-2.122L7.66 3.625a1.501 1.501 0 012.122 0l1.06 1.061-.707.707-1.06-1.06a.501.501 0 00-.708 0L4.833 7.867a.501.501 0 000 .707l1.06 1.061-.707.707zM22 1h1v1h-1V1zM27.01 4.896l-.609-.792a5.83 5.83 0 014.697-1.095l-.196.981a4.836 4.836 0 00-3.891.906zM28.5 6h1v1h-1V6zM25 3.5h-1C24 2.122 25.122 1 26.5 1v1c-.827 0-1.5.673-1.5 1.5z'
                                fill={styles.SELL__BG}
                            />
                        </svg>
                    }>
                    <RowDataModal textLeft={t('acnt_no')} dataSub={[data.c0, data.c1]} />
                    <RowDataModal textLeft={t('contract_no')} textRight={data.c2} />
                    <RowDataModal textLeft={t('settlement_date')} textRight={moment(data.c3, 'DDMMYYYY').format('DD/MM/YYYY')} last />
                    <ButtonCustom
                        text={t('common_button_confirm')}
                        onClick={() => {
                            onCancelAdvanceLending(data)
                            setIsOpenModal(false)
                            history.goBack()
                        }}
                        type='confirm'
                    />
                    <ButtonCustom text={t('common_Cancel')} onClick={() => setIsOpenModal(false)} type='back' last />
                </ModalContent>
            </Modal>
        </div>
    )
}
