import { DatePicker } from 'antd'
import moment from 'moment'
// import { ReactComponent as IC_CALENDAR } from '../../../assets/images/ic_calendar.svg'
import styled from 'styled-components'
import { CalendarFilled } from '@ant-design/icons'

const DatePickerWraper = styled.div`
    background-color: var(--INPUT__BG);
    padding: 4px 8px;
    border-radius: 8px;
    flex: 1;
    margin: 0 0.25rem;
    position: relative;
    .ant-picker {
        border: unset;
        background: unset;
        padding: 0;
    }
`
const SuffixIconWrap = styled.div`
    position: absolute;
    right: 0.5rem;
    top: 30%;

`

const dateFormatList = 'YYYYMMDD'

const InputDatePicker = ({
    label = 'Từ ngày',
    value = '20210325',
    defaultValue = '20210325',
    labelRatio = [2, 3],
    onValueChange = (e) => null,
    placeholder = '',
    labelStyle = {},
    typeLabel = 'horizontal',
    style = {},
    readOnly = false,
    suffixIcon = <CalendarFilled />, //<IC_CALENDAR fill='var(--TEXT__1)' />,
    isRow = false,
    refs,
    formatMonth,
    ...props
}) => {
    const onChange = (_, dateOutPut) => {
        const formatResult = moment(dateOutPut, formatMonth ? 'MM-YYYY' : 'DD/MM/YYYY').format('YYYYMMDD')
        onValueChange(formatResult)
    }
    return (
        <DatePickerWraper>
            {label ? (
                <div className={`alt-label`} style={labelStyle}>
                    {label}
                </div>
            ) : null}
            <DatePicker
                ref={refs}
                className={`alt-date-picker`}
                defaultValue={moment(defaultValue, formatMonth ? 'MM-YYYY' : dateFormatList)}
                value={moment(value, dateFormatList)}
                format={[formatMonth ? 'MM-YYYY' : 'DD/MM/YYYY']}
                onChange={onChange}
                inputReadOnly={readOnly}
                disabled={readOnly}
                placeholder={placeholder}
                style={style}
                suffixIcon=''
                allowClear={false}
                panelRender={(panelNode) => <div style={{ borderRadius: '0.5rem' }}>{panelNode}</div>}
                {...props}
            />
            {suffixIcon && <SuffixIconWrap>{suffixIcon}</SuffixIconWrap>}
        </DatePickerWraper>
    )
}

export default InputDatePicker