import React from 'react';
import { fontSizes, dimensions, fontWeights } from '../../../styles';

const listTimeHis = ['1W', '1M', '3M', '6M', '1Y', '2Y', '3Y'];

export default ({ key, title, typeTop, value, valueCol3, ratioValue, styles, item, value_market, activeOtp, navigateStockInfo }) => {

    const getColor = (value, item) => {
        // REF__COLOR: '#f1c40f',
        // UP__COLOR: '#2ECC71',
        // DOWN__COLOR: '#EB5C55',
        // CEIL__COLOR: '#f618fb',
        // FLOOR__COLOR: '#00d3b8',
        if (value > 0 && value > item.FL && value < item.REF) return styles.DOWN__COLOR
        else if (value > 0 && value < item.CE && value > item.REF) return styles.UP__COLOR
        else if (value === 0 || value === item.REF) return styles.REF__COLOR
        else if (value > 0 && value === item.CE) return styles.CEIL__COLOR
        else if (value > 0 && value === item.FL) return styles.FLOOR__COLOR
    }

    const getColorHis = () => {
        if (item.CHGR > 0) return styles.UP__COLOR
        if (item.CHGR < 0) return styles.DOWN__COLOR
        return styles.REF__COLOR
    }


    return (
        <div className='flex flex-direction-row' style={{ ...UI.HeaderTop_view, borderBottom: ' 1px solid var(--DIVIDER__COLOR)' }} onClick={() => navigateStockInfo(title)} key={key}>
            <div className='flex flex-1 flex-direction-column'
                style={{ justifyContent: 'center', alignItems: 'flex-start' }}>
                <span style={{
                    color: listTimeHis.includes(activeOtp) ? styles.PRIMARY__CONTENT__COLOR : getColor(item.C, item), fontSize: title?.length > 3 ? fontSizes.verySmall : fontSizes.small, fontWeight: fontWeights.medium
                }}
                >{title}</span>
            </div>
            <div className='flex flex-1' style={{ justifyContent: 'flex-end' }}>
                <span style={{
                    color: listTimeHis.includes(activeOtp) ? getColorHis() : getColor(item.C, item), fontSize: String(Math.round(Math.abs(item.ChgRatio))).length > 3 ? fontSizes.verySmall : fontSizes.verySmall, textAlign: 'right', fontWeight: fontWeights.medium
                }}>
                    {item.CHGR > 0 ? '+' : ''}{ratioValue}%
                </span>
            </div>
            <div className='flex flex-1' style={{ justifyContent: 'flex-end' }}>
                <span style={{ color: (listTimeHis.includes(activeOtp) || typeTop === 'FRG_VAL_UP') ? styles.PRIMARY__CONTENT__COLOR : getColor(item.O, item), fontSize: fontSizes.verySmall, fontWeight: fontWeights.medium, textAlign: 'right' }}>
                    {valueCol3}
                </span>
            </div>
            <div className='flex flex-1' style={{ justifyContent: 'flex-end' }}>
                <span style={{ color: (listTimeHis.includes(activeOtp) || typeTop === 'FRG_VAL_UP') ? styles.PRIMARY__CONTENT__COLOR : getColor(item.C, item), fontSize: fontSizes.verySmall, fontWeight: fontWeights.medium, textAlign: 'right' }}>
                    {value}
                </span>
            </div>
            <div className='flex flex-1' style={{ justifyContent: 'flex-end' }}>
                <span style={{ color: 'var(--PRIMARY__CONTENT__COLOR)', fontSize: fontSizes.verySmall, fontWeight: fontWeights.medium, textAlign: 'right' }}>
                    {value_market}
                </span>
            </div>
        </div>
    )
}

const UI = {
    HeaderTop_view: {
        paddingTop: dimensions.vertical(12),
        paddingBottom: dimensions.vertical(12),
        // borderBottomWidth: 1,
        // flexDirection: 'row'
    }
}