import React, { useContext, useState, useRef, useEffect, memo } from 'react';
import { Button, Modal } from 'antd';
import { dimensions, fontSizes, fontWeights, IconSvg } from '../../styles';
import { StoreContext } from '../../store';
import { useTranslation } from 'react-i18next';
import { reqFunct, sendRequest, glb_sv, dataCryption, Screens } from '../../utils';
import moment from 'moment'
import Text from 'antd/lib/typography/Text';
import { InputItem } from 'antd-mobile';

const ServiceInfo = {
	GET_OTP_NUMBER: {
		reqFunct: reqFunct.GET_OTP_NUMBER,
		WorkerName: 'FOSxID01',
		ServiceName: 'FOSxID01_OTPManagement',
		Operation: 'I',
		ClientSentTime: '0'
	},
	SEND_OTP_FUNCTION: {
		reqFunct: reqFunct.SEND_OTP_FUNCTION,
		WorkerName: 'FOSxID01',
		ServiceName: 'FOSxID01_OTPManagement',
		Operation: 'U',
		ClientSentTime: '0'
	}
}

function OtpModal({ isOpenOTP, setIsOpenOTP, OTP_Data }) {
	const { msg, time, type, hasOTP, otp_Type, isLogin, functCallback, isHideBack } = OTP_Data

	const { styles } = useContext(StoreContext)
	const { t } = useTranslation();

	const [code, setCode] = useState('')
	const [timeOTP, setTimeOTP] = useState(time || 0)
	const [msgOTP, setMsgOTP] = useState(msg || '')
	const [typeOTP, setTypeOTP] = useState(type || 'OTP')

	const [errors, setError] = useState('')

	const timeOTPRef = useRef(time || 0);
	const getOtpNumberFlag = useRef(false)
	const otpInput = useRef(null)
	const sendOTPFlag = useRef(false)

	const timeAuthenOtp = useRef({
		timeGetOTP: moment(),
		numberOTP: 0
	})
	const timeoutTimingOTP = useRef(null)

	useEffect(() => {
		if (time) {
			timeOTPRef.current = time
			timeAuthenOtp.current = {
				timeGetOTP: moment(),
				numberOTP: time
			}
			timingOTP();
			if (otpInput.current) otpInput.current.focus();
		} else if (otp_Type !== 3) getOtpNumber();

		return () => {
			if (timeoutTimingOTP.current) clearTimeout(timeoutTimingOTP.current)
		}
	}, []);

	const sendOTP = (value) => {
		setError('');
		if (sendOTPFlag.current) return;
		if (!timeOTP) return;
		const check = value || code;
		if (!check) return;
		// Keyboard.dismiss();
		sendOTPFlag.current = true;
		const password = dataCryption.encryptString(check.trim())
		const InputParams = ['check_otp', password + '']
		sendRequest(ServiceInfo.SEND_OTP_FUNCTION, InputParams, sendOTPProc, true, sendOTPTimeout)
	}

	const sendOTPTimeout = () => {
		sendOTPFlag.current = false;
	}

	const sendOTPProc = (reqInfoMap, message) => {
		sendOTPFlag.current = false;
		if (Number(message['Result']) === 0) {
			const errmsg = message['Message']
			setError(errmsg);
			if (message.Code === '010013') {
				getOtpNumber(true);
			}
		} else {
			glb_sv.objShareGlb['sessionInfo']['Otp'] = reqInfoMap.inputParam[1]
			// navigation.pop();
			// if (isLogin) {
			// 	if (isHideBack) navigation.replace(Screens.HOME)
			// 	else navigation.pop()
			// }
			setIsOpenOTP(false)
			if (functCallback) {
				functCallback();
			}

		}
	}

	const getOtpNumber = (type) => {
		if (!type) setError('');
		// if (otp_Type === 4) {
		//     setTimeOTP(999);
		//     timeOTPRef.current = 999;
		//     setMsgOTP(t('you_enter_otp_static'))
		//     timeAuthenOtp.current = {
		//         timeGetOTP: moment(),
		//         numberOTP: 999
		//     }
		//     timingOTP();
		//     return;
		// }
		// Keyboard.dismiss();
		if (getOtpNumberFlag.current) return;
		getOtpNumberFlag.current = true;
		const InputParams = ['manual_otp']
		sendRequest(ServiceInfo.GET_OTP_NUMBER, InputParams, getOtpNumberProc, true, getOtpNumberTimeout)
	}

	const getOtpNumberTimeout = () => {
		getOtpNumberFlag.current = false;
	}

	const getOtpNumberProc = (reqInfoMap, message) => {
		getOtpNumberFlag.current = false;
		if (Number(message['Result']) === 2) {
			return;
		} else {
			let dataInfo;
			if (!message['Data']) return;
			try {
				dataInfo = JSON.parse(message['Data'])[0]
				const expTimeOtp = Number(dataInfo.c2) || 60
				let reqOtpMessage = 'OTP';
				if (Number(dataInfo['c1']) === 2) { //-- dạng thẻ ma trận
					reqOtpMessage = 'OTP ' + dataInfo['c3']
				}
				setMsgOTP(message['Message'])
				setTimeOTP(expTimeOtp)
				timeOTPRef.current = expTimeOtp
				setTypeOTP(reqOtpMessage)
				timeAuthenOtp.current = {
					timeGetOTP: moment(),
					numberOTP: expTimeOtp
				}
				timingOTP()
				if (otpInput.current) otpInput.current.focus();
			} catch (err) {
				console.log('getOtpNumberProc Lỗi parse json: ' + err)
			}
		}
	}

	const timingOTP = () => {
		if (timeOTPRef.current > 0) {
			if (timeoutTimingOTP.current) clearTimeout(timeoutTimingOTP.current)
			timeoutTimingOTP.current = setTimeout(() => {
				const remainTimeOTP = Math.max(0, timeAuthenOtp.current.numberOTP - Math.round((moment() - timeAuthenOtp.current.timeGetOTP) / 1000))
				timeOTPRef.current = remainTimeOTP
				setTimeOTP(remainTimeOTP)
				timingOTP();
			}, 1000);
		}
	}

	const handleDismiss = () => {
		// navigation.dispatch(state => {
		//     const routes = state.filter(e => e.name !== Screens.OTP_MODAL ).routes.filter(r => r.name !== Screens.OTP_MODAL || r.name !== Screens.SIGN_IN);
		//     return CommonActions.reset({
		//         ...state,
		//         routes,
		//         index: routes.length - 1,
		//     });
		// });
		// navigation.pop();
		setIsOpenOTP(false)
	}

	return (
		<Modal visible={isOpenOTP} closable={false} footer={null} onCancel={() => setIsOpenOTP(false)} style={{ padding: 0 }} bodyStyle={{ padding: '0' }}>
			<div
				style={{
					backgroundColor: styles.PRIMARY__BG__COLOR,
					paddingTop: '2rem',
					paddingBottom: '1rem',
					paddingRight: dimensions.moderate(28),
					paddingLeft: dimensions.moderate(28),
					borderRadius: 12,
					justifyContent: 'center',
					alignItems: 'center',
					// padding: 0
				}}>
				{/* <IconSvg.ClockIcon color={styles.REF__COLOR} /> */}

				<div
					style={{
						marginTop: dimensions.vertical(32),
						marginBottom: dimensions.vertical(16),
						paddingLeft: dimensions.moderate(16),
						paddingRight: dimensions.moderate(16),
						fontSize: fontSizes.xmedium,
						color: styles.PRIMARY__CONTENT__COLOR,
						fontWeight: fontWeights.semiBold,
						textAlign: 'center',
					}}>
					{t('enter_authen_otp')}
				</div>

				<div
					style={{
						fontSize: fontSizes.small,
						color: styles.PRIMARY__CONTENT__COLOR,
						marginBottom: dimensions.moderate(32),
						opacity: 0.64,
						textAlign: 'center',
					}}>
					{/* {otp_Type === 3 && timeOTP === 0 ? t('note_get_otp_authen_login') :
                            (msgOTP || t('otp_sent_to_email_sms'))} */}
					{msgOTP || t('otp_sent_to_email_sms')}
				</div>

				<div className='flex' style={{ flexDirection: 'row', alignItems: 'center' }}>
					<div
						style={{
							fontSize: fontSizes.normal,
							fontWeight: fontWeights.medium,
							color: styles.PRIMARY__CONTENT__COLOR,
							alignItems: 'baseline',
						}}>
						{typeOTP}
					</div>
					<div
						style={{
							backgroundColor: styles.INPUT__BG__LOGIN,
							borderRadius: 20,
							width: '100%',
							height: 40,
							justifyContent: 'center',
							flexDirection: 'row',
							marginLeft: 8,
						}}>
						<InputItem
							value={code}
							onChange={(value) => {
								setError('')
								setCode(value)
								if (value.length === 6) {
									sendOTP(value)
								}
							}}
							maxLength={6}
							autoFocus
							ref={otpInput}
							editable={!!timeOTP}
							textContentType='oneTimeCode'
							keyboardType={otp_Type === 4 ? 'default' : 'number-pad'}
							style={{
								letterSpacing: 16,
								fontSize: fontSizes.medium,
								fontWeight: fontWeights.semiBold,
								color: styles.PRIMARY__CONTENT__COLOR,
								backgroundColor: 'var(--BOTTOM__MODAL__COLOR)',
								textAlign: 'center',
								padding: '0.5rem 0.5rem',
								borderRadius: 50,
								flex: 1,
							}}
						/>
						{/* <TextInput
							value={code}
							onChangeText={value => {
								setError('')
								setCode(value)
								if (value.length === 6) {
									sendOTP(value)
								}
							}}
							maxLength={6}
							autoFocus
							ref={otpInput}
							editable={!!timeOTP}
							textContentType='oneTimeCode'
							keyboardType={otp_Type === 4 ? 'default' : 'number-pad'}
							style={{
								letterSpacing: 16,
								fontSize: fontSizes.medium,
								fontWeight: fontWeights.semiBold,
								color: styles.PRIMARY__CONTENT__COLOR,
								textAlign: 'center',
								flex: 1
							}} /> */}
					</div>
				</div>

				<div
					style={{
						fontSize: fontSizes.verySmall,
						color: styles.SECOND__CONTENT__COLOR,
						marginBottom: dimensions.vertical(16),
						opacity: timeOTP ? 1 : 0,
						marginTop: dimensions.vertical(16),
						textAlign: 'center',
					}}>
					{t('otp_timeout_after')}{' '}
					<Text style={{ color: styles.PRIMARY__CONTENT__COLOR }}>
						{timeOTP} {t('second')}
					</Text>
				</div>

				<div
					style={{
						fontSize: fontSizes.verySmall,
						color: styles.ERROR__COLOR,
						marginBottom: dimensions.vertical(5),
						opacity: errors ? 1 : 0,
						marginTop: dimensions.vertical(5),
					}}>
					{errors}
				</div>

				<div>
					<Button
						style={{
							width: dimensions.moderate(172),
							backgroundColor: code.length > 3 || !timeOTP ? styles.PRIMARY : styles.PRIMARY + '4d',
							justifyContent: 'center',
							borderRadius: 8,
							padding: '0.5rem',
							height: 'unset',
							width: '100%',
						}}
						onClick={() => (timeOTP ? sendOTP(code) : getOtpNumber())}>
						<Text
							style={{
								color: code.length > 3 || !timeOTP ? '#FFF' : styles.SECOND__CONTENT__COLOR,
								fontSize: fontSizes.normal,
								textTransform: 'uppercase',
							}}>
							{timeOTP ? t('title_authen_otp') : t('get_otp_number')}
						</Text>
					</Button>
					<Button
						style={{ width: '100%', justifyContent: 'center', height: 'unset', backgroundColor: 'transparent' }}
						onClick={() => handleDismiss()}>
						<Text style={{ color: styles.SECOND__CONTENT__COLOR, fontSize: fontSizes.normal }}>{t('common_Cancel')}</Text>
					</Button>
				</div>
			</div>
		</Modal>
	)
}

export default memo(OtpModal)