import React, { memo } from 'react';
import { fontSizes, fontWeights, dimensions } from '../../styles';
import { FormatNumber } from '../../utils';

function ViewIndex({ color, title, value, ratioValue, active, onChange, type, valueChang, hiddenChart }) {

    const getColor = (value, ref) => {
        // REF__COLOR: '#f1c40f',
        // UP__COLOR: '#2ECC71',
        // DOWN__COLOR: '#EB5C55',
        if (value > ref) return 'var(--UP__COLOR)'
        if (value < ref) return 'var(--DOWN__COLOR)'
        return 'var(--REF__COLOR)'
    }
    return (
        <div
            className='flex flex-direction-column'
            onClick={() => onChange(type)}
            style={{
                ...UI.view,
                backgroundColor: 'var(--BUTTON__SECONDARY)',
                borderColor: active === type ? 'var(--PRIMARY)' : '',
                borderWidth: active === type ? 1 : 0,
                borderStyle: 'solid'
            }}
        >
            <div className='flex flex-direction-row'>
                <div style={{ ...UI.circle, backgroundColor: color }} />
                <span style={{ ...UI.title, color: 'var(--PRIMARY__CONTENT__COLOR)', paddingLeft: 5}}>
                    {'  '}{title}
                </span>
            </div>
            <span style={{ ...UI.value, color: getColor(ratioValue, 0) }}>{FormatNumber(value, 2)}</span>
            <span style={{ ...UI.ratioValue, color: getColor(ratioValue, 0) }}>
                {valueChang > 0 ? '+' : ''}{FormatNumber(valueChang, 2)} ({ratioValue > 0 ? '+' : ''}{FormatNumber(ratioValue, 2)}%)
            </span>
            {active === type && <div style={{ position: 'absolute', bottom: 0, right: 2 }}>
                <svg
                    width={16}
                    height={16}
                    viewBox="0 0 16 16"
                    fill="none"
                    xmlns="http://www.w3.org/2000/svg"
                >
                    <g opacity={0.64} clipPath="url(#prefix__clip0)" fill="#196EEE">
                        <path d="M13.441 14.737H8.858v1.247h4.583v-1.247z" />
                        <path d="M14.721 13.457v.624a.657.657 0 01-.656.656h-.624v1.247h.624c1.05 0 1.904-.854 1.904-1.903v-.624H14.72zM8.858.016L2.5 0v1.247l6.358.016V.016zM13.441.016H8.858v1.247h4.583V.016z" />
                        <path d="M1.905.016C.855.016.001.87.001 1.919v.624H1.25v-.624c0-.362.294-.656.656-.656h.624V.016h-.624zM14.065.016h-.624v1.247h.624c.362 0 .656.294.656.656v.624h1.248v-.624c0-1.05-.854-1.903-1.904-1.903zM1.249 2.543H0L0 7.283h1.247l.002-4.74zM16 7.283h-1.248l-.03 6.174h1.247L16 7.283zM15.969 2.543H14.72l.031 4.74H16l-.031-4.74zM8.266 3.23v1.247h2.437L6.589 8.774a1.86 1.86 0 00-.974-.274H1.873C.84 8.5 0 9.34 0 10.37v3.743c0 1.032.84 1.871 1.872 1.871h3.742c1.032 0 1.871-.839 1.871-1.87V10.37c0-.217-.037-.425-.106-.62l4.16-4.346v2.346h1.248V3.23H8.266zM6.239 14.112c0 .344-.28.624-.624.624H1.873a.624.624 0 01-.624-.624v-3.742c0-.344.28-.624.624-.624h3.742c.344 0 .624.28.624.624v3.742z" />
                    </g>
                    <defs>
                        <clipPath id="prefix__clip0">
                            <path fill="#fff" d="M0 0h16v16H0z" />
                        </clipPath>
                    </defs>
                </svg>
            </div>}
        </div>
    )
}

export default memo(ViewIndex)

const widthDetech = Math.min(window.innerWidth, 550)

const UI = {
    view: {
        // width: (dimensions.WIDTH - dimensions.moderate(12, 0.3) * 2) / 3 - dimensions.moderate(4, 0.3) * 2,
        // height: dimensions.verticalIndent*5,
        minWidth: (widthDetech - 45) / 2.5,
        paddingRight: 10,
        paddingLeft: 10,
        margin: `0 ${dimensions.moderate(4, 0.3)}px`,
        paddingTop: dimensions.vertical(5, 0.3),
        paddingBottom: dimensions.vertical(9, 0.3),
        borderRadius: 4,
        position: 'relative'
    },
    circle: {
        width: 8,
        height: 8,
        borderRadius: 4,
        marginTop: dimensions.vertical(7, 0.3)
    },
    title: {
        fontSize: fontSizes.verySmall,
        fontWeight: fontWeights.normal,
        lineHeight: `${dimensions.moderate(22, 0.3)}px`
    },
    value: {
        fontSize: fontSizes.medium,
        fontWeight: fontWeights.medium,
        lineHeight: `${dimensions.moderate(22, 0.3)}px`
    },
    ratioValue: {
        fontSize: fontSizes.smallest,
        fontWeight: fontWeights.medium
    }
}

