// Khai báo ServiceInfo (nếu có)
const ServiceInfo = {
    GET_CASH_CAN_LENDING: {
        reqFunct: reqFunct.GET_CASH_CAN_LENDING, // Lấy số tiền có thể ứng
        WorkerName: 'FOSqPIA',
        ServiceName: 'FOSqPIA_1302_3',
        ClientSentTime: '0',
        Operation: 'Q',
        // InVal = [' ', this.actn_curr, this.sub_curr];
    },
    GET_LENDING_FEE: {
        reqFunct: reqFunct.GET_LENDING_FEE, // Lấy phí ứng tiền
        WorkerName: 'FOSqPIA',
        ServiceName: 'FOSqPIA_1302_8',
        ClientSentTime: '0',
        Operation: 'Q',
        // InVal = [' ', this.actn_curr, this.sub_curr, pia_amount + ''];
    },
    REQ_NEW_LENDING: {
        reqFunct: reqFunct.REQ_NEW_LENDING, // Yêu cầu ứng tiền
        WorkerName: 'FOSxPIA',
        ServiceName: 'FOSxPIA_1302_1',
        ClientSentTime: '0',
        Operation: 'I',
        // InVal: [' ', ' ', ' ', this.actn_curr, this.sub_curr, ' ', ' ', ' ', pia_amount + ''];
    },
    GET_HIS_ADVANCE_LENDING: {
        reqFunct: reqFunct.GET_HIS_ADVANCE_LENDING, // Yêu cầu ứng tiền
        WorkerName: 'FOSqPIA',
        ServiceName: 'FOSqPIA_1302_1',
        // WorkerName: 'ALTqLendingAdvance',
        // ServiceName: 'ALTqLendingAdvance_1302_1',
        ClientSentTime: '0',
        Operation: 'Q',
        // InVal: [' ', ' ', ' ', this.actn_curr, this.sub_curr, ' ', ' ', ' ', pia_amount + ''];
    },
}
const initErrCtrl = [
    {
        error: false,
        message: 'input_pia_value',
    },
]
// Khai báo component
const AdvanceLendingLayout = ({}) => {
    const { styles } = useContext(StoreContext)
    const { userInfo } = useContext(StoreTrading)
    const history = useHistory()
    const { t } = useTranslation()
    // All orther state
    const [isOpenModal, setIsOpenModal] = useState(false)
    const [refreshing, setRefreshing] = React.useState(false)
    const [errCtrl, setErrCtrl] = useState(initErrCtrl)

    // Start define all bussiness state
    const [canLendingAmount, setCanLendingAmount] = useState({}) // Object
    const [amoutFee, setAmoutFee] = useState({})
    const [dataLastLending, setDataLastLending] = useState([])
    const [lendingAmount, setLendingAmount] = useState('')
    const [checkDraler, setCheckDraler] = useState(false)
    const [isOpenFilterGroup, setIsOpenFilterGroup] = useState(false)
    const [filterOption, setFilterOption] = useState({
        fromDt: moment(glb_sv.objShareGlb['workDate']).subtract(6, 'months').toDate(),
        toDt: moment(glb_sv.objShareGlb['workDate']).toDate(),
        contractType: '0',
    })

    // End define form bussiness state
    // -------------------------------------------   Dùng useEffect get các dữ liệu cần thiết còn thiếu
    useEffect(() => {
        getCashCanLending()

        const commonEvent = glb_sv.commonEvent.subscribe((msg) => {
            if (msg.type === eventList.LOGIN_SUCCESS) {
                // reCall service FOSq
                getCashCanLending()
                getHisAdvanceLending()
                if (canLendingAmount?.c8) {
                    getLendingFee()
                } else {
                    setAmoutFee({})
                }
            }
            if (msg.type === eventList.NOTIFY_SERVER) {
                const message = msg.data
                if (message.MsgTp?.includes('PIA_')) {
                    getCashCanLending()
                    getHisAdvanceLending()
                    if (canLendingAmount?.c8) {
                        getLendingFee()
                    } else {
                        setAmoutFee({})
                    }
                }
            }
        })

        return () => {
            commonEvent.unsubscribe()
        }
    }, [userInfo.sub_curr])

    useEffect(() => {
        getHisAdvanceLending()
    }, [filterOption])

    useEffect(() => {
        if (canLendingAmount?.c8) {
            getLendingFee()
        } else {
            setAmoutFee({})
        }
    }, [canLendingAmount])
    // useUpdateEffect(() => _validateValue(0), [lendingAmount])

    // -------------------------------------------   Viết các hàm để set lại bussiness form state. Nên viết đúng một chuẩn duy nhất để dễ control

    const switchStep = {
        submit: () => confirmLending(),
        onFinish: () => setIsOpenModal(true),
    }

    const hideModal = () => {
        setIsOpenModal(false)
    }
    // -------------------------------------------   Khai báo các hàm gửi (request) lên server
    const getCashCanLending = () => {
        const inputParams = [' ', userInfo.actn_curr, userInfo.sub_curr]
        sendRequest(ServiceInfo.GET_CASH_CAN_LENDING, inputParams, handleGetCashCanLending)
    }

    const getLendingFee = () => {
        const inputParams = [' ', userInfo.actn_curr, userInfo.sub_curr, String(canLendingAmount?.c8)]
        sendRequest(ServiceInfo.GET_LENDING_FEE, inputParams, handleGetLendingFee)
    }

    const getHisAdvanceLending = () => {
        const inputParams = [
            userInfo.actn_curr,
            userInfo.sub_curr,
            String(filterOption.contractType),
            moment(filterOption.fromDt).format('YYYYMMDD'),
            moment(filterOption.toDt).format('YYYYMMDD'),
            // '20210419',
            // '20210519',
            '',
        ]
        sendRequest(ServiceInfo.GET_HIS_ADVANCE_LENDING, inputParams, handlegetHisAdvanceLending)
    }
    const contractTypeList = [
        { index: 0, value: '0', label: t('all_contract_status') },
        { index: 1, value: '1', label: t('all_contract_not_expire') },
        { index: 2, value: '2', label: t('all_contract_need_repay') },
    ]
    const fullAdvancePayment = () => {
        if (canLendingAmount) {
            setLendingAmount(canLendingAmount?.c10)
            setIsOpenModal(true)
        }
    }

    const confirmLending = () => {
        hideModal()
        const inputParams = [' ', ' ', ' ', userInfo.actn_curr, userInfo.sub_curr, ' ', ' ', ' ', String(lendingAmount)]
        // svInputPrm.InVal = [this.actn_curr, this.sub_curr, '2', rcvBankacnt, rcvBnk, trnBnkacnt, trnBnk, deposit_amt + '', noteInfo];
        sendRequest(ServiceInfo.REQ_NEW_LENDING, inputParams, handleConfirmLending)
    }

    // -------------------------------------------   Khai báo các hàm gửi (request) và nhận (handle respone) dữ liệu từ server
    const handleGetCashCanLending = (reqInfoMap, message) => {
        // -- process after get result --
        // getLendingFee
        if (Number(message['Result']) === 0) {
            return
        } else {
            let jsondata
            try {
                jsondata = JSON.parse(message['Data'])
            } catch (err) {
                // glb_sv.logMessage(err);
                jsondata = []
            }
            if (Number(message['Packet']) <= 0) {
                console.log('Log target: jsondata', jsondata)
                setCanLendingAmount(jsondata[0])
            }
        }
    }
    // ----------------------------------------------------

    const handleGetLendingFee = (reqInfoMap, message) => {
        // -- process after get result --
        if (Number(message['Result']) === 0) {
            return
        } else {
            let jsondata
            try {
                jsondata = JSON.parse(message['Data'])
            } catch (err) {
                jsondata = []
            }
            if (Number(message['Packet']) <= 0) {
                console.log('Log target: setAmoutFee', jsondata)
                setAmoutFee(jsondata[0])
            }
        }
    }
    // ----------------------------------------------------

    const handlegetHisAdvanceLending = (reqInfoMap, message) => {
        console.log('Log target: handlegetHisAdvanceLending', message)
        // -- process after get result --
        if (Number(message['Result']) === 0) {
            setLendingAmount([])
            return
        } else {
            let jsondata
            try {
                jsondata = JSON.parse(message['Data'])
            } catch (err) {
                jsondata = []
            }
            if (Number(message['Packet']) <= 0) {
                setDataLastLending(jsondata)
            }
        }
    }

    // const handleConfirmLending = (reqInfoMap, message) => {
    //     setIsOpenModal(false)
    //     if (Number(message['Result']) === 0) {
    //         if (message.Code == 'XXXXX5' || message.Code === '080063' || message.Code === '010012') {
    //             notification.warn({
    //                 message: t('common_notify'),
    //                 description: message['Message'],
    //                 duration: 4,
    //             })
    //         } else if (message['Code'] !== '080128') {
    //             notification.warn({
    //                 message: t('common_notify'),
    //                 description: message['Message'],
    //                 duration: 4,
    //             })
    //         }
    //         return
    //     } else {
    //         let jsondata = []
    //         try {
    //             jsondata = message['Data'] ? JSON.parse(message['Data']) : []
    //         } catch (err) {}
    //         notification.success({
    //             message: t('common_notify'),
    //             description: message['Message'],
    //             duration: 4,
    //         })
    //         setLendingAmount('')
    //         getHisAdvanceLending()
    //         getCashCanLending()
    //     }
    // }
    const handleConfirmLending = (reqInfoMap, message) => {
        setIsOpenModal(false)
        // _resetInput()
        // -- process after get result --
        if (Number(message['Result']) === 0) {
            if (message.Code == 'XXXXX5' || message.Code === '080063' || message.Code === '010012') {
                notification.warn({
                    message: t('common_notify'),
                    description: t('request_hanlde_not_success_try_again'),
                    duration: 4,
                })
            } else if (glb_sv.otpCases.includes(message.Code)) {
                glb_sv.objShareGlb['sessionInfo']['Otp'] = ''

                let data
                try {
                    data = JSON.parse(message.Data)[0]
                } catch (error) {
                    return
                }

                const otp_Type = Number(data.c1)
                const expTimeOtp = Number(data.c2)
                let reqOtpMessage = 'OTP'
                if (Number(data['c1']) === 2) {
                    //-- dạng thẻ ma trận
                    reqOtpMessage = 'OTP ' + data['c3']
                }
                glb_sv.commonEvent.next({
                    type: eventList.OTP_MODAL,
                    time: expTimeOtp,
                    message: reqOtpMessage,
                    messageOTP: message.Message,
                    hasOTP: false,
                    otp_Type,
                })
            } else {
                notification.warn({
                    message: t('common_notify'),
                    description: message['Message'],
                    duration: 4,
                })
            }
        } else {
            notification.success({
                message: t('common_notify'),
                description: message['Message'],
                duration: 4,
            })
            setLendingAmount('')
            getHisAdvanceLending()
            getCashCanLending()
        }
    }
    // --------------------------------------------
    const getColor = (status) => {
        if (status === 'N') return styles.REF__COLOR
        if (status === 'R' || status === 'D') return styles.DOWN__COLOR
        if (status === 'Y') return styles.UP__COLOR
    }
    const onSelectContractType = (item) => {
        setFilterOption({ ...filterOption, contractType: item.value })
        setCheckDraler(false)
    }

    const ViewLastLending = ({ item }) => {
        return (
            <>
                <RowTableData
                    key={item.label}
                    type='table'
                    onClick={() => history.push('/stock/detail-advance-lending', { data: item })}
                    // onPress={() => navigation.navigate(Screens.DETAIL_ADVANCE_LENDING, { data: item, onRefresh: onRefresh })}
                >
                    <ColTableData text={moment(item.c3, 'DDMMYYYY').format('DD/MM/YYYY')} />
                    <ColTableData text={FormatNumber(item?.c7)} textAlign='center' />
                    <ColTableData text={t(item?.c17)} colorText={getColor(item.c12)} textAlign='right' />
                </RowTableData>
            </>
        )
    }
    const rightComponent = (
        <svg onClick={() => setIsOpenFilterGroup(!isOpenFilterGroup)} width={36} height={36} viewBox='0 0 36 36' fill='none' xmlns='http://www.w3.org/2000/svg'>
            <path fillRule='evenodd' clipRule='evenodd' d='M0 18C0 8.059 8.059 0 18 0s18 8.059 18 18-8.059 18-18 18S0 27.941 0 18z' fill='#F8F8F8' />
            <path
                d='M26.438 14.514H9.563a.938.938 0 010-1.875h16.874a.937.937 0 110 1.875zm-3.125 4.375H12.688a.938.938 0 010-1.875h10.624a.937.937 0 110 1.875zm-3.75 4.375h-3.125a.938.938 0 010-1.875h3.125a.937.937 0 110 1.875z'
                fill='#24253D'
            />
        </svg>
    )
    return (
        <div>
            <HeaderComponent title={t('request_cash_in_advance')} titleAlign='start' transparent colorTitle={styles.PRIMARY__CONTENT__COLOR} />
            <div className='mt-2 mb-3 pl-2' style={{ overflow: 'hidden' }}>
                <Account />
            </div>
            <div style={{ backgroundColor: styles.PRIMARY__BG__COLOR }}>
                <div>
                    <RowData
                        textLeft={t('cash_lending_available')}
                        textRight={FormatNumber(canLendingAmount?.c8)}
                        rightColor={styles.PRIMARY__CONTENT__COLOR}
                        type='bold'
                    />
                    <RowData
                        textLeft={t('total_pia_amount_wait_approve')}
                        textRight={FormatNumber(canLendingAmount?.c11)}
                        rightColor={styles.REF__COLOR}
                        type='bold'
                    />
                    <div onClick={fullAdvancePayment}>
                        <RowData
                            textLeft={t('total_pia_amount_wait_remain')}
                            textRight={FormatNumber(canLendingAmount?.c10)}
                            rightColor={styles.PRIMARY}
                            type='bold'
                            pointerEvents='none'
                        />
                    </div>
                    <RowData
                        textLeft={t('advance_debt_fee')}
                        textRight={FormatNumber(amoutFee?.c0)}
                        rightColor={styles.PRIMARY__CONTENT__COLOR}
                        type='bold'
                        last
                    />
                    {/* <div className='mb-3'>
                        <CustomFloatInput
                            // errCtrl={t(errCtrl[0])}
                            // animationDuration={100}
                            label={t('cash_of_PIA')}
                            value={FormatNumber(lendingAmount) === '0' ? '' : FormatNumber(lendingAmount)}
                            onChange={(e) => setLendingAmount(glb_sv.filterNumber(e.target.value))}
                            placeholder={t('cash_of_PIA')}
                        />
                    </div> */}
                    <div className='pl-2 pr-2 mt-2 mb-3 border-none'>
                        <Input
                            style={{ padding: '12px 16px', background: 'var(--INPUT__BG)', borderRadius: '8px', border: 0 }}
                            size='middle'
                            className='input__component'
                            placeholder={t('cash_of_PIA')}
                            onChange={(e) => setLendingAmount(glb_sv.filterNumber(e.target.value))}
                            value={FormatNumber(lendingAmount) === '0' ? '' : FormatNumber(lendingAmount)}
                        />
                    </div>
                    <div className='mb-2'>
                        <ButtonCustom
                            text={t('common_button_confirm')}
                            type='confirm'
                            onClick={() => {
                                if (lendingAmount) {
                                    switchStep.onFinish()
                                }
                            }}
                        />
                    </div>
                    <div className='pt-1'>
                        {' '}
                        <RowTitleGroup hasDivider />
                    </div>
                    <RowTitleGroup text={t('pia_history')} rightComponent={rightComponent} />
                    <div style={{ display: isOpenFilterGroup ? 'none' : '' }}>
                        <div className='pl-1 pr-1'>
                            <FilterGroup
                                isOpenFilterGroup={isOpenFilterGroup}
                                setIsOpenFilterGroup={setIsOpenFilterGroup}
                                filterOption={filterOption}
                                setFilterOption={setFilterOption}
                            />
                        </div>
                        <div className='pr-2 pl-2 mt-2 border-none'>
                            <Input
                                suffix={'>'}
                                style={{ padding: '16px 16px', background: 'var(--INPUT__BG)', borderRadius: '8px' }}
                                size='middle'
                                className='input__component'
                                placeholder={t('choose_trans_acc')}
                                onClick={() => {
                                    setCheckDraler(true)
                                }}
                                value={t(contractTypeList.filter((o) => o.value === filterOption.contractType)?.pop()?.label)}
                                // value={objSubReceive?.c2}
                            />
                        </div>
                    </div>
                    <HeaderList typeHeader='ADVANCE_LENDING' />
                    <div className='pl-1'>
                        {' '}
                        {dataLastLending.length ? (
                            dataLastLending.map((item, i) => <ViewLastLending key={i} item={item} />)
                        ) : (
                            <div style={{ textAlign: 'center', alignSelf: 'center', height: '200px' }}>
                                <img src={IC_FOLDER_NULL} />
                            </div>
                        )}
                    </div>
                </div>
            </div>
            {/* **************************************** Modal submit ******************************************/}
            <Modal visible={isOpenModal} style={{ width: '85vw' }} transparent maskClosable={false}>
                <ModalContent
                    title={t('common_confirm')}
                    type='confirm'
                    iconComponent={
                        <svg width={33} height={32} viewBox='0 0 33 32' fill='none' xmlns='http://www.w3.org/2000/svg'>
                            <g clipPath='url(#prefix__clip0)' fill='#2ECC71'>
                                <path d='M28.604 19.64l-3.547 4.638a3.75 3.75 0 01-2.979 1.472h-6.584a.938.938 0 010-1.875h5.707c1.226 0 2.254-1.004 2.23-2.23a2.188 2.188 0 00-2.187-2.145h-6.562a5.43 5.43 0 00-6.92-.504l-.705.504v10.625H23.36a5.625 5.625 0 004.731-2.583l3.659-5.692c.2-.31.306-.671.306-1.04 0-1.84-2.336-2.631-3.454-1.17zM4.244 17.625H.994a.937.937 0 00-.937.938v12.5c0 .517.42.937.937.937h3.25c.518 0 .938-.42.938-.938v-12.5a.937.937 0 00-.938-.937zM17.95 5.181a.93.93 0 00-.332.56c-.05.265.014.511.162.627.051.04.109.08.17.12V5.181z' />
                                <path d='M18.62 15.625c4.307 0 7.812-3.505 7.812-7.813C26.432 3.505 22.927 0 18.619 0s-7.812 3.505-7.812 7.813c0 4.307 3.504 7.812 7.812 7.812zm-2.366-5.258a.625.625 0 01.865-.181c.338.22.55.316.832.356v-2.64a4.612 4.612 0 01-.942-.55c-.519-.407-.756-1.113-.618-1.842.15-.795.703-1.428 1.444-1.65a3.54 3.54 0 01.116-.034v-.389a.625.625 0 011.25 0v.32c.607.106 1.036.405 1.244.643a.625.625 0 01-.94.825.86.86 0 00-.304-.18v2.02l.33.119c1.1.388 1.71 1.405 1.521 2.53-.147.875-.813 1.762-1.851 2.026v.447a.625.625 0 01-1.25 0V11.8c-.505-.044-.923-.18-1.516-.568a.625.625 0 01-.18-.865z' />
                                <path d='M19.82 9.508c.04-.244.058-.826-.619-1.111v2.008c.352-.202.562-.558.619-.897z' />
                            </g>
                            <defs>
                                <clippath id='prefix__clip0'>
                                    <path fill='#fff' transform='translate(.057)' d='M0 0h32v32H0z' />
                                </clippath>
                            </defs>
                        </svg>
                    }>
                    <RowDataModal textLeft={t('acnt_no')} dataSub={[userInfo.actn_curr, userInfo.sub_curr]} />
                    <RowDataModal textLeft={t('amount_drawal')} textRight={FormatNumber(lendingAmount)} />
                    <RowDataModal
                        textLeft={t('total_fee')}
                        textRight={FormatNumber(amoutFee?.c0 * (lendingAmount / (canLendingAmount?.c8 || lendingAmount))) || ''}
                    />
                    <div className='mt-3'>
                        <ButtonCustom text={t('common_button_confirm')} onClick={switchStep.submit} type='confirm' />
                        <ButtonCustom text={t('common_Cancel')} onClick={hideModal} type='back' last />
                    </div>
                </ModalContent>
            </Modal>
            <div>
                <Drawer
                    onClose={() => {
                        setCheckDraler(false)
                    }}
                    title={<div className='text-center text-primary'>{t('choose_account')}</div>}
                    placement={'bottom'}
                    closable={false}
                    visible={checkDraler}>
                    {contractTypeList.map((item, index) => (
                        <ModalBottomRowSelect
                            onClick={() => onSelectContractType(item)}
                            text={t(item.label)}
                            key={index}
                            checked={filterOption.contractType === item.value}
                        />
                    ))}
                </Drawer>
            </div>
            {/* **************************************** Modal Filter ******************************************/}
        </div>
    )
}

export default AdvanceLendingLayout

const FilterGroup = ({ filterOption, setFilterOption }) => {
    const { styles, theme } = useContext(StoreContext)
    const { t } = useTranslation()

    const [isFocus, setIsFocus] = useState({
        from: false,
        to: false,
        stock: false,
        order_tp: false,
        side: false,
    })

    const onFinish = () => {
        setIsFocus({
            from: false,
            to: false,
            stock: false,
            order_tp: false,
            side: false,
        })
    }
    const onChangeStartday = (_, dateOutPut) => {
        const formatResult = moment(dateOutPut, 'DD/MM/YYYY').format('YYYYMMDD')
        setFilterOption({ ...filterOption, fromDt: formatResult })
    }
    const onChangeEndDay = (_, dateOutPut) => {
        const formatResult = moment(dateOutPut, 'DD/MM/YYYY').format('YYYYMMDD')
        setFilterOption({ ...filterOption, toDt: formatResult })
    }

    return (
        <>
            <div style={UI.GroupFilter}>
                <div className='flex flex-1'>
                    <InputDatePicker labelStyle={{ fontSize: 12 }} value={filterOption.fromDt} onChange={onChangeStartday} />
                    <InputDatePicker labelStyle={{ fontSize: 12 }} value={filterOption.toDt} onChange={onChangeEndDay} />
                </div>
            </div>
        </>
    )
}
