import React, { useState, useContext, useEffect, useCallback } from 'react'
import { useTranslation } from 'react-i18next'
import { StoreContext } from '../../../store'
import { fontSizes, dimensions, fontWeights } from '../../../styles'
import Account from '../../../components/account'
import { Screens, sendRequest, reqFunct, FormatNumber, FormatNumberAC, glb_sv } from '../../../utils'
import { useHistory } from 'react-router-dom'
import { Row, Col, List, Steps, Button, message as Message } from 'antd'
import Text from 'antd/lib/typography/Text'
import { routes } from 'navigation/routes'
import HeaderList from '../../../components/trading-component/header-list'
import { RowTableData, RowData, ButtonCustom, RowTitleGroup } from '../../../components/trading-component'
import { StoreTrading } from "store-trading";
// Khai báo ServiceInfo (nếu có)
const ServiceInfo = {
    GET_CURRENT_CASH_BY_SUB: {
        reqFunct: reqFunct.GET_CURRENT_CASH_BY_SUB, //Lấy "Số dư hiện tại"
        WorkerName: 'ALTqCash',
        ServiceName: 'ALTqCash_0201_2',
        ClientSentTime: '0',
        Operation: 'Q',
        // TestInput: ['1', userInfo.actn_curr, '00']
    },
    GET_HIS_PAY_ON_ACCOUT_BY_SUB: {
        reqFunct: reqFunct.GET_HIS_PAY_ON_ACCOUT_BY_SUB, // Lấy DS giao dịch gần nhất theo theo sub
        WorkerName: 'ALTqCash',
        ServiceName: 'ALTqCash_0201_1',
        ClientSentTime: '0',
        Operation: 'Q',
        // TestInput: ["2", "888c000354", "00"]
    },
}

// Khai báo component
const StepOne = ({ setBankTransfer, setBankTransferAct, setActReceive, setBankReceive, setAmoutTransfer, currentSubCash, setCurrentSubCash, switchStep }) => {
    const { styles} = useContext(StoreContext)
    const {userInfo} = useContext(StoreTrading)
    const { t } = useTranslation()
    let history = useHistory()
    // -------------------------------------------   Khai báo các state nội bộ component

    const [dataLastTransfer, setDataLastTransfer] = useState([])
    const [dataLastTranferAllSub, setDataLastTranferAllSub] = useState([])
    // -------------------------------------------   Dùng useEffect get các dữ liệu cần thiết còn thiếu
    useEffect(() => {
        console.log('Prepare data')
        getCurrentCashBySub() // Lấy số dư hiện tại
        getHisPayOnAccountBySub() // Lấy lịch sử chuyển tiền
    }, [userInfo.sub_curr])

    // -------------------------------------------   Viết các hàm để set lại bussiness form state. Nên viết đúng một chuẩn duy nhất để dễ control
    // const selectPastTransfer = (item) => {
    //     console.log('selectPastTransfer', item)
    //     setBankTransfer({
    //         // label: item.c10, // tên ngân hàng chuyển
    //         // value: item.c2 // Id ngân hàng chuyển
    //     })
    //     setBankTransferAct(item.c7) // Số tài khoản ngân hàng chuyển
    //     setActReceive({
    //         // label: item.c10,
    //         // value: {}
    //     })
    //     setBankReceive({
    //         // label: item.c10,
    //         // value: {}
    //     })
    //     setAmoutTransfer(item.c11) // số tiền chuyển
    //     switchStep.next()
    // }

    // -------------------------------------------   Khai báo các hàm gửi (request) lên server
    const getCurrentCashBySub = () => {
        const inputParams = ['1', userInfo.actn_curr, userInfo.sub_curr]
        sendRequest(ServiceInfo.GET_CURRENT_CASH_BY_SUB, inputParams, handleGetCurrentCashBySub)
    }
    const getHisPayOnAccountBySub = () => {
        const inputParams = ['2', userInfo.actn_curr, userInfo.sub_curr]
        sendRequest(ServiceInfo.GET_HIS_PAY_ON_ACCOUT_BY_SUB, inputParams, handleGetHisPayOnAccountBySub)
    }
    // -------------------------------------------   Khai báo các hàm nhận (handle respone) dữ liệu từ server
    const handleGetCurrentCashBySub = (reqInfoMap, message) => {
        console.log('handleGetCurrentCashBySub', reqInfoMap, message['Data'], message['Message'])

        // -- process after get result --
        if (Number(message['Result']) === 0) {
            console.warn('false handleGetCurrentCashBySub', reqInfoMap, message['Data'], message['Message'])
            setCurrentSubCash({})
            return
        } else {
            let jsondata = []
            try {
                jsondata = message['Data'] ? JSON.parse(message['Data']) : []
            } catch (err) {}
            setCurrentSubCash(jsondata[0])
        }
    }
    // -----------------------------------------
    const handleGetHisPayOnAccountBySub = (reqInfoMap, message) => {
        console.log('handleGetHisPayOnAccountBySub', reqInfoMap, message['Data'], message['Message'])

        // -- process after get result --
        if (Number(message['Result']) === 0) {
            console.warn('false handleGetHisPayOnAccountBySub', reqInfoMap, message['Data'], message['Message'])
            return
        } else {
            let jsondata = []
            try {
                jsondata = message['Data'] ? JSON.parse(message['Data']) : []
            } catch (err) {}
            // console.log(jsondata);
            setDataLastTransfer(jsondata)
        }
    }
    const getColor = (status) => {
        if (status === "N") return 'var(--REF__COLOR)'
        if (status === "Y") return 'var(--DOWN__COLOR)'
        if (status === "P") return 'var(--UP__COLOR)'
    }

    const ViewLastTransfer = ({ item }) => {
        return (
            // <>
            //  <RowTableData
            //         key={item.label}
            //         type='table'
            //         onPress={() => navigation.navigate(Screens.DETAIL_HIS_PAYMENT_ON_ACCOUNT, { data: item, onRefresh: onRefresh })}
            //     >
            //         <ColTableData
            //             colSpan={colSpan[0]}
            //             dataSub={[item.c2, item.c3]}
            //             colorSub={getColorSub(item.c3)}
            //             text2={item.c4}
            //         />
            //         <ColTableData
            //             colSpan={colSpan[1]}
            //             text={FormatNumber(item.c11)}
            //             textAlign='right'
            //         />
            //         <ColTableData
            //             colSpan={colSpan[1]}
            //             text={t(item.c27)}
            //             colorText={getColor(item.c21)}
            //             textAlign='right'
            //         />
            //     </RowTableData>
            // </>

            <div
                key={item.label}
                className='flex'
                style={UI.HeaderTop_view}
                onClick={() => history.push(routes.DETAIL_HIS_PAYMENT_ON_ACCOUNT.path, { data: item })}>
                <div className='flex flex-1 --fs__normal justify-content-start flex-direction-column'>
                    <span style={{ color: 'var(--PRIMARY__CONTENT__COLOR)', fontSize: fontSizes.small }}>
                        {item.c2}
                    </span>
                    <span style={{ color: 'var(--PRIMARY__CONTENT__COLOR)', fontSize: fontSizes.verySmall }}>
                        {item.c4}
                    </span>
                </div>
                <div className='flex flex-1 justify-content-center'>
                    <span style={{ color: 'var(--PRIMARY__CONTENT__COLOR)', fontSize: fontSizes.verySmall, textAlign: 'right' }}>
                        {FormatNumber(item.c11, 0, 1, 'short')}
                    </span>
                </div>
                <div className='flex flex-1 justify-content-center'>
                    <span style={{ color: 'var(--PRIMARY__CONTENT__COLOR)', fontSize: fontSizes.verySmall, textAlign: 'right' }}>
                        {item.c25}
                    </span>
                </div>
                <div className='flex flex-1 justify-content-center'>
                    <span style={{ color: getColor(item.c21), fontSize: fontSizes.verySmall, textAlign: 'right' }}>
                        {item.c21 === 'N' ? t('Chưa xử lý') : ''}
                        {item.c21 === 'Y' ? t('Đã duyệt') : ''}
                        {item.c21 === 'P' ? t('Từ chối') : ''}
                    </span>
                </div>
            </div>
        )
    }
    return (
        <div className="w-100">
            <div>
                <Account />
            </div>
            <div>
                <RowData textLeft={t('cash_amount')} textRight={FormatNumber(Number(currentSubCash.c4), 0)} rightColor={styles.PRIMARY} last type='info' />
            </div>
            <div className='center__item'>
                <ButtonCustom
                    text={t('continue')}
                    type='confirm'
                    onClick={() => {
                        switchStep.next()
                    }}
                />
            </div>

            <RowTitleGroup
                hasDivider
                text={t("title_payment_notice_history")}
                type = "group"
            />
            <div style={{ marginLeft: dimensions.indent, marginRight: dimensions.indent, paddingBottom: dimensions.vertical(16) }}>
                <HeaderList typeHeader='DEPOSIT_HISTORY' />
                {dataLastTransfer.map((item, i) => (
                    <ViewLastTransfer key={i} item={item} />
                ))}
            </div>
        </div>
    )
}

export default StepOne

const UI = {
    name_act: {
        flex: 4,
        flexDirection: 'row',
        paddingVertical: 5,
        paddingTop: dimensions.halfVerticalIndent,
    },
    flex1: {
        flex: 1,
    },
    margin_top_row: {
        marginTop: dimensions.halfIndent,
    },
    view_lasttransfer: {
        width: dimensions.indent * 9,
        height: dimensions.halfVerticalIndent * 8,
        borderRadius: 8,
        marginLeft: dimensions.halfIndent,
        marginRight: dimensions.halfIndent,
        paddingTop: dimensions.halfVerticalIndent,
        paddingBottom: dimensions.halfVerticalIndent,
    },
    HeaderTop_view: {
        paddingTop: dimensions.vertical(12),
        paddingBottom: dimensions.vertical(12),
        borderBottom: '1px solid var(--DIVIDER__COLOR)'
    },
}
