import { Button as ButtonBasic } from 'basic-components/button';
import { memo, useState, useEffect, useContext } from 'react';
import { useTranslation } from 'react-i18next';
import { fontSizes } from 'styles';
import { FormatNumber, Screens, glb_sv, eventList } from 'utils';
import { Collapse, Button } from 'antd';
import { StoreContext } from 'store';
import IC_FOLDER_NULL from 'assets/images/common/ic_folder_null.png';

const { Panel } = Collapse;

const StockInfo = ({ stockInfo, onRefresh }) => {
    const { t } = useTranslation();
    const { styles } = useContext(StoreContext);
    const [costPrice, setCostPrice] = useState(0);
    const [valuePrice, setValuePrice] = useState(0);
    const [refreshFlag, setRefreshFlag] = useState(false)

    useEffect(() => {
        if (stockInfo.length > 0) {
            const costPrice = stockInfo.reduce((total, item) => total + Number(item.c18), 0);
            const valuePrice = stockInfo.reduce((total, item) => total + Number(item.c16), 0);
            setCostPrice(costPrice);
            setValuePrice(valuePrice);
        }
        // return () => {
        //     cleanup
        // }
    }, [stockInfo])

    const handleRefresh = () => {
        setCostPrice(0);
        setValuePrice(0);
        setRefreshFlag(true)
        onRefresh()
    }

    const callback = key => {
        console.log(key);
    }

    const getColor = (value) => {
        if (value > 0) return styles.UP__COLOR
        if (value < 0) return styles.DOWN__COLOR
        return styles.PRIMARY__CONTENT__COLOR
    }

    const renderHeader = data => {
        return (
            <div className='flex flex-direction-row'>
                <div className='flex-2' style={{ color: 'var(--PRIMARY__CONTENT__COLOR)' }}>{data.c2}</div>
                <div className='flex-5 flex-direction-column justify-content-end'>
                    <div className='flex justify-content-end' style={{ fontSize: fontSizes.verySmall, color: 'var(--PRIMARY__CONTENT__COLOR)' }}>
                        {FormatNumber(data.c25, 0)}
                    </div>
                    <div className='flex justify-content-end' style={{ fontSize: fontSizes.verySmall, color: 'var(--SECOND__CONTENT__COLOR)' }}>
                        {FormatNumber(data.c17, 0)}
                    </div>
                </div>
                <div className='flex-5 flex-direction-column text-right'>
                    <div className='flex justify-content-end' style={{ fontSize: fontSizes.verySmall, color: 'var(--PRIMARY__CONTENT__COLOR)' }}>
                        {FormatNumber(data.c18, 0)}
                    </div>
                    <div className='flex justify-content-end' style={{ fontSize: fontSizes.verySmall, color: 'var(--SECOND__CONTENT__COLOR)' }}>
                        {FormatNumber(data.c15, 0)}
                    </div>
                </div>
                <div className='flex-5 flex-direction-column text-right'>
                    <div className='flex justify-content-end' style={{ fontSize: fontSizes.verySmall, color: getColor(Number(data['c19'])) }}>
                        {Number(data['c19']) > 0 ? '' : ''}
                        {FormatNumber(data['c19'])}
                    </div>
                    <div className='flex justify-content-end' style={{ fontSize: fontSizes.verySmall, color: getColor(Number(data['c19'])) }}>
                        {Number(data['c19']) > 0 ? '' : ''}
                        {Math.round(data['c20'] * 100) / 100}%
                    </div>
                </div>
            </div>
        )
    }

    const getOrder = (stock, type) => {
        glb_sv.commonEvent.next({ type: eventList.CHANGE_TAB, data: { screen: Screens.PLACE_ORDER, stock: stock, type: type } })
    }

    return (
        <div className='flex flex-direction-column' style={{ fontSize: fontSizes.small }}>
            <div className='flex flex-direction-row mt-1 mb-1'>
                <div className='flex-4 fw-bold' style={{ fontSize: fontSizes.medium }}>
                    {t('stock_assets_short')}
                </div>
                <div className='flex-2 text-right'>
                    <ButtonBasic type='primary' loading hasLoading={refreshFlag} onClick={handleRefresh} />
                </div>
            </div>
            <div className='flex flex-direction-row mt-1 mb-1'>
                <div className='flex-2'>{t('cost')}</div>
                <div className='flex-2 text-right' style={{ color: 'var(--REF__COLOR)' }}>
                    {FormatNumber(costPrice, 0)}
                </div>
            </div>
            <div className='flex flex-direction-row mt-1 mb-1'>
                <div className='flex-2'>
                    {t('value')}
                </div>
                <div className='flex-2 text-right' style={{ color: Number(valuePrice) == Number(costPrice) ? 'var(--REF__COLOR)' : Number(valuePrice) > Number(costPrice) ? 'var(--UP__COLOR)' : 'var(--DOWN__COLOR)' }}>
                    {FormatNumber(valuePrice, 0)}
                </div>
            </div>
            <div className="ant-divider" role="separator" style={{ backgroundColor: 'var(--DIVIDER__COLOR)', margin: '5px 0' }}></div>
            <div className='flex flex-direction-row mt-1 mb-1'>
                <div className='flex-4 fw-bold' style={{ fontSize: fontSizes.medium }}>
                    {t('stk_own_list')}
                </div>
                <div className='flex-2 text-right'></div>
            </div>
            <div className='list-stock-owned flex flex-direction-column'>
                <div className='flex flex-direction-row'>
                    <div className='flex-2 fw-bold'>{t('symbol_short')}</div>
                    <div className='flex-5 fw-bold flex-direction-column justify-content-end'>
                        <div className='flex justify-content-end'>{t('rgtbuy_registered_value')}</div>
                        <div className='flex justify-content-end'>{t('cost_price')}</div>
                    </div>
                    <div className='flex-5 fw-bold flex-direction-column text-right'>
                        <div className='flex justify-content-end'>{t('avg_buy_price')}</div>
                        <div className='flex justify-content-end'>{t('present_values')}</div>
                    </div>
                    <div className='flex-5 fw-bold flex-direction-column text-right'>
                        <div className='flex justify-content-end'>{t('cal_profit_loss')}</div>
                        <div className='flex justify-content-end'>{t('profit_ratio')}</div>
                    </div>
                </div>
                {stockInfo.length > 0 ? (
                    <Collapse onChange={callback} expandIcon={null} ghost={true} accordion>
                        {stockInfo.map((item, index) => (
                            <Panel
                                className='collapse-item-stock-owned'
                                style={{ borderBottom: '1px solid var(--DIVIDER__COLOR)' }}
                                showArrow={false}
                                key={index}
                                header={renderHeader(item)}>
                                <div style={{ margin: -7 }}>
                                    <div className='flex flex-direction-row'>
                                        <div className='flex-2 flex-direction-column'>
                                            <div className='flex' style={{ color: 'var(--SECOND__CONTENT__COLOR)' }}>
                                                {t('quantity_wait_trading')}
                                            </div>
                                            <div className='flex' style={{ color: 'var(--PRIMARY__CONTENT__COLOR)' }}>
                                                {FormatNumber(item.c6, 0)}
                                            </div>
                                        </div>
                                        <div className='flex-2 flex flex-direction-column justify-content-between'>
                                            <div className='flex center__item' style={{ color: 'var(--SECOND__CONTENT__COLOR)' }}>
                                                {t('head_right_quanlity')}
                                            </div>
                                            <div className='flex center__item' style={{ color: 'var(--PRIMARY__CONTENT__COLOR)' }}>
                                                {FormatNumber(item.c15, 0)}
                                            </div>
                                        </div>
                                        <div className='flex-2 flex flex-direction-column justify-content-between text-right'>
                                            <div className='flex justify-content-end' style={{ color: 'var(--SECOND__CONTENT__COLOR)' }}>
                                                {t('quantity_blockade')}
                                            </div>
                                            <div className='flex justify-content-end' style={{ color: 'var(--PRIMARY__CONTENT__COLOR)' }}>
                                                {FormatNumber(item.c8, 0)}
                                            </div>
                                        </div>
                                    </div>
                                    <div className='flex flex-direction-row'>
                                        <div className='flex-2 flex-direction-column'>
                                            <div className='flex' style={{ color: 'var(--SECOND__CONTENT__COLOR)' }}>
                                                {t('wait_for')}
                                            </div>
                                            <div className='flex' style={{ color: 'var(--PRIMARY__CONTENT__COLOR)' }}>
                                                {FormatNumber(Number(item.c12) + Number(item.c13) + Number(item.c14), 0)}
                                            </div>
                                        </div>
                                        <div className='flex flex-3 flex-direction-row justify-content-end align-items-center'>
                                            <Button
                                                onClick={() => getOrder(item.c2, 'buy')}
                                                type='primary'
                                                style={{ padding: '0 30px', border: 0, color: '#fff', backgroundColor: styles.UP__COLOR }}>
                                                {t('buy_upcase')}
                                            </Button>
                                            <Button
                                                onClick={() => getOrder(item.c2, 'sell')}
                                                type='primary'
                                                style={{ padding: '0 30px', border: 0, marginLeft: 10, color: '#fff', backgroundColor: styles.DOWN__COLOR }}>
                                                {t('sell_upcase')}
                                            </Button>
                                        </div>
                                    </div>
                                </div>
                            </Panel>
                        ))}
                    </Collapse>
                ) : (
                    <div>
                        <div className='flex justify-content-center'>
                            <img src={IC_FOLDER_NULL} />
                        </div>
                        <div style={{ textAlign: 'center', lineHeight: '160%', marginTop: 30, marginBottom: 30 }}>
                            <div style={{ fontSize: '15px', lineHeight: '20px', fontWeight: '500' }}>{t('empty_list')}</div>
                        </div>
                    </div>
                )}
            </div>
        </div>
    )
}

export default memo(StockInfo);