import { Button } from 'antd';
import {Modal} from 'antd-mobile'
import React, { memo, useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { ReactComponent as AlertIcon } from '../../assets/images/common/alert_icon.svg';
import { dimensions } from '../../styles';
import { eventList, glb_sv } from '../../utils';
import './index.scss';


function AlertModal() {
    const { t } = useTranslation();

    const [visible, setVisible] = useState(false)
    const [params, setParams] = useState({})

    const handleConfirm = () => {
        setVisible(false)
        if (params.linkCallback) params.linkCallback()
    }

    const handleCancel = () => {
        setVisible(false)
        if (params.cancelCallBack) params.cancelCallBack()
        else if (!params.showCancel) {
            if (params.linkCallback) params.linkCallback()
        }
    }

    useEffect(() => {
        const commonEvent = glb_sv.commonEvent.subscribe(msg => {
            if (msg.type === eventList.ALERT_MODAL) {
                setParams(msg.params);
                setVisible(true);
                glb_sv.focusInput('modal-button-cfm', 100)
            }
        });

        return () => {
            commonEvent.unsubscribe();
        }
    }, [])
    const getColorButton = (type) => {
        if ('info' === type || 'success' === type || 'confirm' === type) return 'var(--PRIMARY)'
        if ('error' === type) return 'var(--SELL__BG)'
        if ('warn' === type) return 'var(--NOTIFY__WARN)'
        if ('back' === type) return 'transparent'
    }
    return (
        <Modal
            visible={visible}
            onCancel={handleCancel}
            transparent
            style={{width: '85vw'}}
            // footer={null}
            // width={400}
            // zIndex={999999}
        >
            <div className={'flex flex-1 flex-direction-column align-items-center justify-content-center modal-alert ' + params.type}>
                {params.icon || <AlertIcon />}
                <span
                    className='text-primary fs-600 fs-medium'
                    style={{
                        marginTop: dimensions.vertical(16),
                        marginBottom: dimensions.vertical(16),
                    }}>
                    {params.title}
                </span>

                <span
                    className='text-center text-primary'
                    style={{ marginBottom: dimensions.moderate(32), opacity: 0.64 }}>
                    {params.content}
                </span>

                <Button
                    style={{
                        width: '100%',
                        borderRadius: 8,
                        backgroundColor: getColorButton(params.type),
                        paddingLeft: '1rem',
                        paddingRight: '1rem',
                    }}
                    block
                    size='large'
                    onClick={handleConfirm}
                    id='modal-button-cfm'
                >
                    <span className='text-white'>
                        {t('common_Ok')}
                    </span>
                </Button>
                {/* <ButtonCustom  text={t('common_Ok')} onClick={() => switchStep.submit()} type='confirm' /> */}

                {params.showCancel ? <Button
                    ghost
                    block
                    size='large'
                    onClick={handleCancel}
                    className='text-primary'
                >
                    <span style={{ opacity: 0.34 }}>
                        {t('common_Cancel')}
                    </span>
                </Button>
                    : null}

            </div>
        </Modal>
    )
}
export default memo(AlertModal)
