import { Input as AntdInput } from 'antd'
import React from 'react'
import { FormatNumber } from '../../utils'
import styled from 'styled-components'

const InputWrapper = styled.div`
    background-color: var(--INPUT__BG);
    padding: 0.5rem;
    margin: 1rem 0;
    border-radius: 8px;
    width: 100%;
    .ant-input {
        border: unset;
        padding: 0
    }

`


export const Input = ({
    type = 'text',
    typeLabel = 'vertical',
    labelRatio = [2, 3],
    readOnly = false,
    label = 'Text abc',
    labelStyle = {},
    inputStyle = {},
    textAlign = 'left',
    value = '',
    onChange = (e) => null,
    onKeyPress = (e) => null,
    onPressEnter = (e) => null,
    placeholder = '',
    refs,
    className,
    ...props
}) => {
    if (type === 'number') {
        value = FormatNumber(value)
    }

    return (
        <InputWrapper className={`alt-input-label ${typeLabel === 'horizontal' ? 'alt-input-label-horizontal' : ''}`}>
            {label ? (
                <div className={`mr-2 alt-label`} style={labelStyle}>
                    <span>{label}</span>
                </div>
            ) : null}
            <AntdInput
                style={inputStyle}
                className={` ${'text-' + textAlign}` + ' ' + className}
                onChange={onChange}
                onKeyPress={onKeyPress}
                onPressEnter={onPressEnter}
                placeholder={placeholder}
                readOnly={readOnly}
                value={value}
                ref={refs}
                {...props}
            />
        </InputWrapper>
    )
}
