import React, { useContext, memo } from 'react'
import { StoreContext } from '../../../store'
import { Input } from 'antd'

const CustomFloatInput = ({ onChange = () => null, value, label, placeholder, errCtrl, small, isInputModal,refs, ...props }) => {
    const { styles } = useContext(StoreContext)
    return (
        <div className='border-none'>
            <Input
                ref={refs}
                style={{ padding: '13px 16px', background: 'var(--INPUT__BG)', borderRadius: '8px' }}
                size='middle'
                className='input__component'
                placeholder={placeholder}
                onChange={ onChange}
                value={value}
            />
        </div>
    )
}

export default memo(CustomFloatInput)
