import moment from 'moment'
//import { Container, Content } from 'native-base';
import React, { useContext } from 'react'
import { useTranslation } from 'react-i18next'
//import { FlatList, StyleSheet } from 'react-native';
//import ToastGlobal from 'react-native-toast-message';
import HeaderComponent from '../../../../components/header'
import { ButtonCustom, ColTableData, RowData, RowTableData } from '../../../../components/trading-component'
//import EmptyView from '../../../../components/trading-component/empty-view';
import HeaderList from '../../../../components/trading-component/header-list'
import { StoreContext } from '../../../../store'
//import { dimensions as dm, IconSvg } from '../../../../styles';
import { FormatNumber, glb_sv, reqFunct, Screens, sendRequest,eventList } from '../../../../utils'
import { StoreTrading } from '../../../../store-trading'
import { useLocation, useHistory } from 'react-router-dom'
import { notification } from 'antd'

// Khai báo ServiceInfo (nếu có)
const ServiceInfo = {
    CONFIRM_ORDER: {
        reqFunct: reqFunct.CONFIRM_ORDER,
        // WorkerName: 'ALTxOrder',
        // ServiceName: 'ALTxOrder_0510_1',
        WorkerName: 'FOSxOrder',
        ServiceName: 'FOSxOrder_0510_1',
        ClientSentTime: '0',
        Operation: 'I',
    },
}
// Khai báo component
export default function DetailOrders({}) {
    const location = useLocation()
    const history = useHistory()
    let { data } = location.state
    const { styles } = useContext(StoreContext)
    const { userInfo } = useContext(StoreTrading)

    const { t } = useTranslation()
    // const { params = {} } = route
    // const { data = {}, onRefresh = () => null } = params

    const colSpan = [1, 1, 1, 1, 1]
    // Start define all bussiness state
    // End define form bussiness state
    // -------------------------------------------   Dùng useEffect get các dữ liệu cần thiết còn thiếu

    // -------------------------------------------   Viết các hàm để set lại bussiness form state. Nên viết đúng một chuẩn duy nhất để dễ control

    // -------------------------------------------   Khai báo các hàm gửi (request) lên server
    const confirmOrder = () => {
        const inputParams = [
            userInfo.actn_curr,
            userInfo.sub_curr,
            '',
            data.c1,
            moment(data.c0, 'DDMMYYYY').format('YYYYMMDD'),
            moment(data.c0, 'DDMMYYYY').format('YYYYMMDD'),
            '2',
        ]
        sendRequest(ServiceInfo.CONFIRM_ORDER, inputParams, handleConfirmOrder)
    }

    // -------------------------------------------   Khai báo các hàm gửi (request) và nhận (handle respone) dữ liệu từ server
    const handleConfirmOrder = (reqInfoMap, message) => {
        // console.log('handleConfirmOrder', reqInfoMap, message)
        if (Number(message['Result']) === 0) {
            if (message.Code == 'XXXXX5' || message.Code === '080063' || message.Code === '010012') {
                notification.warn({
                    message: t('common_notify'),
                    description: t('request_hanlde_not_success_try_again'),
                    duration: 4,
                })
            } else if (glb_sv.otpCases.includes(message.Code)) {
                glb_sv.objShareGlb['sessionInfo']['Otp'] = ''

                let data
                try {
                    data = JSON.parse(message.Data)[0]
                } catch (error) {
                    return
                }

                const otp_Type = Number(data.c1)
                const expTimeOtp = Number(data.c2)
                let reqOtpMessage = 'OTP'
                if (Number(data['c1']) === 2) {
                    //-- dạng thẻ ma trận
                    reqOtpMessage = 'OTP ' + data['c3']
                }
                glb_sv.commonEvent.next({
                    type: eventList.OTP_MODAL,
                    time: expTimeOtp,
                    message: reqOtpMessage,
                    messageOTP: message.Message,
                    hasOTP: false,
                    otp_Type,
                })
                // navigation.navigate(Screens.OTP_MODAL,
                //   {
                //     time: expTimeOtp,
                //     type: reqOtpMessage,
                //     hasOTP: false,
                //     otp_Type,
                //     msg: message.Message,
                //     // functCallback: showModalAction
                //   });
            } else {
                notification.warn({
                    message: t('common_notify'),
                    description: message['Message'],
                    duration: 4,
                })
            }
        } else {
            notification.success({
                message: t('common_notify'),
                description: message['Message'],
                duration: 4,
            })
            history.goBack()
        }
    }

    const ViewLastTransfer = ({ item }) => {
        return (
            <>
                <RowTableData
                    key={new Date().getTime}
                    type='table'
                    // onPress={() => navigation.navigate(Screens.DETAIL_ORDERS, { data: item })}
                >
                    <ColTableData colSpan={colSpan[0]} text={item.c2} typeOrder={item.c3 === '1' ? 'buy' : 'sell'} />
                    <ColTableData colSpan={colSpan[1]} text={FormatNumber(item.c4)} textAlign='center' />
                    <ColTableData colSpan={colSpan[2]} text={FormatNumber(item.c5)} textAlign='center' />
                    <ColTableData colSpan={colSpan[3]} text={FormatNumber(item.c7)} textAlign='right' />
                    <ColTableData colSpan={colSpan[4]} text={FormatNumber(item.c6)} textAlign='right' />
                </RowTableData>
            </>
        )
    }

    const getColor = (status) => {
        if (status === 'N') return styles.REF__COLOR
        if (status === 'R' || status === 'D') return styles.DOWN__COLOR
        if (status === 'Y') return styles.UP__COLOR
    }

    const getStatus = (param) => {
        const statusArray = [
            t('in_core_short'),
            t('in_core'),
            '0',
            t('wait_to_match'),
            t('match_all'),
            t('match_a_pieces'),
            t('wait_to_cancel_cfm'),
            t('confirm_cancel'),
            t('confirm_stop_order'),
            t('confirm_ice_order'),
            t('reject'),
        ]
        return statusArray[param]
    }
    return (
        <div style={{ backgroundColor: styles.PRIMARY__BG__COLOR }}>
            <HeaderComponent
                // navigation={navigation}
                // isShowLeft
                transparent
                titleAlgin='start'
                title={t('order_detail')}
                colorTitle={styles.PRIMARY__CONTENT__COLOR}
                alignChild={['left', 'center', 'center']}
            />
            <div>
                <HeaderList typeHeader='DETAIL_ORDERS' />
                <div className='ml-2 mr-2'>
                    {[data].map((item, i) => (
                        <ViewLastTransfer key={i} item={item} />
                    ))}
                </div>
                <RowData textLeft={t('order_id')} textRight={data.c1} />

                <RowData textLeft={t('transaction_type')} textRight={data.c18} />

                <RowData textLeft={t('order_status')} rightColor={getColor(data.c9)} textRight={getStatus(data.c8)} />

                <RowData
                    textLeft={t('common_confirm')}
                    rightColor={getColor(data.c9)}
                    textRight={data.c9 === 'Y' ? t('common_confirmed') : data.c9 === 'N' ? t('common_not_yet_confirmed') : t('cancel_order')}
                />

                <RowData textLeft={t('broker_staff')} textRight={getStatus(data.c22)} />

                <RowData textLeft={t('time')} textRight={moment(data.c10, 'DDMMYYYYHHmmss').format('DD/MM/YYYY HH:mm:ss')} />

                <RowData textLeft={t('confirm_staff')} textRight={data.c11} />

                <RowData textLeft={t('confirm_chanel')} textRight={data.c16} />

                <RowData textLeft={t('confirm_time')} textRight={data.c15 === ' ' ? data.c15 : moment(data.c15, 'DDMMYYYY').format('DD/MM/YYYY')} last />
                <div className='mr-2 ml-2'>
                    <ButtonCustom
                        disable={data.c9 !== 'N'}
                        visible={data.c9 === 'N'} // Có thể hủy
                        text={t('common_Cancel')}
                        onClick={() => {
                            confirmOrder()
                            // navigation.navigate(Screens.VERIFY_ORDER)
                            // navigation.goBack();
                            // glb_sv.checkLoadHistory = !glb_sv.checkLoadHistory;
                        }}
                    />
                </div>
            </div>
        </div>
    )
}
