import React, { useEffect, memo, useContext, useState } from 'react';
import { BrowserRouter as Router, Route, Switch, useHistory, Redirect } from 'react-router-dom';
import { createBrowserHistory } from "history";
import { glb_sv, eventList, STORE_KEY } from '../utils'
import { StoreContext } from '../store';
import { routes, routesArray } from './routes'

function SlashScreen({ navigation = {} }) {

  const { styles } = useContext(StoreContext)
  const history = useHistory();

  useEffect(() => {
    setTimeout(() => {
      history.push('/')
    }, 1000);
  }, []);

  return (
    <div style={{ flex: 1, display: 'flex', position: 'fixed', width: window.innerWidth, height: window.innerHeight }}>
      <div style={{
        flex: 1,
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        backgroundColor: 'var(--PRIMARY)',
      }}>
        <span style={{ fontSize: 40, fontWeight: 'bold', color: 'white' }}>{glb_sv.configInfo.shortName}</span>
      </div>
    </div>
  );
}

const history = createBrowserHistory(); 

function AppNavigation() {
  const [isrenderRoute, setIsrenderRoute] = useState(false);
  useEffect(() => {
    if (window.location.pathname !== '/') {
      window.location.replace('/')
      // <Redirect to={{ pathname: '/' }} />
    } else {
      setIsrenderRoute(true)
    }
  }, []);
  const ListRoute = routesArray.map((r, i) => 
    <Route key={r.key} exact={r.exact || true} path={r.path}>
      {r.component}
    </Route>
    )
  return (
    <Router history={history}>
      <Switch>
        <Route
          path="/slash"
          component={SlashScreen} />
        {isrenderRoute && ListRoute}
      </Switch>
    </Router>
  );
}

export default AppNavigation;