import React, { useContext, useState, useEffect, memo, useRef } from 'react'
import { StoreContext } from '../../store';
import { fontSizes, dimensions } from '../../styles';
import { FormatNumber, glb_sv, eventList, FormatNumberAC, subcribeFunct } from '../../utils';
import { useTranslation } from 'react-i18next';
import { LeftOutlined } from '@ant-design/icons'
import cloneDeep from 'lodash/cloneDeep'
import { Button } from 'antd'
import { useHistory } from 'react-router-dom'
import dayjs from 'dayjs'
import { List } from 'react-virtualized'
import throttle from 'lodash/throttle'

// import image

import IC_BACK from '../../assets/images/common/ic-back.svg'

const Item = memo(({ item, stock }) => {
    const { fractionPrice, fractionQty, styles } = useContext(StoreContext)
    return (
        <div className='flex align-items-center' style={UI.row}>
            <span className='fs__small' style={{ ...UI.title, width: '17%', color: 'var(--SECOND__CONTENT__COLOR)' }}>
                {dayjs(item.t52, 'YYYYMMDD-HH:mm:ss').format('HH:mm:ss')}
            </span>
            <span className='fs__small text-right' style={{ ...UI.title, width: '20%', color: 'var(--PRIMARY__CONTENT__COLOR)' }}>
                {fractionQty ? FormatNumberAC(item.t32) : FormatNumber(item.t32)}
            </span>
            <span className='fs__small text-right' style={{ ...UI.title, width: '28%', color: glb_sv.getColor(item.t31, stock, styles, 't31') }}>
                {fractionPrice ? FormatNumber(item.t31 / 1000, 2) : FormatNumber(item.t31)}
            </span>
            <span className='fs__small text-right' style={{ ...UI.title, width: '17.5%', color: glb_sv.getColor(item.t31, stock, styles, 't31') }}>
                {fractionPrice ? FormatNumber((item.t31 - stock.t260) / 1000, 2) : FormatNumber(item.t31 - stock.t260)}
            </span>
            <span className='fs__small text-right' style={{ ...UI.title, width: '17.5%', color: glb_sv.getColor(item.t31, stock, styles, 't31') }}>
                {FormatNumber((item.t31 - stock.t260) / stock.t260 * 100, 2)}%
            </span>
        </div>
    );
}, () => true);

const MatchListFull = () => {
    let history = useHistory()
    const { stockCode } = history.location.state
    const { styles } = useContext(StoreContext)
    const { t } = useTranslation();
    const throttled = useRef(throttle(() => {
        const newData = glb_sv.StockMarket[stockCode].EP;
        setData(cloneDeep(newData).sort(function (a, b) {
            if (a.t52 < b.t52) return 1
            if (a.t52 > b.t52) return -1
            if (a.t52 === b.t52) {
                if (a.subseq < b.subseq) return 1
                if (a.subseq > b.subseq) return -1
            }
            return 0
        }));
    }, 500, { trailing: true }))
    const [stock, setStock] = useState(glb_sv.StockMarket[stockCode]);
    const [data, setData] = useState([]);
    const [rowHeight, setRowHeight] = useState(dimensions.HIEGHT - 145)

    useEffect(() => {
        subcribeFunct(null, 'SUB', '*', [stockCode]);
        const eventMarket = glb_sv.eventMarket.subscribe(msg => {
            if (msg.type === eventList.SUB_STOCK && msg.msgKey === stockCode && msg.message === 'EP') {
                const newData = glb_sv.StockMarket[stockCode].EP;
                setData(cloneDeep(newData).sort(function (a, b) {
                    if (a.t52 < b.t52) return 1
                    if (a.t52 > b.t52) return -1
                    if (a.t52 === b.t52) {
                        if (a.subseq < b.subseq) return 1
                        if (a.subseq > b.subseq) return -1
                    }
                    return 0
                }));

                throttled.current();
            }
        });
        const height = document.getElementById('view_list').offsetHeight;
        setRowHeight(height);

        const newData = glb_sv.StockMarket[stockCode].EP;
        setData(cloneDeep(newData).sort(function (a, b) {
            if (a.t52 < b.t52) return 1
            if (a.t52 > b.t52) return -1
            if (a.t52 === b.t52) {
                if (a.subseq < b.subseq) return 1
                if (a.subseq > b.subseq) return -1
            }
            return 0
        }))
        return () => {
            eventMarket.unsubscribe();
        }
    }, []);

    const rowRenderer = ({ index, isScrolling, key, style }) => {
        const item = data[index];
        return (
            <div key={key} style={style}>
                <Item key={item.t52 + item.seq + item.subseq} item={item} stock={stock} />
            </div>
        );
    };

    return (
        <div className='flex flex-1 flex-direction-column'>
            <div className=' flex align-items-center' style={{ height: 50, paddingRight: 5, paddingLeft: 5, marginTop: 5, position: 'sticky', top: 0, backgroundColor: 'var(--PRIMARY__BG__COLOR)' }}>
                <Button ghost onClick={() => history.goBack()} style={{ height: 35, paddingRight: 6, paddingLeft: 6, border: 'none' }}>
                    <img src={IC_BACK} />
                </Button>
                <div className='flex flex-1 justify-content-center align-items-center' style={{ borderRadius: 10 }}>
                    <span className='fs__xmedium' style={{ color: 'var(--PRIMARY__CONTENT__COLOR)' }}>{t('time_sales')}</span>
                </div>
            </div>
            <div className='flex flex-1 flex-direction-column overflow-hidden'>
                <div className='flex flex-direction-column' style={UI.container}>
                    <div className='flex flex-direction-row'>
                        <span className='fs__normal' style={{ color: 'var(--PRIMARY__CONTENT__COLOR)' }}>{stock.t55}</span>&ensp;&ensp;
                        <span className='fs__smal' style={{ lineHeight: fontSizes.tiny + 8 + 'px', color: 'var(--SECOND__CONTENT__COLOR)' }}> {stock.U9}</span>
                    </div>
                    <div className='flex flex-direction-row fs__normal'>
                        <span style={{ color: glb_sv.getColor(stock.t31, stock, styles, 't31') }}>{FormatNumber(stock.t31)} </span>&ensp;&ensp;
                        <span style={{ color: glb_sv.getColor(stock.t31, stock, styles, 't31') }}> {FormatNumber(stock.t31_incr)} </span>&ensp;&ensp;
                        <span style={{ color: glb_sv.getColor(stock.t31, stock, styles, 't31') }}> {FormatNumber(stock.t31_incr_per, 2)}%</span>
                    </div>
                </div>

                <div style={{ height: 1, backgroundColor: 'var(--DIVIDER__COLOR)' }} />
                <div className='flex flex-direction-row fs-normal' style={UI.container}>
                    <span style={{ width: '17%', color: 'var(--HEADER__CONTENT__COLOR)' }}>{t('priceboard_time')}</span>
                    <span className='text-right' style={{ width: '20%', color: 'var(--HEADER__CONTENT__COLOR)' }}>{t('priceboard_matching_quantity')}</span>
                    <span className='text-right' style={{ width: '25%', color: 'var(--HEADER__CONTENT__COLOR)' }}>{t('priceboard_matching_price')}</span>
                    <span className='text-right' style={{ width: '17.5%', color: 'var(--HEADER__CONTENT__COLOR)' }}>+/-</span>
                    <span className='text-right' style={{ width: '17.5%', color: 'var(--HEADER__CONTENT__COLOR)' }}>+/- (%)</span>
                </div>
                <div style={{ height: 1, backgroundColor: 'var(--DIVIDER__COLOR)' }} />
                <div id='view_list' className='flex flex-1 flex-direction-column overflow-hidden fs-normal' style={{ paddingLeft: dimensions.halfIndent, paddingRight: dimensions.halfIndent, overflowY: 'auto' }}>
                    <List
                        rowCount={data.length}
                        width={dimensions.WIDTH - dimensions.halfIndent * 2}
                        height={rowHeight}
                        rowHeight={dimensions.vertical(8, 0.3) * 2 + fontSizes.small}
                        rowRenderer={rowRenderer}
                        overscanRowCount={20}
                    />
                </div>
            </div>
        </div >
    );
}

const UI = {
    container: {
        // position: 'sticky',
        // top: 0,
        paddingRight: dimensions.halfIndent,
        paddingLeft: dimensions.halfIndent,
        marginTop: dimensions.halfIndent,
        marginBottom: dimensions.halfIndent
    },
    item: {
        alignItems: 'center',
    },
    title: {
        paddingTop: dimensions.vertical(8, 0.3),
        paddingBottom: dimensions.vertical(8, 0.3)
    },
    header: {
        paddingBottom: 6,
        paddingTop: 6
    },
    row: { height: 35 }
};

export default MatchListFull