import React, { useContext} from 'react';
import { Col, List } from 'antd'
import { useHistory } from "react-router-dom";
import { CheckOutlined, LeftOutlined } from '@ant-design/icons';
import HeaderComponent from '../../components/header';
import { useTranslation } from 'react-i18next';
import { StoreContext } from '../../store';
import STORE_KEY from '../../utils/constant/store_key';
import { glb_sv } from '../../utils';
import Text from 'antd/lib/typography/Text';
import {ReactComponent as IconBack} from '../../assets/images/common/ic_back.svg'


const TimeoutConnectLayout = () => {
    const { t } = useTranslation();
    let history = useHistory()
    const { timeoutConnect, setTimeoutConnect } = useContext(StoreContext)

    const changeTimeoutConnect = (key) => {
        if (timeoutConnect === key) return;
        setTimeoutConnect(key)
        localStorage.setItem(STORE_KEY.TIMEOUT_CONNECT, key)
        glb_sv.timeoutConnect = key;
        history.goBack();
    }
    return (
        <div className='menu__layout' style={{ backgroundColor: 'var(--PRIMARY__BG__COLOR)', height: '100%' }}>
            <HeaderComponent
                title={t('time_stay_connected')}
                iconLeft={[
                    <IconBack  style={{ fontSize: 16 }} onClick={() => history.goBack()} />,
                ]}
                iconRight={[]}
            />
            <div>
                <List>
                    <List.Item onClick={() => changeTimeoutConnect('timeout_30m')}>
                        <Col span={20}>
                            <Text className='fs__medium' style={{ color: timeoutConnect === 'timeout_30m' ? 'var(--PRIMARY)':  '', }}>{t('timeout_30m')}</Text>
                        </Col>
                        <Col span={4} className="center__item">
                            {timeoutConnect === 'timeout_30m' && <CheckOutlined style={{ color: timeoutConnect === 'timeout_30m' ? 'var(--PRIMARY)':  '', fontSize: 16 }} />}
                        </Col>
                    </List.Item>
                    <List.Item onClick={() => changeTimeoutConnect('timeout_1h')}>
                        <Col span={20}>
                            <Text className='fs__medium' style={{ color: timeoutConnect === 'timeout_1h' ? 'var(--PRIMARY)':  '', }}>{t('timeout_1h')}</Text>
                        </Col>
                        <Col span={4} className="center__item">
                            {timeoutConnect === 'timeout_1h' && <CheckOutlined style={{ color: timeoutConnect === 'timeout_1h' ? 'var(--PRIMARY)':  '', fontSize: 16 }} />}
                        </Col>
                    </List.Item>
                    <List.Item onClick={() => changeTimeoutConnect('timeout_2h')}>
                        <Col span={20}>
                            <Text className='fs__medium' style={{ color: timeoutConnect === 'timeout_2h' ? 'var(--PRIMARY)':  '', }}>{t('timeout_2h')}</Text>
                        </Col>
                        <Col span={4} className="center__item">
                            {timeoutConnect === 'timeout_2h' && <CheckOutlined style={{ color: timeoutConnect === 'timeout_2h' ? 'var(--PRIMARY)':  '', fontSize: 16 }} />}
                        </Col>
                    </List.Item>
                    <List.Item onClick={() => changeTimeoutConnect('timeout_4h')}>
                        <Col span={20}>
                            <Text className='fs__medium' style={{ color: timeoutConnect === 'timeout_4h' ? 'var(--PRIMARY)':  '', }}>{t('timeout_4h')}</Text>
                        </Col>
                        <Col span={4} className="center__item">
                            {timeoutConnect === 'timeout_4h' && <CheckOutlined style={{ color: timeoutConnect === 'timeout_4h' ? 'var(--PRIMARY)':  '', fontSize: 16 }} />}
                        </Col>
                    </List.Item>
                    <List.Item onClick={() => changeTimeoutConnect('timeout_8h')}>
                        <Col span={20}>
                            <Text className='fs__medium' style={{ color: timeoutConnect === 'timeout_8h' ? 'var(--PRIMARY)':  '', }}>{t('timeout_8h')}</Text>
                        </Col>
                        <Col span={4} className="center__item">
                            {timeoutConnect === 'timeout_8h' && <CheckOutlined style={{ color: timeoutConnect === 'timeout_8h' ? 'var(--PRIMARY)':  '', fontSize: 16 }} />}
                        </Col>
                    </List.Item>
                    <List.Item onClick={() => changeTimeoutConnect('timeout_outday')}>
                        <Col span={20}>
                            <Text className='fs__medium' style={{ color: timeoutConnect === 'timeout_outday' ? 'var(--PRIMARY)':  '', }}>{t('timeout_outday')}</Text>
                        </Col>
                        <Col span={4} className="center__item">
                            {timeoutConnect === 'timeout_outday' && <CheckOutlined style={{ color: timeoutConnect === 'timeout_outday' ? 'var(--PRIMARY)':  '', fontSize: 16 }} />}
                        </Col>
                    </List.Item>
                </List>
            </div>
        </div>
    )
}



export default TimeoutConnectLayout

