import React, { useContext, useState, memo, useEffect, useRef, useCallback } from 'react';
import { StoreContext } from '../../store';
import { useTranslation } from 'react-i18next';
import { FormatNumber, glb_sv, eventList, subcribeFunct, Screens, RealTimeHandler } from '../../utils';
import { fontSizes, dimensions } from '../../styles';
import debounce from 'lodash/debounce'
import FlatList from 'flatlist-react'
import isEqual from 'react-fast-compare'
import { LeftOutlined } from '@ant-design/icons';
import { Button, Affix } from 'antd';
import {
    useHistory
} from 'react-router-dom'
import Text from 'antd/lib/typography/Text';
import HeaderTop from '../market-tab/TopMarket/HeaderTop'
import HeaderComponent from '../header'
import { ReactComponent as IconBack } from '../../assets/images/common/ic_back.svg'

const StockItem = memo(({ item, styles, onChangeStkInfo }) => {
    return (
        <div className='flex flex-direction-row' style={UI.HeaderTop_view} onClick={() => onChangeStkInfo(item)}>
            <div className='flex flex-2 --fs__normal justify-content-start flex-direction-column'>
                <Text style={{ color: 'var(--PRIMARY__CONTENT__COLOR)', fontSize: item.t55.length > 3 ? fontSizes.verySmall : fontSizes.small }}>
                    {item.t55}
                </Text>
            </div>
            <div className='flex flex-1 justify-content-center'>
                {item.t31 ? <Text style={{ color: glb_sv.getColor(item.t31, item), fontSize: fontSizes.verySmall, textAlign: 'right' }}>
                    {item.t31_incr_per > 0 ? '+' : ''}{FormatNumber(item.t31_incr_per, 2)}%
                </Text> : null}
            </div>
            <div className='flex flex-1 justify-content-center'>
                <Text style={{ color: glb_sv.getColor(item.t260, item), fontSize: fontSizes.verySmall, textAlign: 'right' }}>
                    {FormatNumber(item.t137, 0, 1)}
                </Text>
            </div>
            <div className='flex flex-1 justify-content-center'>
                <Text style={{ color: glb_sv.getColor(item.t31, item), fontSize: fontSizes.verySmall, textAlign: 'right' }}>
                    {FormatNumber(item.t31, 0, 1)}
                </Text>
            </div>
            <div className='flex flex-1 justify-content-center'>
                <Text style={{ color: glb_sv.getColor(item.t31, item), fontSize: fontSizes.verySmall, textAlign: 'right' }}>
                    {FormatNumber(item.t387, 0, 1, 'short')}
                </Text>
            </div>
        </div>
    )
}, compareStockItem);

function compareStockItem(prev, next) {
    if (prev.item.U9 === next.item.U9 && prev.item.t31 === next.item.t31 && prev.item.t31_incr_per === next.item.t31_incr_per) return true
    return false
}

function ListStockAll({ navigation, route }) {
    let history = useHistory()
    const { listStock, indexCode } = history.location.state
    const indexView = new RealTimeHandler('ALL_STOCK_LIST');

    const { styles } = useContext(StoreContext)
    const [list, setList] = useState(listStock);
    const { t } = useTranslation();

    const [index, setIndex] = useState(glb_sv.IndexMarket[indexCode])

    const list_subscribe = useRef([])
    const timeoutChangeView = useRef(null)

    const throttled = useRef(debounce(() => {
        const newList = listStock.map(e => {
            return glb_sv.StockMarket[e.t55] ? glb_sv.StockMarket[e.t55] : { t55: e.t55, U9: '', t31: 0, t31_incr_per: 0 }
        })
        setList(JSON.parse(JSON.stringify(newList)));
    }, 500, { trailing: true }))

    const throttledIndex = useRef(debounce(() => {
        setIndex({ ...glb_sv.IndexMarket[indexCode] })
    }, 500, { trailing: true }))

    const handleViewableItemsChanged = useRef(({ viewableItems }) => {
        if (!viewableItems.length) return;
        if (isEqual(list_subscribe.current, viewableItems.map(e => e.key))) return;
        if (timeoutChangeView.current) clearTimeout(timeoutChangeView.current)
        timeoutChangeView.current = setTimeout(() => {
            if (list_subscribe.current.length) subcribeFunct(null, 'UNSUB', '*', list_subscribe.current);
            list_subscribe.current = viewableItems.map(e => e.key)
            subcribeFunct(null, 'SUB', '*', list_subscribe.current);
        }, 400);
    })

    useEffect(() => {
        list_subscribe.current = listStock.map(e => e.t55)
        // subcribeFunct(null, 'SUB', '*', list_subscribe.current);
        indexView.subscribe({
            topic: ['MDDS|SI', 'MDDS|TP'],
            value: list_subscribe.current.slice(30)
        })
        requestAnimationFrame(() => setList(listStock));
        // indexView.unsubscribe({
        //     topic: ['MDDS|SI', 'MDDS|TP'],
        //     value: list_subscribe.current.slice(0, 20),
        // })
        // subcribeFunct(null, 'UNSUB', '*', list_subscribe.current.slice(30));
        list_subscribe.current = list_subscribe.current.slice(0, 20)
        const eventMarket = glb_sv.eventMarket.subscribe(msg => {
            if (msg.type === eventList.SUB_STOCK && msg.message === 'SI') {
                throttled.current();
            }
            if (msg.msgKey === indexCode) {
                throttledIndex.current();
            }
        });

        const viewHeader = document.getElementById('view-header');
        const viewList = document.getElementById('view-stock-list');
        const heightViewList = dimensions.HIEGHT - viewHeader.offsetHeight - 3;
        viewList.style.height = heightViewList + 'px';
        const heightRow = viewList.firstChild.offsetHeight;
        const list = listStock.map(e => e.t55);
        viewList.addEventListener("scroll", function (ev) {
            if (timeoutChangeView.current) clearTimeout(timeoutChangeView.current)
            timeoutChangeView.current = setTimeout(() => {
                const NoHeadArr = Math.round(ev.target.scrollTop / heightRow)
                const NoTailArr = Math.ceil(heightViewList / heightRow)
                const newList = list.slice(NoHeadArr, NoHeadArr + NoTailArr)
                if (list_subscribe.current.length) {
                    // subcribeFunct(null, 'UNSUB', '*', list_subscribe.current)
                    indexView.unsubscribe({
                        topic: ['MDDS|SI', 'MDDS|TP'],
                        value: list_subscribe.current,
                    })
                }
                list_subscribe.current = newList
                // subcribeFunct(null, 'SUB', '*', list_subscribe.current);
                indexView.subscribe({
                    topic: ['MDDS|SI', 'MDDS|TP'],
                    value: list_subscribe.current,
                })
            }, 400);
        });
        return () => {
            viewList.removeEventListener('scroll', () => null);
            eventMarket.unsubscribe();
            indexView.unsubscribe({
                topic: ['MDDS|SI', 'MDDS|TP'],
                value: list_subscribe.current,
            })
            indexView.clearRealTimeListener({ unsubscribe: true });
            throttled.current.cancel();
            throttledIndex.current.cancel();
            // subcribeFunct(null, 'UNSUB', '*', list_subscribe.current);
        };
    }, []);

    const onChangeStkInfo = (item) => {
        throttled.current.cancel();
        throttledIndex.current.cancel();
        navigation.replace(Screens.STOCK_INFO, item.t55)
    }

    const renderItem = useCallback((item) => {
        return (
            <StockItem key={item.t55} item={item} styles={styles} onChangeStkInfo={onChangeStkInfo} />
        )
    });

    const getColor = () => {
        if (index.indexValueChang > 0) return 'var(--UP__COLOR)'
        if (index.indexValueChang === 0) return 'var(--REF__COLOR)'
        if (index.indexValueChang < 0) return 'var(--DOWN__COLOR)'
    }

    return (
        <div className='flex flex-1 flex-direction-column'>
            <HeaderComponent
                id='view-header'
                title={t('list_stock')}
                transparent
                iconLeft={[
                    <IconBack style={{ fontSize: dimensions.iconSize }} onClick={() => history.goBack()} />,
                ]}
                iconRight={[]}
            />
            {/* <div id='view-header' className='flex' style={{ height: glb_sv.isTablet ? 60 : 50, paddingRight: 5, paddingLeft: 5, paddingTop: 5 }}>
                <Button className="center__item" ghost onClick={() => history.goBack()} style={UI.Incon__Back}>
                    <LeftOutlined style={{ fontSize: dimensions.iconSize, color: styles.PRIMARY__CONTENT__COLOR }} />
                </Button>
                <div className='flex flex-1 flex-direction-column justify-content-center align-items-center' style={{ borderRadius: 10, padding: 0 }}>
                    <span className='fs__xmedium' style={{ color: 'var(--PRIMARY__CONTENT__COLOR)' }}>{t('list_stock')}</span>
                </div>
            </div> */}

            <div className='flex flex-1 flex-direction-column'>
                <div style={{ height: 1, backgroundColor: 'var(--DIVIDER__COLOR)' }} />
                <div id='view-stock-list' className='flex flex-direction-column'
                    style={{
                        paddingRight: dimensions.halfIndent,
                        paddingLeft: dimensions.halfIndent,
                        backgroundColor: 'var(--PRIMARY__BG__COLOR)',
                        overflow: 'hidden',
                        overflowY: 'auto'
                    }}>
                    <Affix offsetTop={glb_sv.isTablet ? 60 : 50}>
                        <HeaderTop type='TOP_PRI_UP' activeOtp='1D' bgColor="var(--PRIMARY__BG__COLOR)" />
                    </Affix>
                    <FlatList
                        list={list}
                        renderItem={renderItem}
                    />
                </div>

            </div>
        </div >
    );
}

const UI = {
    container: {
        paddingRight: dimensions.moderate(16),
        paddingLeft: dimensions.moderate(16),
        marginTop: dimensions.halfIndent,
        marginBottom: dimensions.halfIndent
    },
    HeaderTop_view: {
        paddingTop: dimensions.vertical(12),
        paddingBottom: dimensions.vertical(12),
        borderBottom: '1px solid var(--DIVIDER__COLOR)'
    },
    Incon__Back: {
        paddingTop: 0,
        paddingBottom: 0,
        height: '100%',
        paddingRight: 6,
        paddingLeft: 6,
        border: 'none'
    }
};

export default ListStockAll