import { useState, useContext, useEffect, useRef, useCallback } from 'react'
import { useTranslation } from 'react-i18next'
import { ScrollView } from '../../basic-components'
import { TopMarket } from '../../components/market-tab'
import { glb_sv, subcribeFunct, eventList, STORE_KEY, RealTimeHandler, reqFunct, sendRequest } from '../../utils'
import throttle from 'lodash/throttle'
import { dimensions } from '../../styles'
import { notification } from 'antd'
import { useHistory } from 'react-router-dom'
import { routes } from 'navigation/routes'
import PortfolioLayout from '../portfolio/index'
import HeaderMarket from '../../components/market-tab/HeaderMarket'
import MarketTrading from 'layouts/mkt-trading'
import MarketCap from '../market-cap'
import { StoreTrading } from 'store-trading'
import { clearAllLocalFavList, getAllLocalFavList } from '../../components/portfolio/favorite-func/favorite.action'

const colorIndex = ['#0B90FF', '#D100E3', '#03BD85', '#FB8C00', '#00796B']

const listTopMktInit = ['TOP_PRI_UP', 'TOP_PRI_DWN', 'TOP_QTY_UP', 'TOP_VAL_UP']

const ServiceInfo = {
    getStockListByIndex: {
        reqFunct: reqFunct.GET_STK_LIST_PRB,
        WorkerName: 'FOSqMkt02',
        ServiceName: 'FOSqMkt02_IndexMgt',
        ClientSentTime: '0',
        Operation: 'Q',
    },
    getFav: {
        reqFunct: reqFunct.GET_FAV_INFO,
        WorkerName: 'FOSqID01',
        ServiceName: 'FOSqID01_FavoritesMgt',
        Operation: 'Q',
        ClientSentTime: '0',
    },
    AddNewFavList: {
        reqFunct: reqFunct.ADD_NEW_FAV_LIST,
        WorkerName: 'FOSxID01',
        ServiceName: 'FOSxID01_FavoritesMgt',
        ClientSentTime: '0',
        Operation: 'I',
    },
    getStkListInFav: {
        reqFunct: reqFunct.GET_DETAIL_FAV_LIST,
        WorkerName: 'FOSqID01',
        ServiceName: 'FOSqID01_FavoritesMgt',
        Operation: 'Q',
        ClientSentTime: '0',
    },
    GET_ASSETS_STOCK_LIST: {
        reqFunct: reqFunct.GET_ASSETS_STOCK_LIST,
        WorkerName: 'FOSqAsset',
        ServiceName: 'FOSqAsset_1801_1',
        Operation: 'Q',
        ClientSentTime: '0',
    },
}
export default function MarketLayout() {
    const IndexMarketView = new RealTimeHandler('MARKET_VIEW')
    const SubscribeStockIndex = new RealTimeHandler('STOCK_INDEX')
    const history = useHistory()
    const { t } = useTranslation()

    const { authFlag, userInfo } = useContext(StoreTrading)
    const [active, setActive] = useState('')
    const timeoutRef = useRef(null)

    const activeRef = useRef('')
    const subListIndex = useRef(true)

    const [listTopMkt, setListTopMkt] = useState(listTopMktInit)

    const [objIndex, setObjIndex] = useState({
        dataIndex: [],
        ListViewIndex: [],
    })
    const objIndexRef = useRef({
        dataIndex: [],
        ListViewIndex: [],
    })

    const [indexCd, setIndexCd] = useState('VN30')
    const [priceBoard, setPriceBoard] = useState(glb_sv.stkListPrb)
    const stkListResRef = useRef([])
    const stkListSubRef = useRef([])
    const stkListSubIssuerRef = useRef([])
    const [activeSelect, setActiveSelect] = useState({ list_fav: true })
    let syncFvlCount = 0

    const [listIndex, setListIndex] = useState([])
    const [listStockOwned, setListStockOwned] = useState({})

    const throttled = useRef(
        throttle((newValue) => {
            if (newValue === activeRef.current) {
                // const data = glb_sv.IndexMarket[activeRef.current].indexArr;
                // objIndexRef.current.dataIndex = data
            }

            const list = glb_sv.listActiveIndex.map((e, index) => {
                return {
                    color: colorIndex[index],
                    title: glb_sv.IndexMarket[e].t18,
                    value: glb_sv.IndexMarket[e].indexValue,
                    ratioValue: glb_sv.IndexMarket[e].indexRatioChang,
                    valueChang: glb_sv.IndexMarket[e].indexValueChang,
                    indexTotalValue: glb_sv.IndexMarket[e].indexTotalValue,
                    type: e,
                }
            })
            objIndexRef.current.ListViewIndex = list
            setObjIndex({ ...objIndexRef.current })
        }, 500)
    )

    useEffect(() => {
        if (!authFlag) {
            if (glb_sv.allListFav.length === 0 || glb_sv.allListFav.some(x => x.c1 !== 'VN30' && x.c1 !== 'HNX30')) {
                glb_sv.allListFav = [
                    {
                        c1: 'VN30',
                        c2: 'VN30',
                        ListStock: [],
                    },
                    {
                        c1: 'HNX30',
                        c2: 'HNX30',
                        ListStock: [],
                    },
                ]
                glb_sv.allListFav = [...new Set([...glb_sv.allListFav, ...getAllLocalFavList()])]
                getStkDefault()
                const indexCurrent = !!glb_sv.activeList['c1'] ? glb_sv.allListFav.findIndex(x => x['c1'] === glb_sv.activeList['c1']) : 0
                if (indexCurrent !== 0) {
                    glb_sv.activeList = glb_sv.allListFav[indexCurrent]
                }else{
                    glb_sv.activeList = glb_sv.allListFav[0]
                }
            }
        } else {
            if (glb_sv.allListFav.some(x => x.c1 === 'VN30' || x.c1 === 'HNX30')) {
                getFavInfo()
                getStockOwned()
            }
            // glb_sv.allListFav = [...new Set(getAllLocalFavList())];
        }

        IndexMarketView.listen((msg) => {
            if (msg.type === eventList.GET_INDEXS_DONE) {
                IndexMarketView.subscribe({
                    onTimeout: () => { },
                    topic: ['MDDS|I', 'MDDS|BI'],
                    value: ['HSXIndex', 'HNXIndex', 'HNXUpcomIndex', 'VN30'],
                })
                // getStkDefault()
                // getStkList(indexCd)
            }

            if (msg.type === eventList.LOADING_FAV) {
                getStkList(indexCd)
            }

            if (msg.type === eventList.SUB_INDEX) {
                const data = glb_sv.listIndex.server.map((e, index) => {
                    return { key: String(index), sortIndex: getSortIndex(e), ...glb_sv.IndexMarket[e] } || {}
                })
                const sortList = data.sort((a, b) => a.sortIndex - b.sortIndex)
                if (activeRef?.current?.length == 0) {
                    activeRef.current = data[0]?.indexCode
                    setActive(data[0]?.indexCode)
                }
                setListIndex([...sortList])
            }
        })

        return () => {
            throttled.current.cancel()
            IndexMarketView.clearRealTimeListener({ unsubscribe: true })
        }
    }, [])

    const getStkDefault = () => {
        glb_sv.allListFav.forEach((e) => {
            if (e.c1 === 'HNX30' || e.c1 === 'VN30') {
                const InputParams = ['stk_list', e.c1]
                sendRequest(ServiceInfo.getStockListByIndex, InputParams, handleGetStkDefault)
            }
        })
    }

    const handleGetStkDefault = (reqInfoMap, message) => {
        if (Number(message['Result']) === 0) {
            return
        } else {
            let jsondata
            try {
                jsondata = message['Data'] ? JSON.parse(message['Data']) : []
                const i = glb_sv.allListFav.findIndex((x) => x.c1 === reqInfoMap?.inputParam[1])
                glb_sv.currentIndex_FAV = 0
                glb_sv.allListFav[i]['ListStock'] = jsondata.map((x) => x.c0)
                // glb_sv.activeList = glb_sv.allListFav[glb_sv.allListFav.length - 1]
                glb_sv.commonEvent.next({ type: eventList.CHANGE_LIST_ACTIVE })
            } catch (err) {
                console.log('handleGetListStk -> err', err)
                jsondata = []
            }
        }
    }

    const getFavInfo = () => {
        const InputParams = ['3', '', '0']
        sendRequest(ServiceInfo.getFav, InputParams, getFavInfoProc)
    }

    const getFavInfoProc = (reqInfoMap, message) => {
        let jsonArr = []
        const strdata = message['Data']
        try {
            jsonArr = strdata ? JSON.parse(strdata) : []
        } catch (error) {
            console.log(error)
            return
        }
        if (Number(message['Result']) === 1) {
            try {
                const newallListFav = []
                for (let i = 0; i < jsonArr.length; i++) {
                    const element = { c1: jsonArr[i]['c1'], c2: jsonArr[i]['c2'] }
                    newallListFav.push({
                        ...element,
                        ListStock: [],
                    })
                }
                glb_sv.allListFav = newallListFav
                glb_sv.activeList = newallListFav[0] ? { ...newallListFav[0] } : { ListStock: [] }
                jsonArr.forEach((e) => getDetailFavList(e))

                //-- Mở modal thông báo đồng bộ dữ dm quan tâm nếu tồn tại dm local
                let newWatchList = getAllLocalFavList()
                if (newWatchList.length === 0) return
                glb_sv.showAlert({
                    title: t('common_notify'),
                    content: t('are_you_want_synce_fav'),
                    type: 'warn',
                    showCancel: true,
                    cancelCallBack: () => clearAllLocalFavList(),
                    linkCallback: () => synceFav(),
                })
            } catch (error) { }
        }
    }

    const getStockOwned = () => {
        const { actn_curr } = userInfo
        const InputParams = ['02', actn_curr, '%']
        sendRequest(
            ServiceInfo.GET_ASSETS_STOCK_LIST,
            InputParams,
            getAssetsStockListResult,
            true,
            () => { },
            '',
            0,
            'equal_service'
        )
    }

    const getAssetsStockListResult = (reqInfoMap, message) => {
        if (Number(message['Result']) === 0) {
            return
        } else {
            let jsondata = []
            try {
                jsondata = message['Data'] ? JSON.parse(message['Data']) : []
            } catch (err) {
                console.log('getAssetsStockListResult', err)
                return
            }
            if (Number(message['Packet']) <= 0) {
                const stkList = jsondata.map((item) => item.c2)
                const stockOwned = {
                    c1: '00',
                    c2: t('stock_owned'),
                    ListStock: [...new Set(stkList)],
                }
                glb_sv.allListFav.unshift(stockOwned)
                glb_sv.activeList = glb_sv.allListFav[0]
                glb_sv.commonEvent.next({ type: eventList.CHANGE_LIST_ACTIVE })
                setListStockOwned(stockOwned)
            }
        }
    }

    const synceFav = () => {
        let newWatchList = getAllLocalFavList()
        syncFvlCount = newWatchList.length
        if (syncFvlCount === 0) return
        newWatchList.forEach((elm) => {
            const curFav = glb_sv.allListFav.find((e) => e.c2 === elm.c2)
            if (!!curFav) {
                --syncFvlCount
                elm.ListStock.forEach((item) => {
                    if (!curFav.ListStock.some((e) => e === item)) {
                        glb_sv.commonEvent.next({
                            type: eventList.MSG_ADD_NEW_STK_2FAV,
                            data: { stk: item, keyFlv: curFav.c1 },
                        })
                    }
                })
                if (syncFvlCount == 0) {
                    notification.success({
                        message: t('common_notify'),
                        description: t('synce_fav_success'),
                        duration: 3,
                    })
                    setTimeout(() => {
                        clearAllLocalFavList()
                        glb_sv.commonEvent.next({ type: eventList.CHANGE_LIST_ACTIVE })
                    }, 1500)
                }
            } else if (elm.c2) {
                const InputParams = ['FAV_ADD', elm.c2]
                sendRequest(ServiceInfo.AddNewFavList, InputParams, handleAddNewListFavSync)
            }
        })
    }

    const handleAddNewListFavSync = (reqInfoMap, message) => {
        --syncFvlCount
        if (Number(message['Result']) === 0) {
            return
        } else {
            try {
                const dataObj = JSON.parse(message['Data'])
                glb_sv.allListFav.push({
                    c1: dataObj[0]['c0'],
                    c2: reqInfoMap.inputParam[1],
                    ListStock: [],
                })
                let newWatchList = getAllLocalFavList()

                const curFav = newWatchList.find((e) => e.c2 == reqInfoMap.inputParam[1])
                if (curFav) {
                    curFav.ListStock.forEach((item) => {
                        glb_sv.commonEvent.next({
                            type: eventList.MSG_ADD_NEW_STK_2FAV,
                            data: { stk: item, keyFlv: dataObj[0]['c0'] },
                        })
                    })
                }
            } catch (error) {
                console.log('error JSON.parse fvl', error)
                return
            }
        }
        if (syncFvlCount === 0) {
            notification.success({
                message: t('common_notify'),
                description: t('synce_fav_success'),
                duration: 3,
            })
            setTimeout(() => {
                clearAllLocalFavList()
                glb_sv.commonEvent.next({ type: eventList.CHANGE_LIST_ACTIVE })
            }, 1500)
        }
    }

    const getDetailFavList = (element) => {
        const groupId = element.c1
        const InputParams = ['4', String(groupId), '0']
        sendRequest(ServiceInfo.getStkListInFav, InputParams, handleGetDetailFavList)
    }

    const handleGetDetailFavList = (reqInfoMap, message) => {
        if (Number(message['Result']) === 0) {
            return
        } else {
            reqInfoMap.procStat = 1
            let dataArr = []
            try {
                let strdata = message['Data']
                dataArr = JSON.parse(strdata)
                const findIndex = glb_sv.allListFav.findIndex((x) => x.c1 == reqInfoMap.inputParam[1])
                if (findIndex >= 0) {
                    glb_sv.allListFav[findIndex].ListStock = dataArr.map((e) => e.c3)
                    glb_sv.activeList = glb_sv.allListFav[findIndex]
                    glb_sv.commonEvent.next({ type: eventList.CHANGE_LIST_ACTIVE })
                }
            } catch (error) {
                return
            }
        }
    }

    const getStkList = (indexCd) => {
        if (!indexCd || indexCd.trim() === '') return
        const InputParams = ['stk_list', indexCd]
        sendRequest(ServiceInfo.getStockListByIndex, InputParams, handleGetListStk)
        stkListResRef.current = []
        //-- call unsubcrible at here
        if (stkListSubRef.current && stkListSubRef.current.length > 0) {
            // subcribeFunct(null, 0, 'UNSUB', ['MDDS|SI', 'MDDS|TP'], stkListSubRef.current)
            SubscribeStockIndex.unsubscribe({
                topic: ['MDDS|SI', 'MDDS|TP'],
                value: stkListSubRef.current,
            })
            if (stkListSubIssuerRef.current.length > 0)
                // subcribeFunct(null, 0, 'UNSUB', ['MDDS|SI'], stkListSubIssuerRef.current)
                SubscribeStockIndex.unsubscribe({
                    topic: ['MDDS|SI'],
                    value: stkListSubIssuerRef.current,
                })
            stkListSubIssuerRef.current = []
            stkListSubRef.current = []
        }
        glb_sv.stkListPrb = []
    }

    const handleGetListStk = (reqInfoMap, message) => {
        // console.log("handleGetListStk -> message", reqInfoMap, message)
        // -- process after get result --
        if (Number(message['Result']) === 0) {
            //-- show thong bao loi tai day
            const errmsg = message['Message']
            glb_sv.showAlert({
                title: t('common_notify'),
                content: errmsg,
                type: 'warn',
            })
            return
        } else {
            let jsondata
            try {
                jsondata = message['Data'] ? JSON.parse(message['Data']) : []
                const i = glb_sv.allListFav.findIndex((x) => x.c1 === glb_sv.allListFav[glb_sv.currentIndex_FAV]?.c1)
                glb_sv.currentIndex_FAV = i
                glb_sv.allListFav[i]['ListStock'] = jsondata.map((x) => x.c0)
                glb_sv.activeList = glb_sv.allListFav[i]
            } catch (err) {
                console.log('handleGetListStk -> err', err)
                jsondata = []
            }
            stkListResRef.current = stkListResRef.current.concat(jsondata)
            if (Number(message['Packet']) <= 0) {
                stkListSubRef.current = stkListResRef.current.map((item) => {
                    return item['c0']
                })
                if (indexCd === 'VN30' || indexCd === 'HNX30') {
                    glb_sv.IndexMarket[indexCd].STOCK_INFO = stkListResRef.current.map((item) => {
                        return { t55: item['c0'] }
                    })
                }

                glb_sv.stkListPrb = stkListSubRef.current.map((item) => {
                    return glb_sv.StockMarket[item] || { t55: item }
                })
                //-- lay du lieu table priceboard
                if (!glb_sv.stkListPrb) glb_sv.stkListPrb = []
                if (glb_sv.stkListPrb.length > 0) {
                    // subcribeFunct(null, 0, 'SUB', ['MDDS|SI', 'MDDS|TP'], stkListSubRef.current)
                    SubscribeStockIndex.subscribe({
                        topic: ['MDDS|SI', 'MDDS|TP', 'MDDS|EP'],
                        value: stkListSubRef.current,
                    })
                    // setDataTablPrcb([...glb_sv.stkListPrb])

                    if (!glb_sv.configInfo.sub_all_priceboard) {
                        setTimeout(() => {
                            // handleSubView()
                        }, 1000)
                    }
                }
            }
        }
    }

    const getSortIndex = (index) => {
        if (index === 'HSXIndex') return 1
        if (index === 'HNXIndex') return 2
        if (index === 'HNXUpcomIndex') return 3
        if (index === 'VN30') return 4
        if (index === 'HNX30') return 5
        return 1000 // Một số lớn k thể vượt qua
    }

    const getColorByRef = (value, ref) => {
        if (value > ref) return 'var(--UP__COLOR)'
        if (value < ref) return 'var(--DOWN__COLOR)'
        return 'var(--REF__COLOR)'
    }
    const changeActiveIndex = (value) => {
        if (value === activeRef.current) {
            subListIndex.current = false
            history.push(routes.INDEX_INFO.path, { indexCode: activeRef.current })
            return
        }
        // glb_sv.listViewableTopMrk.forEach(e => e.data = []);
        setActive(value)
        if (timeoutRef.current) clearTimeout(timeoutRef.current)
        timeoutRef.current = setTimeout(
            (newValue) => {
                if (activeRef.current.includes('HNXUpcomIndex')) {
                    subcribeFunct(null, 'UNSUB', 'UPC', ['MKT_TOP', 'UPC'])
                } else if (activeRef.current.includes('HNX')) {
                    subcribeFunct(null, 'UNSUB', 'HNX', ['MKT_TOP', 'HNX'])
                } else subcribeFunct(null, 'UNSUB', 'HSX', ['MKT_TOP', 'HSX'])
                activeRef.current = newValue

                if (activeRef.current.includes('HNXUpcomIndex')) {
                    subcribeFunct(null, 'SUB', 'UPC', ['MKT_TOP', 'UPC'])
                } else if (activeRef.current.includes('HNX')) {
                    subcribeFunct(null, 'SUB', 'HNX', ['MKT_TOP', 'HNX'])
                } else subcribeFunct(null, 'SUB', 'HSX', ['MKT_TOP', 'HSX'])

                if (glb_sv.LIST_GET_INDEX_MSG.includes(activeRef.current)) {
                    throttled.current(activeRef.current)
                } else subcribeFunct(null, 'INDEX_MSG', activeRef.current)
            },
            400,
            value
        )
    }

    const navigateIndexInfo = () => {
        subListIndex.current = false
        history.push(routes.INDEX_INFO.path, { indexCode: activeRef.current })
    }

    const navigateStockInfo = (stock) => {
        history.push(routes.STOCK_INFO.path, { stockCode: stock })
    }

    const navigateAllTopMarket = (title, type, act) => {
        // navigation.navigate(Screens.TOP_MARKET_DETAIL, { title, type, active: act })
    }

    const RowTopMkt = useCallback(
        ({ item }) => {
            return (
                <TopMarket
                    key={item}
                    type={item}
                    active={active}
                    navigateStockInfo={navigateStockInfo}
                    navigateAllTopMarket={navigateAllTopMarket}
                    pushTop={pushTop}
                    listTopMkt={listTopMkt}
                />
            )
        },
        [active, listTopMkt]
    )

    const pushTop = (type) => {
        const newList = listTopMkt.filter((e) => e !== type)
        newList.unshift(type)
        setListTopMkt(newList)
        localStorage.setItem(STORE_KEY.LIST_TOP_MARKET, JSON.stringify(newList))

        glb_sv.listViewableTopMrk.forEach((e) => {
            if (e.isViewable) {
                glb_sv.eventMarket.next({ type: eventList.MKT_TOP, MsgKey: e.item })
            }
        })
    }

    return (
        <>
            <HeaderMarket activeSelect={activeSelect} setActiveSelect={setActiveSelect} setIndexCd={setIndexCd} />
            <ScrollView className="flex flex-1 flex-direction-column">
                {activeSelect['market_info_tab'] && <MarketTrading active={indexCd} />}
                {activeSelect['list_fav'] && <PortfolioLayout getStkList={(index) => getStkList(index)} />}
                {activeSelect['market_cap'] && <MarketCap />}
            </ScrollView>
        </>
    )
}

const UI = {
    chart: {
        height: 260,
        justifyContent: 'center',
        marginHorizontal: dimensions.moderate(16, 0.3),
        borderRadius: 8,
        marginTop: dimensions.halfVerticalIndent,
        marginBottom: dimensions.verticalIndent,
        marginLeft: dimensions.indent,
        marginRight: dimensions.indent,
    },
}
