										<Text style={{ color: 'white', fontSize: 9, fontWeight: fontWeights.semiBold, position: 'absolute', left: dimensions.halfIndent, zIndex: 10 }}>{bid_ask_ratio.current.bid ? bid_ask_ratio.current.bid + '%' : ''}</Text>
