import { Empty, notification, Select } from 'antd'
import moment from 'moment'
import { StoreContext } from '../../../store'
import React, { useContext, useEffect, useRef, useState } from 'react'
import { useTranslation } from 'react-i18next'
// import { Button, InputSelect } from '../../../components/basic-component'
import { InputSelect } from '../../../basic-components'
import { StoreTrading } from '../../../store-trading'
import { FormatNumber, glb_sv, reqFunct, sendRequest } from '../../../utils'
import { openStockDetail } from '../../../utils/openStockDetail'
import HeaderComponent from '../../../components/header'
import HeaderList from '../../../components/trading-component/header-list'
import { RowTitleGroup, RowData, ButtonCustom, RowTableData, ColTableData } from "../../../components/trading-component";
import {useHistory} from 'react-router-dom'
//import ExportExcel from '../../basic-component/export-excel'
const { Option } = Select
const ServiceInfo = {
    GET_LIST_RIGHT_TYPE: {
        reqFunct: reqFunct.GET_LIST_RIGHT_TYPE, //Lấy DS loại quyền
        // svInputPrm.WorkerName = 'ALTqRights';
        // svInputPrm.ServiceName = 'ALTqRights_Common';
        WorkerName: 'FOSqRights',
        ServiceName: 'FOSqRights_Common',
        ClientSentTime: '0',
        Operation: 'Q',
    },
    GET_LIST_RIGHT_TYPE_REGISTER: {
        reqFunct: reqFunct.GET_LIST_RIGHT_TYPE_REGISTER, //Lấy DS Kết quả
        // svInputPrm.WorkerName = 'ALTqRights01';
        // svInputPrm.ServiceName = 'ALTqRights01_OnlineRightsInfo_1';
        // WorkerName: 'FOSqRights01',
        // ServiceName: 'FOSqRights01_OnlineRightsInfo_1',
        WorkerName: 'FOSqRights',
        ServiceName: 'FOSqRights_OnlineRightsInfo_1',
        ClientSentTime: '0',
        Operation: 'Q',
    },
}
const RightToBuy = ({}) => {
    const history=useHistory()
    const { styles } = useContext(StoreContext)
    const { t } = useTranslation()
    const { userInfo, setUserInfo } = useContext(StoreTrading)
    const [listRightType, setListRightType] = useState([])
    const [valueBankStock, setValueBankStock] = useState('')
    const [valueRightType, setValueRightType] = useState('%')
    const [listRightToBuy, setListRightToBuy] = useState([])
    const [nameStockCode, setNameStockCode] = useState(t('choose_symbol_trading'))
    const [labelValueRightType, setLabelValueRightType] = useState(t('common_all'))
    const [checkIcon, setCheckIcon] = useState(false)
    const selectStockCodeRef = useRef(null)
    const selectTypeRef = useRef(null)
    const jsonListRightType = useRef([])
    const jsonListRightToBuy = useRef([])
    useEffect(() => {
        getListRightType()
        selectStockCodeRef.current.focus()
    }, [userInfo.actn_curr])

    useEffect(() => {}, [userInfo.actn_curr])

    const getExchanges = (U10) => {
        switch (U10) {
            case '01':
                return 'HOSE'
                break
            case '03':
                return 'HNX'
                break
            case '05':
                return 'UPCOM'
                break
            default:
                break
        }
    }

    const getListRightType = () => {
        const inputParams = ['02', 'rgt_tp', '%']
        sendRequest(ServiceInfo.GET_LIST_RIGHT_TYPE, inputParams, handleGetListRightType)
    }

    const handleGetListRightType = (reqInfoMap, message) => {
        jsonListRightType.current = []
        if (Number(message['Result']) === 0) {
            setListRightType([])
            return
        } else {
            let jsondata = []
            try {
                jsondata = JSON.parse(message['Data'])
            } catch (err) {
                jsonListRightType.current = null
                setListRightType([])
                return
            }
            jsonListRightType.current = jsonListRightType.current.concat(jsondata)
            if (Number(message['Packet']) <= 0) {
                const allStock = {
                    checked: false,
                    label: t('common_all'),
                    value: {
                        c0: '%',
                        c1: t('common_all'),
                    },
                }
                const rightTypeList = jsonListRightType.current.map((item) => {
                    const label = item.c1
                    const value = item
                    const checked = false
                    return { label, value, checked }
                })
                rightTypeList.unshift(allStock)
                setListRightType(rightTypeList)
            }
        }
    }
    const formatTimeNull = (timeData) => {
        return !timeData.trim() ? null : moment(timeData, 'DDMMYYYY').format('DD/MM/YYYY')
    }
    const getListRightRegister = (valueBankStock = '', valueRightType = '%') => {
        const inputParams = [valueBankStock, valueRightType]
        sendRequest(ServiceInfo.GET_LIST_RIGHT_TYPE_REGISTER, inputParams, handleGetListRightRegister)
    }

    const handleGetListRightRegister = (reqInfoMap, message) => {
        jsonListRightToBuy.current = []
        if (Number(message['Result']) === 0) {
            setListRightToBuy([])
            return
        } else {
            let jsondata = []
            try {
                jsondata = message['Data'] ? JSON.parse(message['Data']) : []
            } catch (err) {
                jsonListRightToBuy.current = null
                setListRightToBuy([])
                return
            }
            jsonListRightToBuy.current = jsonListRightToBuy.current.concat(jsondata)
            if (Number(message['Packet']) <= 0) {
                setTimeout(() => {
                    setCheckIcon(false)
                }, 500)
                setListRightToBuy(jsonListRightToBuy.current)
            }
        }
    }
    // const convertDataListRightToBuy = (dataList) => {
    //     const result = dataList.map((data, index) => {
    //         data.key = index + 1
    //         return data
    //     })
    //     setListRightToBuy(result)
    // }
    const headersCSV = [
        { label: t('common_index'), key: 'common_index' },
        { label: t('right_type'), key: 'right_type' },
        { label: t('date_trading_not_right'), key: 'date_trading_not_right' },
        { label: t('record_date'), key: 'record_date' },
        { label: t('exercise_date'), key: 'exercise_date' },
        { label: t('content'), key: 'content' },
    ]
    const dataCSV = () => {
        const result = listRightToBuy.map((item, index) => {
            const data = item
            item = {}
            item['common_index'] = index + 1
            item['right_type'] = data.c33
            item['date_trading_not_right'] = formatTimeNull(data.c29)
            item['record_date'] = formatTimeNull(data.c2)
            item['exercise_date'] = !!formatTimeNull(data.c21)
                ? formatTimeNull(data.c21)
                : !!formatTimeNull(data.c24)
                ? formatTimeNull(data.c24)
                : formatTimeNull(data.c2)
            item['content'] = data.c31
            return item
        })
        return result
    }
    const ViewLastTransfer = ({ item }) => {
        return (
            <>
                <RowTableData
                   onClick={() => history.push('/stock/detail-right-info', { data: item })}
                //  onPress={() => navigation.navigate(Screens.DETAI_RIGHT, { data: item, stockData: stockCode })}
                 >
                    <ColTableData
                        text={item.c33}
                        text2={item.c34}
                        colSpan={12}
                    />

                    <ColTableData
                        text={moment(item.c29, "DDMMYYYY").format('DD/MM/YYYY')}
                        colSpan={12}
                        textAlign='right'
                    />
                </RowTableData>
            </>
        );
    };
    return (
        <div className=''>
            <HeaderComponent title={t('title_rights_info')} transparent titleAlgin='start' colorTitle={styles.PRIMARY__CONTENT__COLOR} />
            <div className=''>
                <div className='ml-2 mr-2'>
                    <InputSelect
                        refs={selectStockCodeRef}
                        placeholder={t('choose_symbol_trading')}
                        dataSelect={glb_sv.mrk_stklist}
                        renderSuggestion={(item) => item.t55 + ' - ' + getExchanges(item.U10) + ' - ' + item.U9}
                        keyOption='t55'
                        onSelect={(value, selected) => {
                            setNameStockCode(selected.selected.t55)
                            setValueBankStock(selected.selected.t55)
                            getListRightRegister(selected.selected.t55, valueRightType)
                            selectTypeRef.current.focus()
                        }}
                        value={nameStockCode}
                        inputStyle={{ width: '100%' }}
                    />
                </div>
                <div className='ml-2 mr-2'>
                    <InputSelect
                        refs={selectTypeRef}
                        keyOption='label'
                        dataSelect={listRightType}
                        onSelect={(value, selected) => {
                            setValueRightType(selected?.selected?.value?.c0)
                            setLabelValueRightType(value)
                            getListRightRegister(valueBankStock, selected?.selected?.value?.c0)
                        }}
                        value={labelValueRightType}
                        inputStyle={{ width: '100%' }}
                    />
                </div>
                <div className="">
                    <HeaderList typeHeader='RIGHT_INFO' />
                </div>
                <div className="ml-2 mr-2">
                {' '}
                {listRightToBuy.map((item, i) => (
                    <ViewLastTransfer key={i} item={item} />
                ))}
            </div>
            </div>
        </div>
    )
}
export default RightToBuy
