import { StickyContainer } from 'react-sticky';
import { Button } from 'antd';
import { ScrollView } from 'basic-components';
import HeaderTop from 'components/market-tab/TopMarket/HeaderTop';
import OptionsTop from 'components/market-tab/TopMarket/OptionsTop';
import RowTop from 'components/market-tab/TopMarket/RowTop';
import React, { useCallback, useContext, useEffect, useRef, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { StoreContext } from 'store';
import { dimensions, fontSizes } from 'styles';
import { eventList, FormatNumber, glb_sv, sendRequest, STORE_KEY } from 'utils';
import { RealTimeHandler, subcribeFunctStream } from 'utils/service/subcribe_function';
import { useHistory } from 'react-router-dom'

const ServiceInfo = {
    GetLiquidityData: {
        WorkerName: 'FOSqMkt02',
        ServiceName: 'FOSqMkt02_MktInfo',
        Operation: 'Q',
    },
    GET_MARKET_TOP: {
        WorkerName: 'FOSqMkt02',
        ServiceName: 'FOSqMkt02_MktInfo',
        Operation: 'Q',
    },
    GetHistAverage5Day: {
        WorkerName: 'FOSqMkt03',
        ServiceName: 'FOSqMkt03_HistTrading',
        Operation: 'Q',
    },
}

const MarketTrading = ({ active }) => {
    const { t } = useTranslation();
    const { styles, fractionPrice } = useContext(StoreContext)
    const [activeOtp, setActiveOtp] = useState('1D')
    const activeOtpRef = useRef('1D')
    const [type, setType] = useState(glb_sv.TYPE_TOP_MARKET)
    const dataRef = useRef([])
    const [typeTop, setTypeTop] = useState(glb_sv.TYPE_TOP_MARKET)

    const [dataTopValue, setDataTopValue] = useState([]);
    const [dataTopVolume, setDataTopVolume] = useState([]);
    const [dataTopFrg, setDataTopFrg] = useState([]);
    const [dataTopInc, setDataTopInc] = useState([]);
    const [dataTopDec, setDataTopDec] = useState([]);
    const [defaultKey, setDefaultKey] = useState(0);

    const [, updateState] = useState();
    const forceUpdate = useCallback(() => updateState({}), []);
    const timeoutChangeTabRef = useRef(null);
    const MarketRealTime = new RealTimeHandler('MARKET_VIEW')

    const history = useHistory()
    const listTimeHis = ['1W', '1M', '3M', '6M', '1Y', '2Y', '3Y'];

    useEffect(() => {
        if (activeOtpRef.current === '1D') {
            if (active.includes('HNXUpcomIndex')) {
                MarketRealTime.unsubscribe({
                    topic: [glb_sv.TYPE_TOP_MARKET],
                    value: ['UPC']
                })
                MarketRealTime.subscribe({
                    topic: [glb_sv.TYPE_TOP_MARKET],
                    value: ['UPC']
                })
            } else if (active.includes('HNX')) {
                MarketRealTime.unsubscribe({
                    topic: [glb_sv.TYPE_TOP_MARKET],
                    value: ['HNX']
                })
                MarketRealTime.subscribe({
                    topic: [glb_sv.TYPE_TOP_MARKET],
                    value: ['HNX']
                })
            } else {
                MarketRealTime.unsubscribe({
                    topic: [glb_sv.TYPE_TOP_MARKET],
                    value: ['HSX']
                })
                MarketRealTime.subscribe({
                    topic: [glb_sv.TYPE_TOP_MARKET],
                    value: ['HSX']
                })
            }

            setDataTopValue([...glb_sv.lastDataMarketTop.TOP_VAL_UP])
            setDataTopVolume([...glb_sv.lastDataMarketTop.TOP_QTY_UP])
            setDataTopFrg([...glb_sv.lastDataMarketTop.FRG_VAL_UP])
            setDataTopInc([...glb_sv.lastDataMarketTop.TOP_PRI_UP])
            setDataTopDec([...glb_sv.lastDataMarketTop.TOP_PRI_DOWN])
        } else {
            setDataTopValue([])
            setDataTopVolume([])
            setDataTopFrg([])
            setDataTopInc([])
            setDataTopDec([])
            if (listTimeHis.includes(activeOtpRef.current)) {
                const index = active.includes('HSX') ? '01' : active.includes('HNXUpcomIndex') ? '05' : '03';
                let typee = 'TU';
                if (glb_sv.TYPE_TOP_MARKET === 'TOP_PRI_DOWN') typee = 'TD';
                if (glb_sv.TYPE_TOP_MARKET === 'TOP_QTY_UP') typee = 'TV';
                if (glb_sv.TYPE_TOP_MARKET === 'TOP_VAL_UP') typee = 'TA';
                if (glb_sv.TYPE_TOP_MARKET === 'FRG_VAL_UP') typee = 'FA';
                if (glb_sv.dataHisMrktop[activeOtpRef.current][glb_sv.TYPE_TOP_MARKET + index]) {
                    const newData = glb_sv.dataHisMrktop[activeOtpRef.current][glb_sv.TYPE_TOP_MARKET + index].map(item => {
                        return {
                            S: item.c3,
                            C: item.c6 * 1000,
                            CHGR: item.c11,
                            TVOL: item.c8,
                            TVAL: item.c9,
                            O: item.c4 * 1000,
                            FBA: item.c16 * 1E9,
                            FSA: item.c17 * 1E9
                        }
                    })
                    if (glb_sv.TYPE_TOP_MARKET === 'TOP_VAL_UP') setDataTopValue(newData)
                    if (glb_sv.TYPE_TOP_MARKET === 'TOP_QTY_UP') setDataTopVolume(newData)
                    if (glb_sv.TYPE_TOP_MARKET === 'FRG_VAL_UP') setDataTopFrg(newData)
                    if (glb_sv.TYPE_TOP_MARKET === 'TOP_PRI_UP') setDataTopInc(newData)
                    if (glb_sv.TYPE_TOP_MARKET === 'TOP_PRI_DOWN') setDataTopDec(newData)
                    return;
                }
                const inval = ['1', index, activeOtpRef.current, typee, '-1'];
                sendRequest(ServiceInfo.GET_MARKET_TOP, inval, handleGetMarketTop, true, handleGetMarketTopTimeout)
                dataRef.current = [];
            }
        }

        const eventMarket = glb_sv.eventMarket.subscribe(msg => {
            if (msg.type === eventList.MKT_TOP && glb_sv.TYPE_TOP_MARKET === msg.TOP && msg.EX === convertIndex(active)) {
                if (activeOtpRef.current !== '1D') return;
                const newData = msg.ITEMS;
                if (glb_sv.TYPE_TOP_MARKET === 'TOP_VAL_UP') setDataTopValue([...newData])
                if (glb_sv.TYPE_TOP_MARKET === 'TOP_QTY_UP') setDataTopVolume([...newData])
                if (glb_sv.TYPE_TOP_MARKET === 'FRG_VAL_UP') setDataTopFrg([...newData])
                if (glb_sv.TYPE_TOP_MARKET === 'TOP_PRI_UP') setDataTopInc([...newData])
                if (glb_sv.TYPE_TOP_MARKET === 'TOP_PRI_DOWN') setDataTopDec([...newData])

            }

            if (msg.type === eventList.RECONNECT_MARKET) {
                if (activeOtpRef.current === '1D') {
                } else {
                    setDataTopValue([])
                    setDataTopVolume([])
                    setDataTopFrg([])
                    setDataTopInc([])
                    setDataTopDec([])
                    if (listTimeHis.includes(activeOtpRef.current)) {
                        const index = active.includes('HSX') ? '01' : active.includes('HNXUpcomIndex') ? '05' : '03';
                        let typee = 'TU';
                        if (glb_sv.TYPE_TOP_MARKET === 'TOP_PRI_DOWN') typee = 'TD';
                        if (glb_sv.TYPE_TOP_MARKET === 'TOP_QTY_UP') typee = 'TV';
                        if (glb_sv.TYPE_TOP_MARKET === 'TOP_VAL_UP') typee = 'TA';
                        if (glb_sv.TYPE_TOP_MARKET === 'FRG_VAL_UP') typee = 'FA';
                        if (glb_sv.dataHisMrktop[activeOtpRef.current][type + index]) {
                            const newData = glb_sv.dataHisMrktop[activeOtpRef.current][type + index].map(item => {
                                return {
                                    S: item.c3,
                                    C: item.c6 * 1000,
                                    CHGR: item.c11,
                                    TVOL: item.c8,
                                    TVAL: item.c9,
                                    O: item.c4 * 1000,
                                    FBA: item.c16 * 10E9,
                                    FSA: item.c17 * 10E9
                                }
                            })
                            if (glb_sv.TYPE_TOP_MARKET === 'TOP_VAL_UP') setDataTopValue(newData)
                            if (glb_sv.TYPE_TOP_MARKET === 'TOP_QTY_UP') setDataTopVolume(newData)
                            if (glb_sv.TYPE_TOP_MARKET === 'FRG_VAL_UP') setDataTopFrg(newData)
                            if (glb_sv.TYPE_TOP_MARKET === 'TOP_PRI_UP') setDataTopInc(newData)
                            if (glb_sv.TYPE_TOP_MARKET === 'TOP_PRI_DOWN') setDataTopDec(newData)
                            return;
                        }
                        const inval = ['1', index, activeOtpRef.current, typee, '-1'];
                        sendRequest(ServiceInfo.GET_MARKET_TOP, inval, handleGetMarketTop, true, handleGetMarketTopTimeout)
                        dataRef.current = [];
                    }
                }
            }

            if (msg.type === eventList.RESET_DATA) {
                setDataTopValue([])
                setDataTopVolume([])
                setDataTopFrg([])
                setDataTopInc([])
                setDataTopDec([])
            }
        });
        return () => {
            eventMarket.unsubscribe();
        };
    }, [active])

    const convertIndex = value => {
        if (value === 'HNXUpcomIndex') {
            return 'UPC'
        } else if (value === 'HNXIndex') {
            return 'HNX'
        } else {
            return 'HSX'
        }
    }

    const changeOtpion = useCallback((value) => {
        setActiveOtp(value);
        setDataTopValue([])
        setDataTopVolume([])
        setDataTopFrg([])
        setDataTopInc([])
        setDataTopDec([])


        activeOtpRef.current = value;
        if (timeoutChangeTabRef.current) clearTimeout(timeoutChangeTabRef.current)
        timeoutChangeTabRef.current = setTimeout(() => {
            if (value === '1D') {
                setDataTopValue(glb_sv.lastDataMarketTop.TOP_VAL_UP)
                setDataTopVolume(glb_sv.lastDataMarketTop.TOP_QTY_UP)
                setDataTopFrg(glb_sv.lastDataMarketTop.FRG_VAL_UP)
                setDataTopInc(glb_sv.lastDataMarketTop.TOP_PRI_UP)
                setDataTopDec(glb_sv.lastDataMarketTop.TOP_PRI_DOWN)
                // console.log('subsribe active 1', active)
                if (active.includes('HNXUpcomIndex')) {
                    MarketRealTime.subscribe({
                        topic: [glb_sv.TYPE_TOP_MARKET],
                        value: ['UPC']
                    })
                } else if (active.includes('HNX')) {
                    MarketRealTime.subscribe({
                        topic: [glb_sv.TYPE_TOP_MARKET],
                        value: ['HNX']
                    })
                } else {
                    MarketRealTime.subscribe({
                        topic: [glb_sv.TYPE_TOP_MARKET],
                        value: ['HSX']
                    })
                }
            } else {
                // console.log('subsribe active 2', active)
                if (active.includes('HNXUpcomIndex')) {
                    MarketRealTime.unsubscribe({
                        topic: [glb_sv.TYPE_TOP_MARKET],
                        value: ['UPC']
                    })
                } else if (active.includes('HNX')) {
                    MarketRealTime.unsubscribe({
                        topic: [glb_sv.TYPE_TOP_MARKET],
                        value: ['HNX']
                    })
                } else {
                    MarketRealTime.unsubscribe({
                        topic: [glb_sv.TYPE_TOP_MARKET],
                        value: ['HSX']
                    })
                }
                const index = active.includes('HSX') ? '01' : active.includes('HNXUpcomIndex') ? '05' : '03';
                let typee = 'TU';
                if (glb_sv.TYPE_TOP_MARKET === 'TOP_PRI_DOWN') typee = 'TD';
                if (glb_sv.TYPE_TOP_MARKET === 'TOP_QTY_UP') typee = 'TV';
                if (glb_sv.TYPE_TOP_MARKET === 'TOP_VAL_UP') typee = 'TA';
                if (glb_sv.TYPE_TOP_MARKET === 'FRG_VAL_UP') typee = 'FA';
                if (glb_sv.dataHisMrktop[value][glb_sv.TYPE_TOP_MARKET + index]) {
                    const newData = glb_sv.dataHisMrktop[value][glb_sv.TYPE_TOP_MARKET + index].map(item => {
                        return {
                            S: item.c3,
                            C: item.c6 * 1000,
                            CHGR: item.c11,
                            TVOL: item.c8,
                            TVAL: item.c9,
                            O: item.c4 * 1000,
                            FBA: item.c16 * 1E9,
                            FSA: item.c17 * 1E9
                        }
                    })
                    if (glb_sv.TYPE_TOP_MARKET === 'TOP_VAL_UP') setDataTopValue(newData)
                    if (glb_sv.TYPE_TOP_MARKET === 'TOP_QTY_UP') setDataTopVolume(newData)
                    if (glb_sv.TYPE_TOP_MARKET === 'FRG_VAL_UP') setDataTopFrg(newData)
                    if (glb_sv.TYPE_TOP_MARKET === 'TOP_PRI_UP') setDataTopInc(newData)
                    if (glb_sv.TYPE_TOP_MARKET === 'TOP_PRI_DOWN') setDataTopDec(newData)
                    return;
                }
                const inval = ['1', index, value, typee, '-1'];
                sendRequest(ServiceInfo.GET_MARKET_TOP, inval, handleGetMarketTop, true, handleGetMarketTopTimeout)
                dataRef.current = [];
            }
        }, 500);
    }, [])


    const handleGetMarketTopTimeout = () => {
    }

    const handleGetMarketTop = (reqInfoMap, message) => {
        if (Number(message['Result']) === 0) {
            return;
        } else {
            let jsondata = [];
            try {
                jsondata = message['Data'] ? JSON.parse(message['Data']) : [];
            } catch (err) {
            }
            dataRef.current = dataRef.current.concat(jsondata);
            if (Number(message['Packet']) <= 0) {
                // console.log('hanldeDataHistoryMarketTop', dataRef.current)
                hanldeDataHistoryMarketTop(dataRef.current)
            }
        }
    }

    const hanldeDataHistoryMarketTop = data => {
        const newData = data.map(item => {
            return {
                S: item.c3,
                C: item.c6 * 1000,
                CHGR: item.c11,
                TVOL: item.c8,
                TVAL: item.c9,
                O: item.c4 * 1000,
                FBA: item.c16 * 1E9,
                FSA: item.c17 * 1E9
            }
        })
        if (glb_sv.TYPE_TOP_MARKET === 'TOP_VAL_UP') setDataTopValue(newData)
        if (glb_sv.TYPE_TOP_MARKET === 'TOP_QTY_UP') setDataTopVolume(newData)
        if (glb_sv.TYPE_TOP_MARKET === 'FRG_VAL_UP') setDataTopFrg(newData)
        if (glb_sv.TYPE_TOP_MARKET === 'TOP_PRI_UP') setDataTopInc(newData)
        if (glb_sv.TYPE_TOP_MARKET === 'TOP_PRI_DOWN') setDataTopDec(newData)
        const index = active.includes('HSX') ? '01' : active.includes('HNXUpcomIndex') ? '05' : '03';
        glb_sv.dataHisMrktop[activeOtpRef.current][glb_sv.TYPE_TOP_MARKET + index] = data.slice();
    }

    const changeTab = (i) => {
        setDataTopValue([])
        setDataTopVolume([])
        setDataTopFrg([])
        setDataTopInc([])
        setDataTopDec([])
        setDefaultKey(i)

        let tab = 'TOP_VAL_UP';
        if (i == 1) tab = 'TOP_QTY_UP';
        if (i == 2) tab = 'FRG_VAL_UP';
        if (i == 3) tab = 'TOP_PRI_UP';
        if (i == 4) tab = 'TOP_PRI_DOWN';
        if (glb_sv.TYPE_TOP_MARKET == tab) return;

        setTypeTop(tab)
        glb_sv.TYPE_TOP_MARKET = tab;

        if (active.includes('HNXUpcomIndex')) {
            MarketRealTime.unsubscribe({
                topic: [glb_sv.TYPE_TOP_MARKET],
                value: ['UPC']
            })
        } else if (active.includes('HNX')) {
            MarketRealTime.unsubscribe({
                topic: [glb_sv.TYPE_TOP_MARKET],
                value: ['HNX']
            })
        } else {
            MarketRealTime.unsubscribe({
                topic: [glb_sv.TYPE_TOP_MARKET],
                value: ['HSX']
            })
        }

        setType(tab);

        localStorage.setItem(STORE_KEY.TYPE_TOP_MARKET, tab);

        // console.log('activeOtpRef', activeOtpRef.current)
        if (activeOtpRef.current === '1D') {
            if (active.includes('HNXUpcomIndex')) {
                MarketRealTime.subscribe({
                    topic: [glb_sv.TYPE_TOP_MARKET],
                    value: ['UPC']
                })
            } else if (active.includes('HNX')) {
                MarketRealTime.subscribe({
                    topic: [glb_sv.TYPE_TOP_MARKET],
                    value: ['HNX']
                })
            } else {
                MarketRealTime.subscribe({
                    topic: [glb_sv.TYPE_TOP_MARKET],
                    value: ['HSX']
                })
            }
        } else {
            setDataTopValue([])
            setDataTopVolume([])
            setDataTopFrg([])
            setDataTopInc([])
            setDataTopDec([])
            if (listTimeHis.includes(activeOtpRef.current)) {
                const index = active.includes('HSX') ? '01' : active.includes('HNXUpcomIndex') ? '05' : '03';
                let typee = 'TU';
                if (tab === 'TOP_PRI_DOWN') typee = 'TD';
                if (tab === 'TOP_QTY_UP') typee = 'TV';
                if (tab === 'TOP_VAL_UP') typee = 'TA';
                if (tab === 'FRG_VAL_UP') typee = 'FA';
                if (glb_sv.dataHisMrktop[activeOtpRef.current][tab + index]) {
                    const newData = glb_sv.dataHisMrktop[activeOtpRef.current][tab + index].map(item => {
                        return {
                            S: item.c3,
                            C: item.c6 * 1000,
                            CHGR: item.c11,
                            TVOL: item.c8,
                            TVAL: item.c9,
                            O: item.c4 * 1000,
                            FBA: item.c16 * 1E9,
                            FSA: item.c17 * 1E9
                        }
                    })

                    // console.log('New Data')
                    if (glb_sv.TYPE_TOP_MARKET === 'TOP_VAL_UP') setDataTopValue(newData)
                    if (glb_sv.TYPE_TOP_MARKET === 'TOP_QTY_UP') setDataTopVolume(newData)
                    if (glb_sv.TYPE_TOP_MARKET === 'FRG_VAL_UP') setDataTopFrg(newData)
                    if (glb_sv.TYPE_TOP_MARKET === 'TOP_PRI_UP') setDataTopInc(newData)
                    if (glb_sv.TYPE_TOP_MARKET === 'TOP_PRI_DOWN') setDataTopDec(newData)
                    return;
                }
                const inval = ['1', index, activeOtpRef.current, typee, '-1'];
                sendRequest(ServiceInfo.GET_MARKET_TOP, inval, handleGetMarketTop, true, handleGetMarketTopTimeout)
                dataRef.current = [];
            }
        }
    }

    const getData = () => {
        if (glb_sv.TYPE_TOP_MARKET == 'TOP_VAL_UP') {
            return dataTopValue
        }
        if (glb_sv.TYPE_TOP_MARKET == 'TOP_QTY_UP') {
            return dataTopVolume
        }
        if (glb_sv.TYPE_TOP_MARKET == 'FRG_VAL_UP') {
            return dataTopFrg
        }

        if (glb_sv.TYPE_TOP_MARKET == 'TOP_PRI_UP') {
            return dataTopInc
        }

        if (glb_sv.TYPE_TOP_MARKET == 'TOP_PRI_DOWN') {
            return dataTopDec
        }
        return []
    }

    const navigateStockInfo = (item) => {
        history.push('/stock-info', { stockCode: item })
    }

    return <div style={{ paddingRight: dimensions.indent, paddingLeft: dimensions.indent, marginBottom: dimensions.halfVerticalIndent }}>
        <StickyContainer className='sticky-container'>
            <div className='pt-2' style={{ backgroundColor: 'var(--PRIMARY__BG__COLOR)', position: 'sticky', top: 0 }}>
                <div className='title-market-trading market__top--tab-view'>
                    <ScrollView horizontal style={{ marginTop: dimensions.vertical(4, 0.3), marginBottom: dimensions.vertical(4), overflow: 'scroll' }}>
                        <div className='flex'>
                            <Button onClick={() => changeTab(0)} type="text" className='flex' style={{ backgroundColor: 'var(--PRIMARY__BG__COLOR)', marginRight: 10, padding: 0, fontSize: fontSizes.normal, color: defaultKey === 0 ? 'var(--ICON__COLOR)' : 'var(--PLACEHODLER__COLOR)', fontWeight: 400 }}>
                                {t('top_10_stocks_value_trade')}
                            </Button>
                            <Button onClick={() => changeTab(1)} type="text" className='flex' style={{ backgroundColor: 'var(--PRIMARY__BG__COLOR)', marginRight: 10, padding: 0, fontSize: fontSizes.normal, color: defaultKey === 1 ? 'var(--ICON__COLOR)' : 'var(--PLACEHODLER__COLOR)', fontWeight: 400 }}>
                                {t('top_10_stocks_shares_trade')}
                            </Button>
                            <Button onClick={() => changeTab(2)} type="text" className='flex' style={{ backgroundColor: 'var(--PRIMARY__BG__COLOR)', marginRight: 10, padding: 0, fontSize: fontSizes.normal, color: defaultKey === 2 ? 'var(--ICON__COLOR)' : 'var(--PLACEHODLER__COLOR)', fontWeight: 400 }}>
                                {t('top_value_frg')}
                            </Button>
                            <Button onClick={() => changeTab(3)} type="text" className='flex' style={{ backgroundColor: 'var(--PRIMARY__BG__COLOR)', marginRight: 10, padding: 0, fontSize: fontSizes.normal, color: defaultKey === 3 ? 'var(--ICON__COLOR)' : 'var(--PLACEHODLER__COLOR)', fontWeight: 400 }}>
                                {t('top_10_stocks_incre')}
                            </Button>
                            <Button onClick={() => changeTab(4)} type="text" className='flex' style={{ backgroundColor: 'var(--PRIMARY__BG__COLOR)', marginRight: 10, padding: 0, fontSize: fontSizes.normal, color: defaultKey === 4 ? 'var(--ICON__COLOR)' : 'var(--PLACEHODLER__COLOR)', fontWeight: 400 }}>
                                {t('top_10_stocks_decre')}
                            </Button>
                        </div>
                    </ScrollView>
                </div>
                <div className="ant-divider" role="separator"></div>
                <ScrollView horizontal style={{ paddingTop: dimensions.vertical(4, 0.3), paddingBottom: dimensions.vertical(4), overflow: 'scroll' }}>
                    <OptionsTop onChange={() => changeOtpion('1D')} title={'1 ' + t('day')} active styles={styles} active={activeOtp === '1D'} />
                    <OptionsTop onChange={() => changeOtpion('1W')} title={'5 ' + t('day')} styles={styles} active={activeOtp === '1W'} />
                    <OptionsTop onChange={() => changeOtpion('1M')} title={t('one_month')} styles={styles} active={activeOtp === '1M'} />
                    <OptionsTop onChange={() => changeOtpion('3M')} title={t('three_month')} styles={styles} active={activeOtp === '3M'} />
                    <OptionsTop onChange={() => changeOtpion('6M')} title={t('six_month')} styles={styles} active={activeOtp === '6M'} />
                    <OptionsTop onChange={() => changeOtpion('1Y')} title={'52 ' + t('weeks')} styles={styles} active={activeOtp === '1Y'} />
                    <OptionsTop onChange={() => changeOtpion('2Y')} title={'2 ' + t('year')} styles={styles} active={activeOtp === '2Y'} />
                    <OptionsTop isLast={true} onChange={() => changeOtpion('3Y')} title={'3 ' + t('year')} styles={styles} active={activeOtp === '3Y'} />
                </ScrollView>
                <HeaderTop activeOtp={activeOtp} typeTop={glb_sv.TYPE_TOP_MARKET} />
            </div>
            <div>
                {getData().map(item =>
                    <RowTop
                        key={item.S}
                        item={item}
                        title={item.S} // col 1
                        activeOtp={activeOtp}
                        navigateStockInfo={navigateStockInfo}
                        typeTop={typeTop}
                        ratioValue={FormatNumber(item.CHGR, 2)} // col 2
                        valueCol3={typeTop === 'FRG_VAL_UP' ? FormatNumber(item.FBA, 2, 0, 'short') : // col 3
                            fractionPrice ? FormatNumber(item.O / 1000, 2, 1) : FormatNumber(item.O, 0, 1)}
                        value={typeTop === 'FRG_VAL_UP' ? FormatNumber(item.FSA, 2, 0, 'short') : // col 4
                            item.C === 777777710000 ? 'ATO' :
                                item.C === 777777720000 ? 'ATC' :
                                    (fractionPrice ? FormatNumber(item.C / 1000, 2, 1) : FormatNumber(item.C, 0, 1))}
                        value_market={typeTop === 'FRG_VAL_UP' ? FormatNumber(item.FBA - item.FSA, 2, 0, 'short') : // col 5
                            type !== 'TOP_VAL_UP'
                                ? FormatNumber(item.TVOL, 0, 1, 'short')
                                : FormatNumber(item.TVAL, 0, 1, 'short')}
                        styles={styles} />)}
            </div>
        </StickyContainer>
    </div>
}

export default MarketTrading