import React, { useState, useContext, useEffect } from "react";
import HeaderComponent from '../../components/header';
import { StoreContext } from '../../store';
import NotAuthView from '../../components/notAuth-view'
import { StoreTrading } from "store-trading";

/**
 * 
 * @param {String} typeUser     Loại User có thể sử dụng màn hình đó 
 * ['normal', 'broker', 'both']
 * @returns {Component}         Return Right Component went active Company have permission to access this Page|Tab|Scree|Component
 * else return a Prevention Screen and navigate to LOGIN Screen
 */
const RequiredAuth = (typeUser = 'normal') => (Component) => { // ['normal', 'broker', 'both']
    
    const CheckUser = (props) => {
        const { isBroker = false } = useContext(StoreContext)
        const { authFlag} = useContext(StoreTrading)
        if (!authFlag) {
            return <NotAuthView {...props} />
        } else {
            if (('broker' === typeUser) && isBroker) { // Nếu đúng là Broker
                console.log('Broker');
                return <Component {...props} />
            }
            if (('normal' === typeUser) && !isBroker) { // Nếu là người dùng thông thường
                console.log('Normal');
                return <Component {...props} />
            }
            if ('both' === typeUser) { // Nếu có thể dùng chưng
                console.log('Both');
                return <Component {...props} />
            }
            return <NotAuthView {...props} wrongUser />
        }
        
    }
    return CheckUser
}

export default RequiredAuth