import { routes } from "navigation/routes";
import React, { useState, useContext, useEffect } from "react";
import { useTranslation } from 'react-i18next';
import { StoreContext } from '../../../store';
import { fontSizes, dimensions as dm, fontWeights } from '../../../styles';
import { glb_sv, reqFunct, dataCryption, Screens, sendRequest } from '../../../utils'
import FormatNumber from '../../../utils/formatNumber/FormatNumber'
import { useHistory } from "react-router-dom";
import { Input, Row, Form, InputNumber, Button } from 'antd'
import Text from "antd/lib/typography/Text";
import Title from "antd/lib/typography/Title";
import ModalSelection from '../../../components/list-selections'

// Khai báo ServiceInfo (nếu có)
const ServiceInfo = {
    getListBankTran: {
        reqFunct: reqFunct.GET_LIST_BANK_TRANS, //Lấy DS ngân hàng chuyển
        WorkerName: 'ALTqAccount',
        ServiceName: 'ALTqAccount_Common',
        ClientSentTime: '0',
        Operation: 'Q',
        // TestInput: ["07", "%"]
    },
    getListActReceive: {
        reqFunct: reqFunct.GET_LIST_ACT_RECEIVE, // Lấy DS số tài khoản hưởng (CTCK)
        WorkerName: 'ALTqCash',
        ServiceName: 'ALTqCash_Common',
        ClientSentTime: '0',
        Operation: 'Q',
        // TestInput: ["02", "00"]
    },
}
// Khai báo component
const StepTwo = ({
    bankTransfer,
    setBankTransfer,
    bankTransferAct,
    setBankTransferAct,
    actReceive,
    setActReceive,
    bankReceive,
    setBankReceive,
    amoutTransfer,
    setAmoutTransfer,
    switchStep
}) => {
    const { t } = useTranslation();
    const { styles } = useContext(StoreContext);
    let history = useHistory()
    const [isOpenModalSelection, setIsOpenModalSelection] = useState(false);
    const [infoSelection, setInfoSelection] = useState({
        type: '',
        callback: () => { }
    });
    // -------------------------------------------   Dùng useEffect get các dữ liệu cần thiết còn thiếu
    useEffect(() => {
        if (glb_sv.ListBankTrans.length === 0) {
            getListBankTran()
        }
        if (glb_sv.ListBankReceive.length === 0) {
            getListActReceive()
        }
    }, []);

    // -------------------------------------------   Viết các hàm để set lại bussiness form state. Nên viết đúng một chuẩn duy nhất để dễ control 
    const selectBankTransfer = (value) => {
        setBankTransfer(value)
    }
    const selectBankTransferAct = (value) => {
        setBankTransferAct(value)
    }
    const selectActReceive = (value) => {
        setActReceive(value)
        setBankReceive(value)
    }
    const selectBankReceive = (value) => {
        setActReceive(value)
        setBankReceive(value)
    }
    const selectAmoutTransfer = (value) => {
        const formatValue = glb_sv.filterNumber(value)
        setAmoutTransfer(formatValue)
    }
    // -------------------------------------------   Khai báo các hàm gửi (request) lên server
    const getListBankTran = () => {
        const inputParams = ["07", "%"]
        sendRequest(ServiceInfo.getListBankTran, inputParams, handleGetListBankTran)
    }
    const getListActReceive = () => {
        const inputParams = ["02", "00"]
        sendRequest(ServiceInfo.getListActReceive, inputParams, handleGetListActReceive)
    }
    // -------------------------------------------   Khai báo các hàm nhận (handle respone) dữ liệu từ server

    const handleGetListBankTran = (reqInfoMap, message) => {
        console.log("ListBankTrans", message);
        // -- process after get result --
        let ListBankTranTemp = []
        if (Number(message['Result']) === 0) {
            return;
        } else {
            let jsondata;
            try {
                jsondata = JSON.parse(message['Data']);
            } catch (err) {
                // glb_sv.logMessage(err);
                jsondata = [];
            }
            ListBankTranTemp = ListBankTranTemp.concat(jsondata);
            if (Number(message['Packet']) <= 0) {
                const bankTransferList = ListBankTranTemp.map(item => {
                    const label = item.c1;
                    const value = item;
                    return { label, value }
                });
                glb_sv.ListBankTrans = bankTransferList
            }

        }
    }
    // ----------------------------------------------------

    const handleGetListActReceive = (reqInfoMap, message) => {
        console.log("ListBankReceive", message);
        // -- process after get result --
        let ListBankReceiveTemp = []
        if (Number(message['Result']) === 0) {
            return;
        } else {
            let jsondata;
            try {
                jsondata = JSON.parse(message['Data']);
            } catch (err) {
                jsondata = [];
            }
            ListBankReceiveTemp = ListBankReceiveTemp.concat(jsondata);
            if (Number(message['Packet']) <= 0) {
                const bankSecList = ListBankReceiveTemp.map(item => {
                    const label = item.c2 + ' - ' + item.c1;
                    const value = item;
                    return { label, value }
                });
                glb_sv.ListBankReceive = bankSecList
                // console.log('hhhhhhhhhhhhhhhhhhhhhhhh', glb_sv.ListBankReceive);
            }
        }
    }
    const onFinish = () => {
        switchStep.onFinish()
        console.log("onFinish");
    }
    const IconSelect = () => {
        return <svg width="20" height="20" viewBox="0 0 20 20" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path d="M4.375 7.1875L10 12.8125L15.625 7.1875" stroke="#24253D" strokeWidth="1.78125" strokeLinecap="round" strokeLinejoin="round" />
        </svg>

    }
    return (
        <div style={{ flex: 1, marginRight: dm.halfIndent }}>
            <div style={{ ...UI.GroupInput, borderColor: styles.PRIMARY__BORDER__COLOR }}>
                <Form
                    name="normal_login"
                    className="common__form"
                    initialValues={{ remember: true }}
                    onFinish={onFinish}
                >
                    <Row>
                        <Title level={5}>{t('Tài khoản chuyển')}</Title>
                    </Row>
                    <Form.Item
                        onClick={() => { setInfoSelection({ type: 'PICK_BANK_TRANSFER', callback: selectBankTransfer }); setIsOpenModalSelection(true); }}
                        name="pick_bank_transfer"
                        // rules={[{ required: true, message: 'Please select your bank transfer!' }]}
                    >
                        <Text className="common__form__input-label fs-normal">{t('select_bank_from')}</Text>
                        <span className="common__form__input-icon-select"><IconSelect /></span>
                        <Input
                            size='large'
                            className='input__component'
                            defaultValue={bankTransfer?.label}
                            value={bankTransfer?.label}
                            placeholder={t('select_bank_from')} />
                    </Form.Item>
                    <Form.Item
                        name="bankTransfer"
                        // rules={[{ required: true, message: 'Please input your Password!' }]}
                    >
                        <Text className="common__form__input-label fs-normal">{t('input_transfer_account_number')}</Text>
                        <Input
                            size='large'
                            className='input__component'
                            placeholder={t('input_transfer_account_number')}
                            onChange={e => selectBankTransferAct(e.target.value)}
                            defaultValue={bankTransferAct}
                            value={bankTransferAct}
                        />
                    </Form.Item>
                    <Row>
                        <Title level={5}>{t('Tài khoản nhận')}</Title>
                    </Row>
                    <Form.Item
                        onClick={() => { setInfoSelection({ type: 'PICK_BANK_RECEIVE', callback: selectBankReceive }); setIsOpenModalSelection(true); }}
                        name="chose_bank_account"
                        // rules={[{ required: true, message: 'Please input your Password!' }]}
                    >
                        <Text className="common__form__input-label fs-normal">{t('chose_bank_account')}</Text>
                        <span className="common__form__input-icon-select"><IconSelect /></span>
                        <Input
                            size='large'
                            className='input__component'
                            placeholder={t('chose_bank_account')}
                            // onChange={e => selectBankTransferAct(e.target.value)}
                            defaultValue={bankReceive?.value?.c2}
                            value={bankReceive?.value?.c2}
                        />
                    </Form.Item>
                    <Form.Item
                        onClick={() => { setInfoSelection({ type: 'PICK_ACCOUNT_RECEIVE', callback: selectActReceive }); setIsOpenModalSelection(true); }}
                        name="choose_heir_bank"
                        // rules={[{ required: true, message: 'Please input your Password!' }]}
                    >
                        <Text className="common__form__input-label fs-normal">{t('choose_heir_bank')}</Text>
                        <span className="common__form__input-icon-select"><IconSelect /></span>
                        <Input
                            size='large'
                            className='input__component'
                            placeholder={t('choose_heir_bank')}
                            // onChange={e => selectBankTransferAct(e.target.value)}
                            defaultValue={actReceive?.value?.c1}
                            value={actReceive?.value?.c1}
                        />
                    </Form.Item>
                    <Row>
                        <Title level={5}>{t('deposit_amount')}</Title>
                    </Row>
                    <Form.Item
                        name="deposit_amount"
                        // rules={[{ required: true, message: 'Please input your Password!' }]}
                    >
                        <Text className="common__form__input-label fs-normal">{t('deposit_amount')}</Text>
                        <Input
                            size='large'
                            className='input__component'
                            placeholder={t('deposit_amount')}
                            onChange={e => selectAmoutTransfer(e.target.value)}
                            defaultValue={FormatNumber(amoutTransfer, 0)}
                            value={FormatNumber(amoutTransfer, 0)}
                        />
                    </Form.Item>

                    <div style={{ ...UI.styleButton }}>
                        <Button size='large' htmlType="submit" className="login-form-button" block 
                            style={{
                                backgroundColor: 'var(--PRIMARY)', 
                                borderRadius: 8,
                                height: dm.verticalIndent * 3 }}>
                            <Text style={{ color: 'white' }}>{t('confirm').toLocaleUpperCase()}</Text>
                        </Button>                    
                    </div>
                    <div style={{ ...UI.styleButton }}>
                        <Button size='large' type='text' onClick={() => switchStep.prev()} className="login-form-button" block 
                            style={{
                                height: dm.verticalIndent * 3 }}>
                            <Text style={{}}>{t('common_Cancel').toLocaleUpperCase()}</Text>
                        </Button>
                    </div>
                </Form>
            </div>
            <ModalSelection
                isOpen={isOpenModalSelection}
                actionType={infoSelection.type}
                getSelectValue={infoSelection.callback}
                setIsOpen={setIsOpenModalSelection} />
        </div>
    )
}
const UI = {
    GroupInput: {
        borderWidth: 1,
        borderRadius: 4,
        marginY: dm.halfVerticalIndent,
        paddingX: dm.halfIndent,
    },
    styleButton: {
        marginTop: dm.verticalIndent,
        marginBottom: dm.verticalIndent,
        marginLeft: dm.moderate(4),
        marginRight: dm.moderate(4),
    }
}
export default StepTwo