
import React, { memo, useState, useEffect } from 'react';
import { useTranslation } from 'react-i18next';

import { reqFunct, glb_sv, eventList, sendRequest } from '../../utils'
import { AddStockToListFav, RemoveStockInListFav } from '../portfolio/favorite-func/favorite.action'
import { dimensions } from '../../styles';
import { Modal, Checkbox } from 'antd-mobile';
import { useHistory } from 'react-router-dom'
import { routes } from 'navigation/routes';
import AddNewFavList from '../portfolio/add-favlist-tab'
import IC_ADD_CATEGORY from "assets/images/common/ic_add_category.svg";

const ServiceInfo = {
    AddStockToFavList: {
        reqFunct: reqFunct.ADD_STOCK_TO_FAV_LIST,
        WorkerName: 'ALTxCommon01',
        ServiceName: 'ALTxCommon01_0820',
        ClientSentTime: '0',
        Operation: 'I',
    },
    removeStockFromFavList: {
        reqFunct: reqFunct.REMOVE_STOCK_IN_FAV_LIST,
        WorkerName: 'ALTxCommon01',
        ServiceName: 'ALTxCommon01_0820',
        ClientSentTime: '0',
        Operation: 'D',
    }
}

function closest(el, selector) {
    const matchesSelector = el.matches || el.webkitMatchesSelector || el.mozMatchesSelector || el.msMatchesSelector;
    while (el) {
        if (matchesSelector.call(el, selector)) {
            return el;
        }
        el = el.parentElement;
    }
    return null;
}


const AddStockFav = ({ isModalVisible, setModal, selectStock, onChangeStatus, onChangeStatusStk }) => {
    const { t } = useTranslation();
    const history = useHistory();

    const [allListFav, setAllListFav] = useState(glb_sv.allListFav)
    const [showAddFav, setShowAddFav] = useState(false);
    useEffect(() => {
        const commonEvent = glb_sv.commonEvent.subscribe(msg => {
            if (msg.type === eventList.CHANGE_LIST_FAV) {
                setAllListFav([...glb_sv.allListFav])
            }
            if (msg.type === eventList.CHANGE_LIST_ACTIVE) {
                setAllListFav([...glb_sv.allListFav]);
            }
        });

        return () => {
            commonEvent.unsubscribe();
        }
    }, [])

    function checkHidenPopupAdd() {
        setShowAddFav(false)
    }
    const handleAddStockToFavList = (reqInfoMap, message) => {
        // -- process after get result --
        if (Number(message['Result']) === 0) {
            return;
        } else {
            try {
                const current = glb_sv.allListFav.find((x) => x.c1 === reqInfoMap.inputParam[1])
                current.ListStock.push(selectStock)
                setAllListFav([...glb_sv.allListFav]);
                if (glb_sv.activeList.c1 === current.c1) {
                    glb_sv.activeList = { ...current };
                    glb_sv.commonEvent.next({ type: eventList.CHANGE_LIST_STOCK })
                } else {
                    setTimeout(() => {
                        onChangeStatus();
                    }, 100);
                }
                onChangeStatusStk && onChangeStatusStk(true)
            } catch (err) {
                console.log('error', err)
            }
        }
    }

    const handleRmStkFrFavList = (reqInfoMap, message) => {
        if (Number(message['Result']) === 0) {
            return
        } else {
            try {
                const current = glb_sv.allListFav.find((x) => x.c1 === reqInfoMap.inputParam[1])
                current.ListStock = current.ListStock.filter(e => e !== selectStock)
                setAllListFav([...glb_sv.allListFav])
                if (glb_sv.activeList.c1 === current.c1) {
                    glb_sv.activeList = { ...current };
                    glb_sv.commonEvent.next({ type: eventList.CHANGE_LIST_STOCK })
                } else {
                    setTimeout(() => {
                        onChangeStatus();
                    }, 100);
                }
                const isFav = glb_sv.allListFav.find(e => {
                    if (e.ListStock.find(stk => stk === selectStock)) {
                        return e
                    }
                });
                if (isFav) {
                    onChangeStatusStk && onChangeStatusStk(true)
                } else {
                    onChangeStatusStk && onChangeStatusStk(false)
                }
            } catch (err) {
                console.log('error', err)
            }

        }
    }

    const hanldeStockFav = (item) => {


        console.log('hanldeStockFav', item)
        if (glb_sv.authFlag) {
            const value = !checkExistInWatchList(item.ListStock, selectStock)
            if (value) {
                const InputParams = ['FAV_ITEM_ADD', item.c1, selectStock]
                sendRequest(ServiceInfo.AddStockToFavList, InputParams, handleAddStockToFavList)
            }
            if (value === false) {
                const InputParams = ['FAV_ITEM_REMOVE', item.c1, selectStock]
                sendRequest(ServiceInfo.removeStockFromFavList, InputParams, handleRmStkFrFavList)
            }

        } else {
            const value = !checkExistInWatchList(item.ListStock, selectStock)
            if (value) AddStockToListFav(item.c1, selectStock);
            if (value === false) {
                RemoveStockInListFav(item.c1, selectStock);
            }
            setAllListFav([...glb_sv.allListFav]);

            const isFav = glb_sv.allListFav.find(e => {
                if (e.ListStock.find(stk => stk === selectStock)) {
                    return e
                }
            });
            if (isFav) {
                onChangeStatusStk && onChangeStatusStk(true)
            } else {
                onChangeStatusStk && onChangeStatusStk(false)
            }

            setTimeout(() => {
                onChangeStatus();
            }, 100);
        }
    }

    const checkExistInWatchList = (ListStock, stock) => {
        console.log('checkExistInWatchList', ListStock, stock);
        if (ListStock.some(e => e === stock)) return true
        return false
    }

    const onWrapTouchStart = (e) => {
        // fix touch to scroll background page on iOS
        if (!/iPhone|iPod|iPad/i.test(navigator.userAgent)) {
            return;
        }
        const pNode = closest(e.target, '.am-modal-content');
        if (!pNode) {
            e.preventDefault();
        }
    }

    const addNewFav = () => {
        setModal(false);
        requestAnimationFrame(() => history.push(routes.ADD_FAV_LIST.path, { actionType: 'ADD_FAV_LIST' }))
    }


    return (
        <>
            <Modal
                visible={isModalVisible}
                transparent
                maskClosable={false}
                onClose={() => setModal(false)}
                wrapProps={{ onTouchStart: onWrapTouchStart }}
                wrapClassName='modal-add-fav-stock'
            >
                <div
                    className="flex justify-content-start flex-direction-column"
                    style={UI.content}
                >

                    <span className='fs__big' style={{ marginTop: 5, marginBottom: 5 }}>
                        {t('favorites')}
                    </span>
                    <div
                        className="flex flex-direction-column"
                        style={UI.categoryItem}
                    >
                        {allListFav.map((item, index) =>
                            <Checkbox.CheckboxItem
                                key={item.c1}
                                disabled={(item.c1 === '00' || item.c1 === 'HNX30' || item.c1 === 'VN30') ? true : false}
                                checked={checkExistInWatchList(item.ListStock, selectStock)}
                                style={{ backgroundColor: 'var(--HEADER__BG__COLOR)' }}
                                onClick={() => hanldeStockFav(item)}>
                                <span className='fs__normal' style={{ color: 'var(--PRIMARY__CONTENT__COLOR)' }}>{item.c2}</span>
                            </Checkbox.CheckboxItem>
                        )}
                    </div>
                    <div className="flex justify-content-between" style={{ marginTop: "20px" }}>
                        <div
                            className="flex"
                            onClick={() => {
                                setShowAddFav(true)
                            }}
                        >
                            <span style={{ color: "#06923D", fontSize: "20px", fontFamily: "SF pro Display" }}>
                                <img src={IC_ADD_CATEGORY} />{t("add_new_favorites")}
                            </span>
                        </div>
                        <div className="flex" onClick={() => setModal(false)}>
                            <span style={{ fontSize: "20px", fontFamily: "SF pro Display" }}>{t("common_Close")}</span>
                        </div>
                    </div>
                </div>
            </Modal>
            <AddNewFavList checkPopupAdd={showAddFav} checkHidenPopupAdd={checkHidenPopupAdd} />
        </>
    )
}

const UI = {
    content: {
        backgroundColor: 'var(--HEADER__BG__COLOR)',
        color: 'var(--PRIMARY__CONTENT__COLOR)',
        padding: 22,
        borderRadius: 4,
        borderColor: 'rgba(0, 0, 0, 0.1)',
    },
    row: {
        paddingRight: dimensions.moderate(16, 0.3),
        paddingTop: dimensions.moderate(13, 0.3),
        paddingBottom: dimensions.moderate(13, 0.3),
        marginLeft: dimensions.moderate(16, 0.3)
    },
    categoryItem: { textAlign: "left", overflowY: "scroll", height: "26vh" }
}

export default memo(AddStockFav)