import React, { useState, useContext, useEffect } from "react";
import { useTranslation } from 'react-i18next';
import HeaderComponent from '../../../components/header';
import { StoreContext } from '../../../store';
import { fontSizes as fs, dimensions as dm, fontWeights as fw } from '../../../styles';
import StepOne from './step-one'
import StepTwo from './step-two'
import { reqFunct, FormatNumber } from '../../../utils'
import sendRequest from '../../../utils/service/sendRequest'
import { RequiredAuth } from '../../../hoc'
import { useHistory } from "react-router-dom";
import Title from 'antd/lib/typography/Title';
import { CheckOutlined, LeftOutlined } from '@ant-design/icons';
import { Row, Col, List, Button, message as Message, Input} from 'antd'
import { Steps, Badge, Tabs } from 'antd-mobile'
import { Modal } from 'antd-mobile';
import { StoreTrading } from "store-trading";
import {ReactComponent as IconBack} from '../../../assets/images/common/ic_back.svg'

// Khai báo ServiceInfo (nếu có)
const ServiceInfo = {
    ADD_NEW_TRAN_MONEY_NOTIFY: {
        reqFunct: reqFunct.ADD_NEW_TRAN_MONEY_NOTIFY, // Thêm giao dịch thông báo nộp tiền
        WorkerName: 'ALTxCash',
        ServiceName: 'ALTxCash_0201_1',
        ClientSentTime: '0',
        Operation: 'I',
        // TestInput: ['', '0', '', ...]
    },
}
// Khai báo component
const PaymentOnAccountLayout = () => {
    const { styles  } = useContext(StoreContext);
    const {userInfo} = useContext(StoreTrading)
    const { t } = useTranslation();
    let history = useHistory()
    // All orther state 
    const [activeStep, setActiveStep] = useState(0);
    const [isOpenModal, setIsOpenModal] = useState(false);

    // Start define all bussiness state
    const [currentSubCash, setCurrentSubCash] = useState({});
    const [bankTransfer, setBankTransfer] = useState({});
    const [bankTransferAct, setBankTransferAct] = useState('');
    const [actReceive, setActReceive] = useState({});
    const [bankReceive, setBankReceive] = useState({});
    const [amoutTransfer, setAmoutTransfer] = useState('0');
    // End define form bussiness state
    // -------------------------------------------   Dùng useEffect get các dữ liệu cần thiết còn thiếu
    // -------------------------------------------   Viết các hàm để set lại bussiness form state. Nên viết đúng một chuẩn duy nhất để dễ control 

    const hideModal = () => {
        setIsOpenModal(false)
    }
    const _resetInput = () => {
        setBankTransfer({})
        setBankTransferAct('')
        setActReceive({})
        setBankReceive({})
        setAmoutTransfer('0')
    }

    const switchStep = {
        goTo: (step) => setActiveStep(step),
        next: () => setActiveStep(activeStep + 1),
        prev: () => setActiveStep(activeStep - 1),
        submit: () => confirmTranferCash(),
        onFinish: () => setIsOpenModal(true),
        getCurrent: () => { return activeStep }
    }

    // -------------------------------------------   Khai báo các hàm gửi (request) lên server
    const confirmTranferCash = () => {
        if (Number(amoutTransfer) <= 100000) {
            hideModal()
            // Toast.show({
            //     text: 'Số tiền phải lớn hơn 100.000 VNĐ', //t('pass_confirm_not_correct'),
            //     type: 'warning',
            //     position: 'top'
            // })
            return
        }
        if (Number(amoutTransfer) >= Number(currentSubCash.c4)) { // Lớn hơn tài sản hiện có
            hideModal()
            // Toast.show({
            //     text: 'Bạn không đủ tiền trong tài khoản', //t('pass_confirm_not_correct'),
            //     type: 'warning',
            //     position: 'top'
            // })
            return
        }
        const inputParams = [
            userInfo.actn_curr,
            userInfo.sub_curr,
            '2',
            bankReceive?.value?.c2 || '',   // Bank recieve account no
            bankReceive?.value?.c0 || '',   // Ngân hàng nhận
            bankTransferAct,                // Tài khoản chuyển
            bankTransfer?.value?.c0 || '',  // Ngân hàng chuyển
            String(amoutTransfer),          // Số tiền chuyển
            ''                              // Ghi chú
        ]
        // svInputPrm.InVal = [this.actn_curr, this.sub_curr, '2', rcvBankacnt, rcvBnk, trnBnkacnt, trnBnk, deposit_amt + '', noteInfo];
        sendRequest(ServiceInfo.ADD_NEW_TRAN_MONEY_NOTIFY, inputParams, handleConfirmTranferCash)
    }

    // -------------------------------------------   Khai báo các hàm gửi (request) và nhận (handle respone) dữ liệu từ server
    const handleConfirmTranferCash = (reqInfoMap, message) => {
        setIsOpenModal(false)
        console.warn("handleConfirmTranferCash", reqInfoMap, message['Data'], message['Message']);
        _resetInput()
        // -- process after get result --
        if (Number(message['Result']) === 0) {
            console.warn("false handleConfirmTranferCash", reqInfoMap, message['Data'], message['Message']);
            // Toast.show({
            //     text: message['Message'], //t('pass_confirm_not_correct'),
            //     type: 'warning',
            //     position: 'top'
            // })
            return;
        } else {
            let jsondata = [];
            try {
                jsondata = message['Data'] ? JSON.parse(message['Data']) : [];
            } catch (err) {
            }
            console.log(jsondata);
            // Toast.show({
            //     text: message['Message'], //t('pass_confirm_not_correct'),
            //     type: 'success',
            //     position: 'top'
            // })
        }
    }

    const StepIcon = [
        <svg onClick={() => switchStep.goTo(0)} width="30" height="30" viewBox="0 0 30 30" fill="none" xmlns="http://www.w3.org/2000/svg">
            <circle cx="15" cy="15" r="15" fill="#60B44A" />
            <path d="M14.9999 21.9161L16.4687 21.5771L15.3388 20.4473L14.9999 21.9161Z" fill="white" />
            <path d="M14.7101 20.9488L14.9531 19.8957C14.9537 19.8931 14.9546 19.8907 14.9552 19.8882C14.9564 19.8838 14.9576 19.8795 14.9589 19.8752C14.9603 19.8709 14.9617 19.8673 14.9632 19.8634C14.9646 19.8596 14.9659 19.8561 14.9675 19.8525C14.9693 19.8483 14.9713 19.8441 14.9734 19.84C14.9749 19.8369 14.9765 19.8339 14.9781 19.831C14.9805 19.8266 14.9831 19.8224 14.9857 19.8182C14.9874 19.8156 14.9891 19.8129 14.9909 19.8103C14.9937 19.8062 14.9968 19.8021 14.9999 19.7982C15.0018 19.7957 15.0037 19.7933 15.0057 19.7909C15.0088 19.787 15.0124 19.7833 15.0158 19.7797C15.0172 19.7782 15.0185 19.7766 15.0199 19.7752L18.2951 16.5C17.3807 14.6193 15.69 13.5 13.75 13.5C12.3274 13.5 11.0132 14.1089 10.0493 15.2145C9.07866 16.3281 8.53169 17.8802 8.50134 19.5986C9.07522 19.888 11.4316 21 13.75 21C14.0707 20.9995 14.3911 20.9824 14.7101 20.9488Z" fill="white" />
            <path d="M15.5482 19.9521L19.4367 16.0636L20.8507 17.4776L16.9622 21.3661L15.5482 19.9521Z" fill="white" />
            <path d="M13.75 13C15.1307 13 16.25 11.8807 16.25 10.5C16.25 9.11929 15.1307 8 13.75 8C12.3693 8 11.25 9.11929 11.25 10.5C11.25 11.8807 12.3693 13 13.75 13Z" fill="white" />
            <path d="M21.5 16.4159C21.5 16.2181 21.4413 16.0248 21.3314 15.8604C21.2215 15.6959 21.0654 15.5678 20.8826 15.4921C20.6999 15.4164 20.4989 15.3966 20.3049 15.4352C20.1109 15.4738 19.9328 15.5691 19.7929 15.7089L21.2071 17.1231C21.3003 17.0304 21.3741 16.9202 21.4244 16.7988C21.4747 16.6774 21.5004 16.5473 21.5 16.4159Z" fill="white" />
        </svg>,
        <svg onClick={() => switchStep.goTo(1)} width="30" height="30" viewBox="0 0 30 30" fill="none" xmlns="http://www.w3.org/2000/svg">
            <circle opacity="0.12" cx="15" cy="15" r="15" fill="#24253D" />
            <g opacity="0.24">
                <path d="M15 8.5C11.4159 8.5 8.5 11.4159 8.5 15C8.5 18.5841 11.4159 21.5 15 21.5C18.5841 21.5 21.5 18.5841 21.5 15C21.5 11.4159 18.5841 8.5 15 8.5ZM13.8125 18.2619L11.2937 15.4628L12.0372 14.7937L13.7872 16.7381L17.9375 11.7956L18.7044 12.4375L13.8125 18.2619Z" fill="#24253D" />
            </g>
        </svg>
    ]

    const tabs = [
        { title: '1' },
        { title: '2' },
        { title: '3' },
    ];

    return (
        <div className="menu__layout">
            <HeaderComponent
                title={t('title_add_money')}
                transparent
                titleAlign='start'
                iconCol={[
                    <IconBack  style={{ fontSize: 16 }} onClick={() => history.goBack()} />,
                ]}
                iconCol={[]}
            />
            <div className="step__section" style={{ backgroundColor: styles.PRIMARY__BG__COLOR }}>
                <div className="header__step">
                    <Steps direction="horizontal" current={activeStep} 
                        style={{ 
                            paddingCol: dm.indent * 3, 
                            paddingCol: dm.indent * 3,
                            marginTop: dm.verticalIndent,
                            marginBottom: dm.verticalIndent }}>
                        <Steps.Step title={t('account_in_advance')} icon={StepIcon[0]} />
                        <Steps.Step title={t('trading_confirm')} icon={StepIcon[1]} /> 
                    </Steps>
                </div>
                <div className="content__step">
                    <Tabs tabs={tabs}
                        swipeable={false}
                        initialPage={1}
                        page={activeStep}
                        onChange={(tab, index) => { console.log('onChange', index, tab); }}
                        onTabClick={(tab, index) => { console.log('onTabClick', index, tab); }}
                    >
                        <div className="flex align-items-center justify-content-center h-auto" >
                            <StepOne
                                setBankTransfer={setBankTransfer}
                                setBankTransferAct={setBankTransferAct}
                                setActReceive={setActReceive}
                                setBankReceive={setBankReceive}
                                setAmoutTransfer={setAmoutTransfer}
                                currentSubCash={currentSubCash}
                                setCurrentSubCash={setCurrentSubCash}
                                switchStep={switchStep}
                            />
                        </div>
                        <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', height: 'auto', backgroundColor: '#fff' }}>
                            <StepTwo
                                bankTransfer={bankTransfer}
                                setBankTransfer={setBankTransfer}
                                bankTransferAct={bankTransferAct}
                                setBankTransferAct={setBankTransferAct}
                                actReceive={actReceive}
                                setActReceive={setActReceive}
                                bankReceive={bankReceive}
                                setBankReceive={setBankReceive}
                                amoutTransfer={amoutTransfer}
                                setAmoutTransfer={setAmoutTransfer}
                                switchStep={switchStep}
                                 />
                        </div>
                    </Tabs>
                </div>
            </div>
            {/* **************************************** Modal submit ******************************************/}
            <Modal
                visible={isOpenModal}
                style={{ width: '80vw'}}
                transparent
                maskClosable={false}
                onClose={hideModal}
                wrapClassName='modal-submit'
            >
                <span style={{ marginVertical: dm.halfIndent, fontSize: fs.medium, color: styles.PRIMARY__CONTENT__COLOR }}>{t('confirm_send_deposite_inform')}</span>
                <div style={{ display: 'flex', flexDirection: 'column' }}>
                    <div style={UI.ViewAccount}>
                        <div><span style={{ fontSize: fs.small, lineHeight: 1.1, fontWeight: fw.medium }}>{userInfo.actn_curr}</span></div>
                        <span style={{ fontSize: fs.verySmall, lineHeight: 1.1 }}>{userInfo.actn_name}</span>
                    </div>
                    <div className="center__item">
                        <div style={{
                            width: dm.vertical(24),
                            height: dm.vertical(24),
                            backgroundColor: styles.PRIMARY,
                            borderRadius: 4
                        }}>
                            <span style={{ color: styles.PRIMARY__CONTENT__COLOR, fontSize: fs.verySmall }}>{userInfo.sub_curr}</span>
                        </div>
                    </div>
                </div>
                <Row style={{ }}>
                    <Col span={12}>
                        <span style={{ color: styles.PRIMARY__CONTENT__COLOR, fontSize: fs.verySmall }}>{t('Tài khoản chuyển')}</span>
                    </Col>
                    <Col span={12}>
                        <span style={{ color: styles.PRIMARY__CONTENT__COLOR, fontSize: fs.verySmall }}>{bankTransfer?.label || 'Test'}</span>
                        <span style={{ color: styles.PRIMARY__CONTENT__COLOR, fontSize: fs.verySmall }}>{bankTransferAct}</span>
                    </Col>
                </Row>
                <Row style={{ }}>
                    <Col span={12}>
                        <span style={{ color: styles.PRIMARY__CONTENT__COLOR, fontSize: fs.verySmall }}>{t('Tài khoản nhận')}</span>
                    </Col>
                    <Col span={12}>
                        <span style={{ color: styles.PRIMARY__CONTENT__COLOR, fontSize: fs.verySmall }}>{actReceive?.value?.c2}</span>
                        <span style={{ color: styles.PRIMARY__CONTENT__COLOR, fontSize: fs.verySmall }}>{bankReceive?.value?.c1 || 'Test'}</span>
                    </Col>
                </Row>
                <Row style={{ }}>
                    <Col span={12}>
                        <span style={{ color: styles.PRIMARY__CONTENT__COLOR, fontSize: fs.verySmall }}>{t('transfer_amount')}</span>
                    </Col>
                    <Col span={12}>
                        <span style={{ color: styles.PRIMARY__CONTENT__COLOR, fontSize: fs.verySmall }}>
                            {FormatNumber(amoutTransfer)}
                        </span>
                    </Col>
                </Row>
                <div style={{ flexDirection: 'column' }}>
                    <Button block onClick={() => switchStep.submit()} style={{ backgroundColor: styles.UP__COLOR, borderRadius: 4 }}>
                        <span style={{ color: styles.PRIMARY__CONTENT__COLOR }}>{t('common_button_confirm')}</span>
                    </Button>
                    <Button block onClick={hideModal} style={{ marginTop: 8, borderRadius: 4 }}>
                        <span style={{ color: styles.PRIMARY__CONTENT__COLOR }}>{t('common_Cancel')}</span>
                    </Button>
                </div>
            </Modal>
            </div>
    );
};

export default PaymentOnAccountLayout

