import { useState, useContext, memo, useEffect, useRef } from 'react';
import { useTranslation } from 'react-i18next';
import { Modal } from 'antd-mobile';
import moment from 'moment';
import { StoreTrading } from '../../store-trading';
import { reqFunct, sendRequest } from '../../utils';
import { fontSizes } from 'styles';

const ServiceInfo = {
    GET_LIST_RIGHT_TYPE_REGISTER: {
        reqFunct: reqFunct.GET_LIST_RIGHT_TYPE_REGISTER, //Lấy DS Kết quả
        WorkerName: 'FOSqRights',
        ServiceName: 'FOSqRights_OnlineRightsInfo_1',
        ClientSentTime: '0',
        Operation: 'Q',
    }
}

const Events = ({ stockCode }) => {
    const { t } = useTranslation();
    const { userInfo } = useContext(StoreTrading);

    const [eventData, setEventData] = useState([]);
    const [dataSelect, setDataSelect] = useState({});
    const [shouldShowModal, setShouldShowModal] = useState(false);

    useEffect(() => {
        getEventData();
    }, [stockCode])

    const getEventData = () => {
        const inputParams = [stockCode, '%'];
        sendRequest(ServiceInfo.GET_LIST_RIGHT_TYPE_REGISTER, inputParams, handleGetEventDataResult);
    }

    const handleGetEventDataResult = (reqInfoMap, message) => {
        if (Number(message['Result']) === 0) {
            setEventData([])
            return
        } else {
            let jsondata = []
            try {
                jsondata = message['Data'] ? JSON.parse(message['Data']) : [];
                setEventData(jsondata);
            } catch (err) {
                setEventData([])
                return
            }
        }
    }

    const formatTimeNull = (timeData) => {
        return !timeData.trim() ? null : moment(timeData, 'DDMMYYYY').format('DD/MM/YYYY')
    }

    const setShowModal = value => {
        setShouldShowModal(value);
    }

    const openModalDetail = data => {
        setDataSelect(data);
        setShouldShowModal(true);
    }

    return (
        <div className='right__to__buy'>
            <div className='table-right-to-buy mt-2 pr-2 pl-2'>
                <table className='w-100'>
                    <thead className='mb-3'>
                        <tr className='text-center' style={{ height: 41, borderBottom: '1px solid var(--TABBAR__SHADOW__COLOR)' }}>
                            <th className='text-left' style={{ fontSize: fontSizes.small }}>
                                {t('right')}
                            </th>
                            <th className='text-right' style={{ fontSize: fontSizes.small }}>{t('last_regist_date')}</th>
                        </tr>
                    </thead>
                    <tbody>
                        {eventData.length > 0 ? (
                            eventData.map((data, index) => (
                                <tr onClick={() => openModalDetail(data)} className='text-center' key={index} style={{ borderBottom: '1px solid var(--TABBAR__SHADOW__COLOR)', height: 38 }}>
                                    <td className='text-left flex flex-direction-column'>
                                        <div className='right' style={{ fontSize: fontSizes.small }}>
                                            {data.c33}
                                        </div>
                                        <div className='content' style={{ fontSize: fontSizes.verySmall, color: 'var(--SECOND__CONTENT__COLOR)' }}>
                                            {data.c31}
                                        </div>
                                    </td>
                                    <td className='text-right' style={{ fontSize: fontSizes.small }}>
                                        {formatTimeNull(data.c29)}
                                    </td>
                                </tr>
                            ))
                        ) : t('common_NoDataFound')}
                    </tbody>
                </table>
            </div>
            <ModalDetail data={dataSelect} isModalVisible={shouldShowModal} setModal={setShowModal} />
        </div>
    )
}

const ModalDetail = ({ data, isModalVisible, setModal }) => {
    const { t } = useTranslation()

    const formatTimeNull = (timeData) => {
        return timeData !== undefined ? !timeData.trim() ? null : moment(timeData, 'DDMMYYYY').format('DD/MM/YYYY') : ''
    }

    return (
        <Modal
            visible={isModalVisible}
            transparent
            maskClosable={false}
            onClose={() => setModal(false)}
            wrapClassName='modal-add-fav-stock'
        >
            <div className='detail-modal-content flex justify-content-start flex-direction-column p-3'>
                <div className='justify-content-center flex fs-medium ' style={{ marginBottom: 10, color: 'var(--PRIMARY__CONTENT__COLOR)' }}>{t('right_info_detail')}</div>
                <div className='ant-divider' role='separator'></div>
                <div className='content-right'>
                    <div className='flex flex-1'>
                        <div className='flex flex-2' style={{ color: 'var(--PRIMARY__CONTENT__COLOR)' }}>{t('right_code')}</div>
                        <div className='flex flex-2 justify-content-end' style={{ color: 'var(--SECOND__CONTENT__COLOR)' }}>
                            {data.c0}
                        </div>
                    </div>
                    <div className='flex flex-1'>
                        <div className='flex flex-2' style={{ color: 'var(--PRIMARY__CONTENT__COLOR)' }}>{t('short_symbol')}</div>
                        <div className='flex flex-2 justify-content-end' style={{ color: 'var(--SECOND__CONTENT__COLOR)' }}>
                            {data.c1}
                        </div>
                    </div>
                    <div className='flex flex-1'>
                        <div className='flex flex-2' style={{ color: 'var(--PRIMARY__CONTENT__COLOR)' }}>{t('last_regist_date')}</div>
                        <div className='flex flex-2 justify-content-end' style={{ color: 'var(--SECOND__CONTENT__COLOR)' }}>
                            {formatTimeNull(data.c29)}
                        </div>
                    </div>
                    <div className='flex flex-1'>
                        <div className='flex flex-2' style={{ color: 'var(--PRIMARY__CONTENT__COLOR)' }}>{t('record_date')}</div>
                        <div className='flex flex-2 justify-content-end' style={{ color: 'var(--SECOND__CONTENT__COLOR)' }}>
                            {formatTimeNull(data.c2)}
                        </div>
                    </div>
                    <div className='flex flex-1'>
                        <div className='flex flex-2' style={{ color: 'var(--PRIMARY__CONTENT__COLOR)' }}>{t('exercise_date')}</div>
                        <div className='flex flex-2 justify-content-end' style={{ color: 'var(--SECOND__CONTENT__COLOR)' }}>
                            {(Number(data?.c21?.slice(4, 8)) !== 3000 && !!formatTimeNull(data.c21)) ? formatTimeNull(data.c21) : (Number(data?.c24?.slice(4, 8)) !== 3000 && !!formatTimeNull(data.c24)) ? formatTimeNull(data.c24) : formatTimeNull(data.c2)}
                        </div>
                    </div>
                    <div className='flex flex-1'>
                        <div className='flex flex-2' style={{ color: 'var(--PRIMARY__CONTENT__COLOR)' }}>{t('right_type')}</div>
                        <div className='flex flex-2 justify-content-end' style={{ color: 'var(--SECOND__CONTENT__COLOR)' }}>
                            {data.c33}
                        </div>
                    </div>
                    <div className='flex flex-1'>
                        <div className='flex flex-2' style={{ color: 'var(--PRIMARY__CONTENT__COLOR)' }}>{t('right_type')}</div>
                        <div className='flex flex-2 justify-content-end' style={{ color: 'var(--SECOND__CONTENT__COLOR)' }}>
                            {data.c34}
                        </div>
                    </div>
                    <div className='flex flex-1'>
                        <div className='flex flex-2' style={{ color: 'var(--PRIMARY__CONTENT__COLOR)' }}>{t('date_trading_not_right')}</div>
                        <div className='flex flex-2 justify-content-end' style={{ color: 'var(--SECOND__CONTENT__COLOR)' }}>
                            {formatTimeNull(data.c29)}
                        </div>
                    </div>
                    <div className='flex flex-1'>
                        <div className='flex flex-2' style={{ color: 'var(--PRIMARY__CONTENT__COLOR)' }}>{t('content')}</div>
                    </div>
                    <div className='flex flex-1'>
                        {data.c31}
                    </div>
                </div>
                <div className='ant-divider' role='separator'></div>
                <div className='flex justify-content-between' style={{ marginTop: 10 }}>
                    <div className='flex'></div>
                    <div className='flex justify-content-end' onClick={() => setModal(false)}>
                        <span style={{ color: 'var(--PRIMARY__CONTENT__COLOR)' }}>{t('common_Close')}</span>
                    </div>
                </div>
            </div>
        </Modal>
    )
}

export default memo(Events)