                        {options.orderStatus === '1' ? t('wait_to_process') :
                            options.orderStatus === '2' ? t('processed') :
                                options.orderStatus === '3' ? t('common_Cancel') :
                                    options.orderStatus === '4' ? t('wait_tranfer') :
                                        options.orderStatus === '9' ? t('reject') : t('common_all')}
