                    <ArrowClose style={{ color: 'var(--PRIMARY__CONTENT__COLOR)', marginLeft: '0.25rem', display: 'block', paddingBottom: '3px' }} />
                    {/* <AntDesign onClick={() => setVisibleStatus(true)} name='caretdown' color={styles.SECOND__CONTENT__COLOR} size={fontSizes.normal} /> */}
