export default memo(AdvanceOrderList, areEqual)

function areEqual(prev, next) {
    return true
}

function propsAreEqual(prev, next) {
    return true
}

const RowItem = memo(({ c4, c3, c7, c6, c19, c8, styles, t, changeOrderDetail }) => {

    const colorOrderStatus = (value) => {
		if (value === '0') return 'var(--SEND_TO_EXCHANGE_COLOR)';
		else if (value === '1') return 'var(--SEND_TO_EXCHANGE_COLOR)';
		else if (value === '3') return 'var(--WAIT_TO_MATCH_COLOR)';
		else if (value === '4') return 'var(--MATCH_A_PIECES_COLOR)';
		else if (value === '5') return 'var(--MATCH_A_PIECES_COLOR)';
		else if (value === '6') return '';
		else if (value === '7') return 'var(--REJECT_COLOR)';
		else if (value === '8') return '';
		else if (value === '9') return '';
		else if (value === 'X') return 'var(--SEND_TO_EXCHANGE_COLOR)';
        return 'var(--PRIMARY__CONTENT__COLOR)'
	}


    return (
        <div
            onClick={changeOrderDetail}
            style={{ display: 'flex', flexDirection: 'row', paddingTop: '0.75rem', borderBottomWidth: 1, borderBottomColor: styles.DIVIDER__COLOR, backgroundColor: styles.PRIMARY__BG__COLOR }}>
            <div style={{ flexDirection: 'row', flex: 0.8, display: 'flex' }}>
                <div style={{ width: 18, height: 18, borderRadius: 9, backgroundColor: c3 === '1' ? styles.UP__COLOR : styles.DOWN__COLOR, alignItems: 'center', justifyContent: 'center', display: 'flex'}}>
                    <Text style={{ color: '#FFF', fontSize: dimensions.moderate(8), fontWeight: fontWeights.medium, marginLeft: 1.8 }}>
                        {c3 === '1' ? t('buy_short') : t('sell_short')}
                    </Text>
                </div>
                <Text style={{ color: styles.PRIMARY__CONTENT__COLOR, fontSize: c4?.length > 3 ? fontSizes.smallest : fontSizes.verySmall, fontWeight: fontWeights.medium }}>
                    {'  '}{c4}
                </Text>
            </div>
            <Text style={{ color: styles.PRIMARY__CONTENT__COLOR, textAlign: 'center', flex: 1 }}>
                {FormatNumber(c7, 0, 0)}
            </Text>
            <Text style={{ color: styles.PRIMARY__CONTENT__COLOR, textAlign: 'center', flex: 1 }}>
                {FormatNumber(c6, 0, 1)}
            </Text>

            <Text style={{ color: colorOrderStatus(c8), textAlign: 'right', flex: 1 }}>
                {c19}
            </Text>
        </div>
    );
}, propsAreEqual);

const HiddenItemWithActions = memo(props => {
    const {
        data,
        showModalCancel
    } = props;

    const { styles } = useContext(StoreContext);

    return (
        <div style={[UI.rowBack, { borderBottomColor: styles.DIVIDER__COLOR }]}>
            <div
                style={[UI.backRightBtn, UI.backRightBtnRight]}
                disabled={data.item.c8 === '2' || data.item.c8 === '3'}
                onClick={() => showModalCancel(data.item)}>
                {/* <AntDesign
                    name="delete"
                    size={22}
                    color={data.item.c8 === '2' || data.item.c8 === '3' ? styles.SECOND__CONTENT__COLOR : styles.DOWN__COLOR}
                /> */}
            </div>
        </div>
    );
}, areEqual);


