import React, { useEffect, useState, useContext, useRef, useCallback } from 'react';
import { StoreContext } from '../../store';
import { useTranslation } from 'react-i18next';
import { fontWeights, fontSizes } from '../../styles';
import { glb_sv, socket_sv, Screens } from '../../utils'
import { Button, Drawer, Input } from 'antd'
import { useHistory } from "react-router-dom";
import HeaderComponent from '../../components/header'

export default function ModalSelection({ 
    isOpen,
    actionType,
    getSelectValue,
    setIsOpen }) {

    let history = useHistory()
    const { styles } = useContext(StoreContext)
    const { t } = useTranslation();
    const [seach, setSearch] = useState('');
    const [listSelections, setListSelections] = useState([]);
    const timeOutSearch = useRef(null);

    useEffect(() => {
        if ('PICK_BANK_TRANSFER' === actionType) {
            const listBank = glb_sv.ListBankTrans
            setListSelections(listBank)
        }
        if ('PICK_BANK_RECEIVE' === actionType) {
            const listBankReceive = glb_sv.ListBankReceive
            setListSelections(listBankReceive)
        }
        if ('PICK_ACCOUNT_RECEIVE' === actionType) {
            const listBankReceive = glb_sv.ListBankReceive
            setListSelections(listBankReceive)
        }
    }, [isOpen])

    const loadStkList = async () => {

    }

    const getListBank = (name) => {
        console.log("name", name);
        setSearch(name);
        if (name === '') setListSelections([])
        if (timeOutSearch.current) clearTimeout(timeOutSearch.current);
        timeOutSearch.current = setTimeout(() => {
            const newList = listSelections.filter(o => o.label.toUpperCase().includes(name) || name.toLowerCase().includes(o.label.toUpperCase()))
                .sort(function (a, b) {
                    if (a.label > b.label) return -1
                    if (a.label < b.label) return 1
                    return 0
                })
            setListSelections(newList)
        }, 300);
    }

    const ItemSelect = ({ item }) => {
        return (
            <div
                style={{ ...UI.Item_Row }}
                onClick={() => { onSelect(item); setIsOpen(false) }}>
                <div style={{ flexDirection: 'column', justifyContent: 'flex-start' }}>
                    <span style={{ color: 'var(--PRIMARY__CONTENT__COLOR)', fontWeight: fontWeights.medium }}>
                        {item.label}
                    </span>
                </div>
            </div>
        );
    };


    const resetData = () => {
        setSearch('');
        setListSelections([])
        if ([].length === 0 && '') console.log('asdf')
    }

    const ListEmpty = () => {
        return (
            <div style={{ justifyContent: 'center', flex: 1, margin: 10 }}>
                <span style={{ textAlign: 'center', color: 'var(--PRIMARY__CONTENT__COLOR)' }}>No Matching results.</span>
            </div>
        );
    };
    const onSelect = useCallback(
        (selection) => {
            console.log(actionType, selection);
            if ('PICK_BANK_TRANSFER' === actionType) {
                getSelectValue(selection)
            }
            if ('PICK_BANK_RECEIVE' === actionType) {
                getSelectValue(selection)
            }
            if ('PICK_ACCOUNT_RECEIVE' === actionType) {
                getSelectValue(selection)
            }
        },
        [actionType],
    );
    const IconSearch = () => {
        return (
            <svg
                xmlns="http://www.w3.org/2000/svg"
                width="36"
                height="36"
                fill="none"
                viewBox="0 0 36 36"
            >
                <path
                    fill="#F8F8F8"
                    fillRule="evenodd"
                    d="M0 18C0 8.059 8.059 0 18 0s18 8.059 18 18-8.059 18-18 18S0 27.941 0 18z"
                    clipRule="evenodd"
                ></path>
                <path
                    stroke="#24253D"
                    strokeMiterlimit="10"
                    strokeWidth="1.5"
                    d="M16.636 10.5a6.136 6.136 0 100 12.272 6.136 6.136 0 000-12.272v0z"
                ></path>
                <path
                    stroke="#24253D"
                    strokeLinecap="round"
                    strokeMiterlimit="10"
                    strokeWidth="1.5"
                    d="M21.214 21.215L25.5 25.5"
                ></path>
            </svg>
        )
    }
    const HeaderSelection = () => {
        return (
            <div>
                <HeaderComponent
                    title={t('')}
                    transparent
                    ColumSpan={[18, 0, 6]}
                    iconLeft={[
                        <Input 
                            // onChange={e => getListBank(e.target.value)}
                            // value={seach}
                            key="input_search" placeholder={t('search')} />,
                    ]}
                    iconRight={[
                        <IconSearch key="icon_search" />
                    ]}
                />
                <Button onClick={() => setIsOpen(false)}>Cancel</Button>
            </div>
        )
    }

    return (
        <Drawer
            title={<HeaderSelection />}
            placement={'bottom'}
            closable={false}
            onClose={() => { console.log("Search Bank Close")}}
            visible={isOpen}
            height="100vh"
        >
            <div style={{ flex: 1 }}>
                {listSelections.length === 0 && seach.length === 0 ? <div>
                    <span style={{ color: 'var(--HEADER__CONTENT__COLOR)' }}>Không có lựa chọn</span>
                </div> :
                listSelections.map((item, i) => <ItemSelect key={i} item={item} />)
                    }
            </div>
        </Drawer>
    );
}

const Bank = []
const Account = []

const UI = {
    Item_Row: {
        borderBottomColor: 'var(--DIVIDER__COLOR)', 
        marginLeft: 0,
        height: 40,
    }
}