import React, { useState, useContext, useRef } from 'react';
import { useTranslation } from 'react-i18next';
import { StoreContext } from '../../store';
import { glb_sv, reqFunct, sendRequest, FormatNumber, eventList, Screens } from '../../utils';
import { fontSizes, dimensions, IconSvg } from '../../styles';
import moment from 'moment'
import { ButtonCustom, RowData } from '../../components/trading-component'
import { useLocation } from 'react-router-dom'
import { Modal, Button, Typography } from 'antd'
import { ReactComponent as IconBack } from 'assets/images/common/ic_back.svg'
import HeaderComponent from 'components/header'
import { useHistory } from 'react-router-dom'
import ModalOtp from './modal-otp';

const { Text } = Typography

const ServiceInfo = {
    CANCEL_ADV_ORDER: {
        reqFunct: reqFunct.CANCEL_ADV_ORDER,
        WorkerName: 'FOSxOrder',
        ServiceName: 'FOSxOrder_0509_2',
        Operation: 'I',
        ClientSentTime: '0'
    }
}

const translateOrderTp = (value) => {
    if (value === '01') {
        return 'order_Limit'
    } else if (value === '02') {
        return 'order_Mp'
    } else if (value === '03') {
        return 'order_ATO'
    } else if (value === '04') {
        return 'order_ATC'
    } else if (value === '06') {
        return 'order_MOK'
    } else if (value === '07') {
        return 'order_MAK'
    } else if (value === '08') {
        return 'order_MTL'
    } else if (value === '15') {
        return 'order_PLO'
    } else return value;
}

const AdvOrderDetail = () => {

    const location = useLocation()
    const { item } = location.state;

    const { styles, theme } = useContext(StoreContext);
    const { t } = useTranslation();

    const [modalCancelOrder, setModalCancelOrder] = useState(false)
    const sendCancelOrderFlag = useRef(false)

    const [spinner, setSpinner] = useState(false)

    const [data, setData] = useState({ ...item })
    const history = useHistory()

    const [isOpenOTP, setIsOpenOTP] = useState(false)
    const [OTP_Data, setOTP_Data] = useState({})


    const colorOrderStatus = (value) => {
        if (value === '0') return 'var(--SEND_TO_EXCHANGE_COLOR)';
        else if (value === '1') return 'var(--SEND_TO_EXCHANGE_COLOR)';
        else if (value === '3') return 'var(--WAIT_TO_MATCH_COLOR)';
        else if (value === '4') return 'var(--MATCH_A_PIECES_COLOR)';
        else if (value === '5') return 'var(--MATCH_A_PIECES_COLOR)';
        else if (value === '6') return '';
        else if (value === '7') return 'var(--REJECT_COLOR)';
        else if (value === '8') return '';
        else if (value === '9') return '';
        else if (value === 'X') return 'var(--SEND_TO_EXCHANGE_COLOR)';
        return 'var(--PRIMARY__CONTENT__COLOR)'
    }

    const sendCancelAdvOrder = () => {
        if (sendCancelOrderFlag.current) return
        sendCancelOrderFlag.current = true;
        setModalCancelOrder(false)
        setSpinner(true);
        const InputParams = [data.c17, data.c0, data.c1, '', data.c22]
        sendRequest(ServiceInfo.CANCEL_ADV_ORDER, InputParams, handleSendCancelOrder, true, sendCancelOrderTimeout)
    }

    const sendCancelOrderTimeout = () => {
        sendCancelOrderFlag.current = false;
        setSpinner(false);
        // navigation.navigate(Screens.ALERT_MODAL, {
        //     title: t('common_notify'),
        //     content: t('request_hanlde_not_success_try_again'),
        //     typeColor: styles.WARN__COLOR
        // })
    }

    const handleSendCancelOrder = (reqInfoMap, message) => {
        console.log('handleSendCancelOrder', reqInfoMap, message)
        sendCancelOrderFlag.current = false;
        setSpinner(false);
        if (Number(message['Result']) === 0) {
            if (message.Code == 'XXXXX5' || message.Code === '080063' || message.Code === '010012') {
                // navigation.navigate(Screens.ALERT_MODAL, {
                //     icon: <IconSvg.ErrorIcon color={styles.WARN__COLOR} />,
                //     title: t('common_notify'),
                //     content: t('request_hanlde_not_success_try_again'),
                //     typeColor: styles.WARN__COLOR
                // })
            }
            else if (glb_sv.otpCases.includes(message.Code)) {
                glb_sv.objShareGlb['sessionInfo']['Otp'] = ''

                let data
                try {
                    data = JSON.parse(message.Data)[0]
                } catch (error) {
                    return
                }

                const otp_Type = Number(data.c1);
                const expTimeOtp = Number(data.c2)
                let reqOtpMessage = 'OTP';
                if (Number(data['c1']) === 2) { //-- dạng thẻ ma trận
                    reqOtpMessage = 'OTP ' + data['c3']
                }
                // navigation.navigate(Screens.OTP_MODAL,
                //     {
                //         time: expTimeOtp,
                //         type: reqOtpMessage,
                //         hasOTP: false,
                //         otp_Type,
                //         msg: message.Message,
                //         functCallback: showModalAction
                //     });
                setIsOpenOTP(true);
                setOTP_Data({
                    time: expTimeOtp,
                    type: reqOtpMessage,
                    hasOTP: false,
                    otp_Type,
                    msg: message.Message,
                    functCallback: showModalAction
                })

            }
            else {
                // navigation.navigate(Screens.ALERT_MODAL, {
                //     icon: <IconSvg.ErrorIcon color={styles.WARN__COLOR} />,
                //     title: t('common_notify'),
                //     content: message['Message'],
                //     typeColor: styles.WARN__COLOR
                // })
            }
        }
        else {
            glb_sv.commonEvent.next({ type: eventList.REFRESH_ADVORDER_LIST })
            // navigation.navigate(Screens.ALERT_MODAL, {
            //     title: t('common_notify'),
            //     content: message.Message,
            //     typeColor: styles.SUCCESS__COLOR,
            //     linkCallback: () => navigation.goBack()
            // });
            glb_sv.showAlert({
                title: t('common_notify'),
                content: message['Message'],
                type: 'success',
                showCancel: true,
                linkCallback: () => history.goBack(),
                cancelCallBack: () => null,
                icon: (
                    <svg width={33} height={32} viewBox='0 0 33 32' fill='none' xmlns='http://www.w3.org/2000/svg'>
                        <g clipPath='url(#prefix__clip0)' fill='#2ECC71'>
                            <path d='M5.754 23.077V0H4.217A4.111 4.111 0 00.11 4.106v20.275a5.343 5.343 0 013.501-1.304h2.143zM24.93 15.934c.201 0 .402.008.6.021V0H7.625v23.077h8.547c.834-4.07 4.444-7.143 8.758-7.143zM13.305 5.511h6.264v1.871h-6.264V5.511zM11.19 9.574h10.496v1.871H11.189V9.574zM17.213 29.381H4.853V27.51h11.534a8.906 8.906 0 01-.396-2.62H3.61a3.56 3.56 0 00-3.555 3.556A3.56 3.56 0 003.611 32H19.54a9.007 9.007 0 01-2.327-2.62z' />
                            <path d='M24.93 17.746a7.135 7.135 0 00-7.127 7.127c0 3.93 3.197 7.126 7.127 7.126 3.93 0 7.126-3.197 7.126-7.126a7.135 7.135 0 00-7.126-7.127zm-.001 2.86c.526 0 .915.43.937.938.023.506-.447.937-.937.937-.526 0-.915-.43-.938-.937-.022-.507.448-.938.938-.938zm.94 7.92h-1.875v-4.8h1.875v4.8z' />
                        </g>
                        <defs>
                            <clippath id='prefix__clip0'>
                                <path fill='#fff' transform='translate(.056)' d='M0 0h32v32H0z' />
                            </clippath>
                        </defs>
                    </svg>
                ),
            })
        }
    }

    const showModalAction = () => {
        setModalCancelOrder(true)
    }

    return (
        <div style={{ flex: 1, backgroundColor: styles.PRIMARY__BG__COLOR }}>
            <HeaderComponent title={t('order_detail')} iconLeft={[<IconBack style={{ fontSize: 16 }} onClick={() => history.goBack()} />]} iconRight={[]} />
            {/* <FocusAwareStatusBar translucent={true} backgroundColor={'transparent'} barStyle={theme.includes('DARK') ? 'light-content' : 'dark-content'} /> */}

            {/* <Spinner
                visible={spinner}
                textContent={'...'}
                textStyle={{ color: styles.PRIMARY__CONTENT__COLOR }}
            /> */}

            <div>
                <RowData textLeft={t('hint_stock_search')} textRight={data.c4} rightColor={data.c3 === '1' ? styles.UP__COLOR : styles.DOWN__COLOR} />

                <RowData textLeft={t('qty')} textRight={FormatNumber(data.c6)} />

                <RowData textLeft={t('price')} textRight={FormatNumber(data.c7)} />

                <RowData textLeft={t('session')} textRight={data.c21} />

                <RowData textLeft={t('order')} textRight={t(translateOrderTp(data.c5))} />

                <RowData textLeft={t('effect_date')} textRight={data ? moment(data.c9, 'DDMMYYYY').format('DD/MM/YYYY') : ''} />
                <RowData textLeft={t('expire_date')} textRight={data ? moment(data.c10, 'DDMMYYYY').format('DD/MM/YYYY') : ''} />
                <RowData textLeft={t('place_order_time')} textRight={data ? moment(data.c12, 'DDMMYYYYHHmmss').format('DD/MM/YYYY HH:mm:ss') : ''} />

                <RowData textLeft={t('order_status')} textRight={data.c19} rightColor={colorOrderStatus(data.c8)} last />

                {data.c8 === '2' || data.c8 === '3' ? null : (
                    <ButtonCustom
                        text={t('info_cal_ord')}
                        onClick={() => {
                            // if (!glb_sv.checkOtp(navigation, showModalAction)) return;
                            setModalCancelOrder(true)
                        }}
                        disabled={data.c8 === '2' || data.c8 === '3'}
                        type='cancel'
                    />
                )}
            </div>

            {isOpenOTP && <ModalOtp isOpenOTP={isOpenOTP} setIsOpenOTP={setIsOpenOTP} OTP_Data={OTP_Data} />}

            <Modal
                visible={modalCancelOrder}
                bodyStyle={{ backgroundColor: 'var(--HEADER__BG__COLOR)', borderRadius: '1rem' }}
                footer={true}
                closable={false}
                onClick={() => setModalCancelOrder(false)}>
                <div
                    style={{
                        backgroundColor: 'var(--HEADER__BG__COLOR)',
                        paddingHorizontal: 20,
                        paddingTop: 32,
                        paddingBottom: 8,
                        justifyContent: 'flex-start',
                        borderRadius: 4,
                        borderColor: 'rgba(0, 0, 0, 0.1)',
                    }}>
                    <div className='mb-2' style={{ alignItems: 'center', justifyContent: 'center', display: 'flex' }}>
                        <svg width={32} height={32} viewBox='0 0 32 32'>
                            <g clipPath='url(#prefix__clip0)'>
                                <path
                                    d='M32 16a15.9 15.9 0 01-4.653 11.281A15.897 15.897 0 0116.105 32h-.032L16 32a15.893 15.893 0 01-11.313-4.686A15.893 15.893 0 010 16c0-4.274 1.664-8.291 4.687-11.314A15.895 15.895 0 0116.107.001a15.898 15.898 0 0111.24 4.718A15.9 15.9 0 0132 16z'
                                    fill={"var(--DOWN__COLOR)"}
                                />
                                <path
                                    d='M18.745 9.404v7.152a2.669 2.669 0 01-2.666 2.666h-.006c-.687-.02-1.396-.297-1.88-.78a2.65 2.65 0 01-.78-1.886V9.404c0-1.454 1.207-2.625 2.66-2.666h.006a2.67 2.67 0 012.666 2.666z'
                                    fill='#fff'
                                />
                                <path
                                    d='M18.602 22.602a2.526 2.526 0 01-2.523 2.524h-.006a2.526 2.526 0 01-2.517-2.524c0-1.389 1.128-2.52 2.517-2.523h.006a2.526 2.526 0 012.523 2.523z'
                                    fill='#fff'
                                />
                            </g>
                            <defs>
                                <clipPath id='prefix__clip0'>
                                    <path fill='#fff' d='M0 0h32v32H0z' />
                                </clipPath>
                            </defs>
                        </svg>
                    </div>
                    <div style={{ marginVertical: 22, fontSize: fontSizes.big, color: styles.DOWN__COLOR, textAlign: 'center' }}>{t('info_cal_ord')}</div>

                    <div style={{ color: styles.PRIMARY__CONTENT__COLOR, fontSize: fontSizes.normal }}>{t('are_you_sure_you_want_cancel_order')}</div>

                    <Button
                        onClick={sendCancelAdvOrder}
                        transparent
                        block
                        style={{
                            backgroundColor: styles.DOWN__COLOR,
                            justifyContent: 'center',
                            alignSelf: 'center',
                            marginTop: dimensions.vertical(25),
                            borderRadius: 8,
                            padding: '0.5rem',
                            width: '100%',
                            height: 'unset',
                        }}>
                        <span style={{ color: '#FFF', fontSize: fontSizes.small }}>{t('common_Ok').toUpperCase()}</span>
                    </Button>

                    <Button
                        onClick={() => setModalCancelOrder(false)}
                        style={{
                            justifyContent: 'center',
                            alignSelf: 'center',
                            width: '100%',
                            padding: '0.5rem',
                            backgroundColor: 'transparent',
                            borderWidth: 0,
                            height: 'unset',
                        }}>
                        <Text style={{ color: styles.PRIMARY__CONTENT__COLOR, fontSize: fontSizes.small, opacity: 0.34 }}>{t('common_Cancel').toUpperCase()}</Text>
                    </Button>
                </div>
            </Modal>
        </div>
    )
}

export default AdvOrderDetail