import React, { useContext, useRef, useLayoutEffect, memo, useState, useEffect, useCallback } from 'react'
import { StoreContext } from '../../store';
import { useTranslation } from 'react-i18next';
import { dimensions, fontSizes } from '../../styles';
import { FormatNumber, glb_sv, FormatNumberAC, eventList } from '../../utils';
import ChartBuySell from './chart-price/chart-buy-sell'
import { Button, Row, Spin, Col } from 'antd';

const maxCallback = (max, cur) => Math.max(max, cur.t1321, cur.t1331);

const ListBuySell = ({ stockCode }) => {
    const { t } = useTranslation();
    const [stock, setStock] = useState(glb_sv.StockMarket[stockCode] ? { ...glb_sv.StockMarket[stockCode] } : { TP: [] })

    useEffect(() => {
        const eventMarket = glb_sv.eventMarket.subscribe(msg => {
            if (msg.type === eventList.SUB_STOCK && msg.msgKey === stockCode && msg.message === 'TP') {
                setStock({ ...glb_sv.StockMarket[stockCode] })
            }
            if (msg.type === eventList.SUBSCRIBE_DONE && msg.msgKey === stockCode) {
                setStock({ ...glb_sv.StockMarket[stockCode] })
            }
            setStock(glb_sv.StockMarket[stockCode] ? { ...glb_sv.StockMarket[stockCode] } : { TP: [] })
        });
        return () => {
            eventMarket.unsubscribe();
        }
    }, [])

    const total = stock.TP.reduce(maxCallback, -Infinity);

    return (
        <div style={UI.Table__View}>
            <span style={{ color: 'var(--PRIMARY__CONTENT__COLOR)', marginBottom: dimensions.halfIndent }}>{t('priceboard_over_buy_sell_info')}</span>
            <ChartBuySell TP={stock.TP} />
            <Row style={{ ...UI.Item__Row__Head, backgroundColor: 'var(--SECOND__BG__COLOR)' }}>
                <Col className='flex-1 text-right' style={{ ...UI.Item__Left, color: 'var(--UP__COLOR)' }}>
                    {t('priceboard_bid_short')}
                </Col>
                <Col className='flex-1' style={{ ...UI.Item__Right, color: 'var(--DOWN__COLOR)' }}>
                    {t('priceboard_over_sell_quantity')}
                </Col>
            </Row>
            {stock.TP.map((item, i) =>
                <RowBuySell
                    key={`BuySell__Row__${i}`}
                    i={i}
                    t1321={item.t1321}
                    t132={item.t132}
                    t1331={item.t1331}
                    t133={item.t133}
                    stock={stock}
                    total={total} />
            )}
        </div>
    )
}

// const WIDTH_BG = 175

export const RowBuySell = memo(({ i, t1321 = 0, t132 = 0, t133 = 0, t1331 = 0, stock, total }) => {
    const { fractionPrice, fractionQty } = useContext(StoreContext)
    const [, updateState] = useState();
    const forceUpdate = useCallback(() => updateState({}), []);

    const WIDTH_BG = useRef(0)
    const widthBuy = useRef(0)
    const widthSell = useRef(0)

    useEffect(() => {
        const row = document.getElementById('RowBuySell_' + i);
        if (row) {
            WIDTH_BG.current = row.offsetWidth - 5
            widthBuy.current = t1321 / total * WIDTH_BG.current
            widthSell.current = t1331 / total * WIDTH_BG.current
            forceUpdate();
        }
    }, [])

    useLayoutEffect(() => {
        if (!WIDTH_BG.current) return;
        widthBuy.current = t1321 / total * WIDTH_BG.current;
        widthSell.current = t1331 / total * WIDTH_BG.current;
    }, [t1321, t1331, total])

    return (
        <div className='flex flex-direction-row' style={UI.Item__Row}>
            <div className='flex direction-row flex-1'>
                <div className='flex' style={{ backgroundColor: 'var(--SECOND__BG__COLOR)', ...UI.Item__Badge }}>
                    <span className='fs-normal fw__light' style={{ color: 'var(--PRIMARY__CONTENT__COLOR)', lineHeight: fontSizes.verySmall + 2 + 'px' }}>{i + 1}</span>
                </div>
                <div id={'RowBuySell_' + i} className='flex' style={UI.Item__Left__Row}>
                    <span className='flex fs-normal fw__light' style={{ color: 'var(--PRIMARY__CONTENT__COLOR)', lineHeight: fontSizes.verySmall + 'px', ...UI.Item__View }}>
                        {fractionQty ? FormatNumberAC(t1321, 0, 1) : FormatNumber(t1321, 0, 1, 'short')}
                    </span>
                    <span className='flex fs-normal fw__light' style={{ color: glb_sv.getColor(t132, stock), lineHeight: fontSizes.verySmall + 'px', ...UI.Item__View }}>
                        {t132 == 777777710000 ? 'ATO' :
                            t132 == 777777720000 ? 'ATC' :
                                (fractionPrice ? FormatNumber(t132 / 1000, 2, 1) : FormatNumber(t132, 0, 1))}
                    </span>
                </div>
                <div style={{ backgroundColor: 'var(--BID__COLOR)', width: widthBuy.current, position: 'absolute', right: 'calc(50vw)', height: 21 }} />
            </div>
            <div className='flex flex-direction-row-reverse flex-1'>
                <div className='flex' style={{ backgroundColor: 'var(--SECOND__BG__COLOR)', ...UI.Item__Badge }}>
                    <span style={{ color: 'var(--PRIMARY__CONTENT__COLOR)', fontSize: fontSizes.verySmall, lineHeight: fontSizes.verySmall + 2 + 'px' }}>{i + 1}</span>
                </div>
                <div className='flex' style={UI.Item__Right_Row}>
                    <span className='flex fs-normal fw__light text-left' style={{ color: 'var(--PRIMARY__CONTENT__COLOR)', lineHeight: fontSizes.verySmall + 'px', ...UI.Item__View }}>
                        {fractionQty ? FormatNumberAC(t1331, 0, 1) : FormatNumber(t1331, 0, 1, 'short')}
                    </span>
                    <span className='flex fs-normal fw__light' style={{ color: glb_sv.getColor(t133, stock), lineHeight: fontSizes.verySmall + 'px', ...UI.Item__View }}>
                        {t133 == 777777710000 ? 'ATO' :
                            t133 == 777777720000 ? 'ATC' :
                                (fractionPrice ? FormatNumber(t133 / 1000, 2, 1) : FormatNumber(t133, 0, 1))}
                    </span>
                </div>
                <div style={{ backgroundColor: 'var(--PRIMARY)', width: widthSell.current, position: 'absolute', left: 'calc(50vw)', height: 21 }} />
            </div>
        </div>
    )
})


export default memo(ListBuySell)

