import { Col } from 'antd'
import React, { useContext } from 'react'
import { useTranslation } from 'react-i18next'
//import { StyleSheet, View } from "react-native";
//import { Text } from "../../../basic-components";
import { StoreContext } from '../../../store'
//import { dimensions as dm, fontSizes as fs, fontWeights as fw } from '../../../styles';

const RowTableData = ({
    type = 'table', // type của table
    colSpan, //
    dataSub = [], //
    colorSub, //
    text,
    text2,
    textTime,
    textAlign = 'left', //
    colorText,
    colorText2,
    typeOrder,
    last,
    elipse,
    ...props
}) => {
    const { styles, language } = useContext(StoreContext)
    const { t } = useTranslation()
    const getTextTypeOrder = (typeOrder) => {
        if (language === 'VI') {
            if (typeOrder === 'buy') return 'M'
            if (typeOrder === 'sell') return 'B'
        } else {
            if (typeOrder === 'buy') return 'B'
            if (typeOrder === 'sell') return 'S'
        }
    }
    return (
        <Col
            className=''
            span={colSpan}
            style={{
                justifyContent: typeOrder ? 'flex-start' : 'center',
                flexDirection: typeOrder ? 'row' : 'column',
                alignItems: typeOrder ? 'center' : null,
                display: 'flex',
                paddingTop:'5px'
                // borderBottom: last ? '0' : '1px solid var(--DIVIDER__MODAL__COLOR)',
            }}>
            {typeOrder && (
                <div
                    style={{
                        width: '18px',
                        height: '18px',
                        marginRight: 4,
                        backgroundColor: typeOrder === 'buy' ? styles.PRIMARY : styles.SELL__BG,
                        justifyContent: 'center',
                        alignItems: 'center',
                        borderRadius: 16,
                    }}>
                    <div style={{ color: '#fff', fontWeight: 'bold' }}>{getTextTypeOrder(typeOrder)}</div>
                </div>
            )}
            {dataSub[0] && (
                <div className='flex' style={{ flex: 1, flexDirection: 'row' }}>
                    <div style={{ color: styles.PRIMARY__CONTENT__COLOR }}>{dataSub[0]}</div>
                    <div className='radius-1  ml-1' style={{ backgroundColor: colorSub }}>
                        <div className='p-1 ' style={{ color: '#fff', fontWeight: 'bold' }}>
                            {dataSub[1]}
                        </div>
                    </div>
                </div>
            )}
            {text && (
                <>
                <div
                    style={{
                        whiteSpace: elipse ?'nowrap' : '',
                        textOverflow:elipse ? 'ellipsis' : '',
                        overflow:elipse ? 'hidden' : '',
                        color: colorText ? colorText : styles.PRIMARY__CONTENT__COLOR,
                        textAlign: textAlign,
                    }}>
                    {text}
                </div>
                {textTime && (
                    <div style={{fontSize:'11px',opacity:'0.6'}}>{textTime}</div>
                )}
                </>
            )}
            {text2 && <div style={{ color: colorText2 ? colorText2 : styles.SECOND__CONTENT__COLOR, textAlign: textAlign }}>{text2}</div>}
        </Col>
    )
}

export default RowTableData

