import React, { useState, useContext, useRef } from 'react'
import { StoreContext } from '../../store'
import { useTranslation } from 'react-i18next'
import HeaderComponent from '../../components/header'
import { glb_sv, socket_sv, requestInfo, InputServiceBody, reqFunct, IMAGE } from '../../utils'
import Text from 'antd/lib/typography/Text'
import { useHistory } from 'react-router-dom'
import { Button, Form, Input } from 'antd'
import { LeftOutlined } from '@ant-design/icons'
import { dimensions as dm } from 'styles'
import { ReactComponent as IconBack } from '../../assets/images/common/ic_back.svg'

export default function ForgetPass() {
    const { activeCode, styles } = useContext(StoreContext)
    const { t } = useTranslation()
    let history = useHistory()

    const emailRef = useRef(null)
    const phoneRef = useRef(null)
    const fgtPass_SendReqFlag = useRef(false)
    const timeOutFunct_resetPassword = useRef(null)

    const [username, setUsername] = useState('')
    const [email, setEmail] = useState('')
    const [phone, setPhone] = useState('')

    const resetPassword = () => {
        if (!username.trim() || (!email.trim() && !phone.trim())) return
        if (fgtPass_SendReqFlag.current) {
            return
        }
        fgtPass_SendReqFlag.current = true

        const InVal = ['resetpswd', username.trim(), email.trim(), phone.trim()]

        const clientSeq = socket_sv.getRqSeq()
        const inputServiceBody = new InputServiceBody(clientSeq, 'ALTxCommon', 'ALTxCommon_ChangeInfo', InVal, 'U')
        const reqInfo = new requestInfo(reqFunct.FORGOT_FUNCTION, InVal, resetPasswordResultProc)
        timeOutFunct_resetPassword.current = setTimeout(timeOutFunct, glb_sv.reqTimeout, reqInfo)

        glb_sv.setReqInfoMapValue(clientSeq, reqInfo)
        socket_sv.send2Sv(socket_sv.channels.REQ_MSG, JSON.stringify(inputServiceBody))
    }

    const resetPasswordResultProc = (reqInfoMap, message) => {
        fgtPass_SendReqFlag.current = false
        const cltSeqResult = Number(message['ClientSeq'])
        const errmsg = message['Message']

        if (timeOutFunct_resetPassword.current) clearTimeout(timeOutFunct_resetPassword.current)

        if (Number(message['Result']) === 0) {
            // navigation.navigate(Screens.ALERT_MODAL, {
            //     title: t('common_notify'),
            //     content: errmsg,
            //     typeColor: styles.WARN__COLOR
            // })
        } else {
            // navigation.navigate(Screens.ALERT_MODAL, {
            //     title: t('common_notify'),
            //     content: errmsg,
            //     typeColor: styles.SUCCESS__COLOR,
            //     linkCallback: () => navigation.pop()
            // })
        }
    }

    const timeOutFunct = (reqInfo) => {
        if (reqInfo.reqFunct === reqFunct.FORGOT_FUNCTION) {
            fgtPass_SendReqFlag.current = false
        }

        // navigation.navigate(Screens.ALERT_MODAL, {
        //     title: t('common_notify'),
        //     content: t('common_cant_connect_server'),
        //     typeColor: styles.WARN__COLOR
        // })
    }
    const onFinish = (values) => {
        resetPassword()
    }
    return (
        <div
            className="menu__layout"
            style={{
                backgroundColor: 'var(--PRIMARY__BG__COLOR)',
            }}
        >
            <HeaderComponent
                title={t('login_ForGotPW')}
                transparent
                ColumSpan={[4, 12, 8]}
                alignChild={['center', 'left', 'right']}
                iconLeft={<IconBack style={{ fontSize: 16 }} onClick={() => history.goBack()} />}
            />
            <div style={{ paddingHorizontal: 10 }}>
                <Form
                    name="normal_login"
                    className="common__form"
                    initialValues={{ remember: true }}
                    onFinish={onFinish}
                >
                    <Form.Item>
                        <Text className="common__form__input-label fs-normal">{t('login_user_id')}</Text>
                        <Input
                            allowClear
                            autoFocus
                            size="large"
                            onChange={(e) => setUsername(e.target.value)}
                            defaultValue={username}
                            placeholder=""
                        />
                        {/* {username.length > 4 ? <Icon onClick={() => setUsername('')} type='AntDesign' name='closecircle' style={{ color: '#ccc' }} /> : null} */}
                    </Form.Item>
                    <Form.Item>
                        <Text className="common__form__input-label fs-normal">{t('login_email')}</Text>
                        <Input
                            allowClear
                            size="large"
                            onChange={(e) => setEmail(e.target.value)}
                            defaultValue={email}
                            placeholder=""
                        />
                    </Form.Item>
                    <Form.Item>
                        <Text className="common__form__input-label fs-normal">{t('login_mobile_regist')}</Text>
                        <Input
                            allowClear
                            size="large"
                            onChange={(e) => setPhone(e.target.value)}
                            defaultValue={phone}
                            placeholder=""
                        />
                    </Form.Item>
                    <div
                        className="about__copyright center__item"
                        style={{
                            paddingTop: dm.vertical(48),
                            paddingBottom: dm.vertical(8),
                        }}
                    >
                        <Text className="fs__small">
                            {t(
                                'Chúng tôi sẽ gửi một liên kết đổi mật khẩu tới địa chỉ email của bạn. Vui lòng nhập chính xác!'
                            )}
                            &nbsp;‎‎‎
                        </Text>
                    </div>
                    <Button
                        block
                        type="text"
                        size="large"
                        htmlType="submit"
                        className="login-form-button"
                        disabled={!username.trim() || (!email.trim() && !phone.trim())}
                        style={{
                            backgroundColor: 'var(--PRIMARY)',
                            borderRadius: 8,
                            height: dm.vertical(42),
                            marginTop: dm.vertical(8),
                            marginBottom: dm.vertical(16),
                            opacity: !username.trim() || (!email.trim() && !phone.trim()) ? 0.6 : 1,
                        }}
                    >
                        <Text>{t('login_reset_password')}</Text>
                    </Button>
                </Form>
            </div>
        </div>
    )
}