import React, { memo } from 'react'
import { Col, Row } from 'antd'
import { fontSizes } from '../../styles';
import { StoreContext } from '../../store';

const BasicStockInfo = ({ name, figure, color, right, figure1, figure2, figure1Color, figure2Color, type, center }) => {
    const { styles } = React.useContext(StoreContext)

    return (
        <Row className='flex flex-1 flex-direction-column fs__smallest' style={{ alignItems: 'flex-start', padding: '5px 0 5px 10px', background: 'var(--BUTTON__SECONDARY)', borderRadius: 8, marginBottom: 5, marginRight: 5 }}>
            <span style={{ fontSize: fontSizes.smallest, color: 'var(--SECOND__CONTENT__COLOR)', marginBottom: 5 }}>
                {name}
            </span>
            <span style={{ fontSize: fontSizes.smallest, color: 'var(--PRIMARY__CONTENT__COLOR)' }}>
                {figure ? <span style={{ fontSize: fontSizes.smallest, color: color || 'var(--PRIMARY__CONTENT__COLOR)' }}>{figure}</span> : <>
                    <span style={{ fontSize: fontSizes.smallest, color: figure1Color || 'var(--PRIMARY__CONTENT__COLOR)' }}>{figure1}</span>{type ? type : '/'}
                    <span style={{ fontSize: fontSizes.smallest, color: figure2Color || 'var(--PRIMARY__CONTENT__COLOR)' }}>{figure2}</span>
                </>}
            </span>
        </Row>
    )
}

export default memo(BasicStockInfo)