import React, { useContext, useRef, useState, useEffect } from 'react'
import { useTranslation } from 'react-i18next'
import { Modal, InputItem } from 'antd-mobile'
import { ReactComponent as Search } from '../../assets/images/common/search.svg'
import { StoreContext } from 'store'
import { glb_sv, eventList, socket_sv, STORE_KEY } from 'utils'
import { dimensions, fontSizes } from 'styles'
import { useHistory } from 'react-router-dom'
import Text from 'antd/lib/typography/Text'
import { Col, Row } from 'antd'

const SearchStockModal = ({ onChangeStock, isOnShow, setIsOnShow, searchRef }) => {
	const { t } = useTranslation()
	const history = useHistory();
	
	//  const { actionType, changeStock } = history.location.state;

	const { language, recentStkList, setRecentStkList } = useContext(
		StoreContext
	);

	const nameSearchRef = useRef(null)

	const [seach, setSearch] = useState("");
	const [listStock, setListStock] = useState([]);
	const [isModalVisible, setModal] = useState(false);
	const timeOutSearch = useRef(null);
	const [selectStock, setSelectStock] = useState("");

	const [recentList, setRecentList] = useState([]);

	useEffect(() => {
		console.log('searchRef', searchRef)
		if(searchRef?.current){
			searchRef.current.focus();
		}
		// console.log(history.state)
		if (glb_sv.mrk_stklist.length) {
			if (glb_sv.mrk_stklist_lang !== language) loadStkList();
			else getListRencent();
		} else {
			loadStkList();
		}
		const commonEvent = glb_sv.commonEvent.subscribe((msg) => {
			if (msg.type === eventList.CHANGE_LIST_STOCK) {
				getListRencent();

				if (!seach) return;
				const newList = glb_sv.mrk_stklist
					.filter(
						(e) => e.t55.includes(seach) || seach.toUpperCase().includes(e.t55)
					)
					.slice(0, 30)
					.sort(function (a, b) {
						if (a.t55 > b.t55) return -1;
						if (a.t55 < b.t55) return 1;
						return 0;
					});
				const newList1 = [];
				newList.forEach((element) => {
					const obj = glb_sv.mrk_stklist.find((e) => e.t55 === element.t55);
					if (obj) {
						const cloneObj = { ...obj };
						glb_sv.allListFav.forEach((item) => {
							if (item.ListStock.some((temp) => temp === cloneObj.t55)) {
								cloneObj.isFav = true;
								if (cloneObj.ExistNameFav) {
									cloneObj.ExistNameFav += ", " + item.c2;
								} else cloneObj.ExistNameFav = item.c2;
							}
						});
						newList1.push(cloneObj);
					}
				});
				setListStock(newList1);
			}
			if (msg.type === eventList.REQ_GET_MKT_INF_FIRST) {
				if (glb_sv.mrk_stklist.length) {
					if (glb_sv.mrk_stklist_lang !== language) loadStkList();
					else getListRencent();
				} else {
					loadStkList();
				}
			}
		});
		const eventMarket = glb_sv.eventMarket.subscribe((msg) => {
			if (msg.type === eventList.REQ_RE_GET_MKT_INF) {
				if (glb_sv.mrk_stklist.length) {
					if (glb_sv.mrk_stklist_lang !== language) loadStkList();
					else getListRencent();
				} else {
					loadStkList();
				}
			}
		});
		return () => {
			commonEvent.unsubscribe();
			eventMarket.unsubscribe();
			nameSearchRef.current = null
		};
	}, []);

	const loadStkList = async () => {
		const domain = socket_sv.url_stream[socket_sv.url_stream_index];
		const url = [
			domain +
			"/assets/STOCK_INFO.OTS.01" +
			(language === "VI" ? "" : "." + language) +
			".json",
			domain +
			"/assets/STOCK_INFO.OTS.03" +
			(language === "VI" ? "" : "." + language) +
			".json",
			domain +
			"/assets/STOCK_INFO.OTS.05" +
			(language === "VI" ? "" : "." + language) +
			".json",
		];
		const promises = url.map(async (pageNo) => {
			const req = await fetch(pageNo, {
				headers: {
					"Content-Type": "application/json",
					Accept: "application/json",
				},
			});
			return req.json();
		});
		const data = await Promise.all(promises).catch(function (err) {
			console.log("err loadStkList", err);
		});
		if (data && data.length === 3) {
			glb_sv.IndexMarket["HSX|I|HSXIndex"].STOCK_INFO = data[0];
			glb_sv.IndexMarket["HNX|I|HNXIndex"].STOCK_INFO = data[1];
			glb_sv.IndexMarket["HNX|I|HNXUpcomIndex"].STOCK_INFO = data[2];
			glb_sv.mrk_stklist = data[0].concat(data[1].concat(data[2]));
			glb_sv.mrk_stklist_lang = language;
			getListRencent();
			getListStock(seach);
		} else {
			console.log("err loadStkList");
		}
	};

	const getListRencent = () => {
		const newList = [];
		recentStkList.forEach((element) => {
			const obj = glb_sv.mrk_stklist.find((e) => e.t55 === element);
			if (obj) {
				const cloneObj = { ...obj };
				glb_sv.allListFav.forEach((item) => {
					if (item.ListStock.some((temp) => temp === cloneObj.t55)) {
						cloneObj.isFav = true;
						if (cloneObj.ExistNameFav) {
							cloneObj.ExistNameFav += ", " + item.c2;
						} else cloneObj.ExistNameFav = item.c2;
					}
				});
				newList.push(cloneObj);
			}
		});
		setRecentList(newList);
		return newList;
	};

	const getListStock = (namee) => {
		nameSearchRef.current = namee.toUpperCase()
		const name = namee.toUpperCase();
		setSearch(name);
		if (name === "") {
			if (timeOutSearch.current) clearTimeout(timeOutSearch.current);
			setListStock([]);
			return;
		}
		if (timeOutSearch.current) clearTimeout(timeOutSearch.current);
		timeOutSearch.current = setTimeout(() => {
			const newList = glb_sv.mrk_stklist
				.filter(
					(e) => e.t55.includes(name) || name.toUpperCase().includes(e.t55)
				)
				.slice(0, 30)
				.sort(function (a, b) {
					if (a.t55 > b.t55) return -1;
					if (a.t55 < b.t55) return 1;
					return 0;
				});

			const newList1 = [];
			newList.forEach((element) => {
				const obj = glb_sv.mrk_stklist.find((e) => e.t55 === element.t55);
				if (obj) {
					const cloneObj = { ...obj };
					// console.log('glb_sv.allListFav', glb_sv.allListFav)
					glb_sv.allListFav.forEach((item) => {
						if (item.ListStock.some((temp) => temp === cloneObj.t55)) {
							cloneObj.isFav = true;
							if (cloneObj.ExistNameFav) {
								cloneObj.ExistNameFav += ", " + item.c2;
							} else cloneObj.ExistNameFav = item.c2;
						}
					});
					newList1.push(cloneObj);
				}
			});
			setListStock(newList1);
		}, 300);
	};

	const onPressAddStockToList = (stk) => {
		setSelectStock(stk);
		setModal(true);

		return;
	};



	const StockItem = ({ item }) => {
		// console.log('StockItem', item)
		return (
			<Row
				className="flex list"
				onClick={() => handleStock(item)}
			>
				<Col span={5} style={{ display: 'flex', alignItems: 'center', marginRight: '0.5rem' }}>{item.t55}</Col>
				<Col span={18} style={{
					display: 'flex',
					flexDirection: 'column',
					alignItems: 'flex-start',
				}}>
					<Text style={{ color: "var(--SECOND__CONTENT__COLOR)", textAlign: 'start' }} className="fs-normal fw__normal">{getExchange(item.U10)}</Text>
					<Text style={{ color: "var(--SECOND__CONTENT__COLOR)", textAlign: 'start' }} className="fs-normal"> {item.U9}</Text>
				</Col>
			</Row>
		);
	};

	const handleStock = (item) => {
		const newList = recentStkList.filter((e) => e !== item.t55);
		if (newList.length >= 5) {
			newList.pop();
		}
		newList.unshift(item.t55);
		localStorage.setItem(STORE_KEY.RECENT_STOCK_LIST, JSON.stringify(newList));
		setRecentStkList(newList);

		const newList1 = [];
		newList.forEach((element) => {
			const obj = glb_sv.mrk_stklist.find((e) => e.t55 === element);
			if (obj) {
				const cloneObj = { ...obj };
				glb_sv.allListFav.forEach((item) => {
					if (item.ListStock.some((temp) => temp === cloneObj.t55)) {
						cloneObj.isFav = true;
						if (cloneObj.ExistNameFav) {
							cloneObj.ExistNameFav += ", " + item.c2;
						} else cloneObj.ExistNameFav = item.c2;
					}
				});
				newList1.push(cloneObj);
			}
		});
		setRecentList(newList1);

		onChangeStock(item?.t55)
		setIsOnShow(false);
	};

	const getExchange = (type) => {
		if (type === "01") return "HOSE";
		if (type === "03") return "HNX";
		if (type === "05") return "UPCOM";
	};

	const resetData = () => {
		setSearch("");
		setListStock([]);
	};

	const onChangeStatus = () => {
		getListRencent();

		if (!seach) return;
		const newList = glb_sv.mrk_stklist
			.filter(
				(e) => e.t55.includes(seach) || seach.toUpperCase().includes(e.t55)
			)
			.slice(0, 30)
			.sort(function (a, b) {
				if (a.t55 > b.t55) return -1;
				if (a.t55 < b.t55) return 1;
				return 0;
			});
		const newList1 = [];
		newList.forEach((element) => {
			const obj = glb_sv.mrk_stklist.find((e) => e.t55 === element.t55);
			if (obj) {
				const cloneObj = { ...obj };
				glb_sv.allListFav.forEach((item) => {
					if (item.ListStock.some((temp) => temp === cloneObj.t55)) {
						cloneObj.isFav = true;
						if (cloneObj.ExistNameFav) {
							cloneObj.ExistNameFav += ", " + item.c2;
						} else cloneObj.ExistNameFav = item.c2;
					}
				});
				newList1.push(cloneObj);
			}
		});
		setListStock(newList1);
	};



	return <Modal
		visible={isOnShow}
		onCancel={() => setIsOnShow(false)}
	>
		<div style={{
			padding: '1rem 0.5rem'
		}}>
			<div>
				<Row>
					<Col span={21}>
						<InputItem placeholder={t('choose_symbol_trading')} style={{color: 'var(--PRIMARY__CONTENT__COLOR'}} ref={searchRef} onChange={(value) => getListStock(value)} value={nameSearchRef.current}>
							<Search style={{ display: 'block', color: 'var(--PRIMARY__CONTENT__COLOR)' }} />
						</InputItem>
					</Col>
					<Col span={2} style={{ display: 'flex', alignItems: 'center', marginLeft: '0.5rem' }} onClick={() => setIsOnShow(false)}>
						<Text>{t('common_accomplished')}</Text>
					</Col>
				</Row>
				<div>
					{listStock.length === 0 && seach.length === 0 ? (
						recentList.length ? (
							<div
								style={{
									marginTop: '0.5rem',
									paddingRight: dimensions.halfIndent,
									paddingLeft: dimensions.halfIndent,
								}}
							>
								<span
									style={{
										color: "var(--HEADER__CONTENT__COLOR)",
										paddingHorizontal: 5,
									}}
								>
									{t("search_recent")}
								</span>
								{recentList.map((item) => (
									<StockItem item={item} key={item.t55} />
								))}
							</div>
						) : null
					) : (
						<div className="flex flex-1 flex-direction-column" style={UI.view_list}>
							{listStock.map((item) => {
								return <StockItem key={item.t55} item={item} key={item.t55} />;
							})}
						</div>
					)}
				</div>
			</div>
		</div>
	</Modal>

}

const UI = {
	view_list: {
		padding: dimensions.halfIndent,
	},
};



export default SearchStockModal