import { Button } from 'antd';
import { TextareaItem } from 'antd-mobile';
import React, { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { useHistory } from 'react-router-dom';
import { ReactComponent as IconBack } from '../../assets/images/common/ic_back.svg';
import { dimensions } from '../../styles';
import { STORE_KEY } from '../../utils';

const NoteMarket = () => {
    const history = useHistory();
    const { t55, U9 } = history.location.state;
    const { t } = useTranslation();
    const [note, setNote] = useState('')

    useEffect(() => {
        const dataPlain = localStorage.getItem(STORE_KEY.NOTE_MARKET);
        const data = dataPlain ? JSON.parse(dataPlain) : {};
        if (data && data[t55]) {
            setNote(data[t55])
        }
    }, []);

    const saveNote = () => {
        const dataPlain = localStorage.getItem(STORE_KEY.NOTE_MARKET);
        const data = dataPlain ? JSON.parse(dataPlain) : {};
        if (data) {
            data[t55] = note;
            localStorage.setItem(STORE_KEY.NOTE_MARKET, JSON.stringify(data));
        } else {
            const data = {};
            data[t55] = note;
            localStorage.setItem(STORE_KEY.NOTE_MARKET, JSON.stringify(data));
        };
        history.goBack()
    }

    return (
        <div className='flex flex-1 flex-direction-column'>
            <div className='flex align-items-center' style={UI.header}>
                <Button ghost onClick={() => history.goBack()} style={UI.button}>
                    <IconBack />
                </Button>
                <div className='flex flex-1 flex-direction-column justify-content-center align-items-center' style={{ borderRadius: 10, padding: 0 }}>
                    <span className='fs__xmedium' style={{ color: 'var(--PRIMARY__CONTENT__COLOR)' }}>{t55}</span>
                </div>
                <Button onClick={saveNote} style={UI.button}>
                    {t('save')}
                </Button>
            </div>
            <span className='fs_small' style={{ color: 'var(--SECOND__CONTENT__COLOR)', padding: dimensions.halfIndent }}>{U9}</span>
            <div className='flex flex-1' style={{ borderTop: '1px solid var(--DIVIDER__COLOR)', backgroundColor: 'var(--PRIMARY__BG__COLOR)' }}>
                <TextareaItem
                    placeholder={t('note_something')}
                    autoHeight
                    className='fs__small flex-1 align-items-start'
                    style={{
                        color: 'var(--PRIMARY__CONTENT__COLOR)'
                    }}
                    value={note}
                    onChange={newNote => setNote(newNote)}
                />
            </div>
        </div>
    );
}

const UI = {
    header: {
        height: 50,
        paddingRight: 5,
        paddingLeft: 5,
        marginTop: 5
    },
    button: {
        // paddingTop: 0, 
        // paddingBottom: 0, 
        height: 35,
        paddingRight: 6,
        paddingLeft: 6,
        border: 'none',
        borderRadius: '21px',
        padding: '5px 16px',
        background: '#60B44A',
        color: '#fff'
    }
}

export default NoteMarket