import React, { useContext, useState, useEffect } from 'react';
import { Row, Col, List, Button } from 'antd'
import { useHistory } from "react-router-dom";
import { RightOutlined } from '@ant-design/icons';
import HeaderComponent from '../../components/header';
import { useTranslation } from 'react-i18next';
import Text from 'antd/lib/typography/Text';
import { glb_sv, sendRequest, reqFunct } from '../../utils';
import { routes } from 'navigation/routes';
import { StoreTrading } from 'store-trading';
import {ReactComponent as IconBack} from '../../assets/images/common/ic_back.svg'


const ServiceInfo = {
    GET_USER_INFO: {
        reqFunct: reqFunct.GET_USER_INFO,
        WorkerName: 'ALTqAccount',
        ServiceName: 'ALTqAccount_0102_1',
        Operation: 'Q',
        ClientSentTime: '0'
    }
}

const UserInfoLayout = () => {
    const { t } = useTranslation();
    let history = useHistory()
    const { authFlag } = useContext(StoreTrading)
    const [data, setData] = useState({});

    useEffect(() => {
        if (!authFlag) history.replace(routes.APP.path, {})
        getAccoutInfo();
    }, [])

    const getAccoutInfo = () => {
        const InputParams = ['01', glb_sv.objShareGlb['AcntMain']];
        sendRequest(ServiceInfo.GET_USER_INFO, InputParams, handleGetAccoutInfo)
    }

    const handleGetAccoutInfo = (reqInfoMap, message) => {
        if (Number(message['Result']) === 0) {
            return;
        } else {
            let jsondata;
            try {
                jsondata = message['Data'] ? JSON.parse(message['Data']) : [];
                setData(jsondata[0] ? jsondata[0] : {});
            } catch (err) {
                console.log("handleGetAccoutInfo -> err", err)
                return
            }
        }
    }

    const OpenLink = (url, type) => {
        switch (type) {
            case 'tel':
                window.open('tel:' + url)
                break;
            case 'email':
                window.open('mailto:' + url)
                break;
            default:
                window.open(url, '_blank');
                break;
        }
    };

    return (
        <div className='menu__layout' style={{ backgroundColor: 'var(--PRIMARY__BG__COLOR)', height: '100%' }}>
            <HeaderComponent
                title={t('account_security')}
            />
            <div>
                <List>
                    <List.Item>
                        <Col span={10}>
                            <Text>{t('customer_fullname')}</Text>
                        </Col>
                        <Col span={14}>
                            <Text>{data.c1}</Text>
                        </Col>
                    </List.Item>
                    <List.Item>
                        <Col span={10}>
                            <Text>{t('login_user_id')}</Text>
                        </Col>
                        <Col span={14}>
                            <Text>{glb_sv.objShareGlb.AcntMain}</Text>
                        </Col>
                    </List.Item>
                    <List.Item>
                        <Col span={10}>
                            <Text>{t('account_number')}</Text>
                        </Col>
                        <Col span={14}>
                            <Text>{data.c2}</Text>
                        </Col>
                    </List.Item>
                    <List.Item>
                        <Col span={10}>
                            <Text>{t('account_type')}</Text>
                        </Col>
                        <Col span={14}>
                            <Text>{data.c3}</Text>
                        </Col>
                    </List.Item>
                    <List.Item>
                        <Col span={10}>
                            <Text>{t('trading_fee_type')}</Text>
                        </Col>
                        <Col span={14}>
                            <Text>{data.c38}</Text>
                        </Col>
                    </List.Item>
                    <List.Item>
                        <Col span={10}>
                            <Text>{t('branch_open_account')}</Text>
                        </Col>
                        <Col span={14}>
                            <Text>{data.c32}</Text>
                        </Col>
                    </List.Item>
                    <List.Item>
                        <Col span={10}>
                            <Text>{t('branch_manage')}</Text>
                        </Col>
                        <Col span={14}>
                            <Text>{data.c33}</Text>
                        </Col>
                    </List.Item>
                    <List.Item>
                        <Col span={10}>
                            <Text>{t('broker_manage')}</Text>
                        </Col>
                        <Col span={14}>
                            <Text>{data.c34}</Text>
                        </Col>
                    </List.Item>
                    <List.Item onClick={() => OpenLink(glb_sv.objShareGlb['userInfo']?.c26, 'email')}>
                        <Col span={10}>
                            <Text>{t('email')}</Text>
                        </Col>
                        <Col span={14}>
                            <Text>{glb_sv.objShareGlb['userInfo']?.c26}</Text>
                        </Col>
                    </List.Item>
                    <List.Item onClick={() => OpenLink(glb_sv.objShareGlb['userInfo']?.c27, 'tel')}>
                        <Col span={10}>
                            <Text>{t('phone')}</Text>
                        </Col>
                        <Col span={14}>
                            <Text>{glb_sv.objShareGlb['userInfo']?.c27}</Text>
                        </Col>
                    </List.Item>
                </List>
                <Row style={{ backgroundColor: 'var(--SECOND__BG__COLOR)', padding: 8 }}>
                    <Text>{t('account__security')}</Text>
                </Row>
                <List>
                    <List.Item onClick={() => history.push(routes.CHANGE_PASSWORD.path, { type: 'change_login'})}>
                        <Col span={20}>
                            <Text style={{ color: 'var(--BLUE__COLOR)'}}>{t('common_change_login_pass')}</Text>
                        </Col>
                        <Col span={4} className='center__item'>
                            <RightOutlined style={{ fontSize: 16 }} />
                        </Col>
                    </List.Item>
                    <List.Item onClick={() => history.push(routes.CHANGE_PASSWORD.path, { type: 'change_order'})}>
                        <Col span={20}>
                            <Text style={{ color: 'var(--BLUE__COLOR)' }}>{t('common_change_order_pass')}</Text>
                        </Col>
                        <Col span={4} className='center__item'>
                            <RightOutlined style={{ fontSize: 16 }} />
                        </Col>
                    </List.Item>
                </List>
                <Row>
                    <Button type="danger" block size='large' onClick={() => history.go(0)}>
                        <Text style={{ color: 'white' }}>{t('logout').toLocaleUpperCase()}</Text>
                    </Button>
                </Row>
            </div>
        </div>
    )
}

export default UserInfoLayout

