import React, { useState, useContext, useRef, useEffect, useCallback } from 'react';
import { StoreContext } from '../../store';
import { RowNews } from '../../components/news';
import { reqFunct, InputServiceBody, requestInfo, socket_sv, glb_sv } from '../../utils';
import dayjs from 'dayjs'
import { Carousel } from 'antd'
import { useHistory } from "react-router-dom";
import { routes } from 'navigation/routes';
import { dimensions, fontSizes } from '../../styles';

const contentStyle = {
    height: dimensions.indent*12.5,
    color: '#fff',
    backgroundSize: 'cover',
};


export default function DeepNews() {
    let history = useHistory()
    const { styles, language } = useContext(StoreContext);
    const [newsList, setNewsList] = useState(glb_sv.dataDeepNews.list)
    const getDeepNewsFlag = useRef(false)
    const timeOutFunct_getDeepNews = useRef(null)
    const newsListRef = useRef([])

    const [refreshing, setRefreshing] = useState(false);

    useEffect(() => {
        if (glb_sv.dataDeepNews.language !== language) getDeepNews();
    }, [language])

    const getDeepNews = () => {

        if (getDeepNewsFlag.current) return;
        const InVal = ['03', '%'];
        const clientSeq = socket_sv.getRqSeq();
        const inputServiceBody = new InputServiceBody(clientSeq, 'FOSqNews', 'FOSqNews_NewsInfo', InVal, 'Q');
        const reqInfo = new requestInfo(reqFunct.GET_DEEP_NEWS, InVal, getDeepNewsResultProc)
        glb_sv.setReqInfoMapValue(clientSeq, reqInfo)
        socket_sv.send2Sv(socket_sv.channels.REQ_MSG, JSON.stringify(inputServiceBody))
        getDeepNewsFlag.current = true
        newsListRef.current = []
        timeOutFunct_getDeepNews.current = setTimeout(timeOutFunct, glb_sv.reqTimeout, reqInfo);
    }

    const getDeepNewsResultProc = (reqInfoMap, message) => {
        setRefreshing(false);
        if (Number(message['Result']) === 0) {
            return
        } else {
            let jsondata = []
            try {
                if (!message['Data']) {
                } else {
                    jsondata = JSON.parse(glb_sv.filterStrBfParse(message['Data']))
                }
            } catch (err) {
                getDeepNewsFlag.current = false
                return
            }
            newsListRef.current = newsListRef.current.concat(jsondata)
            if (Number(message['Packet']) <= 0) {
                getDeepNewsFlag.current = false
                setNewsList(newsListRef.current)
                glb_sv.dataDeepNews.list = newsListRef.current;
                glb_sv.dataDeepNews.language = language;
            }
        }
    }

    const timeOutFunct = reqInfo => {
        if (reqInfo.reqFunct === reqFunct.GET_DEEP_NEWS) {
            getDeepNewsFlag.current = false;
        }
    }

    const onRefresh = useCallback(() => {
        setRefreshing(true);
        getDeepNews();
    }, []);


    return (
        <>
        {/* {
            newsList.map(item =>
                <RowNews
                    key={item.c0}
                    // onChange={data => navigation.navigate(Screens.NEWS_DETAIL, data)}
                    link={item.c3}
                    styles={styles}
                    title={item.c4}
                    subtitle={item.c9 + ' ∙ ' + dayjs(item.c6, 'YYYYMMDDHHmmss').format('DD/MM HH:mm:ss')}
                    uri={item.c7} />
            )
        } */}
                {newsList.length > 3 &&
                    <Carousel autoplay>
                        {newsList.slice(0, 3).map((news) =>
                            <div key={news.c0} onClick={() => history.push(routes.NEWS_DETAIL.path, { title: news.c4, link: news.c3 })}>
                                <h3 className="center__item" style={{ ...contentStyle, backgroundImage: `url(${news.c7})` }}>
                                    {news.c4}
                                </h3>
                            </div>
                        )}
                    </Carousel>
                }
                {newsList.length > 3 ? <>
                    {newsList.slice(3, newsList.length).map(item =>
                        <RowNews
                            key={item.c0}
                            link={item.c3}
                            styles={styles}
                            title={item.c4}
                            subtitle={item.c9 + ' ∙ ' + dayjs(item.c6, 'YYYYMMDDHHmmss').format('DD/MM HH:mm:ss')}
                            uri={item.c7} />
                    )}
                </> : <>
                        {newsList.map(item =>
                            <RowNews
                                key={item.c0}
                                link={item.c3}
                                styles={styles}
                                title={item.c4}
                                subtitle={item.c9 + ' ∙ ' + dayjs(item.c6, 'YYYYMMDDHHmmss').format('DD/MM HH:mm:ss')}
                                uri={item.c7} />
                        )}
                    </>}
        </>
        // <Content
        //     refreshControl={
        //         <RefreshControl refreshing={refreshing} onRefresh={onRefresh} />}
        //     style={{ padding: dimensions.halfIndent, backgroundColor: 'var(--PRIMARY__BG__COLOR)' }}>
        //     {newsList.map(item =>
        //         <RowNews
        //             key={item.c0}
        //             onChange={data => navigation.navigate(Screens.NEWS_DETAIL, data)}
        //             link={item.c3}
        //             styles={styles}
        //             title={item.c4}
        //             subtitle={item.c9 + ' ∙ ' + dayjs(item.c6, 'YYYYMMDDHHmmss').format('DD/MM HH:mm:ss')}
        //             uri={item.c7} />
        //     )}
        // </Content>
    );
}
