import { Col, Row } from 'antd';
import Title from 'antd/lib/typography/Title';
import React, { memo } from 'react';
import { useHistory } from "react-router-dom";
import { glb_sv } from 'utils';
import { ReactComponent as IconBack } from '../../assets/images/common/ic_back.svg';
import { dimensions } from '../../styles';

/**
 * 
 * @param {alignChild} param0 array left|center|right
 */
const HeaderComponent = ({
    title = 'config',
    iconLeft,
    iconRight,
    titleAlign,
    colorTitle,
    ColumSpan = [2, 18, 4],
    onClickBack,
    alignChild = ['center', 'center', 'center'],
    transparent,
    backgroundColor = 'var(--SECOND__BG__COLOR)',
    ...rest }) => {
    let history = useHistory()

    const goBack = () => history.goBack();

    return (
        <Row {...rest}
            className="header__navigation"
            style={{ backgroundColor: transparent ? 'transparent' : backgroundColor, height: glb_sv.isTablet ? 60 : 50 }}>
            <Col className={`center__item--${alignChild[0]}`} span={ColumSpan[0]}>
                {iconLeft ? iconLeft : <IconBack style={{ fontSize: dimensions.iconSize }} onClick={onClickBack || goBack} />}
            </Col>
            <Col className={`center__item--${alignChild[1]}`} span={ColumSpan[1]} style={{ justifyContent: titleAlign }}>
                <Title className='header__title fs__medium' level={5} style={{ color: colorTitle }}>
                    {title.length > 40 ? title.slice(0, 40) + '' : title}
                </Title>
            </Col>
            <Col className={`center__item--${alignChild[2]}`} span={ColumSpan[2]}>
                {iconRight}
            </Col>
        </Row>
    )
}

export default memo(HeaderComponent)