import React, { useContext, memo } from 'react';
import { StoreContext } from '../../store';
import { useTranslation } from 'react-i18next';

import { fontSizes, dimensions, IconSvg, fontWeights } from '../../styles';
import { Modal } from 'antd';
import Text from 'antd/lib/typography/Text';
import Checkbox from 'antd/lib/checkbox/Checkbox';

function ModalChangeSeasontp({ visible, setVisible, list, handleChangeSessontp, seasonTp }) {

    const { styles } = useContext(StoreContext);
    const { t } = useTranslation();

    return (
        <Modal
            visible={visible}
            footer={null}
            onClose={() => setVisible(false)}
            style={UI.modal}>
            <div style={{
                backgroundColor: styles.PRIMARY__BG__COLOR,
                padding: dimensions.moderate(24),
                justifyContent: 'flex-start',
                borderTopLeftRadius: 12,
                borderTopRightRadius: 12
            }}>
                <Text style={{ marginBottom: dimensions.vertical(24), fontSize: fontSizes.xmedium, color: styles.PRIMARY__CONTENT__COLOR, fontWeight: fontWeights.semiBold }}>
                    {t('choose_sesson_tp')}
                </Text>

                {list.map(item =>
                    <div key={item.key} onClick={() => {
                        handleChangeSessontp(item.key)
                    }} style={UI.row}>
                        <Checkbox checked={seasonTp === item.key}></Checkbox>
                        <Text style={{ color: styles.PRIMARY__CONTENT__COLOR, fontSize: fontSizes.medium, marginLeft: dimensions.moderate(12) }}>
                            {t(item.name)}
                        </Text>
                    </div>
                )}

            </div>
        </Modal>
    );
}

export default memo(ModalChangeSeasontp, areEqual)

function areEqual(prev, next) {
    if (prev.visible === next.visible) return true
    return false
}

const UI = {
    modal: {
        justifyContent: 'flex-end',
        margin: 0,
    },
    row: {
        flexDirection: 'row',
        padding: dimensions.moderate(7),
        marginBottom: dimensions.vertical(12),
        alignItems: 'center'
    }
}