import React, { useState, useEffect, useContext, useRef } from 'react';
import { useTranslation } from 'react-i18next';
import { StoreTrading } from 'store-trading';
import { Button, Spin } from 'antd';
import { glb_sv, sendRequest, FormatNumber } from '../../utils';
import MarketCapTable from './table';

const ServiceInfo = {
    getMarketCapData: {
        WorkerName: 'FOSqMkt03',
        ServiceName: 'FOSqMkt03_MarketCap',
        ClientSentTime: '0',
        Operation: 'Q',
    }
}

const MarketCap = ({ }) => {
    const { t } = useTranslation();
    const { filterOption, setFilterOption } = useContext(StoreTrading);
    const [data, setData] = useState([]);
    const [isLoading, setIsLoading] = useState(false);
    const dataRef = useRef([]);

    useEffect(() => {
        // Kiểm tra glb_sv đã có data chưa 
        const dataMktCap = glb_sv.StatisticMarket.getStatisticData({
            key: getValueSub(filterOption.exchangeType),
            time: 'STATIC',
            topic: 'MARKET_CAP',
        })
        if (dataMktCap?.length > 0) {
            // Nếu có rồi thì lấy lại, không gọi service
            const newData = dataMktCap.map(item => ({
                name: item['c0'],
                percent: FormatNumber(glb_sv.StockMarket[item['c0']]?.t31_incr_per, 2, 0, ''),
                value: Number(item['c1'])
            }));
            setData(newData);
            setIsLoading(false);
        } else {
            getMarketCapData()
        }
    }, [filterOption.exchangeType])

    const getValueSub = (code) => {
        if (code === '01') return 'HSX'
        if (code === '03') return 'HNX'
        if (code === '05') return 'UPC'
    }

    const getMarketCapData = () => {
        const InputParams = ['TOP', '50', getValueSub(filterOption.exchangeType)]
        sendRequest(ServiceInfo.getMarketCapData, InputParams, handleGetMarketCapData)
    }

    const handleGetMarketCapData = (reqInfoMap, message) => {
        if (Number(message['Result']) === 0) {
            setIsLoading(false);
            return
        } else {
            let jsondata = []
            try {
                jsondata = message['Data'] ? JSON.parse(message['Data']) : []
            } catch (err) { }
            dataRef.current = dataRef.current.concat(jsondata)
            const data = jsondata.map(item => ({
                name: item['c0'],
                percent: FormatNumber(glb_sv.StockMarket[item.c0]?.t31_incr_per, 2, 1, ''),
                value: Number(item['c1'])
            }))
            setData(data);
            setIsLoading(false);
            if (Number(message['Packet']) <= 0) {
                glb_sv.StatisticMarket.updateStatisticData({
                    key: getValueSub(filterOption.exchangeType),
                    time: 'STATIC',
                    topic: 'MARKET_CAP',
                    data: jsondata,
                })
                dataRef.current = []
            }
        }
    }

    const changeIndex = value => {
        filterOption.exchangeType = value;
        setFilterOption({ ...filterOption });
        setIsLoading(true);
    }

    return (
        <Spin spinning={isLoading}>
            <div className='flex flex-direction-column' style={{ position: 'relative' }}>
                <div className='' style={{ display: 'block', width: '100vw', position: 'sticky', top: 0, backgroundColor: 'var(--PRIMARY__BG__COLOR)' }}>
                    <div className='flex flex-direction-row justify-content-center'>
                        <Button style={{ border: filterOption.exchangeType === '01' ? '1px solid var(--PRIMARY)' : '1px solid var(--BORDER__MODAL)', color: filterOption.exchangeType === '01' ? 'var(--PRIMARY)' : 'var(--SECOND__CONTENT__COLOR)', marginRight: 5, backgroundColor: 'var(--BUTTON__SECONDARY)', borderRadius: 5 }} className='flex mt-2 mb-2 mr-1 ml-1' onClick={() => changeIndex('01')}>HSX</Button>
                        <Button style={{ border: filterOption.exchangeType === '03' ? '1px solid var(--PRIMARY)' : '1px solid var(--BORDER__MODAL)', color: filterOption.exchangeType === '03' ? 'var(--PRIMARY)' : 'var(--SECOND__CONTENT__COLOR)', marginRight: 5, backgroundColor: 'var(--BUTTON__SECONDARY)', borderRadius: 5 }} className='flex mt-2 mb-2 mr-1 ml-1' onClick={() => changeIndex('03')}>HNX</Button>
                        <Button style={{ border: filterOption.exchangeType === '05' ? '1px solid var(--PRIMARY)' : '1px solid var(--BORDER__MODAL)', color: filterOption.exchangeType === '05' ? 'var(--PRIMARY)' : 'var(--SECOND__CONTENT__COLOR)', marginRight: 5, backgroundColor: 'var(--BUTTON__SECONDARY)', borderRadius: 5 }} className='flex mt-2 mb-2 mr-1 ml-1' onClick={() => changeIndex('05')}>UPC</Button>
                    </div>
                </div>
                <div className='flex'>
                    <MarketCapTable data={data} />
                </div>
            </div>
        </Spin>
    )
}

export default React.memo(MarketCap);
