import { Col, Row } from 'antd'
import React, { useContext } from 'react'
import { useTranslation } from 'react-i18next'
//import { Text } from "../../../basic-components";
import { StoreContext } from '../../../store'
//import { dimensions as dm, fontSizes as fs, fontWeights as fw } from '../../../styles';

const StockInfoRowView = ({
    onClick = () => {},
    stockSymbol, // Nội dung header
    stockName,
    qtyNote,
    qty,
    iconRight = null, // Right Component
    type = 'buy', // type của header
    key,
    ...props
}) => {
    const { styles } = useContext(StoreContext)
    const { t } = useTranslation()

    const getColor = (type) => {
        if ('buy' === type || 'confirm' === type) return 'var(--PRIMARY)'
        if ('sell' === type || 'cancel' === type) return 'var(--SELL__BG)'
        if ('back' === type) return 'transparent'
    }

    return (
        <div
            key={key}
            onClick={onClick}
            className='p-2 flex flex-direction-row justify-content-between mb-2 w-100'
            style={{
                backgroundColor: styles.INPUT__BG,
                borderRadius: 8,
            }}>
            <div style={{overflow:'hidden'}}>
                <div style={{ color: 'var(--PRIMARY__CONTENT__COLOR)',whiteSpace: 'nowrap',textOverflow:'ellipsis' }}>
                    <span className='fw-bold fs-16'>{stockSymbol}</span>
                    <span className="text-second" style={{fontSize:'var(--fs__normal)'}}> - {stockName}</span>
                </div>
                <div className='flex'>
                    <div className='text-second'>{qtyNote}</div>
                    <div className='pl-1 fw-bold' style={{ color: getColor(type) }}>
                        {qty}
                    </div>
                </div>
            </div>
            <div>
                <div>{iconRight}</div>
            </div>
        </div>
    )
}

export default StockInfoRowView
