import moment from 'moment'
//import { Button, Container, Content, Tab, Tabs, Text } from 'native-base';
import React, { useContext, useEffect, useState } from 'react'
import { useTranslation } from 'react-i18next'
//import { RefreshControl, StyleSheet, View } from "react-native";
//import Modal from 'react-native-modal';
//import StepIndicator from 'react-native-step-indicator';
//import Svg, { Circle, Path } from "react-native-svg";
//import ToastGlobal from 'react-native-toast-message';
import HeaderComponent from '../../../components/header'
import { fontSizes as fs, dimensions as dm, fontWeights as fw } from '../../../styles'
import { ButtonCustom, ModalContent, RowDataModal } from '../../../components/trading-component'
import { StoreContext } from '../../../store'
import { StoreTrading } from '../../../store-trading'
//import { dimensions as dm, fontSizes as fs, fontWeights, IconSvg } from '../../../styles';
import { glb_sv, reqFunct, Screens, wait, sendRequest } from '../../../utils'
import { Steps, Badge, Tabs, Modal } from 'antd-mobile'
import {notification} from 'antd'
// import sendRequest from '../../../utils/sendRequest';
import StepOne from './step-one'
import StepTwo from './step-two'

// Khai báo ServiceInfo (nếu có)
const ServiceInfo = {
    CONFIRM_RENEW_MARGIN_CONTRACT: {
        reqFunct: reqFunct.CONFIRM_RENEW_MARGIN_CONTRACT,
        // WorkerName: 'ALTxLendingMargin',
        // ServiceName: 'ALTxLendingMargin_1404_1',
        WorkerName: 'FOSxMargin',
        ServiceName: 'FOSxMargin_1404_1',
        ClientSentTime: '0',
        Operation: 'I',
    },
}

const initErrCtrl = [
    {
        error: false,
        message: 'input_renew_value',
    },
]
// Khai báo component
const RenewMarginContract = ({ navigation }) => {
    const { styles } = useContext(StoreContext)
    const { userInfo, setUserInfo } = useContext(StoreTrading)
    const { t } = useTranslation()
    // All orther state
    const [activeStep, setActiveStep] = useState(0)
    const [isOpenModal, setIsOpenModal] = useState(false)
    const [refreshing, setRefreshing] = React.useState(false)

    // Start define all bussiness state
    const [listContractRenew, setListContractRenew] = useState([])
    const [contractSelect, setContractSelect] = useState({})

    const [dateRenew, setDateRenew] = useState(null)
    const [errCtrl, setErrCtrl] = useState(initErrCtrl)

    // End define form bussiness state
    // -------------------------------------------   Dùng useEffect get các dữ liệu cần thiết còn thiếu

    useEffect(() => {
        const dataSub = glb_sv.objShareGlb['acntNoInfo'].find((e) => e.AcntNo + e.SubNo === userInfo.actn_curr + userInfo.sub_curr)
        if (dataSub) {
            if (!dataSub.MarginYN) {
                userInfo.sub_list.includes('01') &&
                    setUserInfo({
                        ...userInfo,
                        sub_curr: '01',
                    })
                glb_sv.userInfo.sub_curr = '01'
                return
            }
        } else {
            return
        }
    }, [userInfo.sub_curr])
    // -------------------------------------------   Viết các hàm để set lại bussiness form state. Nên viết đúng một chuẩn duy nhất để dễ control
    const onRefresh = React.useCallback(() => {
        setRefreshing(true)
        wait(300).then(() => setRefreshing(false))
    }, [])

    const switchStep = {
        goTo: (step) => setActiveStep(step),
        next: () => setActiveStep(activeStep + 1),
        prev: () => {
            setDateRenew(null)
            setActiveStep(activeStep - 1)
        },
        submit: () => confirmRenewContractMargin(),
        onFinish: () => _checkValueBfSend(),
        getCurrent: () => {
            return activeStep
        },
    }

    const _validateValue = (inputIndex) => {
        const newErrCtrl = [...initErrCtrl]

        if (inputIndex === 0) newErrCtrl[0].error = Number(dateRenew) <= 0 || !dateRenew
        if (!inputIndex && inputIndex !== 0) {
            newErrCtrl[0].error = Number(dateRenew) <= 0
        }
        setErrCtrl(newErrCtrl)
    }

    const hideModal = () => {
        setIsOpenModal(false)
    }
    // const _resetInput = () => {
    //     setDateRenew(null)
    // }
    const _onClickBackButton = () => {
        if (activeStep > 0) switchStep.prev()
        if (activeStep === 0) navigation.goBack()
    }

    const _checkValueBfSend = () => {
        setIsOpenModal(true)
        return
    }

    // -------------------------------------------   Khai báo các hàm gửi (request) lên server
    const confirmRenewContractMargin = () => {
        const inputParams = [contractSelect.c0, contractSelect.c1, String(dateRenew)]

        ServiceInfo.CONFIRM_RENEW_MARGIN_CONTRACT.AcntNo = userInfo.actn_curr
        ServiceInfo.CONFIRM_RENEW_MARGIN_CONTRACT.SubNo = userInfo.sub_curr
        sendRequest(ServiceInfo.CONFIRM_RENEW_MARGIN_CONTRACT, inputParams, handleConfirmRenewContractMargin)
    }

    // -------------------------------------------   Khai báo các hàm gửi (request) và nhận (handle respone) dữ liệu từ server
    const handleConfirmRenewContractMargin = (reqInfoMap, message) => {
        setIsOpenModal(false)
        if (Number(message['Result']) === 0) {
            if (message.Code == 'XXXXX5' || message.Code === '080063' || message.Code === '010012') {
                notification.warn({
                    message: t('common_notify'),
                    description: t('request_hanlde_not_success_try_again'),
                    duration: 4,
                })
            } else if (message['Code'] !== '080128') {
                notification.warn({
                    message: t('common_notify'),
                    description: message['Message'],
                    duration: 4,
                })
            }
            return
        } else {
            notification.success({
                message: t('common_notify'),
                description: message['Message'],
                duration: 4,
            })
            setDateRenew(null)
            switchStep.goTo(0)
        }
    }
    const StepIcon = [
        <svg onClick={() => switchStep.goTo(0)} width='30' height='30' viewBox='0 0 30 30' fill='none' xmlns='http://www.w3.org/2000/svg'>
            <circle cx='15' cy='15' r='15' fill={activeStep >= 0 ? '#60B44A' : '#D1D5DB'} />
            <path d='M14.9999 21.9161L16.4687 21.5771L15.3388 20.4473L14.9999 21.9161Z' fill='white' />
            <path
                d='M14.7101 20.9488L14.9531 19.8957C14.9537 19.8931 14.9546 19.8907 14.9552 19.8882C14.9564 19.8838 14.9576 19.8795 14.9589 19.8752C14.9603 19.8709 14.9617 19.8673 14.9632 19.8634C14.9646 19.8596 14.9659 19.8561 14.9675 19.8525C14.9693 19.8483 14.9713 19.8441 14.9734 19.84C14.9749 19.8369 14.9765 19.8339 14.9781 19.831C14.9805 19.8266 14.9831 19.8224 14.9857 19.8182C14.9874 19.8156 14.9891 19.8129 14.9909 19.8103C14.9937 19.8062 14.9968 19.8021 14.9999 19.7982C15.0018 19.7957 15.0037 19.7933 15.0057 19.7909C15.0088 19.787 15.0124 19.7833 15.0158 19.7797C15.0172 19.7782 15.0185 19.7766 15.0199 19.7752L18.2951 16.5C17.3807 14.6193 15.69 13.5 13.75 13.5C12.3274 13.5 11.0132 14.1089 10.0493 15.2145C9.07866 16.3281 8.53169 17.8802 8.50134 19.5986C9.07522 19.888 11.4316 21 13.75 21C14.0707 20.9995 14.3911 20.9824 14.7101 20.9488Z'
                fill='white'
            />
            <path d='M15.5482 19.9521L19.4367 16.0636L20.8507 17.4776L16.9622 21.3661L15.5482 19.9521Z' fill='white' />
            <path
                d='M13.75 13C15.1307 13 16.25 11.8807 16.25 10.5C16.25 9.11929 15.1307 8 13.75 8C12.3693 8 11.25 9.11929 11.25 10.5C11.25 11.8807 12.3693 13 13.75 13Z'
                fill='white'
            />
            <path
                d='M21.5 16.4159C21.5 16.2181 21.4413 16.0248 21.3314 15.8604C21.2215 15.6959 21.0654 15.5678 20.8826 15.4921C20.6999 15.4164 20.4989 15.3966 20.3049 15.4352C20.1109 15.4738 19.9328 15.5691 19.7929 15.7089L21.2071 17.1231C21.3003 17.0304 21.3741 16.9202 21.4244 16.7988C21.4747 16.6774 21.5004 16.5473 21.5 16.4159Z'
                fill='white'
            />
        </svg>,
        <svg onClick={() => switchStep.goTo(0)} width='30' height='30' viewBox='0 0 30 30' fill='none' xmlns='http://www.w3.org/2000/svg'>
            <circle cx='15' cy='15' r='15' fill={activeStep >= 1 ? '#60B44A' : '#D1D5DB'} />
            <g>
                <path
                    d='M15 8.5C11.4159 8.5 8.5 11.4159 8.5 15C8.5 18.5841 11.4159 21.5 15 21.5C18.5841 21.5 21.5 18.5841 21.5 15C21.5 11.4159 18.5841 8.5 15 8.5ZM13.8125 18.2619L11.2937 15.4628L12.0372 14.7937L13.7872 16.7381L17.9375 11.7956L18.7044 12.4375L13.8125 18.2619Z'
                    fill='white'
                />
            </g>
        </svg>,
    ]
    const tabs = [{ title: '1' }, { title: '2' }, { title: '3' }]
    return (
        <div>
            <HeaderComponent
                title={t('margin_postpone_contract')}
                transparent
                titleAlign='start'
                // iconCol={[<LeftOutlined key='ColOutlined' style={{ fontSize: 16 }} onClick={() => history.goBack()} />]}
                alignChild={['left', 'left', 'center']}
            />
            <div className='step__section' style={{ backgroundColor: styles.PRIMARY__BG__COLOR }}>
                <div className='header__step'>
                    <Steps
                        direction='horizontal'
                        current={activeStep}
                        style={{
                            paddingCol: dm.indent * 6,
                            paddingCol: dm.indent * 6,
                            marginTop: dm.verticalIndent,
                            marginBottom: dm.verticalIndent,
                        }}>
                        <Steps.Step title={t('select_contract')} icon={StepIcon[0]} disabled />
                        <Steps.Step title={t('trading_confirm')} icon={StepIcon[1]} disabled />
                    </Steps>
                </div>
                <div className='content__step'>
                    <Tabs
                        tabs={tabs}
                        swipeable={false}
                        initialPage={1}
                        page={activeStep}
                        onChange={(tab, index) => {
                            console.log('onChange', index, tab)
                        }}
                        onTabClick={(tab, index) => {
                            console.log('onTabClick', index, tab)
                        }}>
                        <div className=' align-items-center justify-content-center h-auto'>
                            <StepOne
                                switchStep={switchStep}
                                listContractRenew={listContractRenew}
                                setListContractRenew={setListContractRenew}
                                setContractSelect={setContractSelect}
                            />
                        </div>
                        <div className='align-items-center justify-content-center h-auto'>
                            <StepTwo
                                switchStep={switchStep}
                                contractSelect={contractSelect}
                                dateRenew={dateRenew}
                                setDateRenew={setDateRenew}
                                // errCtrl={errCtrl}
                                // setErrCtrl={setErrCtrl}
                                _validateValue={_validateValue}
                            />
                        </div>
                    </Tabs>
                </div>
            </div>
            {/* **************************************** Modal submit ******************************************/}
            <Modal visible={isOpenModal} style={{ width: '85vw' }} transparent maskClosable={false} onClose={hideModal}>
                <ModalContent
                    title={t('trading_confirm')}
                    type='confirm'
                    iconComponent={
                        <svg width={33} height={32} viewBox='0 0 33 32' fill='none' xmlns='http://www.w3.org/2000/svg'>
                            <g clipPath='url(#prefix__clip0)' fill='#2ECC71'>
                                <path d='M5.754 23.077V0H4.217A4.111 4.111 0 00.11 4.106v20.275a5.343 5.343 0 013.501-1.304h2.143zM24.93 15.934c.201 0 .402.008.6.021V0H7.625v23.077h8.547c.834-4.07 4.444-7.143 8.758-7.143zM13.305 5.511h6.264v1.871h-6.264V5.511zM11.19 9.574h10.496v1.871H11.189V9.574zM17.213 29.381H4.853V27.51h11.534a8.906 8.906 0 01-.396-2.62H3.61a3.56 3.56 0 00-3.555 3.556A3.56 3.56 0 003.611 32H19.54a9.007 9.007 0 01-2.327-2.62z' />
                                <path d='M24.93 17.746a7.135 7.135 0 00-7.127 7.127c0 3.93 3.197 7.126 7.127 7.126 3.93 0 7.126-3.197 7.126-7.126a7.135 7.135 0 00-7.126-7.127zm-.001 2.86c.526 0 .915.43.937.938.023.506-.447.937-.937.937-.526 0-.915-.43-.938-.937-.022-.507.448-.938.938-.938zm.94 7.92h-1.875v-4.8h1.875v4.8z' />
                            </g>
                            <defs>
                                <clippath id='prefix__clip0'>
                                    <path fill='#fff' transform='translate(.056)' d='M0 0h32v32H0z' />
                                </clippath>
                            </defs>
                        </svg>
                    }>
                    <RowDataModal textLeft={t('contract_num')} textRight={contractSelect.c0} />
                    <RowDataModal textLeft={t('date_loan')} textRight={moment(contractSelect.c3, 'DDMMYYYY').format('DD/MM/YYYY')} />
                    <RowDataModal textLeft={t('date_due')} textRight={moment(contractSelect.c29, 'DDMMYYYY').format('DD/MM/YYYY')} />
                    <RowDataModal textLeft={t('date_renew')} textRight={dateRenew} />

                    {/* <RowDataModal textLeft={t('trans_sub_account')} dataSub={[dataSubCurrTransfer.dataActn_curr, dataSubCurrTransfer.dataSub_curr]} /> */}
                    {/* <RowDataModal textLeft={t('receive_sub_account')} dataSub={[dataSubCurrTransfer.dataActn_curr, sub_curr2]} /> */}
                    {/* <RowDataModal textLeft={t('symbol_short')} textRight={dataSubCurrTransfer.codeCk} /> */}
                    {/* <RowDataModal textLeft={t('qty')} textRight={FormatNumber(amountMove)} last /> */}
                </ModalContent>
                <div className="mt-3">
                    <ButtonCustom text={t('common_button_confirm')} onClick={() => switchStep.submit()} type='confirm' />
                    <ButtonCustom text={t('common_Cancel')} onClick={hideModal} type='back' last />
                </div>
            </Modal>
        </div>
    )
}

export default RenewMarginContract
