import { Button, Checkbox, Collapse, DatePicker, message, Modal, Select, Spin } from 'antd';
import moment from 'moment';
import React, { memo, useContext, useEffect, useRef, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { StoreContext } from '../../store';
import { StoreTrading } from '../../store-trading';
import { dimensions } from '../../styles';
import { eventList, glb_sv, reqFunct, sendRequest } from '../../utils';
import './index.scss';
import HeaderComponent from 'components/header';
import { useHistory } from 'react-router-dom'
import { ReactComponent as IconBack } from '../../assets/images/common/ic_back.svg';

const { Option } = Select;

const ServiceInfo = {
    EKYC_UPDATE: {
        reqFunct: reqFunct.EKYC_UPDATE,
        WorkerName: 'FOSxID01',
        ServiceName: 'FOSxID01_EKYCMgt',
        ClientSentTime: '0',
        Operation: 'U',
    },
    API_UPLOAD: {
        reqFunct: reqFunct.API_UPLOAD,
        WorkerName: 'ALTxCommon02',
        ServiceName: 'ALTxCommon02_Token_Mgt',
        ClientSentTime: '0',
        Operation: 'Q',
    },
    GET_BROKER_REMISIER_LIST: {
        reqFunct: reqFunct.GET_BROKER_REMISIER_LIST,
        WorkerName: 'FOSqID02',
        ServiceName: 'FOSqID02_HOSUserInformation',
        ClientSentTime: '0',
        Operation: 'Q',
    },
    QUERY_USER_INFORMATION: {
        reqFunct: reqFunct.QUERY_USER_INFORMATION,
        WorkerName: 'FOSqID02',
        ServiceName: 'FOSqID02_UserInformation',
        ClientSentTime: '0',
        Operation: 'Q',
    },
}

function OpenAccount() {

    const history = useHistory();

    const { styles } = useContext(StoreContext)
    const { setUserInfo } = useContext(StoreTrading)
    const { t } = useTranslation();

    const [visible, setVisible] = useState(false);

    const [method, setMethod] = useState(history.location.state.method)

    const vnpt_ekyc_sdk = useRef(null)

    const [id, setID] = useState('-')
    const [name, setName] = useState('-')
    const [birthday, setBirthday] = useState(moment().subtract(20, 'year'))
    const [addr, setAddr] = useState('-')
    const [addrContact, setAddrContact] = useState('-')
    const [gender, setGender] = useState('')
    const [idDate, setIDDate] = useState(moment())
    const [idPlace, setIDPlace] = useState('-')

    const [modalBirthday, setModalBirthday] = useState(false)
    const [modalIssueDate, setModalIssueDate] = useState(false)

    const [isAuthen, setAuthen] = useState(true)
    const [imageFront, setImageFront] = useState('')
    const [imageBack, setImageBack] = useState('')
    const [imageFace, setImageFace] = useState('')

    const [visibleGender, setVisibleGender] = useState(false)
    const [checkNote, setCheckNote] = useState(false);

    const idRef = useRef(null);
    const addrContactRef = useRef(null);
    const nameRef = useRef(null);
    const addrRef = useRef(null);
    const idPlaceRef = useRef(null);
    const card_type = useRef(null);

    const flagEkycUpdate = useRef(false)

    const [loading, setLoading] = useState(false)

    const scrolldivRef = useRef(null)

    const [warrningMessage, setWarrningMessage] = useState('')
    const [errorMessage, setErrorMessage] = useState('')

    const validDate = useRef('')

    const [email, setEmail] = useState('')
    const [phone, setPhone] = useState('')
    const [listOther, setListOther] = useState([])
    const [brokerObj, setBrokerObj] = useState(null)
    const [remisierObj, setRemisierObj] = useState(null)

    const [objUserInfo, setObjUserInfo] = useState(null)

    const tempList = useRef([])

    const [loadingFaceVnpt, setLoadingFaceVNPT] = useState(false)

    const dataRef = useRef({
        imageHashFront: '',
        imageHashBack: '',
        imageHashFace: '',
        compareFace: 0
    })

    useEffect(() => {
        if (history.location.state.method === 'normal') {
            setMethod('confirm');
        } else {
            setMethod('ekyc');
            setLoadingFaceVNPT(true);
            setTimeout(() => {
                loadEkycSDK();
            }, 500);
        }

        getBrokerList();

        if (!glb_sv.userInfoServer) {
            getUserInfo();
        }

    }, [])

    const loadEkycSDK = async () => {
        await window.FaceVNPTBrowserSDK.init();

        let initObj = {
            BACKEND_URL: "https://api.idg.vnpt.vn/",

            AUTHORIZION: (glb_sv.dataEkycInfo?.c4 || '').replaceAll('bearer ', ''),
            TOKEN_ID: glb_sv.dataEkycInfo?.c5 || '',
            TOKEN_KEY: glb_sv.dataEkycInfo?.c6 || '',
            ENABLE_GGCAPCHAR: true,
            PARRENT_ID: "ekyc_sdk_intergrated",
            FLOW_TYPE: "DOCUMENT", // DOCUMENT, FACE
            SHOW_RESULT: true,
            SHOW_HELP: true,
            SHOW_TRADEMARK: false,
            CHECK_LIVENESS_CARD: true,
            CHECK_LIVENESS_FACE: true,
            CHECK_MASKED_FACE: true,
            COMPARE_FACE: true,
            LANGUAGE: glb_sv.language === 'VI' ? 'vi' : 'en',
            LIST_ITEM: [-1, 9],
            TYPE_DOCUMENT: 99,
            USE_WEBCAM: true,
            USE_UPLOAD: false,
            ADVANCE_LIVENESS_FACE: true,
            LIST_CHOOSE_STYLE: {
                background: "#fff",
                text_color: "black",
                border_item: "",
                item_active_color: "#18D696",
                background_icon: "#18D696",
                id_icon: "https://ekyc-web.icenter.ai/images/si/id_card.svg",
                passport_icon: "https://ekyc-web.icenter.ai/images/si/passport.svg",
                drivecard_icon: "https://ekyc-web.icenter.ai/images/si/drivecard.svg",
                army_id_icon: "https://ekyc-web.icenter.ai/images/si/other_doc.svg",
                id_chip_icon: "https://ekyc-web.icenter.ai/images/si/id_chip.svg",
                start_button_background: "#18D696",
                start_button_color: "#111127"

            },
            CAPTURE_IMAGE_STYLE: {
                popup1_box_shadow: "0px 0px 2px rgba(0, 0, 0, 0.06), 0px 3px 8px rgba(0, 0, 0, 0.1)",
                popup1_title_color: "#C8242D",
                description1_color: "#fff",
                capture_btn_background: "#18D696",
                capture_btn_color: "#111127",
                capture_btn_icon: "https://ekyc-web.icenter.ai/images/hdbank/capture.svg",
                tutorial_btn_icon: "https://ekyc-web.icenter.ai/images/hdbank/help.gif",
                recapture_btn_background: "linear-gradient(180deg, #FDFDFD 0%, #DEDEDE 100%)",
                recapture_btn_color: "#111127",
                recapture_btn_border: "2px solid #FEDC00",
                recapture_btn_icon: "https://ekyc-web.icenter.ai/images/hdbank/capture.svg",
                nextstep_btn_background: "#18D696",
                nextstep_btn_color: "#111127",
                nextstep_btn_icon: "https://ekyc-web.icenter.ai/images/hdbank/next_icon.svg",
                popup2_box_shadow: "0px 0px 2px rgba(0, 0, 0, 0.06), 0px 3px 8px rgba(0, 0, 0, 0.1)",
                popup2_title_header_color: "#C8242D",
                popup2_icon_header: "https://ekyc-web.icenter.ai/images/hdbank/main_icon.svg",
                popup2_icon_warning1: "",
                popup2_icon_warning2: "",
                popup2_icon_warning3: "",

            },
            RESULT_DEFAULT_STYLE: {
                redemo_btn_background: "#18D696",
                redemo_btn_icon: "https://ekyc-web.icenter.ai/images/hdbank/refresh.svg",
                redemo_btn_color: "#111127"
            },
            MOBILE_STYLE: {
                mobile_capture_btn: "https://ekyc-web.icenter.ai/images/capure_mobile.png",
                mobile_capture_desc_color: "#18D696",
                mobile_tutorial_color: "#C8242D",
                mobile_recapture_btn_background: "linear-gradient(180deg, #FDFDFD 0%, #DEDEDE 100%)",
                mobile_recapture_btn_border: "1px solid #18D696",
                mobile_recapture_btn_icon: "https://ekyc-web.icenter.ai/images/hdbank/capture.svg",
                mobile_recapture_btn_color: "#111127",
                mobile_nextstep_btn_background: "#18D696",
                mobile_nextstep_btn_color: "#111127",
                mobile_nextstep_btn_icon: "https://ekyc-web.icenter.ai/images/next_icon_b.png",
                mobile_popup2_icon_header: "https://ekyc-web.icenter.ai/images/hdbank/face_icon_popup.svg",

            }
        }

        setTimeout(() => {
            try {
                setLoadingFaceVNPT(false)
                window.ekycsdk.init(initObj, (res) => null, call_after_end_flow);
            } catch (err) {
                console.log("loadEkycSDK -> err", err)
            }

        }, 100);

        function call_after_end_flow(data) {
            const vnpt_ekyc = document.getElementById("vnpt_ekyc");
            vnpt_ekyc.parentNode.removeChild(vnpt_ekyc);
            window.ekycsdk.init({
                ...initObj,
                FLOW_TYPE: "FACE",
                TYPE_DOCUMENT: data.type_document
            }, (res2) => {
                console.log('res2', res2)
                setInfoEkyc2(
                    data.ocr,
                    res2.compare,
                    {
                        ...data.base64_doc_img,
                        ...res2.base64_face_img
                    },
                    {
                        ...data.ocr.imgs,
                        img_face: res2.compare.imgs.img_face
                    },
                    data.liveness_card_back,
                    data.liveness_card_front
                )
            })

        }
    }

    const setInfoEkyc2 = (infos, compares, image, hash_img, liveness_card_back, liveness_card_front) => {
        const object = infos ? infos.object : null;
        if (image) {
            setImageFront(image.img_front)
            setImageBack(image.img_back)
            setImageFace(image.img_face_near)
        }
        if (!object) {
            setErrorMessage(infos.errors ? infos.errors[0] : t('service_handle_error'))
            setAuthen(false)
            setMethod('confirm')
            return;
        }
        setWarrningMessage(object.warning_msg)
        setErrorMessage(infos.errors)
        card_type.current = String(object.type_id);
        if (object.gender == 'Nam' || object.gender == 'Male') {
            setGender('M')
        }
        else if (object.gender == 'Nữ' || object.gender == 'Female') {
            setGender('F')
        }
        if (object.valid_date && object.valid_date.length >= 8) {
            validDate.current = moment(object.valid_date, 'DD/MM/YYYY').toDate();
        }
        setID(object.id)
        setName(object.name)
        setBirthday(moment(object.birth_day, 'DD/MM/YYYY'))
        setAddr(object.recent_location?.replaceAll('\n', ' '))
        setIDDate(moment(object.issue_date, 'DD/MM/YYYY'))
        setIDPlace(object.issue_place);
        if (hash_img) {
            dataRef.current.imageHashFront = hash_img.img_front;
            dataRef.current.imageHashBack = hash_img.img_back;
            dataRef.current.imageHashFace = hash_img.img_face;
        }
        if (compares) {
            dataRef.current.compareFace = compares.object?.prob;
        }
        if (object.id_fake_warning === 'no' &&
            object.name_prob > 0.99 &&
            liveness_card_back?.object?.liveness === "success" &&
            liveness_card_front?.object?.liveness === "success" &&
            (compares.object?.msg === 'MATCH' || compares.object?.prob > Number(glb_sv.dataEkycInfo.c3))) {
            setAuthen(true)
            // setMethod('sign')
            setMethod('confirm');
        } else {
            setAuthen(false)
            setMethod('confirm')
        }
        if (infos?.statusCode === 400) {
            setAuthen(false)
            setMethod('confirm')
        }
    }

    const updateUserInfo = () => {
        const compareFace = dataRef.current.compareFace;
        if (!id || !id.trim() || id === '-') {
            glb_sv.focusInput('idRef_ekyc')
            message.warning(t('warning_id_require'))
            return;
        }
        if (!name || !name.trim() || name === '-') {
            glb_sv.focusInput('nameRef_ekyc')
            message.warning(t('warning_full_name_require'))
            return;
        }
        if (!addr || !addr.trim() || addr === '-') {
            glb_sv.focusInput('addrRef_ekyc')
            message.warning(t('warning_addr_regit_per_require'))
            return;
        }
        if (!addrContact || !addrContact.trim() || addrContact === '-') {
            glb_sv.focusInput('addrContactRef_ekyc')
            message.warning(t('warning_contact_addr_require'))
            return;
        }
        if (!gender) {
            glb_sv.focusInput('gender_ekyc')
            message.warning(t('warning_gender_require'))
            return;
        }
        if (!idPlace || !idPlace.trim() || idPlace === '-') {
            glb_sv.focusInput('idPlaceRef_ekyc')
            message.warning(t('warning_issue_place_require'))
            return;
        }
        if (!checkNote) return;
        if (flagEkycUpdate.current) return
        flagEkycUpdate.current = true;
        setLoading(true);
        const inval = ['ekyc',
            glb_sv.credentials.username,
            glb_sv.credentials.password,
            card_type.current === '0' ? '1' : (card_type.current === '1' ? '8' : card_type.current),
            id.trim(),
            name.trim(),
            birthday.format('YYYYMMDD'),
            addr.replace(/\n/g, ' ').trim(),
            addrContact.replace(/\n/g, ' ').trim(),
            idDate.format('YYYYMMDD'),
            idPlace.replace(/\n/g, ' ').trim(),
            gender,
            dataRef.current.imageHashFace,
            dataRef.current.imageHashFront,
            dataRef.current.imageHashBack,
            String(compareFace),
            '', // Signture 1
            '', // Signture 2
            email.trim().toLowerCase(), // email
            phone.trim(), // mobile
            brokerObj || '', // Broker
            remisierObj || '', // Remisier
            validDate.current && moment(validDate.current).format('YYYYMMDD') !== 'Invalid date' ? moment(validDate.current).format('YYYYMMDD') : ''
        ];
        sendRequest(ServiceInfo.EKYC_UPDATE, inval, updateUserInfoResult, true, updateUserInfoTimeout)
    }

    const updateUserInfoTimeout = ({ type }) => {
        flagEkycUpdate.current = false;
        setLoading(false)
        glb_sv.showAlert({
            title: t('common_notify'),
            content: t('request_hanlde_not_success_try_again'),
            type: 'warn'
        })
    }

    const updateUserInfoResult = (reqInfoMap, message) => {
        console.log("updateUserInfoResult -> message", message)
        flagEkycUpdate.current = false;
        setLoading(false)
        if (Number(message['Result']) === 0) {
            glb_sv.showAlert({
                title: t('common_notify'),
                content: message.Message,
                type: 'warn',
                zIndex: 999999
            })
        } else {
            glb_sv.showAlert({
                title: t('common_notify'),
                content: message.Message,
                type: 'success',
                linkCallback: () => setVisible(false),
                zIndex: 999999
            })

            let json;
            try {
                json = JSON.parse(message.Data)[0];
            } catch (err) {
                console.log(err);
                if (glb_sv.objShareGlb.userInfo) glb_sv.objShareGlb.userInfo.c9 = 'W'
                glb_sv.commonEvent.next({ type: eventList.EKYC_SUCCESS })
            }
            if (json && json.c0 === 'W') {
                if (glb_sv.objShareGlb.userInfo) glb_sv.objShareGlb.userInfo.c9 = 'W'
                glb_sv.commonEvent.next({ type: eventList.EKYC_SUCCESS })
            } else {
                storeAccountInfo(json)
                if (glb_sv.objShareGlb.userInfo) glb_sv.objShareGlb.userInfo.c9 = 'A'
                glb_sv.commonEvent.next({ type: eventList.EKYC_SUCCESS })
            }
        }
    }

    const hideDatePicker = () => {
        setModalBirthday(false);
        setModalIssueDate(false);
    }

    const validate = () => {
        if (!id || id === '-') return false
        if (!name || name === '-') return false
        if (!birthday) return false
        if (!addr || addr === '-') return false
        if (!addrContact || addrContact === '-') return false
        if (!idDate || idDate.format('YYYYMMDD') === 'Invalid Date') return false
        if (!idPlace || idPlace === '-') return false
        if (!checkNote) return false
        if (!isAuthen) return false
        if (!gender) return false
        return true
    }

    const authenEkyc = () => {
        setImageFront('')
        setImageBack('')
        setImageFace('')

        setMethod('ekyc');
        setTimeout(() => {
            loadEkycSDK();
        }, 100);
    }

    const storeAccountInfo = userInfo => {
        const accountInfo = userInfo.c0
        const array_acnt = accountInfo.split('|')
        const js_acntInfo = []
        const js_acntList = []
        // const js_acntListAll = []
        let acntMain = '',
            acntMainName = ''
        for (let i = 0; i < array_acnt.length; i++) {
            const acntInfo = array_acnt[i]
            const arr_info = acntInfo.split(',')

            if (arr_info[4] === 'Y' && arr_info[1] === '00') {
                acntMain = arr_info[0]
                acntMainName = arr_info[2]
            }
            const js_acnt = {
                AcntNo: arr_info[0],
                SubNo: arr_info[1],
                AcntNm: arr_info[2],
                IsOwnAcnt: arr_info[4],
                MarginYN: arr_info[3] === '2',
                ActType: arr_info[3]
            }
            const js_acntObj = {
                id: arr_info[0] + '.' + arr_info[1],
                name: arr_info[0] + '.' + arr_info[1] + ' - ' + arr_info[2],
            }
            js_acntInfo.push(js_acnt)
            js_acntList.push(js_acntObj)
            // js_acntListAll.push(js_acntObj)
        }

        glb_sv.objShareGlb['acntNoInfo'] = js_acntInfo
        glb_sv.objShareGlb['acntNoList'] = js_acntList
        // glb_sv.objShareGlb['acntNoListAll'] = js_acntListAll
        // --------------------
        glb_sv.objShareGlb['AcntMain'] = acntMain // -- chủ tài khoản lưu ký
        glb_sv.objShareGlb['AcntMainNm'] = acntMainName // -- Tên chủ tài khoản
        glb_sv.objShareGlb['workDate'] = userInfo.c16 // -- Ngày làm việc
        // glb_sv.objShareGlb['brokerId'] = userInfo['c24'] // -- ID nhân viên môi giới
        // glb_sv.objShareGlb['brokerNm'] = userInfo['c25'] // -- Tên nhân viên môi giới
        // glb_sv.objShareGlb['brokerEmail'] = userInfo['c26'] // -- Email nhân viên môi giới
        // glb_sv.objShareGlb['brokerMobile'] = userInfo['c27'] // -- Mobile nhân viên môi giới
        // glb_sv.objShareGlb['secCode'] = userInfo['c9'] // -- Tên nhân viên môi giới
        // glb_sv.objShareGlb['prdYN'] = userInfo['c31'] // -- User trình chiếu bảng điện hay không
        glb_sv.objShareGlb['verify'] = Number(userInfo['c37']) || 0 // -- verify > 0 => Phải xác thực chứng chỉ số
        glb_sv.objShareGlb['serialnum'] = userInfo['c38'] // -- serialNumber

        const sub_list = [];
        const actn_list = [];
        js_acntInfo.forEach(e => {
            if (e.AcntNo === acntMain) sub_list.push(e.SubNo)
            if (!actn_list.find(temp => temp.AcntNo === e.AcntNo))
                actn_list.push({
                    AcntNo: e.AcntNo,
                    actn_name: e.AcntNm,
                    key: e.AcntNo,
                    label: e.AcntNo + ' (' + e.AcntNm + ')'
                })
        })
        const isMargin = glb_sv.objShareGlb['acntNoInfo'].find(e => e.AcntNo === acntMain && e.SubNo === sub_list[0])?.MarginYN
        setUserInfo({
            actn_curr: acntMain,
            sub_curr: sub_list[0],
            actn_name: acntMainName,
            sub_list,
            actn_list,
            actn_full: acntMain + '.00',
            isMargin: isMargin || false
        });
    }


    const onCancel = () => {
        glb_sv.showAlert({
            title: t('common_notify'),
            content: t('msg_you_sure_exit_sign_up'),
            type: 'warn',
            linkCallback: () => {
                const vnpt_ekyc = document.getElementById("vnpt_ekyc");
                vnpt_ekyc.parentNode.removeChild(vnpt_ekyc);
                history.goBack();
            },
            showCancel: true
        })
    }

    const getBrokerList = () => {
        const inval = ['brk_rem']
        sendRequest(ServiceInfo.GET_BROKER_REMISIER_LIST, inval, onGetBrokerListResult)
        tempList.current = [];
    }

    const onGetBrokerListResult = (reqInfoMap, message) => {
        if (Number(message['Result']) === 0) {
            return;
        } else {
            let jsondata = [];
            try {
                jsondata = message['Data'] ? JSON.parse(message['Data']) : [];
            }
            catch (err) {
            }
            tempList.current = tempList.current.concat(jsondata)
            if (Number(message['Packet']) <= 0) {
                setListOther([...tempList.current])
            }
        }
    }

    const getUserInfo = () => {
        const InputParams = ['info'];
        sendRequest(ServiceInfo.QUERY_USER_INFORMATION, InputParams, handleGetUserInfo)
    }

    const handleGetUserInfo = (reqInfoMap, message) => {
        console.log("handleGetUserInfo -> message", message)
        if (Number(message['Result']) === 0) {
            return;
        } else {
            let jsondata;
            if (!message['Data']) return;
            try {
                jsondata = JSON.parse(message['Data']);
                glb_sv.userInfoServer = jsondata[0];
                setObjUserInfo(jsondata[0])
                setPhone((jsondata[0] && jsondata[0].c35) ? jsondata[0].c35.trim() : '')
                setEmail((jsondata[0] && jsondata[0].c32) ? jsondata[0].c32.trim() : '')
            } catch (err) {
                return
            }
        }
    }

    return (
        <div className='menu__layout' style={{ backgroundColor: '#122f41', height: '100vh' }}>
            <HeaderComponent
                transparent
                title={t('common_register_open_account_online')}
                iconLeft={<IconBack style={{ fontSize: dimensions.iconSize, color: '#FFF' }} onClick={onCancel} />}
                colorTitle={'#FFF'} />

            {method === 'ekyc' ? <div id="ekyc_sdk_intergrated">
                {loadingFaceVnpt && <Spin size="large" style={{ margin: '50px 50%' }} />}
            </div> : null}

            {method === 'confirm' ? <div className='flex flex-direction-column'>
                <div className='row-ekyc'>
                    <span className='flex flex-4 text-second fs-normal'>
                        {t('phone_number')}<span className='price-down'>*</span>
                    </span>
                    <div style={{ width: 5 }} />
                    <span className='text-primary fs-normal flex flex-5'>
                        <input
                            value={phone}
                            onChange={e => {
                                setPhone(e.target.value)
                            }}
                            disabled={objUserInfo ? (objUserInfo.c35.trim() === '' ? false : (objUserInfo.c36 === 'Y' ? false : true)) : false}
                            onFocus={() => id === '-' ? setID('') : null}
                            onBlur={() => id === '' ? setID('-') : null}
                            id='phone_ekyc'
                            className='padding-input flex-1'
                        />
                    </span>
                </div>
                <div className='row-ekyc'>
                    <span className='flex flex-4 text-second fs-normal'>
                        {t('email')}<span className='price-down'>*</span>
                    </span>
                    <div style={{ width: 5 }} />
                    <span className='text-primary fs-normal flex flex-5'>
                        <input
                            value={email}
                            onChange={e => {
                                setEmail(e.target.value)
                            }}
                            disabled={objUserInfo ? (objUserInfo.c32.trim() == '' ? false : (objUserInfo.c33 === 'Y' ? false : true)) : false}
                            onFocus={() => id === '-' ? setID('') : null}
                            onBlur={() => id === '' ? setID('-') : null}
                            id='email_ekyc'
                            className='padding-input flex-1'
                        />
                    </span>
                </div>
                <div className='row-ekyc'>
                    <span className='flex flex-4 text-second fs-normal'>
                        {t('id_card_number')}<span className='price-down'>*</span>
                    </span>
                    <div style={{ width: 5 }} />
                    <span className='text-primary fs-normal flex flex-5'>
                        <input
                            value={id}
                            onChange={e => {
                                setID(e.target.value)
                            }}
                            disabled={method === 'ekyc'}
                            onFocus={() => id === '-' ? setID('') : null}
                            onBlur={() => id === '' ? setID('-') : null}
                            id='idRef_ekyc'
                            className='padding-input flex-1'
                        />
                    </span>
                </div>

                <div className='row-ekyc'>
                    <span className='flex flex-4 text-second fs-normal'>
                        {t('common_full_name')}<span className='price-down'>*</span>
                    </span>
                    <div style={{ width: 5 }} />
                    <span className='text-primary fs-normal flex flex-5'>
                        <input
                            value={name}
                            onChange={e => {
                                setName(e.target.value)
                            }}
                            disabled={method === 'ekyc'}
                            onFocus={() => name === '-' ? setName('') : null}
                            onBlur={() => name === '' ? setName('-') : null}
                            id='nameRef_ekyc'
                            className='padding-input'
                        />
                    </span>
                </div>

                <div className='row-ekyc'>
                    <span className='flex flex-4 text-second fs-normal'>
                        {t('common_birthday')}<span className='price-down'>*</span>
                    </span>
                    <div style={{ width: 5 }} />
                    <span className='text-primary fs-normal flex flex-5'>
                        <DatePicker
                            value={birthday}
                            size='middle'
                            onChange={value => setBirthday(value)}
                            format={'DD/MM/YYYY'} />
                    </span>
                </div>

                <div className='row-ekyc'>
                    <span className='text-second fs-normal flex flex-4'>
                        {t('addr_regit_per')}<span className='price-down'>*</span>
                    </span>
                    <div style={{ width: 5 }} />
                    <span className='text-primary fs-normal flex flex-5'>
                        <input
                            value={addr}
                            onChange={e => {
                                setAddr(e.target.value)
                            }}
                            onFocus={() => addr === '-' ? setAddr('') : null}
                            onBlur={() => addr === '' ? setAddr('-') : null}
                            id='addrRef_ekyc'
                            className='padding-input flex flex-1'
                        />
                    </span>
                </div>

                <div className='row-ekyc'>
                    <span className='text-second fs-normal flex flex-4'>
                        {t('contact_address')}<span className='price-down'>*</span>
                    </span>
                    <div style={{ width: 5 }} />
                    <span className='text-primary fs-normal flex flex-5'>
                        <input
                            value={addrContact}
                            onChange={e => {
                                setAddrContact(e.target.value)
                            }}
                            onFocus={() => addrContact === '-' ? setAddrContact('') : null}
                            onBlur={() => addrContact === '' ? setAddrContact('-') : null}
                            id='addrContactRef_ekyc'
                            className='padding-input flex flex-1'
                        />
                    </span>
                </div>

                <div className='row-ekyc'>
                    <span className='flex flex-4 text-second fs-normal align-items-center'>
                        {t('common_your_sex')}<span className='price-down'>*</span>
                    </span>
                    <div style={{ width: 5 }} />
                    <span className='text-primary fs-normal flex flex-5'>
                        <Select
                            onChange={value => setGender(value)}
                            value={gender}
                            size='middle'
                            id='gender_ekyc'
                        >
                            <Option value="M">{t('common_male')}</Option>
                            <Option value="F">{t('common_female')}</Option>
                            <Option value="O">{t('common_other')}</Option>
                        </Select>
                    </span>
                </div>

                <div className='row-ekyc'>
                    <span className='flex flex-4 text-second fs-normal'>
                        {t('common_register_date')}<span className='price-down'>*</span>
                    </span>
                    <div style={{ width: 5 }} />
                    <span className='text-primary fs-normal flex flex-5'>
                        <DatePicker
                            value={idDate}
                            size='middle'
                            onChange={value => setIDDate(value)}
                            format={'DD/MM/YYYY'} />
                    </span>
                </div>

                <div className='row-ekyc'>
                    <span className='flex flex-4 text-second fs-normal'>
                        {t('common_register_place')}<span className='price-down'>*</span>
                    </span>
                    <div style={{ width: 5 }} />
                    <span className='text-primary fs-normal flex flex-5'>
                        <input
                            value={idPlace}
                            onChange={e => {
                                setIDPlace(e.target.value)
                            }}
                            onFocus={() => idPlace === '-' ? setIDPlace('') : null}
                            onBlur={() => idPlace === '' ? setIDPlace('-') : null}
                            id='idPlaceRef_ekyc'
                            className='padding-input flex flex-1'
                        />
                    </span>
                </div>

                <div className='row-ekyc'>
                    <span className='flex flex-4 text-second fs-normal'>
                        {t('broker_staff')}
                    </span>
                    <div style={{ width: 5 }} />
                    <span className='text-primary fs-normal flex flex-5'>
                        <Select
                            onChange={value => setBrokerObj(value)}
                            value={brokerObj}
                            size='middle'
                            id='broker_staff_ekyc'
                        >
                            {listOther.filter(e => e.c2 === '1').map(item =>
                                <Option key={item.c0} value={item.c0}>{item.c1}</Option>
                            )}
                        </Select>
                    </span>
                </div>

                <div className='row-ekyc'>
                    <span className='flex flex-4 text-second fs-normal'>
                        {t('remisier_staff')}
                    </span>
                    <div style={{ width: 5 }} />
                    <span className='text-primary fs-normal flex flex-5'>
                        <Select
                            onChange={value => setRemisierObj(value)}
                            value={remisierObj}
                            size='middle'
                            id='remisier_staff_ekyc'
                        >
                            {listOther.filter(e => e.c2 === '2').map(item =>
                                <Option key={item.c0} value={item.c0}>{item.c1}</Option>
                            )}
                        </Select>
                    </span>
                </div>

                {method === 'ekyc' ? <div className='row-ekyc align-items-center'>
                    <span className='flex flex-4 text-second fs-normal'>
                        {t('authen_face')}
                    </span>
                    <div style={{ width: 5 }} />
                    <div
                        className='flex flex-5'
                        style={{ backgroundColor: isAuthen ? '#E3F6E3' : '#F6E3E3', borderRadius: 5, padding: dimensions.moderate(8) }}>
                        <span className='fs-normal' style={{ color: isAuthen ? styles.UP__COLOR : styles.DOWN__COLOR }}>
                            {isAuthen ? t('cancel_success') : t('cancel_failure')}
                        </span>
                    </div>
                </div> : null}

                {(method === 'ekyc' && (warrningMessage || errorMessage)) ?
                    <div className='flex align-items-center' style={{ marginTop: dimensions.moderate(8), color: warrningMessage ? 'var(--WARN__COLOR)' : 'var(--ERROR__COLOR)' }}>
                        {warrningMessage || errorMessage}!
                    </div> : null}

                <div className='flex flex-direction-row justify-content-center' style={{ marginTop: 6 }}>
                    <div style={{ marginRight: 5, marginLeft: 5 }}>
                        <img
                            style={{ height: 120, width: 200, borderRadius: 10 }}
                            src={imageFront} />
                    </div>
                    <div style={{ marginRight: 5, marginLeft: 5 }}>
                        <img
                            style={{ height: 120, width: 200, borderRadius: 10 }}
                            src={imageBack} />
                    </div>
                    <div style={{ marginRight: 5, marginLeft: 5 }}>
                        <img
                            style={{ height: 120, width: 120, borderRadius: 60 }}
                            src={imageFace} />
                    </div>
                </div>

                <div
                    style={{ flexDirection: 'row', alignItems: 'center', marginTop: dimensions.vertical(8) }}>
                    <Checkbox checked={checkNote} onChange={() => setCheckNote(value => !value)}>
                        <span className='fs-normal text-primary cursor-pointer'>
                            {glb_sv.dataEkycInfo.c1 || t('note_ekyc')}
                            {glb_sv.dataEkycInfo.c2 ? <a className='fs-normal text-brand fw-500'
                                href={glb_sv.dataEkycInfo.c2}
                                target='_blank'>
                                {' '}{t('contact_detail')}
                            </a> : null}
                        </span>
                    </Checkbox>

                </div>

                {isAuthen && method === 'ekyc' ? <Button
                    onClick={updateUserInfo}
                    block
                    loading={loading}
                    className='no-height'
                    size='large'
                    style={{ backgroundColor: validate() ? styles.PRIMARY : styles.PRIMARY + '4d', padding: dimensions.moderate(6), marginTop: 12 }}>
                    <span className='fs-medium' style={{ color: validate() ? '#FFF' : 'var(--SECOND__CONTENT__COLOR)' }}>
                        {t('register')}
                    </span>
                </Button> : null}

                {!isAuthen && method == 'ekyc' ? <Button
                    onClick={authenEkyc}
                    block
                    className='no-height'
                    size='large'
                    danger
                    type="primary"
                    style={{ padding: dimensions.moderate(6), marginTop: 12 }}>
                    <span className='fs-medium'>
                        {t('reauthen_ekyc')}
                    </span>
                </Button> : null}

                {method !== 'ekyc' ? <Button
                    onClick={updateUserInfo}
                    disabled={!validate()}
                    block
                    loading={loading}
                    className='no-height'
                    size='large'
                    style={{ backgroundColor: validate() ? styles.PRIMARY : styles.PRIMARY + '4d', padding: dimensions.moderate(6), marginTop: 12 }}>
                    <span className='fs-medium' style={{ color: validate() ? '#FFF' : 'var(--SECOND__CONTENT__COLOR)' }}>
                        {t('register')}
                    </span>
                </Button> : null}

            </div> : null}
        </div>
    );
}
export default memo(OpenAccount)