import React, { useContext } from 'react'
import { Col, List } from 'antd'
import { useHistory } from 'react-router-dom'
import { CheckOutlined, LeftOutlined } from '@ant-design/icons'
import HeaderComponent from '../../components/header'
import { useTranslation } from 'react-i18next'
import { StoreContext } from '../../store'
import { glb_sv, STORE_KEY } from '../../utils'
import Text from 'antd/lib/typography/Text'
import { ReactComponent as IconBack } from '../../assets/images/common/ic_back.svg'
import preventCompanyRender from 'utils/checkCompanyApplyFunct'

const LanguageLayout = () => {
    const { t, i18n } = useTranslation()
    let history = useHistory()
    const { language, setLanguage } = useContext(StoreContext)

    const changeLanguage = (key) => {
        if (language === key) return
        i18n.changeLanguage(key)
        setLanguage(key)
        localStorage.setItem(STORE_KEY.LANGAGE, key)
        glb_sv.language = key
        history.goBack()
    }

    return (
        <div className="menu__layout" style={{ backgroundColor: 'var(--PRIMARY__BG__COLOR)', height: '100%' }}>
            <HeaderComponent
                title={t('language')}
                iconLeft={[<IconBack style={{ fontSize: 16 }} onClick={() => history.goBack()} />]}
                iconRight={[]}
            />
            <div>
                <List>
                    <List.Item onClick={() => changeLanguage('VI')}>
                        <Col span={20}>
                            <Text className="fs__medium" style={{ color: language === 'VI' ? 'var(--PRIMARY)' : '' }}>
                                Tiếng Việt
                            </Text>
                        </Col>
                        <Col span={4} className="center__item">
                            {language === 'VI' && (
                                <CheckOutlined
                                    style={{ fontSize: 16, color: language === 'VI' ? 'var(--PRIMARY)' : '' }}
                                />
                            )}
                        </Col>
                    </List.Item>
                    <List.Item onClick={() => changeLanguage('EN')}>
                        <Col span={20}>
                            <Text className="fs__medium" style={{ color: language === 'EN' ? 'var(--PRIMARY)' : '' }}>
                                English
                            </Text>
                        </Col>
                        <Col span={4} className="center__item">
                            {language === 'EN' && (
                                <CheckOutlined
                                    style={{ fontSize: 16, color: language === 'EN' ? 'var(--PRIMARY)' : '' }}
                                />
                            )}
                        </Col>
                    </List.Item>
                    {preventCompanyRender(['888']) && (
                        <List.Item onClick={() => changeLanguage('CN')}>
                            <Col span={20}>
                                <Text
                                    className="fs__medium"
                                    style={{ color: language === 'CN' ? 'var(--PRIMARY)' : '' }}
                                >
                                    中文(CN)
                                </Text>
                            </Col>
                            <Col span={4} className="center__item">
                                {language === 'CN' && (
                                    <CheckOutlined
                                        style={{ fontSize: 16, color: language === 'CN' ? 'var(--PRIMARY)' : '' }}
                                    />
                                )}
                            </Col>
                        </List.Item>
                    )}
                    {preventCompanyRender(['888']) && (
                        <List.Item onClick={() => changeLanguage('ZH')}>
                            <Col span={20}>
                                <Text
                                    className="fs__medium"
                                    style={{ color: language === 'ZH' ? 'var(--PRIMARY)' : '' }}
                                >
                                    中文(ZH)
                                </Text>
                            </Col>
                            <Col span={4} className="center__item">
                                {language === 'ZH' && (
                                    <CheckOutlined
                                        style={{ fontSize: 16, color: language === 'ZH' ? 'var(--PRIMARY)' : '' }}
                                    />
                                )}
                            </Col>
                        </List.Item>
                    )}
                    {preventCompanyRender(['888']) && (
                        <List.Item onClick={() => changeLanguage('KO')}>
                            <Col span={20}>
                                <Text
                                    className="fs__medium"
                                    style={{ color: language === 'KO' ? 'var(--PRIMARY)' : '' }}
                                >
                                    한국어
                                </Text>
                            </Col>
                            <Col span={4} className="center__item">
                                {language === 'KO' && (
                                    <CheckOutlined
                                        style={{ fontSize: 16, color: language === 'KO' ? 'var(--PRIMARY)' : '' }}
                                    />
                                )}
                            </Col>
                        </List.Item>
                    )}
                </List>
            </div>
        </div>
    )
}

export default LanguageLayout
