import React, { useContext } from 'react'
import { useTranslation } from 'react-i18next'
import { StoreContext } from '../../../store'
import { dimensions, fontSizes } from '../../../styles'
import { Row, Col, List, Steps, Button, message as Message } from 'antd'

/**
 *
 * @param {String} type ===> buy/confirm/sell/cancel/back
 * @returns
 */

const ButtonCustom = ({
    text = '', // Nội dung button
    disabled, // Có disable hay không
    type = 'buy', // type của button
    last, // có phải là button cuối cùng của list button hay không
    first = true, // có phải là button đầu tiên cùng của list button hay không
    visible = true,
    ...props
}) => {
    const { styles } = useContext(StoreContext)

    const { t } = useTranslation()

    const getColorButton = (type) => {
        if ('buy' === type || 'confirm' === type) return styles.PRIMARY
        if ('sell' === type || 'cancel' === type) return styles.SELL__BG
        if ('back' === type) return 'transparent'
    }
    return (
        <Button
            {...props}
            disabled={disabled}
            visible={visible}
            transparent={type === 'back' ? true : false}
            className='mr-2 ml-2'
            style={{
                width: '-webkit-fill-available',
                // flex: 1,
                // marginTop: last ? dm.vertical(0) : dm.vertical(16),
                justifyContent: 'center',
                borderRadius: 8,
                opacity: disabled ? 0.5 : 1,
                backgroundColor: getColorButton(type),
                height: dimensions.vertical(42),
                paddingLeft: '1rem',
                paddingRight: '1rem',
                border:'0'
            }}>
            <div
                style={{
                    color: type === 'back' || disabled ? styles.PRIMARY__CONTENT__COLOR : '#f5f5f5',
                    // fontWeight: type === 'back' ? 'bo' : fw.bold,
                    fontWeight: 'bold',
                    // fontSize: fs.normal
                }}>
                {t(text).toUpperCase()}
            </div>
        </Button>
    )
}

export default ButtonCustom
