// Có chỉnh sửa
import { useEffect, useState, useContext, useRef, memo, useCallback } from 'react';
import { StoreContext } from '../../store';
import { useTranslation } from 'react-i18next';
import { dimensions, fontSizes } from '../../styles';
import { reqFunct, eventList, subcribeFunct, FormatNumber, glb_sv, sendRequest, RealTimeHandler } from '../../utils'
import HeaderOfList from './header-of-list'
import cloneDeep from 'lodash/cloneDeep'
import throttle from 'lodash/throttle';
import equals from 'react-fast-compare';
import { routes } from 'navigation/routes';
import { SwipeAction, Icon } from 'antd-mobile';
import { Button, notification } from 'antd';
import {
    useHistory
} from 'react-router-dom'
import IC_FOLDER_NULL from '../../assets/images/common/ic_folder_null.png';
import { StoreTrading } from 'store-trading';
import { DeleteOutlined } from '@ant-design/icons';
import { RemoveStockInListFav } from './favorite-func/favorite.action';
const isTablet = glb_sv.isTablet;
const isFiveColumn = dimensions.WIDTH > 420 && dimensions.WIDTH < 910;
const isSevenColumn = dimensions.WIDTH > 910;

const ServiceInfo = {
    removeStockFromFavList: {
        reqFunct: reqFunct.REMOVE_STOCK_IN_FAV_LIST,
        WorkerName: 'FOSxID01',
        ServiceName: 'FOSxID01_FavoritesMgt',
        ClientSentTime: '0',
        Operation: 'D',
    },
    GET_ASSETS_STOCK_LIST: {
        reqFunct: reqFunct.GET_ASSETS_STOCK_LIST,
        WorkerName: 'FOSqAsset',
        ServiceName: 'FOSqAsset_1801_1',
        Operation: 'Q',
        ClientSentTime: '0'
    },
}

export default function ListFavoriteStock({ AddStockToListFav, showRmStkFav, setShowRmStk }) {
    const history = useHistory();
    const [activeList, setActiveList] = useState(glb_sv.activeList) //glb_sv.stk_VN30
    const activeListRef = useRef({});
    const SubscribeStockIndex = new RealTimeHandler('STOCK_INDEX');

    const { userInfo, authFlag } = useContext(StoreTrading);
    const profit_lost_value = useRef(0)
    const profit_lost_ratio = useRef(0)
    const { t } = useTranslation();
    const [listDataSub, setListDataSub] = useState([]);
    const VIEW_HEIGHT_LIST = useRef(dimensions.HIEGHT)
    const VIEW_HEIGHT_ROW = useRef(60.5)
    const CURRENT_SCROLL = useRef(0)

    const [sort, setSort] = useState('')
    const sortRef = useRef('')

    const delayTimeout = useRef(null)
    const removeStockFlag = useRef(false)

    const viewScreen = useRef(true)
    const throttled = useRef(throttle(() => setDataInfoSort(), 500))

    const [loading, setLoading] = useState(false)

    useEffect(() => {
        if (!viewScreen.current) return
        setLoading(false)

        const eventMarket = glb_sv.eventMarket.subscribe(msg => {
            if (msg.type === eventList.SUB_STOCK && glb_sv.activeList?.ListStock?.some(e => e === msg.msgKey)) {
                if (!viewScreen.current) return;
                const newList = glb_sv.activeList.ListStock.map(e => {
                    return glb_sv.StockMarket[e] || { t55: e, EP: [] }
                })
                let updateStock = newList.find(e => e.t55 === msg.msgKey)
                if (!updateStock) return
                if (updateStock) {
                    if (isTablet) {
                        throttled.current();
                        return
                    }
                    if (msg.message === 'SI') throttled.current();
                }
            }
            if (msg.type === eventList.SUBSCRIBE_DONE) {
                throttled.current();
                if (equals(glb_sv.subListCur, msg.arrSub)) {
                    getEpAfterSort();
                }
            }
            if (msg.type === eventList.LOADING_FAV) {
                handleSubRow();
                setLoading(true)
            }
            if (msg.type === eventList.REQ_RE_GET_MKT_INF) {
                subcribeFunct(null, 'SUB', '*', activeList.ListStock);
                handleSubRow()
            }
        });
        const commonEvent = glb_sv.commonEvent.subscribe(msg => {
            if (msg.type === eventList.CHANGE_LIST_STOCK) {
                throttled.current();
                if (!viewScreen.current) return
                handleSubRow();
            }
            if (msg.type === eventList.CHANGE_LIST_ACTIVE) {
                setActiveList({ ...glb_sv.activeList })

                if (!viewScreen.current) return
                if (glb_sv.activeList?.ListStock?.length) subcribeFunct(null, 'UNSUB', '*', glb_sv.activeList.ListStock)
                throttled.current();
                handleSubRow();
                setLoading(false);
            }
        })
        return () => {
            if (glb_sv.activeList?.ListStock?.length) subcribeFunct(null, 'UNSUB', '*', glb_sv.activeList.ListStock);
            eventMarket.unsubscribe();
            commonEvent.unsubscribe();
            throttled.current.cancel();
        }
    }, []);

    useEffect(() => {
        // if (authFlag) getAssetsStockList() // lấy lãi/lỗ
        if (activeListRef.current.length > 0) {
            SubscribeStockIndex.unsubscribe({
                topic: ['MDDS|SI', 'MDDS|TP', 'MDDS|EP'],
                value: activeListRef.current.ListStock
            })
        }
        SubscribeStockIndex.subscribe({
            topic: ['MDDS|SI', 'MDDS|TP', 'MDDS|EP'],
            value: glb_sv.activeList.ListStock
        })
        activeListRef.current = activeList;

        return (() => {
            SubscribeStockIndex.unsubscribe({
                topic: ['MDDS|SI', 'MDDS|TP', 'MDDS|EP'],
                value: activeListRef.current.ListStock
            })
        })
    }, [activeList])

    const handleDeleteStk = () => {
        setShowRmStk(true);
    }

    const handleButton = () => {
        if (activeList.c1) history.push(routes.SEARCH_STOCK.path, { actionType: 'FAV_ACTION', activeList })
        else {
            history.push(routes.ADD_FAV_LIST.path, { actionType: 'ADD_FAV_LIST' })
        }
    }

    const handleSubRow = (y, all) => {
        if (all) {
            const newList = glb_sv.activeList.ListStock.slice()
            if (newList.length) subcribeFunct(null, 'SUB', '*', newList)
            return
        }
        CURRENT_SCROLL.current = y || CURRENT_SCROLL.current

        if (delayTimeout.current) clearTimeout(delayTimeout.current);
        delayTimeout.current = setTimeout(() => {
            const NoHeadArr = Math.round(CURRENT_SCROLL.current / VIEW_HEIGHT_ROW.current)
            const NoTailArr = Math.ceil(VIEW_HEIGHT_LIST.current / VIEW_HEIGHT_ROW.current)

            const newList = glb_sv.activeList?.ListStock?.slice(NoHeadArr, NoHeadArr + NoTailArr)

            if (newList.length) {
                glb_sv.subListCur = newList;

                subcribeFunct(null, 'UNSUB', '*', glb_sv.activeList.ListStock);
                subcribeFunct(null, 'SUB', '*', newList);

                // newList.forEach(stk => {
                //     if (!glb_sv.StockMarket[stk]) return
                //     console.log('glb_sv.StockMarket[stk]: ', glb_sv.StockMarket[stk])
                //     const splited = glb_sv.StockMarket[stk].stockCode.split('_')
                //     const F1 = splited[0] + '|EP|' + splited[1]
                //     subcribeFunct(null, 'RESEND', F1, 99999999, 0, 100)
                // })
                throttled.current();
            }
        }, 200);
    }

    const removeStock = ({ isOpen, direction, value, key }) => {
        if (activeList['c1'] !== 'HNX30' && activeList['c1'] !== 'VN30' && activeList['c1'] !== '00') {
            if (value < -190 && !removeStockFlag.current) {
                removeStockFlag.current = true
                throttled.current();
                if (glb_sv.authFlag) {
                    const InputParams = ['FAV_ITEM_REMOVE', activeList.c1, key]
                    sendRequest(ServiceInfo.removeStockFromFavList, InputParams, handleRmStkFrFavList)
                } else {
                    removeStockFlag.current = false;
                    const currentIndex = glb_sv.allListFav.findIndex((x) => x.c1 === activeList.c1)
                    glb_sv.allListFav[currentIndex].ListStock = glb_sv.allListFav[currentIndex].ListStock.filter(e => e !== key)
                    glb_sv.activeList = { ...glb_sv.allListFav[currentIndex] };
                    RemoveStockInListFav(activeList.c1, key);
                    glb_sv.commonEvent.next({ type: eventList.CHANGE_LIST_STOCK })
                    subcribeFunct(null, 'UNSUB', '*', [key]);

                    throttled.current();
                    if (!viewScreen.current) return
                    handleSubRow();
                }
            }
        } else {
            notification.warning({
                message: t('process_fail'),
                description: t('warn_delete_stock_list'),
                duration: 3,
            })
        }
    };

    const handleRmStkFrFavList = (reqInfoMap, message) => {
        removeStockFlag.current = false
        if (Number(message['Result']) === 0) {
            return
        } else {
            const indexCurrent = glb_sv.allListFav.findIndex((x) => x.c1 === reqInfoMap.inputParam[1])
            glb_sv.allListFav[indexCurrent].ListStock = glb_sv.allListFav[indexCurrent].ListStock.filter(e => e !== reqInfoMap.inputParam[2])
            glb_sv.activeList = { ...glb_sv.allListFav[indexCurrent] };
            subcribeFunct(null, 'UNSUB', '*', [reqInfoMap.inputParam[2]]);
            throttled.current();
            if (!viewScreen.current) return
            handleSubRow();
        }
    }

    const setDataInfoSort = () => {
        const listSub = glb_sv.activeList.ListStock.map(e => {
            return glb_sv.StockMarket[e] || { t55: e, EP: [] }
        })
        const newList = cloneDeep(listSub)
        // newList.sort(sortColumn)
        setListDataSub(newList)
    }

    const changeTypeSort = (changed) => {
        if (sort === '' || !sort.includes(changed)) {
            sortRef.current = changed + '|down'
            setSort(changed + '|down')
            const newList = glb_sv.activeList.ListStock.map(e => {
                return glb_sv.StockMarket[e] || { t55: e, EP: [] }
            })
            newList.sort(sortColumn)
            getEpAfterSort()
            setListDataSub(newList)
        }
        if (sort === (changed + '|down')) {
            sortRef.current = changed + '|up'
            setSort(changed + '|up')
            const newList = glb_sv.activeList.ListStock.map(e => {
                return glb_sv.StockMarket[e] || { t55: e, EP: [] }
            })
            newList.sort(sortColumn)
            getEpAfterSort()
            setListDataSub(newList)

        }
        if (sort === (changed + '|up')) {
            sortRef.current = ''
            setSort('')
            const newList = glb_sv.activeList.ListStock.map(e => {
                return glb_sv.StockMarket[e] || { t55: e, EP: [] }
            })
            setListDataSub(newList)
        }
    }

    function sortColumn(a, b) {
        const [key, direction] = sortRef.current.split('|')
        if (direction === 'up') {
            if ((a[key] ? a[key] : a.t260) > (b[key] ? b[key] : b.t260)) return 1
            if ((a[key] ? a[key] : a.t260) < (b[key] ? b[key] : b.t260)) return -1
            return 0
        }
        if (direction === 'down') {
            if ((a[key] ? a[key] : a.t260) < (b[key] ? b[key] : b.t260)) return 1
            if ((a[key] ? a[key] : a.t260) > (b[key] ? b[key] : b.t260)) return -1
            return 0
        }
    }

    const getEpAfterSort = (list) => {
        const NoHeadArr = Math.round(CURRENT_SCROLL.current / VIEW_HEIGHT_ROW.current)
        const NoTailArr = Math.ceil(VIEW_HEIGHT_LIST.current / VIEW_HEIGHT_ROW.current)

        const newList = glb_sv.activeList.ListStock.slice(NoHeadArr, NoHeadArr + NoTailArr)

        if (newList.length) {
            glb_sv.subListCur = newList

            // newList.forEach(stk => {
            //     if (!glb_sv.StockMarket[stk]) return
            //     const splited = glb_sv.StockMarket[stk].stockCode.split('_')
            //     const F1 = splited[0] + '|EP|' + splited[1]
            //     subcribeFunct(null, 'RESEND', F1, 99999999, 0, 100)
            // })
        }
    }

    const onChangePriceboard = () => {
        viewScreen.current = false
    }


    const renderItem = useCallback((item) => {
        return (
            <StockItem
                activeList={activeList}
                t55={item.t55}
                key={item.t55}
                removeStock={removeStock}
                showRmStkFav={activeList['c1'] !== 'HNX30' && activeList['c1'] !== 'VN30' && activeList['c1'] !== '00' ? true : false}
                handleDeleteStk={handleDeleteStk}
                t31={item.t31}
                t31_incr={item.t31_incr}
                t31_incr_per={item.t31_incr_per}
                t132_1={item.t132_1}
                t133_1={item.t133_1}
                t387={item.t387}
                t3301={item.t3301}
            />
        )
    });

    const getAssetsStockList = () => {
        const { actn_curr } = userInfo
        const InputParams = ['02', actn_curr, '%'];
        sendRequest(ServiceInfo.GET_ASSETS_STOCK_LIST, InputParams, getAssetsStockListResult, true, () => { }, '', 0, 'equal_service');
    }

    const getAssetsStockListResult = (reqInfoMap, message) => {
        if (Number(message['Result']) === 0) {
            return
        } else {
            let jsondata = []
            try {
                jsondata = message['Data'] ? JSON.parse(message['Data']) : []
                profit_lost_value.current = jsondata.reduce((total, currentValue) => total + Number(currentValue['c19']), 0)
                const currValue = jsondata.reduce((total, currentValue) => total + Number(currentValue['c16']), 0)
                profit_lost_ratio.current = profit_lost_value.current / currValue * 100
            } catch (err) {
                console.log('getAssetsStockListResult', err);
                return;
            }
        }
    }

    return (
        <>
            <HeaderOfList
                sort={sort}
                changeTypeSort={changeTypeSort}
                isRotation={true}
                onChangePriceboard={onChangePriceboard}
            />
            {listDataSub.length > 0 ? "" : <div>
                <div style={{ display: "flex", justifyContent: "center" }}>
                    <img src={IC_FOLDER_NULL} />
                </div>
                <div style={{ textAlign: "center", lineHeight: "160%", marginTop: "30px" }}>
                    <div style={{ fontSize: "15px", lineHeight: "20px", fontWeight: "500" }}>{t('watchlist_empty')}</div>
                    <div style={{ fontSize: "13px", color: "gray" }}>{t('let_add_watchlist')}</div>
                </div>
            </div>}
            <div className='flex flex-1 flex-direction-column'>
                <div style={UI.categoryItem} >
                    {listDataSub.map(e => renderItem(e))}
                </div>
                {activeList['c1'] !== 'HNX30' && activeList['c1'] !== 'VN30' && activeList['c1'] !== '00' &&
                    <div className='mt-2 mb-2' style={{ textAlign: "center" }}>
                        <Button onClick={() => AddStockToListFav(true)} style={{ backgroundColor: 'var(--PRIMARY)', color: 'var(--PRIMARY__CONTENT__COLOR)', borderColor: 'var(--PRIMARY__BORDER__COLOR)', borderRadius: 10, padding: '0 15px' }}>{t('new_stock')}</Button>
                    </div>
                }
            </div>
            {/* {authFlag ?
                <div className='flex flex-direction-row' style={{ width: '100vw', backgroundColor: 'var(--PRIMARY__BG_COLOR)' }}>
                    <div className='profit_loss flex flex-direction-row'
                        style={{ margin: '5px 0', position: 'fixed', bottom: 55, width: 'calc(100vw - 34px)', background: profit_lost_value.current > 0 ? 'var(--UP__COLOR)' : 'var(--ERROR__COLOR)', borderRadius: 8, padding: '10px' }}>
                        <span className='flex-2 '>{t('cal_profit_loss')}</span>
                        <span className='flex-3 text-right'
                            style={{ color: profit_lost_value.current > 0 ? '#fff' : '#fff' }}>
                            {FormatNumber(profit_lost_value.current)} <span style={{ fontSize: fontSizes.verySmall }}>({FormatNumber(profit_lost_ratio.current, 2)}%)</span>
                        </span>
                    </div>
                </div>
                : ''} */}
        </>
    );
}

const StockItem = memo(({ t55, removeStock, showRmStkFav, activeList }) => {
    const stockInfo = glb_sv.StockMarket[t55] || { TP: [] }
    const { fractionPrice, fractionQty, styles } = useContext(StoreContext);
    const history = useHistory();

    return (
        <SwipeAction
            // disabled={!showRmStkFav}
            style={{ backgroundColor: 'var(--PRIMARY__BG__COLOR)' }}
            autoClose
            right={[
                {
                    text: <DeleteOutlined style={{ fontSize: 16, color: '#fff' }} className='center__item' />,
                    style: { backgroundColor: 'red', marginTop: 1, marginBottom: 1, color: '#fff', border: 'none' },
                    onPress: () => {
                        removeStock({
                            isOpen: true, direction: 'right', value: - 200, key: t55
                        })
                    },
                },
            ]}
        >
            <div className='flex'>
                <div className='flex flex-direction-column justify-content-start align-content-start flex-4' onClick={() => history.push(routes.STOCK_INFO.path, { stockCode: stockInfo.t55 })} style={{ backgroundColor: 'var(--PRIMARY__BG__COLOR)' }}>
                    <div className='flex' style={{ paddingTop: "10px" }}>
                        <span className='fw__medium fs__normal' style={{ color: 'var(--PRIMARY__CONTENT__COLOR)' }}>
                            {t55}&ensp;
                    </span>
                        <span className='fs__tiny' style={{ color: 'var(--SECOND__CONTENT__COLOR)', paddingTop: 1, lineHeight: fontSizes.normal + 'px' }}>
                            {getExchange(stockInfo.U10)}
                        </span>
                    </div>
                    <span className='fs-normal' style={{ color: 'var(--SECOND__CONTENT__COLOR)', overflow: 'hidden', textOverflow: 'ellipsis', width: 'calc(80vw - 34px)', whiteSpace: 'nowrap' }}>
                        {stockInfo.U9}
                    </span>
                </div>
                <div className='flex list align-items-center' style={UI.row}
                    onClick={() => history.push(routes.STOCK_INFO.path, { stockCode: stockInfo.t55 })}
                >
                    <div className='flex flex-3 flex-direction-column' style={{ minWidth: "80px" }}>
                        <div style={{ backgroundColor: glb_sv.getColor(stockInfo.t31, stockInfo, styles, 't31'), width: '100%', borderRadius: 4, padding: 5 }}>
                            <span className='flex align-self-end ' style={{ color: '#fff', lineHeight: fontSizes.small + 2 + 'px', fontSize: fontSizes.small, justifyContent: "center" }}>
                                {fractionPrice ? FormatNumber((stockInfo.t31 === 0 ? stockInfo.t260 : stockInfo.t31) / 1000, 2, 0) : FormatNumber(stockInfo.t31 === 0 ? stockInfo.t260 : stockInfo.t31)}
                            </span>
                        </div>
                        <span className='fs__smallest' style={{ color: glb_sv.getColor(stockInfo.t31, stockInfo, styles, 't31'), marginTop: "2px", textAlign: "center", fontSize: fontSizes.smallest }}>
                            {stockInfo.t31_incr > 0 ? '+' : ''}
                            {fractionPrice ? FormatNumber(stockInfo.t31_incr / 1000, 2) : FormatNumber(stockInfo.t31_incr)}&ensp;
                    {stockInfo.t31_incr_per > 0 ? '+' : ''}
                            {FormatNumber(stockInfo.t31_incr_per, 2)}%
                </span>
                    </div>
                </div>
            </div >
        </SwipeAction >
    )
}, areEqual);

function areEqual(prev, next) {
    if (!isFiveColumn && !isSevenColumn) {
        return prev.U8 === next.U8 &&
            prev.U9 === next.U9 &&
            prev.t260 === next.t260 &&
            prev.t31 === next.t31 &&
            prev.t31_incr === next.t31_incr &&
            prev.t31_incr_per === next.t31_incr_per &&
            prev.showRmStkFav === next.showRmStkFav;
    }
    if (isFiveColumn || isSevenColumn) {
        return prev.U8 === next.U8 &&
            prev.U9 === next.U9 &&
            prev.t260 === next.t260 &&
            prev.t31 === next.t31 &&
            prev.t31_incr === next.t31_incr &&
            prev.t31_incr_per === next.t31_incr_per &&
            prev.t132_1 === next.t132_1 &&
            prev.t133_1 === next.t133_1 &&
            prev.showRmStkFav === next.showRmStkFav;
    }
    if (isSevenColumn) {
        return prev.U8 === next.U8 &&
            prev.U9 === next.U9 &&
            prev.t31 === next.t31 &&
            prev.t260 === next.t260 &&
            prev.t31_incr === next.t31_incr &&
            prev.t31_incr_per === next.t31_incr_per &&
            prev.t132_1 === next.t132_1 &&
            prev.t133_1 === next.t133_1 &&
            prev.t387 === next.t387 &&
            prev.t3301 === next.t3301 &&
            prev.showRmStkFav === next.showRmStkFav;
    }
    return false;
}

function getExchange(type) {
    if (type === '01') return 'HOSE'
    if (type === '03') return 'HNX'
    if (type === '05') return 'UPCOM'
}

const UI = {
    flex1: {
        flex: 1,
        justifyContent: 'center'
    },
    rowBack: {
        alignItems: 'center',
        flex: 1,
        flexDirection: 'row',
        justifyContent: 'space-between',
        paddingLeft: 15,
        margin: 5,
        marginBottom: 15,
        borderRadius: 5,
    },
    backRightBtn: {
        alignItems: 'flex-end',
        bottom: 0,
        justifyContent: 'center',
        position: 'absolute',
        top: 0,
        width: 500,
        paddingRight: 17,
    },

    backRightBtnRight: {
        backgroundColor: 'red',
        right: 0,
        borderTopRightRadius: 5,
        borderBottomRightRadius: 5,
    },
    trash: {
        height: 25,
        width: 25,
        marginRight: 7,
    },
    row: {
        // borderBottom: '1px solid var(--DIVIDER__COLOR)',
        backgroundColor: 'var(--PRIMARY__BG__COLOR)',
        paddingTop: dimensions.verticalIndent / 3,
        paddingBottom: dimensions.verticalIndent / 3,
        // paddingRight: 8,
        height: 60.5
    },
    categoryItem: {
        // overflowY: "scroll"
    }
};

//  B1: Get list stock into FAV active
//  B2: SUB all stock
//  B3: After receive msg SUBSCRIBE_DONE, handle get stock viewable [subrListCurrent] -> RESEND get EP
//  //////////
//  If receive msg SUB_STOCK, compare [subrListCurrent] with msgKey if true render list else skip
//  Handle the same for SparklinesChart