import React, { useState, useContext } from 'react';
import { StoreContext } from '../../store';
import { useTranslation } from 'react-i18next';
import HeaderComponent from '../../components/header';
import { glb_sv, STORE_KEY, reqFunct, sendRequest, eventList } from '../../utils';
import { Form, Input, Button } from 'antd';
import Text from 'antd/lib/typography/Text'
import { fontSizes, dimensions } from 'styles';
import { routes } from 'navigation/routes';
import { useHistory } from 'react-router-dom'
import ModalAuthenOtp from './modal-authen-otp'

const ServiceInfo = {
    CREATE_ACCOUNT: {
        reqFunct: reqFunct.CREATE_ACCOUNT,
        WorkerName: 'FOSxID01',
        ServiceName: 'FOSxID01_Management',
        ClientSentTime: '0',
        Operation: 'I',
    },
    AUTHEN_OTP: {
        reqFunct: reqFunct.AUTHEN_OTP,
        WorkerName: 'FOSxID01',
        ServiceName: 'FOSxID01_Management',
        ClientSentTime: '0',
        Operation: 'I',
    },
    GET_NEW_OTP: {
        reqFunct: reqFunct.GET_NEW_OTP,
        WorkerName: 'FOSxID01',
        ServiceName: 'FOSxID01_Management',
        ClientSentTime: '0',
        Operation: 'I',
    },
    EKYC_INFO: {
        reqFunct: reqFunct.EKYC_INFO,
        WorkerName: 'FOSqID01',
        ServiceName: 'FOSqID01_EKYCInfo',
        ClientSentTime: '0',
        Operation: 'Q',
    },
    QUERY_USER_INFORMATION: {
        reqFunct: reqFunct.QUERY_USER_INFORMATION,
        WorkerName: 'FOSqID02',
        ServiceName: 'FOSqID02_UserInformation',
        ClientSentTime: '0',
        Operation: 'Q',
    }
}

export default function SignUpScreen() {

    const history = useHistory();
    const { config, styles } = useContext(StoreContext)

    const { t } = useTranslation();

    const [email, setEmail] = useState('')
    const [visibleModalAuthen, setVisibleModalAuthen] = useState(false)
    const [timeOTP, setTimeOTP] = useState(0)
    const [typeModalAuthen, setTypeModalAuthen] = useState('')

    const validateStep1 = () => {
        if (email && email.length > 6) return true
        return false
    }

    const handleCreateAccount = () => {
        if (validateEmailRegex(email)) createAccount(email);
        else createAccount('', email);
    }

    const createAccount = (email, phone) => {
        const type = email ? 'email' : 'phone'
        const InputParams = ['open', type, email || phone, glb_sv.notifyOnesignal.userId || '', glb_sv.notifyOnesignal.userId || ''];
        sendRequest(ServiceInfo.CREATE_ACCOUNT, InputParams, createAccountResult, true, handleTimeoutCreateAccount, null, 5000)
        setTimeOTP(0)
    }

    const handleTimeoutCreateAccount = () => null

    const createAccountResult = (reqInfoMap, message) => {
        console.log("createAccountResult -> message", message)
        if (Number(message['Result']) === 0) {
            glb_sv.showAlert({
                title: t('common_notify'),
                content: message.Message,
                type: 'warn',
                showCancel: true,
                linkCallback: () => 
                message.Code === '010000' ? 
                    history.replace(routes.FORGET_PASS.path) : 
                    glb_sv.focusInput('sign-up'),
                cancelCallBack: () => glb_sv.focusInput('sign-up')
            })
            return
        } else {
            let jsondata = {}
            try {
                if (message['Data']) {
                    jsondata = JSON.parse(message['Data'])[0]
                }
            } catch (err) {
                console.error('getNewsResultProc error', err)
                return
            }
            glb_sv.credentials = {
                username: jsondata.c1,
                password: jsondata.c2,
            }
            localStorage.setItem(STORE_KEY.USER_ID, jsondata.c0)

            setVisibleModalAuthen(true)
            setTimeOTP(Number(jsondata.c3))
            glb_sv.focusInput('id_otp_authen', 100)
        }
    }

    const callBackLogin = (msg) => {
        history.goBack();
        history.goBack()
        if (glb_sv.objShareGlb['userInfo'].c9 === 'N') {
            glb_sv.showAlert({
                title: t('common_notify'),
                content: t('ekyc_notify_open_account'),
                type: 'info',
                showCancel: true,
                linkCallback: () => {
                    if (
                        glb_sv.dataEkycInfo &&
                        glb_sv.dataEkycInfo.c0 !== 'SEC' &&
                        glb_sv.dataEkycInfo.c4 &&
                        glb_sv.dataEkycInfo.c5 &&
                        glb_sv.dataEkycInfo.c6
                    ) {
                        history.push(routes.OPEN_ACCOUNT.path, {})
                    } else {
                        history.push(routes.OPEN_ACCOUNT.path, { method: 'normal' })
                    }
                },
            })
        }
    }

    const callbackResetPassword = () => {
        history.goBack()
    }


    return (
        <div className='menu__layout'>
            <HeaderComponent
                transparent
                title={t('common_register_info')}
                colorTitle={'var(--PRIMARY__CONTENT__COLOR)'} />
            <Form
                name="normal_login"
                className="common__form"
            >
                <Form.Item>
                    <Text className="common__form__input-label fs-small">
                        {t('email_or_phone')}
                    </Text>
                    <Input
                        allowClear
                        size="large"
                        className="input__component"
                        id='sign-up'
                        onChange={(e) => setEmail(e.target.value)}
                        value={email}
                        placeholder=""
                        autoFocus
                        onPressEnter={() => {
                            validateStep1() && handleCreateAccount()
                        }}
                    />
                </Form.Item>
            </Form>

            <Button
                size='large'
                onClick={handleCreateAccount}
                disabled={!validateStep1()}
                style={{
                    backgroundColor: validateStep1() ? 'var(--PRIMARY)' : styles.PRIMARY + '4d',
                    marginTop: dimensions.vertical(32),
                    marginRight: dimensions.moderate(12),
                    marginLeft: dimensions.moderate(12),
                    color: validateStep1() ? '#FFF' : 'var(--SECOND__CONTENT__COLOR)',
                    fontSize: fontSizes.normal
                }}
            >
                {t('create_account')}
            </Button>

            <ModalAuthenOtp
                time={timeOTP}
                visible={visibleModalAuthen}
                setVisible={setVisibleModalAuthen}
                callBackLogin={callBackLogin}
                type={typeModalAuthen}
                callbackResetPassword={callbackResetPassword}
            />

        </div>
    );
}

function validateEmailRegex(email) {
    const re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(String(email).toLowerCase());
}