// Khai báo ServiceInfo (nếu có)
const ServiceInfo = {
    GET_LIST_CONTRACT_RENEW: {
        reqFunct: reqFunct.GET_LIST_CONTRACT_RENEW,
        // WorkerName: 'ALTqLendingMargin',
        // ServiceName: 'ALTqLendingMargin_Online_1404',
        WorkerName: 'FOSqMargin',
        ServiceName: 'FOSqMargin_Online_1404',
        ClientSentTime: '0',
        Operation: 'Q',
        // TestInput: ['1', userInfo.actn_curr, '00']
    },
    GET_HIS_PAY_ON_ACCOUT_BY_SUB: {
        reqFunct: reqFunct.GET_HIS_PAY_ON_ACCOUT_BY_SUB,
        // WorkerName: 'ALTqCash',
        // ServiceName: 'ALTqCash_0201_1',
        WorkerName: 'FOSqCash',
        ServiceName: 'FOSqCash_0201_1',
        ClientSentTime: '0',
        Operation: 'Q',
        // TestInput: ["2", "888c000354", "00"]
    },
    GET_HIS_PAY_ON_ACCOUT_BY_ACT: {
        reqFunct: reqFunct.GET_HIS_PAY_ON_ACCOUT_BY_ACT,
        // WorkerName: 'ALTqCash',
        // ServiceName: 'ALTqCash_0201_1',
        WorkerName: 'FOSqCash',
        ServiceName: 'FOSqCash_0201_1',
        ClientSentTime: '0',
        Operation: 'Q',
        // TestInput: ["2", "888c000354", "%"]
    },
}

// Khai báo component
const StepOne = ({ setContractSelect, switchStep }) => {
    const { userInfo, setUserInfo } = useContext(StoreTrading)

    const { t } = useTranslation()
    // -------------------------------------------   Khai báo các state nội bộ component

    const [listContractRenew, setListContractRenew] = useState([])
    const [dataLastTranferAllSub, setDataLastTranferAllSub] = useState([])
    // -------------------------------------------   Dùng useEffect get các dữ liệu cần thiết còn thiếu
    const flag = true

    const colSpan = [1, 1, 1]
    useEffect(() => {
        if (userInfo.sub_list.length > 1) {
            setUserInfo({ ...userInfo, sub_curr: userInfo.sub_list[1] })
            glb_sv.userInfo.sub_curr = userInfo.sub_list[1]
        }
    }, [flag])

    useEffect(() => {
        // Prepare data
        getListContractRenew() // Lấy danh sách hợp đồng gia hạn

        const commonEvent = glb_sv.commonEvent.subscribe((msg) => {
            if (msg.type === eventList.LOGIN_SUCCESS) {
                // reCall service FOSq
                getListContractRenew()
            }
        })

        return () => {
            commonEvent.unsubscribe()
        }
    }, [userInfo.sub_curr])

    // useEffect(() => {
    //     getHisPayOnAccountByAct()
    // }, []);

    // -------------------------------------------   Viết các hàm để set lại bussiness form state. Nên viết đúng một chuẩn duy nhất để dễ control

    // -------------------------------------------   Khai báo các hàm gửi (request) lên server
    const getListContractRenew = () => {
        const inputParams = [userInfo.actn_curr, userInfo.sub_curr]
        sendRequest(ServiceInfo.GET_LIST_CONTRACT_RENEW, inputParams, handleGetListContractRenew)
    }
    const getHisPayOnAccountBySub = () => {
        const inputParams = ['2', userInfo.actn_curr, userInfo.sub_curr]
        sendRequest(ServiceInfo.GET_HIS_PAY_ON_ACCOUT_BY_SUB, inputParams, handleGetHisPayOnAccountBySub)
    }
    const getHisPayOnAccountByAct = () => {
        const inputParams = ['2', userInfo.actn_curr, '%']
        sendRequest(ServiceInfo.GET_HIS_PAY_ON_ACCOUT_BY_ACT, inputParams, handleGetHisPayOnAccountByAct)
    }
    // -------------------------------------------   Khai báo các hàm nhận (handle respone) dữ liệu từ server

    // -------------------------------------------   Khai báo các hàm nhận (handle respone) dữ liệu từ server
    const handleGetListContractRenew = (reqInfoMap, message) => {
        console.log('handleGetListContractRenew', reqInfoMap, message)
        // -- process after get result --
        if (Number(message['Result']) === 0) {
            setListContractRenew([])
            // ToastGlobal.show({
            //     type: 'warning',
            //     text2: message['Message']
            // })
            return
        } else {
            let jsondata = []
            try {
                jsondata = message['Data'] ? JSON.parse(message['Data']) : []
            } catch (err) {}
            console.log('handleGetListContractRenew', jsondata)
            setListContractRenew(jsondata)
        }
    }
    // -----------------------------------------
    const handleGetHisPayOnAccountBySub = (reqInfoMap, message) => {
        console.log('handleGetHisPayOnAccountBySub', reqInfoMap, message)
        // -- process after get result --
        if (Number(message['Result']) === 0) {
            console.warn('false handleGetHisPayOnAccountBySub', reqInfoMap, message['Data'], message['Message'])
            // ToastGlobal.show({
            //     type: 'warning',
            //     text2: message['Message']
            // })
            return
        } else {
            let jsondata = []
            try {
                jsondata = message['Data'] ? JSON.parse(message['Data']) : []
            } catch (err) {}
            // console.log(jsondata);
            // setDataLastTransfer(jsondata)
        }
    }
    //---------------------------------------------
    const handleGetHisPayOnAccountByAct = (reqInfoMap, message) => {
        console.log('handleGetHisPayOnAccountByAct', reqInfoMap, message)
        // -- process after get result --
        if (Number(message['Result']) === 0) {
            console.warn('false handleGetHisPayOnAccountByAct', reqInfoMap, message['Data'], message['Message'])
            // ToastGlobal.show({
            //     type: 'warning',
            //     text2: message['Message']
            // })
            return
        } else {
            let jsondata = []
            try {
                jsondata = message['Data'] ? JSON.parse(message['Data']) : []
            } catch (err) {}
            // console.log('result handleGetHisPayOnAccountByAct', jsondata);
            setDataLastTranferAllSub(jsondata)
        }
    }

    const ViewLastTransfer = ({ item, index }) => {
        return (
            <>
                <RowTableData
                    key={index}
                    type='table'
                    onClick={() => {
                        setContractSelect(item)
                        switchStep.next()
                    }}>
                    <ColTableData colSpan={8} text={moment(item.c3, 'DDMMYYYY').format('DD/MM/YYYY')} />
                    <ColTableData colSpan={8} text={moment(item.c29, 'DDMMYYYY').format('DD/MM/YYYY')} textAlign='center' />
                    <ColTableData colSpan={8} text={FormatNumber(item.c28, 0, 0)} textAlign='right' />
                </RowTableData>
            </>
        )
    }
    return (
        <div>
            <div className='pl-2' style={{overflow:'hidden'}}>
                {' '}
                <Account />
            </div>
            <div className='mt-1'>
                <RowTitleGroup hasDivider />
            </div>
            <RowTitleGroup text={t('select_contract')} />
            <HeaderList typeHeader='RENEW_MARGIN_CONTRACT' />
            <div className='pl-1'>
                {' '}
                {listContractRenew.length ? (
                    listContractRenew.map((item, i) => <ViewLastTransfer key={i} item={item} />)
                ) : (
                    <div style={{ textAlign: 'center', alignSelf: 'center', height: '200px' }}>
                        <img src={IC_FOLDER_NULL} />
                    </div>
                )}
            </div>
            {/* <FlatList
                data={listContractRenew}
                renderItem={ViewLastTransfer}
                keyExtractor={(item, index) => String(index)}
                ListEmptyComponent={EmptyView}
                style={{ marginBottom: dm.vertical(32), paddingHorizontal: dm.moderate(16) }}
            /> */}
        </div>
    )
}

export default StepOne
