									<Text style={{ color: 'white', fontSize: 9, fontWeight: fontWeights.semiBold, position: 'absolute', right: dimensions.halfIndent, zIndex: 10 }}>{bid_ask_ratio.current.ask ? bid_ask_ratio.current.ask + '%' : ''}</Text>
