import React, { memo } from 'react'
import { useTranslation } from 'react-i18next';
import { IMAGE } from '../../utils'
import { dimensions } from '../../styles';
import { useHistory } from "react-router-dom";
import { Row } from 'antd'

const BlockFeature = (props) => {
    const { item = {} } = props
    const { t } = useTranslation();
    let history = useHistory()
    //-------------------------------
    const onSelectFeature = () => {
        history.push(item.screen?.path, {})
    }
    return (
        <div
            onClick={onSelectFeature}
        >
            <Row className="center__item">
                    {IMAGE[888][item.icon]}
            </Row>
            <Row style={{justifyContent: 'center'}}>
                <span className="fs__small" style={{ color: 'var(--PRIMARY__CONTENT__COLOR)', textAlign: 'center', paddingTop: 8, fontSize: 'var(--fs_small)' }}>{t(item.title)}</span>
            </Row>
        </div>
    )
}
const UI = {
    BlockFeature: {
        flex: 1,
        alignSelf: "center",
        height: 120,
        paddingHorizontal: 8,
        paddingVertical: 8,
        // borderWidth: 1,
    },
    circleBackground: {
        height: dimensions.indent * 1.5,
        width: dimensions.indent * 1.5,
        background: '#f8f8f8',
        borderRadius: '50%',
        lineHeight: `${dimensions.indent * 1.5 - 7}px`,
        paddingLeft: 5
    }
}

export default memo(BlockFeature)