import { useEffect, useState, useRef } from 'react';
import { reqFunct, sendRequest } from '../../utils';
import RowNews from './RowNews'
import dayjs from 'dayjs'
import { useTranslation } from 'react-i18next';
import { Spin } from 'antd';

const ServiceInfo = {
    QUERY_NEW_DATA: {
        reqFunct: reqFunct.QUERY_NEW_DATA,
        WorkerName: 'FOSqNews',
        ServiceName: 'FOSqNews_NewsInfo',
        Operation: 'Q'
    },
}

export default ({ stockCode }) => {

    const { t } = useTranslation()

    const [loading, setLoading] = useState(true)
    const [data, setData] = useState([])
    const newsList = useRef([])

    useEffect(() => {
        setLoading(true);
        getNewData();
    }, [stockCode])

    const getNewData = () => {
        const inputParams = ['03', stockCode];
        sendRequest(ServiceInfo.QUERY_NEW_DATA, inputParams, handleGetNewDataResult, false);
    }

    const handleGetNewDataResult = (reqInfoMap, message) => {
        setLoading(false);
        if (Number(message['Result']) === 0) {
            return
        } else {
            let jsondata
            try {
                if (message['Data']) {
                    jsondata = JSON.parse(message['Data'])
                    setData(jsondata);
                } else {
                    setData([]);
                }
            } catch (err) {
                console.log(err)
                jsondata = []
            }
        }
    }

    return (
        <div >
            {loading ? <Spin className='icon_center' /> :
                (data.length ?
                    data.map(item => <RowNews
                        key={item.c0}
                        link={item.c3}
                        title={item.c4}
                        subtitle={item.c9 + ' ∙ ' + dayjs(item.c6, 'YYYYMMDDHHmmss').format('DD/MM HH:mm:ss')}
                        uri={item.c7} />)
                    : <span style={{ color: 'var(--SECOND__CONTENT__COLOR)' }}>{t('common_NoDataFound')}</span>)

            }
        </div>
    )
}