import React, { useContext, memo, useEffect, useState, useRef } from 'react'
import { StoreContext } from '../../store';
import { MatchList, ListBuySell, ColumnChartPrice } from '../../components/stock-info'
import { Divider } from '../../basic-components';
import { dimensions } from '../../styles';
import { glb_sv, eventList, subcribeFunct, subcribeFunctWithControl, RealTimeHandler } from '../../utils';
import throttle from 'lodash/throttle';
import { useTranslation } from 'react-i18next';
import { useHistory } from 'react-router-dom'
import { routes } from 'navigation/routes';
import { Col } from 'antd'

const Quotes = ({ stockCode }) => {
    const { styles } = useContext(StoreContext)
    return (
        <div className='flex flex-1 flex-direction-column' style={{ backgroundColor: 'var(--PRIMARY__BG__COLOR)' }}>
            <ListBuySell stockCode={stockCode} />
            <Divider styles={styles} />
            <ShortTimeMatch stockCode={stockCode} />
        </div>
    )
}

const ShortTimeMatch = memo(({ stockCode }) => {
    let history = useHistory()
    const { styles } = useContext(StoreContext);
    const [stock, setStock] = useState(glb_sv.StockMarket[stockCode] ? glb_sv.StockMarket[stockCode] : { EP: [] })
    const { t } = useTranslation();
    const throttled = useRef(throttle(() => {
        if (glb_sv.StockMarket[stockCode]) setStock({ ...glb_sv.StockMarket[stockCode] })
    }, 500, { trailing: true }))

    const stockSubcribe = new RealTimeHandler('STOCK_INFO');

    useEffect(() => {
        if (stockCode && glb_sv.StockMarket[stockCode]) {
            glb_sv.StockMarket[stockCode].EP = []
            subcribeFunct(null, 10000, 'GET_HIST', ['MDDS|EP'], [stockCode], [0], [500])
            // stockSubcribe.subscribe({
            //     time: 10000,
            //     onTimeout: null,
            //     Command: 'GET_HIST',
            //     topic: ['MDDS|EP'],
            //     value: [stockCode],
            //     fromseq: [0],
            //     size: [500]
            // })
        }
        const eventMarket = glb_sv.eventMarket.subscribe(msg => {
            if (msg.type === eventList.SUB_STOCK && msg.message === 'EP' && msg.msgKey === stockCode) {
                requestAnimationFrame(() => throttled.current())
            }
            if (msg.type === eventList.SUBSCRIBE_DONE && msg.msgKey === stockCode) {
                requestAnimationFrame(() => throttled.current())
            }
            if (msg.type === eventList.GET_DATA_EP_DONE && msg.msgKey === stockCode) {
                requestAnimationFrame(() => throttled.current())
            }
        });
        throttled.current();
        return () => {
            stockSubcribe.clearRealTimeListener({ unsubscribe: true });
            eventMarket.unsubscribe();
            throttled.current.cancel();
        }
    }, [])


    const changeTimeMatch = () => {
        history.push(routes.TIME_MATCHING_FULL.path, { stockCode })
    }

    return <>
        <div className='flex flex-direction-row' style={UI.container}>
            <Col span={12}>
                <MatchList stock={stock} changeTimeMatch={changeTimeMatch} />
            </Col>
            <Col span={12}>
                <ColumnChartPrice style={styles} stockCode={stockCode} EP={stock.EP} t={t} />
            </Col>

        </div>
        <div >
            {stock.EP.length ? <div className='flex justify-content-center' onClick={changeTimeMatch} style={{ marginTop: dimensions.halfIndent / 2 }}>
                <span className='fs__normal' style={{ color: 'var(--PRIMARY)' }}>{t('view_all') + ' >'}</span>
            </div> : null}
        </div>
    </>
}, () => true);

const UI = {
    container: {
        paddingRight: dimensions.halfIndent,
        paddingLeft: dimensions.halfIndent,
        marginTop: dimensions.halfIndent,
        marginBottom: dimensions.halfIndent,
    }
};

export default memo(Quotes)