import React, { useEffect, useState, useRef, useCallback, useContext } from 'react';
import { useHistory } from 'react-router-dom'
import { useTranslation } from 'react-i18next';
import { glb_sv, eventList, RealTimeHandler, FormatNumber, FBigNumber } from '../../utils';
import { fontSizes, dimensions, fontWeights } from '../../styles';
import { routes } from 'navigation/routes';

const MarketCapTable = ({ data }) => {
    const [dataSource, setDataSource] = useState(data);
    const sortKey = useRef(''); // lưu field cần sort
    const sortType = useRef(true); // lưu thứ tự sort
    const SubStockRealTime = new RealTimeHandler('MARKET_CAP');
    const { t } = useTranslation();
    const stkRef = useRef([])

    useEffect(() => {
        stkRef.current = data.map(x => x.name)
        SubStockRealTime.subscribe({
            topic: ['MDDS|SI', 'MDDS|TP'],
            value: stkRef.current
        })
        setDataSource(data);
        SubStockRealTime.listen(msg => {
            if (msg.type === eventList.SUB_STOCK && data.length > 0 && stkRef.current.includes(msg.msgKey)) {
                const newSource = [...data];
                const index = newSource.findIndex(x => x.name === msg.msgKey);
                if (!!newSource[index] && !!newSource[index].percent) {
                    newSource[index].percent = FormatNumber(glb_sv.StockMarket[msg.msgKey]?.t31_incr_per, 2, 1, '')
                }
                if (!!sortKey.current && sortKey.current.length !== 0) {
                    setDataSource(sortTable(newSource));
                }
                setDataSource(newSource);
            }
        })
        return () => {
            SubStockRealTime.unsubscribe({
                topic: ['MDDS|SI', 'MDDS|TP'],
                value: stkRef.current
            })
            SubStockRealTime.clearRealTimeListener({ unsubscribe: true });
        }
    }, [data])

    const renderItem = useCallback((item) => {
        return (
            <StockMarket item={item} />
        )
    });

    const sortTable = data => {
        return data.sort((a, b) => {
            if (a[sortKey.current] > b[sortKey.current]) return sortType.current ? 1 : -1
            if (a[sortKey.current] < b[sortKey.current]) return sortType.current ? -1 : 1
            return 0
        })
    }

    const handleOnSort = keyData => {
        sortKey.current = keyData;
        sortType.current = !sortType.current;
        setDataSource(sortTable([...dataSource]));
    }

    return (
        <div className='flex flex-1 flex-direction-column ml-3 mr-3' style={UI.container}>
            <div className='flex flex-direction-row'>
                <span style={{ width: '30%', color: 'var(--HEADER__CONTENT__COLOR)', fontSize: fontSizes.verySmall }}>{t('short_symbol')}</span>
                <span style={{ width: '30%', color: 'var(--HEADER__CONTENT__COLOR)', fontSize: fontSizes.verySmall, textAlign: 'center' }}>{t('percent')}</span>
                <span style={{ width: '40%', color: 'var(--HEADER__CONTENT__COLOR)', fontSize: fontSizes.verySmall, textAlign: 'right' }}>{t('market_cap')}</span>
            </div>
            {dataSource.map((item, index) => <StockMarket key={index} item={item} />)}
        </div>
    )
}

const getColorByRef = (value, ref) => {
    if (!value) return 'price-ref'
    if (value > ref) return 'price-up'
    if (value < ref) return 'price-down'
    return 'price-ref'
}

const StockMarket = ({ item }) => {
    const history = useHistory();

    return (
        <div className='flex flex-direction-row' style={UI.item} onClick={() => {history.push(routes.STOCK_INFO.path, { stockCode: item.name }); console.log('nhái à')}}>
            <span className={getColorByRef(glb_sv.StockMarket[item.name]?.t31_incr_per, 0)} style={{ ...UI.title, width: '30%', fontSize: item.name.length > 3 ? fontSizes.verySmall : fontSizes.small }}>{item.name}</span>
            <span className={getColorByRef(glb_sv.StockMarket[item.name]?.t31_incr_per, 0)} style={{ ...UI.title, width: '30%', textAlign: 'center', fontSize: fontSizes.small }}>
                {FormatNumber(glb_sv.StockMarket[item.name]?.t31_incr_per, 2, 0, '')}%
            </span>
            <span style={{ ...UI.title, width: '40%', color: 'var(--PRIMARY__CONTENT__COLOR)', textAlign: 'right', fontSize: FBigNumber(item.value * (glb_sv.StockMarket[item.name]?.t31 / glb_sv.StockMarket[item.name]?.ref || 1), { fixTo: 'B' }).length > 15 ? fontSizes.verySmall : fontSizes.small }}>
                {FBigNumber(item.value * (glb_sv.StockMarket[item.name]?.t31 / glb_sv.StockMarket[item.name]?.ref || 1), { fixTo: 'B' })}
            </span>
        </div>
    )
}

const UI = {
    container: {
        marginTop: dimensions.vertical(12),
        flex: 1,
        backgroundColor: 'var(--PRIMARY__BG__COLOR)',
    },
    title: {
        // fontSize: fontSizes.small,
        width: '33.33%',
        fontWeight: fontWeights.medium
    },
    item: {
        alignItems: 'center',
        flex: 0,
        paddingTop: dimensions.vertical(12),
        paddingBottom: dimensions.vertical(12),
        borderBottom: '1px solid var(--DIVIDER__COLOR)'
    }
}

export default React.memo(MarketCapTable);
